import React from "react";

import { Modal, Button, Form, Spinner, Col } from "react-bootstrap";
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from "react-icons/io";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import ReactTable from "react-table-6";
import { addParticipation, addParticipationNote } from "../../../redux/actions/teacherDashboard/teacherActions";
import { connect } from "react-redux";
import Participation from "./participation";
import moment from "moment";
class StudentTable extends React.Component {
	constructor() {
		super();
		this.state = {
			showNote: false,
			behaviour: "",
			note: "",
			student: {},
			savingNote: false,
			behaviours: [],
			date: new Date(),
		};
	}

	handleParticipationNote = async () => {
		var months = ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"];
		var creativities = this.state.student.participation;
		if (creativities.length > 0) {
			var participation = {};
			creativities.map((b) => {
				if (b.className === this.props.sub.class && b.sectionName === this.props.sub.section && b.subjectName === this.props.sub.subject) {
					participation = b;
				}
			});
			if (participation.status.length > 0) {
				const data = {
					className: this.props.sub.class,
					sectionName: this.props.sub.section,
					subjectName: this.props.sub.subject,
					status: participation.status,
					teacherId: this.props.teacher._id,
					studentId: this.state.student._id,
					note: this.state.note,
					date:
						new Date(this.state.date).getFullYear() +
						"-" +
						months[new Date(this.state.date).getMonth()] +
						"-" +
						new Date(this.state.date).getDate(),
				};
				const check = await this.props.addParticipationNote(data);
				console.log(check);
				if (check) {
					this.setState({
						loading: false,
					});
					if (check.type === "success") {
						toaster.notify("Note added", {
							duration: 3000,
						});
						this.setState({
							note: "",
							showNote: false,
							student: {},
						});
					} else {
						toaster.notify(check.error, {
							duration: 3000,
						});
					}
				}
			} else {
				toaster.notify("Please select a level of participation first", {
					duration: 3000,
				});
			}
		} else {
			toaster.notify("Please select a level of participation first", {
				duration: 3000,
			});
		}
	};

	handleChange = (e) => {
		this.setState({
			note: e.target.value,
		});
	};

	handleParticipation = async (e, f, p) => {
		var months = ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"];
		const data = {
			className: this.props.sub.class,
			sectionName: this.props.sub.section,
			subjectName: this.props.sub.subject,
			status: e,
			teacherId: this.props.teacher._id,
			studentId: f._id,
			note: this.state.note,
			date: moment(new Date()).format("YYYY-MM-DD"),
			push: p,
		};
		const check = await this.props.addParticipation(data);
		console.log(check);
		if (check) {
			this.setState({
				loading: false,
			});
			if (check.type === "success") {
				toaster.notify("Participation updated", {
					duration: 3000,
				});
				this.setState({
					note: "",
				});
			} else {
				toaster.notify(check.error, {
					duration: 3000,
				});
			}
		}
	};

	render() {
		console.log(this.props.teacher);
		const school = this.props.teacher.schoolInfo;
		var students = [];
		this.props.teacher.students.map((stud) => {
			if (stud.class === this.props.sub.class) {
				students.push(stud);
			}
		});
		const data = students;
		return (
			<div className='students-container'>
				<div className='students-header'>
					<div className='students-stats'>
						<h1>Student Participation</h1>
						<div className='officer-stats'>
							<p>
								TOTAL STUDENTS: <span>{students.length}</span>
							</p>
						</div>
					</div>
				</div>
				<div className='students-body'>
					{data.map((data) => {
						return (
							<div className='student-content-table'>
								<img className='student-photo' src={"/api/file/show/" + data.photo.filename} />
								<p className='student-name'>{data.name}</p>
								<Participation
									data={data}
									addParticipation={this.handleParticipation}
									participations={data.participation}
									teacherId={this.props.teacher._id}
									date={moment(this.state.date).format("YYYY-MM-DD")}
								/>
								<div className='student-behaviour-note-container'>
									<div
										onClick={() => {
											var note = "";
											data.participation.map((part) => {
												if (
													part.teacherId === this.props.teacher._id &&
													part.date === moment(this.state.date).format("YYYY-MM-DD")
												) {
													note = part.note;
												}
											});
											console.log(note);
											this.setState({
												showNote: true,
												note: note,
												student: data,
											});
										}}
										className='student-behaviour-note'>
										<i class='fas fa-sticky-note'></i>
									</div>
								</div>
							</div>
						);
					})}
				</div>
				<Modal
					show={this.state.showNote}
					onHide={() =>
						this.setState({
							showNote: false,
						})
					}
					backdrop='static'
					keyboard={false}
					centered>
					<Modal.Header closeButton>
						<Modal.Title>Note</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<textarea name='note' onChange={this.handleChange} value={this.state.note} className='note-text-area' />
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleParticipationNote} className='submit-action' variant='success'>
							<FaCheck />
							Save
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addParticipation: (data) => dispatch(addParticipation(data)),
		addParticipationNote: (data) => dispatch(addParticipationNote(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentTable);
