import React from 'react';
import './gallery.css';
import { connect } from 'react-redux'
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';
import { Form, Modal, Spinner, Button } from 'react-bootstrap';
import { FaCheck } from 'react-icons/fa';

import addImg from '../../assets/more (5).svg';
import imagepicker from '../../assets/photo.png';

class Gallery extends React.Component {
    constructor() {
        super();
        this.state = {
            showAdd: false,
            photo: [],
            showImage: "",
            images: [
                '//placekitten.com/1500/500',
                '//placekitten.com/4000/3000',
                '//placekitten.com/800/1200',
                '//placekitten.com/1500/1500',
            ],
            photoIndex: 0,
            isOpen: false,
        }
    }

    handleShowSecton = (e) => {
        this.setState({
            showSections: true,
            showClass: false,
            classValue: e
        })
    }

    handleShowSectonInfo = (e) => {
        this.setState({
            showSections: false,
            sectionValue: e,
        })
    }

    handleSectionBack = () => {
        this.setState({
            showSections: true
        })
    }

    handleClassBack = () => {
        this.setState({
            showClass: true
        })
    }

    handleShowAdd = () => {
        this.setState({
            showAdd: true
        })
    }

    handleAddPhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                photo: f,
                showImage: URL.createObjectURL(f),
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleUploadPhoto = () => {

    }

    render() {

        return (
            <>
                <div className="gallery-container">
                    <div className="header">
                        <div className="left">
                            <h1>School Gallery</h1>
                            <p>TOTAL GALLERY IMAGES: <span>5</span></p>
                        </div>
                        <div className="right">
                            <div className="add-button" onClick={this.handleShowAdd}>
                                <img src={addImg} />
                                <p>Add New Image</p>
                            </div>
                        </div>
                    </div>
                    <div className="body">
                        <div className="gallery-list">
                            {
                                this.state.images.map(image => {
                                    return (
                                        <div onClick={() => this.setState({
                                            isOpen: true,
                                        })} className="gallery-box">
                                            <img src={image} />
                                        </div>
                                    )
                                })
                            }
                        </div>
                        {
                            this.state.isOpen
                                ?
                                <Lightbox
                                    mainSrc={this.state.images[this.state.photoIndex]}
                                    nextSrc={this.state.images[(this.state.photoIndex + 1) % this.state.images.length]}
                                    prevSrc={this.state.images[(this.state.photoIndex + this.state.images.length - 1) % this.state.images.length]}
                                    onCloseRequest={() => this.setState({ isOpen: false })}
                                    onMovePrevRequest={() =>
                                        this.setState({
                                            photoIndex: (this.state.photoIndex + this.state.images.length - 1) % this.state.images.length,
                                        })
                                    }
                                    onMoveNextRequest={() =>
                                        this.setState({
                                            photoIndex: (this.state.photoIndex + 1) % this.state.images.length,
                                        })
                                    }
                                />
                                :
                                null
                        }
                    </div>
                </div>
                <Modal
                    show={this.state.showAdd}
                    onHide={() => this.setState({
                        showAdd: false
                    })}
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Upload Picture</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group >
                                <div className="image-container">
                                    <div className="image-picker2">
                                        <input id="image-picker" onChange={e => this.handleAddPhoto(e)} type="file" accept="image/*" />
                                        {
                                            this.state.showImage === ""
                                                ?
                                                <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                :
                                                <img htmlFor="#image-picker" src={this.state.editshowImage} />
                                        }
                                    </div>
                                </div>
                            </Form.Group>
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.changingPassword
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleEditPassword} variant="success"><FaCheck />Add</Button>
                        }
                    </Modal.Footer>
                </Modal>
            </>
        )
    }
}
function mapStateToProps(state) {
    return {
    };
}

function mapDispatchToProps(dispatch) {
    return {
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Gallery);