import React from 'react';
import './participation.style.css';

import { Form } from 'react-bootstrap';

import moment from 'moment';

class MyParticipation extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            subject: '',
            format: '',
            date: '',
            month: '',
            participationArray: [],
            filteredArray: [],
            participations: []
        }
    }

    componentDidMount = () => {
        this.setState({
            participationArray: this.props.participation,
            loading: false
        })
    }

    handleSetSubject = (e) => {
        this.setState({
            subject: e.target.value
        })
    }

    handleFormat = (e) => {
        if (this.state.subject === '') {
            alert('Please select subject');
        }
        else {
            this.setState({
                format: e.target.value
            })
        }
    }

    handleSetDate = (e) => {
        this.setState({
            date: e.target.value
        }, () => {
            this.handleFilter('Day')
        })
    }

    handleSetMonth = (e) => {
        this.setState({
            month: e.target.value
        }, () => {
            this.handleFilter('Month');
        })
    }

    handleFilter = (format) => {

        var filteredArray = [], participations = [];

        if (format === 'Day') {
            this.state.participationArray.map((item) => {
                if (item.date === this.state.date) {
                    filteredArray.push(item);
                }
            })
        }

        if (format === 'Month') {
            this.state.participationArray.map((item) => {
                if (item.date.substring(0, item.date.length - 3) === this.state.month) {
                    filteredArray.push(item);
                }
            })
        }

        filteredArray.map((item) => {
            var parti = {};
            parti.date = item.date;
            parti.participation = item.status;
            participations.push(parti);
        })

        this.setState({
            participations: participations
        })
    }


    render() {
        return (
            <div className="my-participation-container">
                <div className="my-participation-wrapper">
                    {
                        this.state.loading
                            ?
                            null
                            :
                            <>
                                <div className="select-sub">
                                    <Form.Group className="container-fluid">
                                        <Form.Label>Select Subject</Form.Label>
                                        <Form.Control as="select" name="subject" onChange={this.handleSetSubject}>
                                            <option selected hidden>Choose...</option>
                                            <option value="Mathematics">Mathematics</option>
                                            <option value="Science">Science</option>
                                            <option value="English">English</option>
                                            <option value="Islamic Studies">Islamic Studies</option>
                                            <option value="Social Studies">Social Studies</option>
                                            <option value="Information Technology">Information Technology</option>
                                            <option value="Life Skills">Life Skills</option>
                                            <option value="Music">Music</option>
                                            <option value="Sports">Sports</option>
                                            <option value="Arts">Arts</option>
                                        </Form.Control>
                                    </Form.Group>
                                </div>

                                <div className="select-sub">
                                    <Form.Group className="container-fluid">
                                        <Form.Label>Select Format</Form.Label>
                                        <div key='inline-radio'>
                                            <Form.Check inline label="Day" type='radio' name="format" value="Day" onChange={this.handleFormat} />
                                            <Form.Check inline label="Monthly" type='radio' name="format" value="Monthly" onChange={this.handleFormat} />
                                        </div>
                                    </Form.Group>
                                </div>

                                {/* Select Day */}
                                {
                                    this.state.format === 'Day'
                                        ?
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Select Date</Form.Label>
                                            <Form.Control type="date" name="date" onChange={this.handleSetDate} />
                                        </Form.Group>
                                        :
                                        null
                                }

                                {/* Select Month */}
                                {
                                    this.state.format === 'Monthly'
                                        ?
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Select Month</Form.Label>
                                            <Form.Control type="month" name="month" onChange={this.handleSetMonth} />
                                        </Form.Group>
                                        :
                                        null
                                }

                                <div className='behaviour-chart'>
                                    {
                                        this.state.participations.map((item) => (
                                            <div className="behaviour-card">
                                                <div className="behaviour-date">
                                                    <p>{moment(item.date).format('LL')}</p>
                                                </div>
                                                <div className='behaviour-list'>
                                                    <div className={item.participation.includes("raised questions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-check-circle'></i>
                                                        <p>raised questions</p>
                                                    </div>
                                                    <div className={item.participation.includes("answered questions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-check-circle'></i>
                                                        <p>answered questions</p>
                                                    </div>
                                                    <div className={item.participation.includes("participated in class discussion") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-check-circle'></i>
                                                        <p>participated in class discussion</p>
                                                    </div>
                                                </div>
                                            </div>
                                        ))
                                    }
                                </div>
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default MyParticipation;