import React from "react";
import "./schoolInfo.css";
import { Form } from "react-bootstrap";

import { connect } from "react-redux";
import "toasted-notes/src/styles.css";

class SchoolInfo extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			showImage: "/api/file/show/" + props.teacher.schoolInfo.logo.filename,
			schoolName: props.teacher.schoolInfo.name,
			schoolBranch: props.teacher.schoolInfo.branch,
			schoolEmail: props.teacher.schoolInfo.email,
			schoolAddress: props.teacher.schoolInfo.address,
			schoolContact: props.teacher.schoolInfo.contact,
			schoolFax: props.teacher.schoolInfo.fax,
			schoolPrincipal: props.teacher.schoolInfo.principal.length > 0 ? props.teacher.schoolInfo.principal[0].name : "",
			schoolDistrictDirector: props.teacher.schoolInfo.districtOfficers.length > 0 ? props.teacher.schoolInfo.districtOfficers[0].name : "",
			schoolWebsite: props.teacher.schoolInfo.website,
			loading: false,
			imageEdited: false,
		};
	}

	render() {
		return (
			<div className='school-info-container'>
				<h1>School Information</h1>
				<Form>
					<div className='container-group'>
						<Form.Group>
							<div className='image-container'>
								<div className='image-picker'>
									<img htmlFor='#image-picker' src={this.state.showImage} />
								</div>
							</div>
						</Form.Group>
					</div>
					<div className='container-group'>
						<Form.Group className='container-fluid'>
							<Form.Label>School Name</Form.Label>
							<Form.Control name='schoolName' type='text' placeholder='Enter School Name' value={this.state.schoolName} disabled />
						</Form.Group>
						<Form.Group className='container-fluid'>
							<Form.Label>School Branch Name</Form.Label>
							<Form.Control
								name='schoolBranch'
								type='text'
								placeholder='Enter School Branch Name'
								value={this.state.schoolBranch}
								disabled
							/>
						</Form.Group>
					</div>

					<div className='container-group'>
						<Form.Group className='container-fluid'>
							<Form.Label>School Address</Form.Label>
							<Form.Control name='schoolAddress' type='text' value={this.state.schoolAddress} disabled />
						</Form.Group>
						<Form.Group className='container-fluid'>
							<Form.Label>School Email</Form.Label>
							<Form.Control name='schoolEmail' type='text' value={this.state.schoolEmail} disabled />
						</Form.Group>
					</div>
					<div className='container-group'>
						<Form.Group className='container-fluid'>
							<Form.Label>School Contact No.</Form.Label>
							<Form.Control name='schoolContact' type='text' value={this.state.schoolContact} disabled />
						</Form.Group>
						<Form.Group className='container-fluid'>
							<Form.Label>School Fax</Form.Label>
							<Form.Control name='schoolFax' type='text' value={this.state.schoolFax} disabled />
						</Form.Group>
						<Form.Group className='container-fluid'>
							<Form.Label>School Website</Form.Label>
							<Form.Control name='schoolWebsite' type='text' value={this.state.schoolWebsite} disabled />
						</Form.Group>
					</div>
					<div className='container-group'>
						<Form.Group className='container-fluid'>
							<Form.Label>School Principle</Form.Label>
							<Form.Control type='text' disabled value={this.state.schoolPrincipal} />
						</Form.Group>
						<Form.Group className='container-fluid'>
							<Form.Label>School District Director</Form.Label>
							<Form.Control type='text' disabled value={this.state.schoolDistrictDirector} />
						</Form.Group>
					</div>
				</Form>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return { teacher: state.teacher.profileData };
}

export default connect(mapStateToProps)(SchoolInfo);
