import React from 'react';

import './attemptQuiz.css';

import check from '../assets/check.svg';
import wrong from '../assets/wrong.json';
import correct from '../assets/433-checked-done.json';
import levelup from '../assets/21671-level-up.json';
import champion from '../assets/champion.json';
import downgrade from '../assets/downgrade.json';
import {saveResult} from '../../../redux/actions/studentDashboard/studentActions';
import MathJax from 'react-mathjax-preview';
import {connect} from 'react-redux';

import Lottie from "lottie-react-web";

var timer ;

class AttemptQuiz extends React.Component {
    
    constructor(props){
        super(props);

        this.state = {
            timer : 0,
            totalCorrect : 0,
            totalWrong : 0,
            totalMarks : 0,
            level1 : [],
            level2 : [],
            level3 : [],
            level4 : [],
            level5 : [],
            attemptedlevel1 : [],
            attemptedlevel2 : [],
            attemptedlevel3 : [],
            attemptedlevel4 : [],
            attemptedlevel5 : [],
            criteria : 0,
            attempted : [],
            attemptedQuestions : [],
            currentLevel : 1,
            currentQuestion : {},
            showExplanation : false,
            showCorrect : false,
            showLevelUpgrade : false,
            showLevelDowngrade : false,
            isEnd : false,
            compliments : ["Keep Going !", "Good", "Great Job !", "Well Done !", "Excellent !", "Fantastic !"],
            consecutive : 0,
            consecutiveWrong : 0,
        }
    }

    componentDidMount() {
        for(var i = 1 ; i <= 5; i++) {
            this.props.questionList["level" + i].map((question, index) => {
                question.index = index;
            })
        }

        this.setState({
            level1 : this.props.questionList.level1,
            level2 : this.props.questionList.level2,
            level3 : this.props.questionList.level3,
            level4 : this.props.questionList.level4,
            level5 : this.props.questionList.level5
        },() => {
            this.handleStartTimer();
            this.handleGenerateRandom();
        })
    }

    handleStartTimer = () => {
        timer = setInterval(() => {
            this.setState({
                timer : this.state.timer + 1
            })
        }, 1000);
    }

    handleFormatTime = () => {
        const getSeconds = `0${(this.state.timer % 60)}`.slice(-2)
        const minutes = `${Math.floor(this.state.timer / 60)}`
        const getMinutes = `0${minutes % 60}`.slice(-2)
        const getHours = `0${Math.floor(this.state.timer / 3600)}`.slice(-2)

        return `${getHours} : ${getMinutes} : ${getSeconds}`
    }

    handleGenerateRandom = () => {
        if (this.state.totalMarks >= 100) {
            this.handleEndPractice();
        }
        else if (this.state.currentLevel === 1 && this.state.criteria === 10) {
            this.handleLevelUpgrade();
        }
        else if (this.state.currentLevel === 2 && this.state.criteria === 15){
            this.handleLevelUpgrade();
        }
        else if (this.state.currentLevel === 3 && this.state.criteria === 15){
            this.handleLevelUpgrade();
        }
        else if (this.state.currentLevel === 4 && this.state.criteria === 30){
            this.handleLevelUpgrade();
        }
        else if (this.state.currentLevel === 5 && this.state.criteria === 30){
            this.handleLevelUpgrade();
        }
        else {
            var random = Math.round(Math.random() * (this.state["level" + this.state.currentLevel.toString()].length-1));
            var found = false;

            this.state["attemptedlevel" + this.state.currentLevel].map((question) => {
                if(question.index === random)
                {
                    found = true;
                    return;
                }
            })

            if (found) {
                this.handleGenerateRandom();
            }

            else {
                var currentQuestion = this.state["level" + this.state.currentLevel.toString()][random];
                var i = 0;
                currentQuestion.options.map((option) => {
                    option.selected = false;
                    option.index = i;
                    i++;
                })
                this.setState({
                    currentQuestion : currentQuestion
                })
            }
        }
    }

    handleLevelUpgrade = () => {
        // End Quiz Session
        if (this.state.currentLevel === 5) {
            this.handleEndPractice();
        }
        else {
            this.setState({
                consecutiveWrong : 0,
                showLevelUpgrade : true,
                currentLevel : this.state.currentLevel + 1,
                consecutive : 0,
                criteria : 0
            },() => {
                setTimeout(() => {
                    this.setState({
                        showLevelUpgrade : false
                    },() => {
                        this.handleGenerateRandom();
                    })
                }, 3000);
            })
        }
    }

    handleLevelDowngrade = () => {
        this.setState({
            consecutiveWrong : 0,
            showLevelDowngrade : true,
            currentLevel : this.state.currentLevel - 1,
            consecutive : 0,
            criteria : 0
        },() => {
            setTimeout(() => {
                this.setState({
                    showLevelDowngrade : false
                },() => {
                    this.handleGenerateRandom();
                })
            }, 3000)
        })
    }

    handleEndPractice =async () => {
        this.setState({
            isEnd : true
        },async() => {
            clearInterval(timer);
            var data={
                practiceId:this.props.practiceId,
                topic:this.props.topic,
                chapter:this.props.chapter,
                subject:this.props.subject,
                studentId:this.props.studentId,
                duration:this.state.timer,
                score:this.state.totalMarks,
                correct:this.state.totalCorrect,
                wrong:this.state.totalWrong,
                level1:this.state.attemptedlevel1,
                level2:this.state.attemptedlevel2,
                level3:this.state.attemptedlevel3,
                level4:this.state.attemptedlevel4,
                level5:this.state.attemptedlevel5,
            }
            console.log(data);
            const check = await this.props.saveResult(data);
            if(check!==null)
            {
                console.log(check);
            }
        })
    }

    handleGetAnswer = (option) => {
        var currentQuestion = this.state.currentQuestion;
        if(currentQuestion.choice === 'single')
        {
            currentQuestion.options.map((op) => {
                if(op.index === option.index)
                {
                    op.selected = true
                }
                else{
                    op.selected = false
                }
            })
            this.setState({
                currentQuestion : currentQuestion
            })
        }

        else if(currentQuestion.choice === 'multiple')
        {
            currentQuestion.options.map((op) => {
                if(op.index === option.index)
                {
                    op.selected = !op.selected
                }
            })
            this.setState({
                currentQuestion : currentQuestion
            })
        }
    }

    handleValidateAnswer = () => {
        var success=0;
        var selected=0;
        
        this.state.currentQuestion.options.map((option) => {
            if(option.answer===true && option.answer===option.selected)
            {
                success+=1;
            }
            if(option.selected)
            {
                selected+=1;
            }
        });

        if(success===0 || success!==selected) //Check for wrong answer
        {
            this.setState({
                showExplanation : true,
                consecutive : 0,
                ["attemptedlevel" + this.state.currentLevel] : [...this.state["attemptedlevel" + this.state.currentLevel], this.state.currentQuestion],
                totalWrong : this.state.totalWrong + 1,
                consecutiveWrong : this.state.consecutiveWrong + 1
            },() => {
                if (this.state.consecutiveWrong === 3 && this.state.currentLevel !== 1) {
                    this.handleLevelDowngrade();
                }
            })
        }

        // Check for correct answer
        else{
            if (this.state.currentLevel === 1) {
                this.setState({
                    criteria : this.state.criteria + 2,
                    totalMarks : this.state.totalMarks + 2
                })
            }
            if (this.state.currentLevel === 2) {
                this.setState({
                    criteria : this.state.criteria + 3,
                    totalMarks : this.state.totalMarks + 3
                })
            }
            if (this.state.currentLevel === 3) {
                this.setState({
                    criteria : this.state.criteria + 3,
                    totalMarks : this.state.totalMarks + 3
                })
            }
            if (this.state.currentLevel === 4) {
                this.setState({
                    criteria : this.state.criteria + 6,
                    totalMarks : this.state.totalMarks + 6
                })
            }
            if (this.state.currentLevel === 5) {
                this.setState({
                    criteria : this.state.criteria + 6,
                    totalMarks : this.state.totalMarks + 6
                })
            }
            this.setState({
                showCorrect : true,
                consecutive : this.state.consecutive + 1,
                consecutiveWrong : 0,
                ["attemptedlevel" + this.state.currentLevel] : [...this.state["attemptedlevel" + this.state.currentLevel], this.state.currentQuestion],
                totalCorrect : this.state.totalCorrect + 1,
                // totalMarks : this.state.totalMarks + parseInt(this.state.currentQuestion.marks) 
            },() => {
                setTimeout(() => {
                    this.setState({
                        showCorrect : false
                    }, () => {
                        this.handleGenerateRandom();
                    })
                },2000);
            })
        }
    }

    handleGotIt = () => {
        this.setState({
            showExplanation : false
        },() => {
            this.handleGenerateRandom();
        })
    }

    render() {
        return (
            <>
            {
                !this.state.isEnd
                ?            
                <div className = "attemptQuiz-container">
                    <div className = "question-board">
                        {
                            this.state.showExplanation
                            ?
                            <div className = "explanation-box">
                                <div className = "wrong-head">
                                    <div>
                                        <Lottie
                                            options={{ animationData: wrong, loop : false }}
                                            width={100}
                                            height={100}
                                            style={{margin : 0}}
                                        />
                                        <h1>Sorry, Incorrect</h1>
                                    </div>
                                    <button id = "gotit" type = "button" onClick = {this.handleGotIt}>Got it !</button>
                                    <button id = "endses" type = "button" onClick = {this.handleEndPractice}>End Practice</button>
                                </div>
                                <div className = "review-ques">
                                    <h3>REVIEW - Your answer</h3>
                                    <MathJax math={this.state.currentQuestion.question}/>
                                    <div className = "box-options">
                                        {
                                            this.state.currentQuestion && this.state.currentQuestion.options && this.state.currentQuestion.options.map((option, index) => {
                                                if(option.selected)
                                                {
                                                    return(
                                                        <>
                                                            <input type="radio" name="options" id={ "option" + index } style={{display : "none"}}/>
                                                            <label htmlFor={"option"+index} className = "options">
                                                                <div className = "option-overlay">
                                                                    <img src={check} alt="check"/>
                                                                </div>
                                                                <MathJax math={option.value}/>
                                                            </label>
                                                        </>
                                                    )
                                                }
                                                else{
                                                return(
                                                    <>
                                                    <input type="radio" name="options" id={ "option" + index } style={{display : "none"}}/>
                                                    <label htmlFor={"option"+index} className = "options">
                                                        <MathJax math={option.value}/>
                                                    </label>
                                                    </>
                                                )
                                                }
                                            })
                                        }
                                    </div>
                                    <h3>EXPLANATION</h3>
                                    {
                                        <MathJax math={this.state.currentQuestion.explanation}/>
                                    }
                                </div>
                            </div>
                            :
                            <>
                            {
                                this.state.showCorrect
                                ?
                                <div className = "correct-msg-box">
                                    <Lottie
                                        options={{ animationData: correct, loop : false }}
                                        width={400}
                                        height={400}
                                        style={{margin : 0}}
                                    />
                                    <h1>
                                        {
                                            this.state.compliments[(this.state.consecutive % 6)]
                                        }
                                    </h1>
                                </div>
                                :
                                <>
                                {
                                    this.state.showLevelUpgrade
                                    ?
                                    <div className = "level-up-box">
                                        <Lottie
                                            options={{ animationData: levelup, loop : false }}
                                            width={500}
                                            height={500}
                                            style={{margin : 0}}
                                        />
                                        <h1>Upgrading to Level {this.state.currentLevel}</h1>
                                    </div>
                                    :
                                    <>
                                    {
                                        this.state.showLevelDowngrade
                                        ?
                                        <div className = "level-down-box">
                                            <Lottie
                                                options={{ animationData: downgrade, loop : false }}
                                                width={500}
                                                height={500}
                                                style={{margin : 0}}
                                            />
                                            <h1>Downgrade to Level {this.state.currentLevel}</h1>
                                        </div>
                                        :
                                        <div className = "question-box">
                                            <MathJax math={this.state.currentQuestion.question}/>
                                            <div className = "box-options">
                                                {
                                                    this.state.currentQuestion && this.state.currentQuestion.options && this.state.currentQuestion.options.map((option, index) => {
                                                        
                                                        return(
                                                            <div>
                                                                <input type="radio" name="options" id={ "option" + index } style={{display : "none"}} onChange={()=>this.handleGetAnswer(option)}/>
                                                                <label htmlFor={"option"+index} className = "options">
                                                                    {
                                                                        option.selected
                                                                        ?
                                                                        <div className = "option-overlay">
                                                                            <img src={check} alt="check"/>
                                                                        </div>
                                                                        :
                                                                        null
                                                                    }
                                                                    <MathJax math={option.value}/>
                                                                </label>
                                                            </div>
                                                        )
                                                    })
                                                }
                                            </div>
                                            <div className = "button-options">
                                                <button id = "submit" type="submit" onClick = {this.handleValidateAnswer}>Submit</button>
                                                <button id = "endses" type="submit" onClick = {this.handleEndPractice}>End Practice</button>
                                            </div>
                                        </div>
                                    }
                                    </>
                                }
                                </>
                            }
                            </>    
                        }
                    </div>
                    <div className = "stats-board">
                        <div className = "stat-box timer-box">
                            <p>Time Elapsed</p>
                            <h1>{this.handleFormatTime()}</h1>
                        </div>
                        <div className = "stat-box">
                            <div className = "left bg-green">    
                                <p>Correct Questions</p>
                            </div>
                            <div className = "right">
                                <h1>{ this.state.totalCorrect }</h1>
                            </div>
                        </div>
                        
                        <div className = "stat-box">
                            <div className = "left bg-red">    
                                <p>Wrong Questions</p>
                            </div>
                            <div className = "right">
                                <h1>{ this.state.totalWrong }</h1>
                            </div>
                        </div>

                        <div className = "stat-box">
                            <div className = "left bg-blue">    
                                <p>Total Score</p>
                            </div>
                            <div className = "right">
                                <h1>{ this.state.totalMarks }</h1>
                            </div>
                        </div>
                    </div>
                </div>
                :
                <div className = "endQuiz-container">

                    <div className = "time-stat">
                        <h3>You've completed in</h3>
                        <h1>{this.handleFormatTime()}</h1>
                    </div>
                    <Lottie
                        options={{ animationData: champion, loop : false }}
                        width={350}
                        height={350}
                        style={{margin : 0}}
                    />
                    <div className = "data-stat">
                        <div className = "stat-box">
                            <div className = "left bg-green">    
                                <p>Correct Questions</p>
                            </div>
                            <div className = "right">
                                <h1>{ this.state.totalCorrect }</h1>
                            </div>
                        </div>
                            
                        <div className = "stat-box">
                            <div className = "left bg-red">    
                                <p>Wrong Questions</p>
                            </div>
                            <div className = "right">
                                <h1>{ this.state.totalWrong }</h1>
                            </div>
                        </div>

                        <div className = "stat-box">
                            <div className = "left bg-blue">    
                                <p>Total Score</p>
                            </div>
                            <div className = "right">
                                <h1>{ this.state.totalMarks }</h1>
                            </div>
                        </div>
                    </div>
                </div>
            }
            </>
        )
    }
}

function mapDispatchToProps(dispatch) {
    return {
        saveResult: (data) => dispatch(saveResult(data)),
    }
}

export default connect(null, mapDispatchToProps)(AttemptQuiz);