import React from 'react';

import './subjectAnalytics.css';

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import logo from '../../assets/iigp.png';
import man from '../../assets/man.png';
import downArrow from '../../assets/down-arrow.png';

import { Link } from 'react-router-dom';

import maths from '../assets/math.svg'; import science from '../assets/science.svg'; import english from '../assets/english.svg'; import islamic from '../assets/islamic.svg'; import computer from '../assets/computer.svg'; import skills from '../assets/skills.svg'; import arts from '../assets/arts.svg'; import sports from '../assets/sports.svg'; import music from '../assets/music-note.svg'; import social from '../assets/social.svg';
import FloralLoading from '../../loadingScreen/floralLoading';

import { BsLaptop, BsClipboard, BsListOl, BsGraphUp } from 'react-icons/bs'
import Usage from './components/usage/usage';
import Progress from './components/progress/progress';
import ScoresList from './components/scores/scores';
import QuestionLogs from './components/questionLogs/questionLogs';

import { connect } from 'react-redux';
import { fetchUser } from '../../../../redux/actions/authActions';
import { fetch_student_profile_data } from '../../../../redux/actions/studentDashboard/studentActions';

import axios from 'axios';
import StudentNavbar from '../../websiteComponents/Navbar/navbar.component';

class SubjectAnalytics extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            tab: 1,
            analyticsData: [],
            subject: ''
        }
    }

    componentDidMount = async () => {
        this.handleGet();
    }

    handleGet = async () => {
        this.setState({
            tab: 0
        })
        const currentUser = await this.props.fetchUser();
        if (currentUser) {

            const data = {
                studentId: currentUser.ref_id,
                subject: this.props.match.params.subject
            }

            var response = await axios.post("/api/analytics/subject", data);
            if (response.data !== null) {
                if (response.data.type === 'success') {
                    this.setState({
                        analyticsData: response.data.data,
                        subject: this.props.match.params.subject,
                        loading: false,
                        tab: 1
                    })
                }
            }
            else {
                this.setState({
                    subject: this.props.match.params.subject,
                    loading: false
                })
            }
        }
    }

    render() {

        return (
            <>
                {
                    this.state.loading
                        ?
                        <FloralLoading />
                        :
                        <div className="sub-analytics-container">
                            <StudentNavbar student={this.props.student} />
                            <div className="learning-breadcrumb">
                                <p>My Learning Analysis / {this.props.match.params.subject.substring(0, 1).toUpperCase() + this.props.match.params.subject.substring(1, this.props.match.params.subject.length)}</p>
                            </div>
                            <div className="learning-choose-subject-breadcrumb">
                                <OwlCarousel className="owl-theme" items={7} dots={false}>
                                    <Link to={'/dashboard/analytics/mathematics'} className="subject-option" onClick={this.handleGet}>
                                        <img src={maths} alt="subject" />
                                        <p>Mathematics</p>
                                    </Link>

                                    <Link to={'/dashboard/analytics/science'} className="subject-option" onClick={this.handleGet}>
                                        <img src={science} alt="subject" />
                                        <p>Science</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={english} alt="subject" />
                                        <p>English</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={islamic} alt="subject" />
                                        <p>Islamic Studies</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={social} alt="subject" />
                                        <p>Social Studies</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={computer} alt="subject" />
                                        <p>Information Technology</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={skills} alt="subject" />
                                        <p>Life Skills</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={sports} alt="subject" />
                                        <p>Sports</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={music} alt="subject" />
                                        <p>Music</p>
                                    </Link>

                                    <Link to={'#'} className="subject-option">
                                        <img src={arts} alt="subject" />
                                        <p>Arts</p>
                                    </Link>
                                </OwlCarousel>
                            </div>
                            <div className="select-analytics-type">
                                <div className={this.state.tab === 1 ? "tab-type active" : "tab-type"} onClick={() => this.setState({ tab: 1 })}>
                                    <h1><BsLaptop /></h1>
                                    <p>USAGE</p>
                                </div>
                                <div className={this.state.tab === 2 ? "tab-type active" : "tab-type"} onClick={() => this.setState({ tab: 2 })}>
                                    <h1><BsClipboard /></h1>
                                    <p>SCORES</p>
                                </div>
                                <div className={this.state.tab === 3 ? "tab-type active" : "tab-type"} onClick={() => this.setState({ tab: 3 })}>
                                    <h1><BsListOl /></h1>
                                    <p>QUESTIONS</p>
                                </div>
                                <div className={this.state.tab === 4 ? "tab-type active" : "tab-type"} onClick={() => this.setState({ tab: 4 })}>
                                    <h1><BsGraphUp /></h1>
                                    <p>PROGRESS</p>
                                </div>
                            </div>
                            <div className="report-whiteboard">
                                {
                                    this.state.tab === 0
                                        ?
                                        null
                                        :
                                        <>
                                            {
                                                this.state.tab === 1
                                                    ?
                                                    <Usage Adata={this.state.analyticsData} />
                                                    :
                                                    <>
                                                        {
                                                            this.state.tab === 2
                                                                ?
                                                                <ScoresList Adata={this.state.analyticsData} />
                                                                :
                                                                <>
                                                                    {
                                                                        this.state.tab === 3
                                                                            ?
                                                                            <QuestionLogs Adata={this.state.analyticsData} />
                                                                            :
                                                                            <Progress Adata={this.state.analyticsData} />
                                                                    }
                                                                </>
                                                        }
                                                    </>
                                            }
                                        </>
                                }
                            </div>
                        </div>
                }
            </>
        )
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        student: state.student.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetchUser: () => dispatch(fetchUser()),
        fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SubjectAnalytics);