import React from 'react';

import folderImg from '../../assets/folderIcon.svg';

class ChooseSubject extends React.Component {
    render() {
        const subjects = this.props.subjects;
        return (
            <div className="chooseClass-container">
                {
                    subjects && subjects.map(sub => {
                        return (
                            <div className="classes-cardFolder-container" onClick={() => this.props.toggleShowChapter(sub)}>
                                <div className="classes-cardFolder-img">
                                    <img src={folderImg} alt="folder-icon" />
                                </div>
                                <div className="classes-cardFolder-name">
                                    <p>{sub.subjectName}</p>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
}

export default ChooseSubject;
