import React from 'react';
import './usage.css';

import Chart from "react-apexcharts";

import { BsPencil, BsClock, BsPuzzle } from 'react-icons/bs'
import JuicerLoading from '../juicerLoading/juicerLoading';

import axios from 'axios';

class Usage extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            loading : true,
            noPractice : false,
            visited : [],

            // usage stats
            totalAnswered : 0,
            totalTime : 0,
            totalSkills : 0,

            // Practice by chapters
            labels : [],
            series : [],

            // Practice by topics
            totalRight : [],
            totalWrong : [],
            totalAttempts : [],
            categories : []
        }
    }

    componentDidMount = async() => {
        console.log(this.props.Adata);
        if (this.props.Adata.length > 0) {
            this.handleLastTenTopics(this.props.Adata);
            var totalAnswered = 0, totalTime = 0;

            this.props.Adata.map((item) => {
                totalAnswered = totalAnswered + (item.correct + item.wrong);
                totalTime = totalTime + (parseInt(item.duration));

                if (!this.state.labels.includes(item.topic)) {
                    this.setState({
                        labels : [...this.state.labels, item.topic]
                    },() => {
                        var ans = this.handleOccurance(item.topic, this.props.Adata);
                        this.setState({
                            series : [...this.state.series, ans]
                        })
                    })
                }
            })

            this.setState({
                totalAnswered : totalAnswered,
                totalTime : totalTime,
                loading: false
            })
        }
        else {
            this.setState({
                noPractice : true
            })
        }
    }

    handleOccurance = (value, array) => {
        var count = 0;
        array.map((item) => {
            if(item.topic === value){
                count++;
            }
        })
        return count;
    }

    handleLastTenTopics = (array) => {
        var categories = [], right = [], wrong = [], attempts = [], count = 0;
        
        if (array.length > 10) {
            while(count <= 10)
            {
                categories.push(array[count].topic);
                right.push(array[count].correct);
                wrong.push(array[count].wrong);
                attempts.push((array[count].correct + array[count].wrong));
                count++;
            }

            this.setState({
                categories : categories,
                totalRight : right,
                totalWrong : wrong,
                totalAttempts : attempts
            })
        }

        else {
            
            array.map((item) => {
                categories.push(item.topic);
                right.push(item.correct);
                wrong.push(item.wrong);
                attempts.push((item.correct + item.wrong));
            })

            this.setState({
                categories : categories,
                totalRight : right,
                totalWrong : wrong,
                totalAttempts : attempts
            })
        }
    }


    render() {
        var series = this.state.series;

        var options = {
            labels: this.state.labels,
            tooltip:{
               enabled : false
            },
            chart: {
                width: 380,
                type: 'donut',
            },
            plotOptions: {
                pie: {
                    startAngle: -45
                }
            },
            dataLabels: {
                enabled: true
            },
            fill: {
                type: 'gradient',
            },
            legend: {
                formatter: function(val, opts) {
                    return val 
                }
            },
            title: {
                text: 'PRACTICE BY CHAPTERS',
            }
        };

        var series2 = [
            {
                name: 'Total Right',
                data: this.state.totalRight
            }, 
            {
                name: 'Total Attempted',
                data: this.state.totalAttempts
            }, 
            {
                name: 'Total Wrong',
                data: this.state.totalWrong
            }
        ];

        var options2 = {
            chart: {
              type: 'bar',
              height: 350
            },
            title: {
                text: 'PRACTICE BY LAST 10 TOPICS',
            },
            plotOptions: {
                bar: {
                    horizontal: false,
                    columnWidth: '55%',
                    endingShape: 'rounded'
                },
            },
            dataLabels: {
                enabled: false
            },
            stroke: {
                show: true,
                width: 2,
                colors: ['transparent']
            },
            xaxis: {
                categories: this.state.categories,
            },
            fill: {
                opacity: 1
            },
            tooltip: {
                y: {
                    formatter: function (val) {
                        return val 
                    }
                }
            }
        };


        return(
            <div className = "usage-container">
                {
                    this.state.loading
                    ?
                    <JuicerLoading />
                    :
                    <div className = "usage-board">
                        <div className = "usage-head">
                            <h1>USAGE DETAILS</h1>
                        </div>
                        <div className = "usage-stats">
                            <div className = "stats-box">
                                <div className = "left">
                                    <p id="pencil"><BsPencil/></p>
                                </div>
                                <div className = "right">
                                    <p>ANSWERED</p>
                                    <h1>{this.state.totalAnswered}</h1>
                                    <p>QUESTIONS</p>
                                </div>
                            </div>
                            <div className = "stats-box">
                                <div className = "left">
                                    <p id="clock"><BsClock/></p>
                                </div>
                                <div className = "right">
                                    <p>SPENT</p>
                                    <h1>{Math.round(this.state.totalTime / 60)} mins</h1>
                                    <p>PRACTICING</p>
                                </div>
                            </div>
                            <div className = "stats-box">
                                <div className = "left">
                                    <p id="puzzle"><BsPuzzle/></p>
                                </div>
                                <div className = "right">
                                    <p>PROGRESS IN SKILLS</p>
                                    <h1>{this.state.totalSkills}</h1>
                                    <p>MASTERED SKILLS</p>
                                </div>
                            </div>
                        </div>
                        <div className = "usage-charts">
                            <div className = "doughnut-chart">
                                <Chart options={options} series={series} type="donut" style = {{height : "90%", width : "90%"}}/>
                            </div>
                            <div className = "bar-chart">
                                <Chart options={options2} series={series2} type="bar" style = {{height : "90%", width : "90%"}}/>
                            </div>
                        </div>
                    </div>
                }
            </div>
        )
    }
}

export default Usage;