import React from "react";

export default class Entrepreneurship extends React.Component {
	constructor(props) {
		super(props);
		var entrepreneurships = [];
		props.entrepreneurships.map((entrepreneurship) => {
			if (entrepreneurship.teacherId === props.teacherId && entrepreneurship.date === props.date) {
				entrepreneurships = entrepreneurship.status;
			}
		});
		console.log(entrepreneurships);
		this.state = {
			entrepreneurships: entrepreneurships,
		};
	}

	add = (e) => {
		var entrepreneurships = this.state.entrepreneurships;
		var push;
		if (!entrepreneurships.includes(e)) {
			entrepreneurships.push(e);
			push = true;
		} else {
			entrepreneurships = entrepreneurships.filter((item) => {
				return item !== e;
			});
			push = false;
		}
		this.setState({
			entrepreneurships: entrepreneurships,
		});
		console.log(entrepreneurships);
		this.props.addEntrepreneurship(entrepreneurships, this.props.data, push);
	};
	render() {
		return (
			<div className='behaviour-chart'>
				<div className='behaviour-list'>
					<div
						className={this.state.entrepreneurships.includes("Presentation") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Presentation")}>
						<i class='fas fa-check-circle'></i>
						<p>Presentation</p>
					</div>
					<div
						className={this.state.entrepreneurships.includes("Member Responsibility") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Member Responsibility")}>
						<i class='fas fa-check-circle'></i>
						<p>Member Responsibility</p>
					</div>
					<div
						className={this.state.entrepreneurships.includes("Research") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Research")}>
						<i class='fas fa-check-circle'></i>
						<p>Research</p>
					</div>
					<div
						className={this.state.entrepreneurships.includes("Organization") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Organization")}>
						<i class='fas fa-check-circle'></i>
						<p>Organization</p>
					</div>
					<div
						className={this.state.entrepreneurships.includes("Cooperation") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Cooperation")}>
						<i class='fas fa-check-circle'></i>
						<p>Cooperation</p>
					</div>
				</div>
			</div>
		);
	}
}
