import React from 'react';

import './topicList.css';

import { Modal, Button, Form, Spinner } from 'react-bootstrap';
import { AiOutlinePlus } from 'react-icons/ai';

import penPaper from '../../assets/penPaper.svg';

import { connect } from 'react-redux';

import { addTopic } from '../../../../redux/actions/superAdminDashboard/superAdminSchoolActions';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

class TopicList extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            topics: ['Velocity', 'Momentum', 'Acceleration'],
            showModal: false,
            topicName: '',
            adding: false
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleSubmit = async () => {
        this.setState({
            adding: true
        })
        const data = {
            classId: this.props.class._id,
            subjectName: this.props.subject.subjectName,
            chapterName: this.props.chapter.chapterName,
            topicName: this.state.topicName
        }
        const check = await this.props.addTopic(data);
        if (check) {
            this.setState({
                adding: false
            })
            if (check.type === 'success') {
                toaster.notify("Topic Added", {
                    duration: 3000
                })
                this.setState({
                    chapterName: "",
                    adding: false,
                    showModal: false,
                })
            }
        }
    }

    render() {
        console.log(this.props);
        const classes = this.props.classes;
        return (
            <div className="topicList-container">

                <div className="topicList-header">
                    <Button variant="primary" onClick={() => this.setState({ showModal: true })}><AiOutlinePlus />Add Topic</Button>
                </div>
                {
                    classes && classes.map(clas => {
                        if (clas.className === this.props.class.className) {
                            return (
                                <>
                                    {
                                        clas.subjects.map(sub => {
                                            if (sub.subjectName === this.props.subject.subjectName) {
                                                return (
                                                    <>
                                                        {
                                                            sub.chapters.map(chapter => {
                                                                if (chapter.chapterName === this.props.chapter.chapterName) {
                                                                    return (
                                                                        <>
                                                                            {
                                                                                chapter.topics && chapter.topics.map(topic => {
                                                                                    return (
                                                                                        <div className="topicList-box">
                                                                                            <img src={penPaper} alt="" />
                                                                                            <div className="topic-name" onClick={() => this.props.toggleShowQuizMaker(topic)}>
                                                                                                <h1>{topic.topicName}</h1>
                                                                                                <p>{this.props.subject.subjectName}, Grade {this.props.class.className}</p>
                                                                                            </div>
                                                                                            <div className="topic-date">
                                                                                                <p>{topic.date}</p>
                                                                                            </div>
                                                                                        </div>
                                                                                    )
                                                                                })
                                                                            }
                                                                        </>
                                                                    )
                                                                }
                                                            })
                                                        }
                                                    </>
                                                )
                                            }
                                        })
                                    }
                                </>
                            )
                        }
                    })
                }

                <Modal
                    show={this.state.showModal}
                    onHide={() => this.setState({ showModal: false })}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Create Test</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Topic Name</Form.Label>
                            <Form.Control type="text" name="topicName" onChange={this.handleChange} />
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.adding
                                ?
                                <Spinner animation="border" role="status" variant="primary">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button variant="primary" onClick={this.handleSubmit}>Create</Button>
                        }
                    </Modal.Footer>
                </Modal>

            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        classes: state.superAdmin.classes
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addTopic: (data) => dispatch(addTopic(data)),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(TopicList);