import { FETCH_PARENT_PROFILE } from '../actions/parentDashboard/parentTypes';

const initialState = {
    profileData: null,
}

const parentReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_PARENT_PROFILE:
            return {
                profileData: action.payload,
            }
        // case SAVE_STUDENT_RESULT_SUCC:
        //     return {
        //         profileData: state.profileData,
        //     }
        // case SAVE_STUDENT_RESULT_ERR:
        //     return {
        //         profileData: state.profileData,
        //     }
        default:
            return state
    }
}

export default parentReducer;