import { FETCH_STUDENT_PROFILE,SAVE_STUDENT_RESULT_ERR,SAVE_STUDENT_RESULT_SUCC } from '../actions/studentDashboard/studentTypes';

const initialState = {
    profileData: null,
}

const studentReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_STUDENT_PROFILE:
            return {
                profileData: action.payload,
            }
        case SAVE_STUDENT_RESULT_SUCC:
            return {
                profileData: state.profileData,
            }
        case SAVE_STUDENT_RESULT_ERR:
            return {
                profileData: state.profileData,
            }
        default:
            return state
    }
}

export default studentReducer;