import React from "react";

import "./studentLearning.css";

import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import logo from "../assets/iigp.png";
import man from "../assets/man.png";
import downArrow from "../assets/down-arrow.png";
import cross from "../assets/cancel.svg";
import pdf from "../assets/pdf.svg";
import examIcon from "../assets/exam.svg";

import pdfIcon from "./assets/pdf.png";
import docIcon from "./assets/doc.png";
import pptIcon from "./assets/ppt.png";
import mp4Icon from "./assets/mp4.png";

import { Link } from "react-router-dom";

import { connect } from "react-redux";
import { fetchUser } from "../../../redux/actions/authActions";
import { fetch_student_profile_data } from "../../../redux/actions/studentDashboard/studentActions";

import maths from "./assets/math.svg";
import science from "./assets/science.svg";
import english from "./assets/english.svg";
import islamic from "./assets/islamic.svg";
import computer from "./assets/computer.svg";
import skills from "./assets/skills.svg";
import arts from "./assets/arts.svg";
import sports from "./assets/sports.svg";
import music from "./assets/music-note.svg";
import social from "./assets/social.svg";
import FloralLoading from "../loadingScreen/floralLoading";
import StudentNavbar from "../websiteComponents/Navbar/navbar.component";

class StudentLearning extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			showResource: false,
			showModal: false,
			practiceTopic: "",
			practiceSubject: "",
			practiceChapter: "",
			practiceId: "",
			resourceId: [],
			loading: true,
		};
	}

	componentDidMount = async () => {
		const currentUser = await this.props.fetchUser();
		if (currentUser) {
			const studentData = {
				studentId: currentUser.ref_id,
			};
			const result = await this.props.fetch_student_profile_data(studentData);
			if (result) {
				this.setState({
					loading: false,
				});
			}
		}
	};

	handlePracticeModal = (topic, chapter, subject) => {
		this.setState({
			showModal: true,
			practiceId: topic.practices[0],
			resourceId: topic.resources,
			practiceTopic: topic.topicName,
			practiceChapter: chapter,
			practiceSubject: subject,
		});
	};

	handleShowResource = () => {
		this.setState({
			showModal: false,
			showResource: true,
		});
	};

	render() {
		return (
			<>
				{this.state.loading ? (
					<FloralLoading />
				) : (
						<div className="student-learning-container">
							<StudentNavbar student={this.props.student} />
							<div className="learning-breadcrumb">
								<p>My Learning</p>
							</div>
							<div className="learning-choose-subject-breadcrumb">
								<OwlCarousel className="owl-theme" items={7} dots={false}>
									<Link
										to={"/dashboard/learning/mathematics"}
										className="subject-option"
									>
										<img src={maths} alt="subject" />
										<p>Mathematics</p>
									</Link>

									<Link
										to={"/dashboard/learning/science"}
										className="subject-option"
									>
										<img src={science} alt="subject" />
										<p>Science</p>
									</Link>

									<Link
										to={"/dashboard/learning/english language"}
										className="subject-option"
									>
										<img src={english} alt="subject" />
										<p>English</p>
									</Link>

									<Link
										to={"/dashboard/learning/islamic studies"}
										className="subject-option"
									>
										<img src={islamic} alt="subject" />
										<p>Islamic Studies</p>
									</Link>

									<Link
										to={"/dashboard/learning/social studies"}
										className="subject-option"
									>
										<img src={social} alt="subject" />
										<p>Social Studies</p>
									</Link>

									<Link
										to={"/dashboard/learning/information technology"}
										className="subject-option"
									>
										<img src={computer} alt="subject" />
										<p>Information Technology</p>
									</Link>

									<Link
										to={"/dashboard/learning/life skills"}
										className="subject-option"
									>
										<img src={skills} alt="subject" />
										<p>Life Skills</p>
									</Link>

									<Link
										to={"/dashboard/learning/sports"}
										className="subject-option"
									>
										<img src={sports} alt="subject" />
										<p>Sports</p>
									</Link>

									<Link
										to={"/dashboard/learning/music"}
										className="subject-option"
									>
										<img src={music} alt="subject" />
										<p>Music</p>
									</Link>

									<Link
										to={"/dashboard/learning/arts"}
										className="subject-option"
									>
										<img src={arts} alt="subject" />
										<p>Arts</p>
									</Link>
								</OwlCarousel>
							</div>
							<div className="learning-grade-breadcrumb">
								<p>
									Grade - {this.props.student.class} <span>/</span>{" "}
									{this.props.match.params.subject.substring(0,1).toUpperCase() + this.props.match.params.subject.substring(1, this.props.match.params.subject.length)}
								</p>
							</div>
							<div className="learning-chapter-list">
								{this.props.student.classInfo.subjects.map((subject) => {
									if (
										subject.subjectName.toLowerCase() ===
										this.props.match.params.subject.toLowerCase()
									) {
										return (
											<>
												{subject.chapters.map((chapter) => {
													if (chapter.topics.length > 0) {
														return (
															<div className="chapter-card">
																<div className="chapter-head">
																	<p>{chapter.chapterName}</p>
																</div>
																<div className="chapter-topics">
																	{chapter.topics.map((topic, index) => {
																		return (
																			<div className="topic-menu">
																				<div className="menu-left">
																					<p>
																						{topic.topicName}
																					</p>
																				</div>
																				<div className="menu-right">
																					<p
																						onClick={() =>
																							this.handlePracticeModal(
																								topic,
																								chapter.chapterName,
																								subject.subjectName
																							)
																						}
																					>
																						Learning Objective
                                        											</p>
																				</div>
																			</div>
																		);
																	})}
																</div>
															</div>
														);
													}
												})}
											</>
										);
									}
								})}
							</div>

							{this.state.showModal ? (
								<div className="modal-overlay-container">
									<div className="modal-box">
										<div className="modal-head">
											<p>{this.state.practiceTopic}</p>
											<img
												src={cross}
												alt="cross"
												onClick={() => this.setState({ showModal: false })}
											/>
										</div>

										<div className="modal-body">
											<div
												className="modal-option"
												onClick={this.handleShowResource}
											>
												<div>
													<img src={pdf} alt="pdf-icon" />
												</div>
												<p>Resources</p>
											</div>

											<Link
												to={
													"/dashboard/learning/" +
													this.state.practiceSubject +
													"/" +
													this.state.practiceChapter +
													"/" +
													this.state.practiceTopic +
													"/practice/" +
													this.state.practiceId
												}
												className="modal-option"
											>
												<div>
													<img src={examIcon} alt="pdf-icon" />
												</div>
												<p>Practice</p>
											</Link>
										</div>
									</div>
								</div>
							) : null}

							{this.state.showResource ? (
								<div className="modal-overlay-container">
									<div className="modal-box">
										<div className="modal-head">
											<p>{this.state.practiceTopic}</p>
											<img
												src={cross}
												alt="cross"
												onClick={() => this.setState({ showResource: false })}
											/>
										</div>

										<div className="modal-body">
											{this.state.resourceId.map((item) => {
												return (
													<Link
														to={
															"/dashboard/learning/" +
															this.state.practiceSubject +
															"/" +
															this.state.practiceChapter +
															"/" +
															this.state.practiceTopic +
															"/resource/" +
															item.resourceName
														}
														className="modal-option"
													>
														<div>
															{item.resourceName.split(".")[1].includes("pdf") ? (
																<img src={pdfIcon} alt="pdf-icon" />
															) : null}
															{item.resourceName.split(".")[1].includes("doc") ? (
																<img src={docIcon} alt="pdf-icon" />
															) : null}
															{item.resourceName.split(".")[1].includes("ppt") ? (
																<img src={pptIcon} alt="pdf-icon" />
															) : null}
															{item.resourceName.split(".")[1].includes("mp4") ? (
																<img src={mp4Icon} alt="pdf-icon" />
															) : null}
														</div>
														<p>{item.resourceName}</p>
													</Link>
												);
											})}
										</div>
									</div>
								</div>
							) : null}
						</div>
					)}
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		student: state.student.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		fetchUser: () => dispatch(fetchUser()),
		fetch_student_profile_data: (studentData) =>
			dispatch(fetch_student_profile_data(studentData)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentLearning);
