import React from 'react';

import './home.css';

import { connect } from 'react-redux';

import schoolImg from '../assets/school.svg';
import clock from '../assets/clock2.svg';
import studentImg from '../assets/student.svg';
import teacherImg from '../assets/teacher.svg';


class Home extends React.Component {
    render() {
        return (
            <div className="superadmin-home-container">
                <div className="stats-header">

                    <div className="stats-card-container">
                        <div className="stats-card-body">
                            <div className="img-part">
                                <div className="img-box y-box">
                                    <img src={schoolImg} alt="school-logo" />
                                </div>
                            </div>
                            <div className="stats-part">
                                <h1>Total Schools</h1>
                                <p>
                                    {
                                        this.props.schools
                                            ?
                                            this.props.schools.length
                                            :
                                            "Calculating..."
                                    }
                                </p>
                            </div>
                        </div>
                        <div className="stats-card-foot">
                            <img src={clock} alt="clock-img" />
                            <p>Updated</p>
                        </div>
                    </div>

                    <div className="stats-card-container">
                        <div className="stats-card-body">
                            <div className="img-part">
                                <div className="img-box b-box">
                                    <img src={teacherImg} alt="school-logo" />
                                </div>
                            </div>
                            <div className="stats-part">
                                <h1>Total Teachers</h1>
                                <p>1</p>
                            </div>
                        </div>
                        <div className="stats-card-foot">
                            <img src={clock} alt="clock-img" />
                            <p>Updated</p>
                        </div>
                    </div>

                    <div className="stats-card-container">
                        <div className="stats-card-body">
                            <div className="img-part">
                                <div className="img-box r-box">
                                    <img src={studentImg} alt="school-logo" />
                                </div>
                            </div>
                            <div className="stats-part">
                                <h1>Total Students</h1>
                                <p>250</p>
                            </div>
                        </div>
                        <div className="stats-card-foot">
                            <img src={clock} alt="clock-img" />
                            <p>Updated</p>
                        </div>
                    </div>

                    <div className="stats-card-container">
                        <div className="stats-card-body">
                            <div className="img-part">
                                <div className="img-box g-box">
                                    <img src={schoolImg} alt="school-logo" />
                                </div>
                            </div>
                            <div className="stats-part">
                                <h1>Other Stats</h1>
                                <p>NaN</p>
                            </div>
                        </div>
                        <div className="stats-card-foot">
                            <img src={clock} alt="clock-img" />
                            <p>Updated</p>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        schools: state.superAdmin.schools,
        districtOfficers: state.superAdmin.officerData
    }
}

export default connect(mapStateToProps)(Home);