import {
    FETCH_SCHOOLADMIN,
    FETCH_SCHOOL,
    ADD_CLASS_ERROR,
    ADD_CLASS_SUCC,
    ADD_TEACHER_ERROR,
    ADD_TEACHER_SUCC,
    EDIT_TEACHER_ERROR,
    EDIT_TEACHER_SUCC,
    EDIT_ADMINPASSWORD_ERROR,
    EDIT_ADMINPASSWORD_SUCC,
    ASSIGN_TEACHER_ERROR,
    ASSIGN_TEACHER_SUCC,
    REMOVE_TEACHER_ERROR,
    REMOVE_TEACHER_SUCC,
    ADD_STUDENT_ERROR,
    ADD_STUDENT_SUCC,
    EDIT_STUDENT_ERROR,
    EDIT_STUDENT_SUCC,
    REMOVE_STUDENT_ERROR,
    REMOVE_STUDENT_SUCC
} from "../actions/schoolAdminDashboard/schoolAdminTypes";

const initialState = {
    schoolAdmin: null,
    school: null,
    schoolError: null,
    schoolSucc: null
};

const schoolAdminReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_SCHOOLADMIN:
            return {
                schoolAdmin: action.payload,
                school: state.school,
                schoolError: null,
                schoolSucc: null
            };
        case FETCH_SCHOOL:
            return {
                schoolAdmin: state.schoolAdmin,
                school: action.payload,
                schoolError: null,
                schoolSucc: null
            };
        case ADD_CLASS_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case ADD_CLASS_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case ADD_TEACHER_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case ADD_TEACHER_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case EDIT_TEACHER_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case EDIT_TEACHER_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case EDIT_ADMINPASSWORD_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case EDIT_ADMINPASSWORD_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case ASSIGN_TEACHER_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case ASSIGN_TEACHER_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case REMOVE_TEACHER_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case REMOVE_TEACHER_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case ADD_STUDENT_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case ADD_STUDENT_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case EDIT_STUDENT_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case EDIT_STUDENT_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        case REMOVE_STUDENT_SUCC:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: null,
                schoolSucc: action.payload
            };
        case REMOVE_STUDENT_ERROR:
            return {
                schoolAdmin: state.schoolAdmin,
                school: state.school,
                schoolError: action.payload,
                schoolSucc: null
            };
        default:
            return state;
    }
};

export default schoolAdminReducer;
