import React from "react";

import "./examList.css";

import { Modal, Button, Form, Spinner } from "react-bootstrap";
import { AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine } from 'react-icons/ri';

import { connect } from "react-redux";

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { addExam, deleteExam } from "../../../../redux/actions/teacherDashboard/teacherActions";
import penPaper from "../../assets/penPaper.svg";

import moment from "moment";

class ExamList extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			showModal: false,
			examName: "",
			examDuration: "",
			startDate: "",
			endDate: "",
			adding: false,
		};
	}

	handleChange = (e) => {
		const { name, value } = e.target;
		this.setState({
			[name]: value,
		});
	};

	handleSubmit = async () => {

		this.setState({
			adding: true,
		});

		if (this.state.addExam === "") {
			toaster.notify("Please enter exam title", {
				duration: 3000,
			});
			this.setState({
				adding: false,
			});
		}
		else if (this.state.examDuration === "") {
			toaster.notify("Please enter exam duration", {
				duration: 3000,
			});
			this.setState({
				adding: false,
			});
		}

		else if (this.state.startDate === "") {
			toaster.notify("Please enter exam start date", {
				duration: 3000,
			});
			this.setState({
				adding: false,
			});
		}

		else if (this.state.endDate === "") {
			toaster.notify("Please enter exam end date", {
				duration: 3000,
			});
			this.setState({
				adding: false,
			});
		}

		else {

			const data = {
				schoolId: this.props.school,
				teacherId: this.props.teacher._id,
				className: this.props.class,
				sectionName: this.props.section,
				subjectName: this.props.subject,
				title: this.state.examName,
				duration: this.state.examDuration,
				startDate: this.state.startDate,
				endDate: this.state.endDate
			};

			const check = await this.props.addExam(data);

			if (check) {

				this.setState({
					adding: false,
				});

				if (check.type === "success") {
					toaster.notify("Exam Added", {
						duration: 3000,
					});
					this.setState({
						examName: "",
						examDuration: "",
						endDate: "",
						startDate: "",
						adding: false,
						showModal: false,
					});
				}
				else {
					toaster.notify("Exam Could Not Be Added", {
						duration: 3000,
					});
					this.setState({
						examName: "",
						examDuration: "",
						endDate: "",
						startDate: "",
						adding: false,
						showModal: false,
					});
				}
			}
		}
	};

	handleDeleteExam = async (examId) => {
		const data = {
			examId: examId,
			teacherId: this.props.teacher._id,
		}

		const check = await this.props.deleteExam(data);

		if (check) {
			if (check.type === "success") {
				toaster.notify("Exam Deleted", {
					duration: 3000,
				});
			}
			else {
				toaster.notify("Exam Could Not Be Deleted", {
					duration: 3000,
				});
			}
		}
	}

	render() {
		return (
			<div className='topicList-container'>
				<div className='topicList-header'>
					<Button variant='primary' onClick={() => this.setState({ showModal: true })}>
						<AiOutlinePlus />
						Create Exam
					</Button>
				</div>

				{this.props.teacher.exams.map((exam) => {
					if (exam.subjectName === this.props.subject && exam.className === this.props.class && exam.sectionName === this.props.section) {
						return (
							<div className='topicList-box'>
								<img src={penPaper} alt='' />
								<div className='topic-name' onClick={() => this.props.activateExamMaker(true, exam)}>
									<h1>{exam.title}</h1>
									<p>
										{exam.subjectName}, {exam.className} {exam.sectionName}
									</p>
								</div>
								<div className='topic-date'>
									<p>Date : {moment(new Date(exam.startDate)).format("LLLL")}</p>
									<span onClick={() => this.handleDeleteExam(exam._id)}><RiDeleteBinLine /></span>
								</div>
							</div>
						);
					}
				})}

				<Modal show={this.state.showModal} onHide={() => this.setState({ showModal: false })} backdrop='static' keyboard={false}>
					<Modal.Header closeButton>
						<Modal.Title>Create Exam</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Exam Name</Form.Label>
							<Form.Control type='text' name='examName' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Exam Duration (In Minutes)</Form.Label>
							<Form.Control type='number' min={0} name='examDuration' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Exam Start Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='startDate' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Exam End Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='endDate' onChange={this.handleChange} />
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button variant='primary' onClick={this.handleSubmit}>
							{
								this.state.adding
									?
									<Spinner animation="border" variant="light" />
									:
									"Create"
							}
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addExam: (data) => dispatch(addExam(data)),
		deleteExam: (data) => dispatch(deleteExam(data))
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(ExamList);
