import React from "react";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { saveSeating } from "../../../redux/actions/teacherDashboard/teacherActions";
import { connect } from "react-redux";
import GridLayout from "react-grid-layout";
import "react-grid-layout/css/styles.css";
import "react-resizable/css/styles.css";

class LessonTable extends React.Component {
	constructor() {
		super();
		this.state = {
			layout: [],
		};
	}

	componentDidMount() {
		this.props.teacher.schoolInfo.classes.map((clas) => {
			if ((clas.name = this.props.sub.class)) {
				clas.sections.map((sec) => {
					if (sec.name === this.props.sub.section) {
						sec.subjects.map((sub) => {
							if (sub.name === this.props.sub.subject) {
								var layout = sub.layout ? sub.layout : [];
								if (layout.length === 0) {
									this.props.teacher.students.map((stud) => {
										if (stud.class === this.props.sub.class && stud.section === this.props.sub.section) {
											var data = {
												i: stud._id,
												x: 0,
												y: 0,
												w: 2,
												h: 2,
												data: stud,
											};
											layout.push(data);
										}
									});
								} else {
									this.props.teacher.students.map((stud) => {
										if (stud.class === this.props.sub.class && stud.section === this.props.sub.section) {
											console.log(stud.class);
											var found = false;
											layout.map((l) => {
												if (stud._id === l.i) {
													found = true;
													console.log("Same");
												}
											});
											if (found === false) {
												var data = {
													i: stud._id,
													x: 0,
													y: 0,
													w: 2,
													h: 2,
													data: stud,
												};
												layout.push(data);
											}
										}
									});
								}
								this.setState({
									layout: layout,
								});
							}
						});
					}
				});
			}
		});
	}

	handleDragPoint = (drag) => {
		console.log("190 TDS", drag);
		var layout = this.state.layout;
		layout.map((l, index) => {
			if (l.i === drag[index].i) {
				l.x = drag[index].x;
				l.y = drag[index].y;
			}
		});
		this.setState({
			layout: layout,
		});
		console.log(layout);
	};

	handleSave = async () => {
		var data = {
			schoolId: this.props.teacher.schoolInfo._id,
			layout: this.state.layout,
			teacherId: this.props.teacher._id,
			className: this.props.sub.class,
			sectionName: this.props.sub.section,
			subjectName: this.props.sub.subject,
		};
		const check = await this.props.saveSeating(data);
		console.log(check);
		if (check) {
			this.setState({
				loading: false,
			});
			if (check.type === "success") {
				toaster.notify("Seating Updated", {
					duration: 3000,
				});
				this.setState({
					showDelete: false,
					lessonPlan: {},
				});
			} else {
				toaster.notify(check.error, {
					duration: 3000,
				});
			}
		}
	};

	render() {
		const school = this.props.teacher.schoolInfo;
		var data = [];
		this.props.teacher.students.map((stud) => {
			if (stud.class === this.props.sub.class && stud.section === this.props.sub.section) {
				data.push(stud);
			}
		});
		return (
			<div className='seating-container'>
				<div className='seating-header'>
					<div className='seating-stats'>
						<h1>
							Seating Arrangement of {this.props.sub.class} {this.props.sub.section}
						</h1>
						<div className='stats'>
							<p>
								TOTAL STUDENTS: <span>{data.length}</span>
							</p>
						</div>
					</div>
					<div className='seating-add'>
						<div onClick={this.handleSave} className='add-button'>
							<p>Save Seating</p>
						</div>
					</div>
				</div>
				<div className='seating-body'>
					<GridLayout
						onDrag={this.handleDragPoint}
						isResizable={false}
						isDroppable={false}
						className='drag-container'
						layout={this.state.layout}
						cols={8}
						rowHeight={30}
						width={1200}>
						{this.state.layout.map((l) => {
							return (
								<div key={l.i} className='seat'>
									<p>{l.data.name}</p>
								</div>
							);
						})}
					</GridLayout>
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		saveSeating: (data) => dispatch(saveSeating(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(LessonTable);
