import React from "react";

import "./studentStats.css";

import { Spinner } from "react-bootstrap";

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";
import { BsLaptop, BsClipboard, BsListOl, BsGraphUp } from "react-icons/bs";

import Usage from "./statsComponents/usage/usage";
import ScoresList from "./statsComponents/scores/scores";
import Progress from "./statsComponents/progress/progress";
import QuestionLogs from "./statsComponents/questionLogs/questionLogs";

import axios from "axios";

class StudentStats extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			tab: 1,
			analyticsData: [],
		};
	}

	componentDidMount = async () => {
		console.log(this.props.sub, this.props.stuDetails);
		const data = {
			studentId: this.props.stuDetails._id,
			subject: this.props.sub,
		};

		var response = await axios.post("/api/teacher/studentAnalytics/subject", data);
		if (response.data !== null) {
			console.log("RAN");
			if (response.data.type === "success") {
				console.log("ENTERED");
				this.setState({
					analyticsData: response.data.data,
					loading: false,
				});
			}
		} else {
			this.setState({
				loading: false,
			});
		}
	};

	render() {
		return (
			<div className='student-stats-container'>
				<div className='students-header'>
					<div className='students-stats'>
						<h1 onClick={() => this.props.goBack(false)}>
							<FaArrowLeft style={{ cursor: "pointer" }} /> IIGP Students
						</h1>
						<div className='officer-stats'>
							<p>
								TOTAL STUDENTS: <span>{this.props.stuDetails.name}</span>
							</p>
						</div>
					</div>
				</div>
				<div>
					{this.state.loading ? (
						<div style={{ width: "100%", height: "60vh", display: "flex", alignItems: "center", justifyContent: "center" }}>
							<Spinner animation='border' role='status' variant='primary'>
								<span className='sr-only'>Loading...</span>
							</Spinner>
						</div>
					) : (
						<>
							<div className='students-body'>
								<div className='teacher-analytics'>
									<div className='select-analytics-type'>
										<div
											className={this.state.tab === 1 ? "tab-type active" : "tab-type"}
											onClick={() => this.setState({ tab: 1 })}>
											<h1>
												<BsLaptop />
											</h1>
											<p>USAGE</p>
										</div>
										<div
											className={this.state.tab === 2 ? "tab-type active" : "tab-type"}
											onClick={() => this.setState({ tab: 2 })}>
											<h1>
												<BsClipboard />
											</h1>
											<p>SCORES</p>
										</div>
										<div
											className={this.state.tab === 3 ? "tab-type active" : "tab-type"}
											onClick={() => this.setState({ tab: 3 })}>
											<h1>
												<BsListOl />
											</h1>
											<p>QUESTIONS</p>
										</div>
										<div
											className={this.state.tab === 4 ? "tab-type active" : "tab-type"}
											onClick={() => this.setState({ tab: 4 })}>
											<h1>
												<BsGraphUp />
											</h1>
											<p>PROGRESS</p>
										</div>
									</div>

									<div className='report-whiteboard'>
										{this.state.tab === 1 ? (
											<Usage Adata={this.state.analyticsData} />
										) : (
											<>
												{this.state.tab === 2 ? (
													<ScoresList Adata={this.state.analyticsData} />
												) : (
													<>
														{this.state.tab === 3 ? (
															<QuestionLogs Adata={this.state.analyticsData} />
														) : (
															<Progress Adata={this.state.analyticsData} />
														)}
													</>
												)}
											</>
										)}
									</div>
								</div>
							</div>
						</>
					)}
				</div>
			</div>
		);
	}
}

export default StudentStats;
