import React from 'react';

import './announcements.style.css';

import axios from 'axios';

import Lottie from "lottie-react-web";
import loadAnimation from './assets/7774-loading.json';
import SingleAnnouncement from './singleAnnouncement/singleAnnouncement';

class MyAnnouncements extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            announcements: []
        }
    }

    componentDidMount = async () => {

        var result = [];
        // Filtering announcement if there is for any individual student
        this.props.announcements.map((item) => {
            if (item.type === 'individual') {
                if (item.studentId === this.props.studentId) {
                    result.push(item);
                }
            }
            else {
                result.push(item);
            }
        })

        this.setState({
            announcements: result,
            loading: false
        })

    }

    render() {
        return (
            <div className="my-announcements-container">
                <div className="announcements-wrapper">
                    {
                        this.state.loading
                            ?
                            <Lottie
                                options={{ animationData: loadAnimation }}
                                width={250}
                                height={250}
                            />
                            :
                            <>
                                {
                                    this.state.announcements.length > 0
                                        ?
                                        <>
                                            {
                                                this.state.announcements.map((item) => {
                                                    return <SingleAnnouncement announcement={item} />
                                                })
                                            }
                                        </>
                                        :
                                        <p>Nothing Found</p>
                                }
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default MyAnnouncements;