import axios from 'axios';
import { CREATE_OFFICER_SUCCESS, CREATE_OFFICER_ERROR, FETCH_OFFICERS, DELETE_OFFICER_ERROR, DELETE_OFFICER_SUCCESS } from './superAdminTypes';

export const fetchDistrictOfficers = () => async (dispatch) => {
    const res = await axios.get("/api/showList/DistrictOfficer");
    dispatch({ type: FETCH_OFFICERS, payload: res.data });
}

export const createDistrictOfficer = (officerData) => async (dispatch) => {
    const res = await axios.post("/api/createDistrictOfficer", officerData);
    if (res.data.type === "error") {
        dispatch({ type: CREATE_OFFICER_ERROR, payload: res.data.error });
        return res.data;
    } else {
        dispatch({ type: CREATE_OFFICER_SUCCESS, payload: res.data.success });
        const resp = await axios.get("/api/showList/DistrictOfficer");
        dispatch({ type: FETCH_OFFICERS, payload: resp.data });
        return res.data;
    }
};

export const deleteDistrictOfficer = (officerId) => async (dispatch) => {
    const res = await axios.post("/api/removeDistrictOfficer", officerId);
    console.log("DA 24", res.data);
    if (res.data.type === 'success') {
        // dispatch({ type: DELETE_OFFICER_SUCCESS, payload: res.data.success });
        const resp = await axios.get("/api/showList/DistrictOfficer");
        dispatch({ type: FETCH_OFFICERS, payload: resp.data });
        return res.data;
    }
    else {
        dispatch({ type: DELETE_OFFICER_ERROR, payload: res.data.error });
        return res.data;
    }
}