import React from 'react';

import './practice.css';

import countdown from './assets/countdownAnimation.json';

import MathJax from 'react-mathjax-preview';
import Lottie from "lottie-react-web";

import { Link } from 'react-router-dom';

import { connect } from 'react-redux';
import { fetchUser } from '../../../redux/actions/authActions';
import { fetch_student_profile_data } from '../../../redux/actions/studentDashboard/studentActions';

import axios from 'axios';
import AttemptQuiz from '../attemptQuiz/attemptQuiz';

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import maths from './assets/math.svg'; import science from './assets/science.svg'; import english from './assets/english.svg'; import islamic from './assets/islamic.svg'; import computer from './assets/computer.svg'; import skills from './assets/skills.svg'; import arts from './assets/arts.svg'; import sports from './assets/sports.svg'; import music from './assets/music-note.svg'; import social from './assets/social.svg';
import FloralLoading from '../loadingScreen/floralLoading';
import StudentNavbar from '../websiteComponents/Navbar/navbar.component';

class IIGPPractices extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			step: 0,
			startQuiz: false,
			showCountdown: false,
			questionList: {},
			loading: true,
			topic: "",
			chapter: "",
			subject: "",
			practiceId: ""
		}
	}

	componentDidMount = async () => {
		this.setState({
			topic: this.props.match.params.topic,
			chapter: this.props.match.params.chapter,
			subject: this.props.match.params.subject,
			practiceId: this.props.match.params.id
		})
		const currentUser = await this.props.fetchUser();
		if (currentUser) {
			const studentData = {
				studentId: currentUser.ref_id
			}
			const result = await this.props.fetch_student_profile_data(studentData);
			if (result) {
				const data = {
					practiceId: this.props.match.params.id
				}
				var response = await axios.post("/api/iigp/getPractice", data);
				this.setState({
					questionList: response.data,
					loading: false
				}, () => {
					setInterval(() => {
						if (this.state.step === 4) {
							this.setState({
								step: 0
							})
						}
						else {
							this.setState({
								step: this.state.step + 1
							})
						}
					}, 2000)
				})
			}
		}
	}

	handleStartQuiz = () => {
		this.setState({
			showCountdown: true
		})
		setTimeout(() => {
			this.setState({
				startQuiz: true
			})
		}, 3500);
	}

	render() {
		return (
			<>
				{this.state.loading ? (
					<FloralLoading />
				) : (
						<div className="student-practice-container">
							<StudentNavbar student={this.props.student} />
							<div className="learning-breadcrumb">
								<p>
									My Learning <span>/</span> Practice
                  				</p>
							</div>
							{!this.state.startQuiz ? (
								<>
									<div className="learning-choose-subject-breadcrumb">
										<OwlCarousel className="owl-theme" items={7} dots={false}>
											<Link
												to={"/dashboard/learning/mathematics"}
												className="subject-option"
											>
												<img src={maths} alt="subject" />
												<p>Mathematics</p>
											</Link>

											<Link
												to={"/dashboard/learning/science"}
												className="subject-option"
											>
												<img src={science} alt="subject" />
												<p>Science</p>
											</Link>

											<Link
												to={"/dashboard/learning/english language"}
												className="subject-option"
											>
												<img src={english} alt="subject" />
												<p>English</p>
											</Link>

											<Link
												to={"/dashboard/learning/islamic studies"}
												className="subject-option"
											>
												<img src={islamic} alt="subject" />
												<p>Islamic Studies</p>
											</Link>

											<Link
												to={"/dashboard/learning/social studies"}
												className="subject-option"
											>
												<img src={social} alt="subject" />
												<p>Social Studies</p>
											</Link>

											<Link
												to={"/dashboard/learning/information technology"}
												className="subject-option"
											>
												<img src={computer} alt="subject" />
												<p>Information Technology</p>
											</Link>

											<Link
												to={"/dashboard/learning/life skills"}
												className="subject-option"
											>
												<img src={skills} alt="subject" />
												<p>Life Skills</p>
											</Link>

											<Link
												to={"/dashboard/learning/sports"}
												className="subject-option"
											>
												<img src={sports} alt="subject" />
												<p>Sports</p>
											</Link>

											<Link
												to={"/dashboard/learning/music"}
												className="subject-option"
											>
												<img src={music} alt="subject" />
												<p>Music</p>
											</Link>

											<Link
												to={"/dashboard/learning/arts"}
												className="subject-option"
											>
												<img src={arts} alt="subject" />
												<p>Arts</p>
											</Link>
										</OwlCarousel>
									</div>
									<div className="learning-grade-breadcrumb">
										<p>
											Grade - {this.props.student.class} <span>/</span>{" "}
											{this.props.match.params.subject} <span>/</span>{" "}
											{this.props.match.params.chapter} <span>/</span>{" "}
											{this.props.match.params.topic}
										</p>
									</div>
									<div className="learning-preview-container">
										<div className="preview-box">
											{this.state.showCountdown ? (
												<>
													<Lottie
														options={{
															animationData: countdown,
															loop: false,
														}}
														width={300}
														height={300}
													/>
													<h1>Get Ready !</h1>
												</>
											) : (
													<>
														<div className="skill-preview-head">
															<p>Skill Preview</p>
															<div className="preview-step">
																<div
																	className={
																		this.state.step === 1 ||
																			this.state.step === 2 ||
																			this.state.step === 3 ||
																			this.state.step === 4
																			? "circle-step active"
																			: "circle-step"
																	}
																></div>
																<div
																	className={
																		this.state.step === 2 ||
																			this.state.step === 3 ||
																			this.state.step === 4
																			? "circle-step active"
																			: "circle-step"
																	}
																></div>
																<div
																	className={
																		this.state.step === 3 ||
																			this.state.step === 4
																			? "circle-step active"
																			: "circle-step"
																	}
																></div>
																<div
																	className={
																		this.state.step === 4
																			? "circle-step active"
																			: "circle-step"
																	}
																></div>
															</div>
														</div>
														<div className="skill-preview-body">
															{this.state.step === 1 ? (
																<div className="skill-box">
																	<div className="skill-question">
																		<MathJax
																			math={
																				this.state.questionList.level1[0]
																					.question
																			}
																		/>
																	</div>
																</div>
															) : (
																	<>
																		{this.state.step === 2 ? (
																			<div className="skill-box">
																				<div className="skill-question">
																					<MathJax
																						math={
																							this.state.questionList.level2[0]
																								.question
																						}
																					/>
																				</div>
																				<div className="skill-options"></div>
																			</div>
																		) : (
																				<>
																					{this.state.step === 3 ? (
																						<div className="skill-box">
																							<div className="skill-question">
																								<MathJax
																									math={
																										this.state.questionList
																											.level2[3].question
																									}
																								/>
																							</div>
																							<div className="skill-options"></div>
																						</div>
																					) : (
																							<>
																								{this.state.step === 4 ? (
																									<div className="skill-box">
																										<div className="skill-question">
																											<MathJax
																												math={
																													this.state.questionList
																														.level1[3].question
																												}
																											/>
																										</div>
																										<div className="skill-options"></div>
																									</div>
																								) : null}
																							</>
																						)}
																				</>
																			)}
																	</>
																)}
														</div>
														<div className="skill-preview-foot">
															<button
																type="submit"
																onClick={this.handleStartQuiz}
															>
																Start Practice
                              </button>
														</div>
													</>
												)}
										</div>
									</div>
								</>
							) : (
									<AttemptQuiz
										questionList={this.state.questionList}
										topic={this.state.topic}
										chapter={this.state.chapter}
										subject={this.state.subject}
										studentId={this.props.student._id}
										practiceId={this.state.practiceId}
									/>
								)}
						</div>
					)}
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		student: state.student.profileData
	}
}

function mapDispatchToProps(dispatch) {
	return {
		fetchUser: () => dispatch(fetchUser()),
		fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData))
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(IIGPPractices);