import React from "react";
import "./districtDirector.style.css";
import menu from "../assets/open-menu.png";
import Home from "./Home/home";
import Schools from "./Schools/schools";
import { connect } from 'react-redux'
import { fetch_district_officer_profile_data } from '../../redux/actions/districtOfficerDashboard/districtOfficerActions';

class DistrictDirectorDashboard extends React.Component {
    constructor() {
        super();
        this.state = {
            sidebar: "sidebar-full",
            content: "content-mini",
            tab: 1,
            currentTabName: "Home",
            showProfileMenu: false,
            showSubMenu1: false,
            currentUser: [],
            loading: true,
        };
    }

    componentDidMount() {
        if (this.props.auth.currentUser._id) {
            const officerData = {
                officerID: this.props.auth.currentUser.ref_id
            }
            this.props.fetch_district_officer_profile_data(officerData);
        }
    }

    handleProfile = (data) => {
        this.setState({
            userData: data,
            loading: false,
        });
    };

    handleMenuToggle = () => {
        if (this.state.sidebar === "sidebar-full") {
            this.setState({
                sidebar: "sidebar-mini",
                content: "content-full",
            });
        } else {
            this.setState({
                sidebar: "sidebar-full",
                content: "content-mini",
            });
        }
    };

    render() {
        const auth = this.props.auth;
        return (
            <div className="layout-container">
                <div className="header">
                    <div className="header-menu">
                        <img
                            className="header-menu-icon"
                            onClick={this.handleMenuToggle}
                            src={menu}
                            alt="image"
                        />
                        <h1>IIGP | {this.state.currentTabName}</h1>
                    </div>
                    <div className="header-buttons">

                        <div className="profile">
                            <img
                                src={auth.currentUser.photo}
                                className="profile-logo"
                                onClick={() =>
                                    this.setState({
                                        showProfileMenu: !this.state.showProfileMenu,
                                    })
                                }
                            />
                            <p
                                onClick={() =>
                                    this.setState({
                                        showProfileMenu: !this.state.showProfileMenu,
                                    })
                                }
                                className="profile-name"
                            >
                                {auth.currentUser.name}
                            </p>
                            <i
                                className="fas fa-caret-down"
                                onClick={() =>
                                    this.setState({
                                        showProfileMenu: !this.state.showProfileMenu,
                                    })
                                }
                            ></i>
                            {this.state.showProfileMenu ? (
                                <div className="profile-menu">
                                    <p>Profile</p>
                                    <p>Settings</p>
                                    <a href="/api/logout">Log Out</a>
                                </div>
                            ) : null}
                        </div>
                    </div>
                </div>

                <div className="body">
                    <div className={this.state.sidebar}>
                        <div className="sidebar-content">
                            <h1>MENU</h1>

                            <div className="menu" onClick={() => this.setState({ tab: 1, currentTabName: "Home" })}>
                                <div className="menu-item">
                                    {this.state.tab === 1 ? <div className="active"></div> : null}
                                    <i class="fas fa-chart-pie"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>HOME</p>
                                    ) : null}
                                </div>
                            </div>

                            <div className="menu" onClick={() => this.setState({ tab: 2, currentTabName: "Schools" })}>
                                <div className="menu-item">
                                    {this.state.tab === 2 ? (
                                        <div className="active"></div>
                                    ) : null}
                                    <i class="fas fa-school"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>SCHOOLS</p>
                                    ) : null}
                                </div>
                            </div>

                        </div>
                    </div>
                    <div className={this.state.content}>
                        {this.state.tab === 1 ? <Home /> : null}
                        {this.state.tab === 2 ? <Schools /> : null}
                    </div>
                </div>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        auth: state.auth,
        districtOfficer: state.districtOfficer.profileData
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetch_district_officer_profile_data: (officerData) => dispatch(fetch_district_officer_profile_data(officerData))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DistrictDirectorDashboard);