import React from 'react';
import './singlePoll.css';

import Skeleton, { SkeletonTheme } from "react-loading-skeleton";

import axios from 'axios';

import moment from 'moment';

import { Modal, Form, Button, Spinner } from 'react-bootstrap';

import Lottie from "lottie-react-web";

import loadAnimation from '../assets/8174-vote.json';

class SinglePoll extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            teacher: "",
            showModal: false,
            submitting: false,
            showAnimation: false
        }
    }

    componentDidMount = async () => {
        const data = {
            teacherId: this.props.poll.teacherId
        }

        var response = await axios.post("/api/myschool/getTeacher", data);
        if (response.data !== null) {
            this.setState({
                teacher: response.data.result,
                loading: false,
                questions: this.props.poll.questions
            })
        }
    }

    handleStoreAnswer = (id, e) => {
        var questions = this.state.questions;
        questions.map((item) => {
            if (item._id === id) {
                var answer = {
                    studentId: this.props.studentId,
                    answer: e.target.value
                }
                var found = false;
                item.answers.map((ans) => {
                    if (ans.studentId === this.props.studentId) {
                        ans.answer = e.target.value;
                        found = true;
                    }
                })
                if (found === false) {
                    item.answers.push(answer);
                }
            }
        })

        this.setState({
            questions
        })
    }

    handleSubmit = async () => {

        this.setState({
            submitting: true
        })

        const data = {
            pollId: this.props.poll._id,
            questions: this.state.questions
        }

        var response = await axios.post("/api/myschool/submitPoll", data);
        if (response.data !== null && response.data.type === 'success') {
            this.setState({
                submitting: false
            }, () => {
                this.showSubmitAnimation();
            })
        }
    }

    showSubmitAnimation = () => {
        this.setState({
            showAnimation: true
        }, () => {
            setTimeout(() => {
                this.setState({
                    showAnimation: false,
                    showModal: false
                })
            }, 4000)
        })
    }

    render() {
        return (
            <div className="singlePoll-container">
                <div className="poll-head">
                    <p>{this.props.poll.pollTitle}</p>
                    <button onClick={() => this.setState({ showModal: true })}>Take Poll</button>
                </div>
                <div className="poll-body">
                    {
                        this.state.loading
                            ?
                            <>
                                <SkeletonTheme>
                                    <div style={{ width: "100%", display: "flex", alignItems: "center" }}>
                                        <p style={{ marginRight: "10px" }}><Skeleton circle={true} height={50} width={50} /></p>
                                        <p><Skeleton height={30} width={250} /></p>
                                    </div>
                                    <div>
                                        <p><Skeleton height={30} width={310} /></p>
                                    </div>
                                </SkeletonTheme>
                            </>
                            :
                            <>
                                <div className="teacher-info">
                                    <img src={"/api/file/show/" + this.state.teacher.photo.filename} alt="teacher-pic" />
                                    <p>{this.state.teacher.name}</p>
                                </div>
                                <p>Created On <span>{moment(this.props.poll.doa).format('LL')}</span></p>
                            </>
                    }

                    <Modal
                        size="lg"
                        aria-labelledby="contained-modal-title-vcenter"
                        centered
                        show={this.state.showModal}
                        onHide={() => this.setState({ showModal: false })}
                        keyboard={false}
                    >
                        <Modal.Body>
                            {
                                this.state.showAnimation
                                    ?
                                    <div className="poll-submit-wrapper">
                                        <Lottie
                                            options={{ animationData: loadAnimation }}
                                            width={250}
                                            height={250}
                                        />
                                    </div>
                                    :
                                    <div className="poll-questions">
                                        {
                                            this.props.poll.questions.map((item) => {
                                                return (
                                                    <div className="question-wrapper">
                                                        <p>{item.question}</p>
                                                        {
                                                            item.questionType === 'y/n'
                                                                ?
                                                                <Form.Group>
                                                                    <Form.Control as="select" onChange={(e) => this.handleStoreAnswer(item._id, e)}>
                                                                        <option selected hidden>Choose...</option>
                                                                        <option value="Yes">Yes</option>
                                                                        <option value="No">No</option>
                                                                    </Form.Control>
                                                                </Form.Group>
                                                                :
                                                                <>
                                                                    {
                                                                        item.questionType === '0-5'
                                                                            ?
                                                                            <Form.Group>
                                                                                <Form.Control as="select" onChange={(e) => this.handleStoreAnswer(item._id, e)}>
                                                                                    <option selected hidden>Choose...</option>
                                                                                    <option value="0">0</option>
                                                                                    <option value="1">1</option>
                                                                                    <option value="2">2</option>
                                                                                    <option value="3">3</option>
                                                                                    <option value="4">4</option>
                                                                                    <option value="5">5</option>
                                                                                </Form.Control>
                                                                            </Form.Group>
                                                                            :
                                                                            <Form.Group>
                                                                                <Form.Control as="select" onChange={(e) => this.handleStoreAnswer(item._id, e)}>
                                                                                    <option selected hidden>Choose...</option>
                                                                                    <option value="0">0</option>
                                                                                    <option value="1">1</option>
                                                                                    <option value="2">2</option>
                                                                                    <option value="3">3</option>
                                                                                    <option value="4">4</option>
                                                                                    <option value="5">5</option>
                                                                                    <option value="6">6</option>
                                                                                    <option value="7">7</option>
                                                                                    <option value="8">8</option>
                                                                                    <option value="9">9</option>
                                                                                    <option value="10">10</option>
                                                                                </Form.Control>
                                                                            </Form.Group>
                                                                    }
                                                                </>
                                                        }
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                            }
                        </Modal.Body>
                        {
                            this.state.showAnimation
                                ?
                                null
                                :
                                <Modal.Footer>
                                    {
                                        this.state.submitting
                                            ?
                                            <Spinner animation="border" variant="success" />
                                            :
                                            <Button variant="success" onClick={this.handleSubmit}>Submit</Button>
                                    }
                                </Modal.Footer>
                        }
                    </Modal>

                </div>
            </div>
        )
    }
}

export default SinglePoll;