import React from 'react';

import './resources.css';

import logo from '../assets/iigp.png';
import man from '../assets/man.png';
import downArrow from '../assets/down-arrow.png';

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';

import FileViewer from 'react-file-viewer';

import { connect } from 'react-redux';
import { fetchUser } from '../../../redux/actions/authActions';
import { fetch_student_profile_data } from '../../../redux/actions/studentDashboard/studentActions';

import maths from './assets/math.svg'; import science from './assets/science.svg'; import english from './assets/english.svg'; import islamic from './assets/islamic.svg'; import computer from './assets/computer.svg'; import skills from './assets/skills.svg'; import arts from './assets/arts.svg'; import sports from './assets/sports.svg'; import music from './assets/music-note.svg'; import social from './assets/social.svg';
import FloralLoading from '../loadingScreen/floralLoading';

import { Document, Page } from 'react-pdf';

import { pdfjs } from 'react-pdf';
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

class IIGPResources extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            filePath: '',
            fileType: '',
            loading: true,
            numPages: null,
            pageNumber: 1
        }
    }

    componentDidMount = async () => {

        const currentUser = await this.props.fetchUser();
        if (currentUser) {
            const studentData = {
                studentId: currentUser.ref_id
            }
            const result = await this.props.fetch_student_profile_data(studentData);
            if (result) {
                console.log(result);
                result.classInfo.subjects.map((subject) => {
                    if (subject.subjectName === this.props.match.params.subject) {
                        subject.chapters.map((chapter) => {
                            if (chapter.chapterName === this.props.match.params.chapter) {
                                chapter.topics.map((topic) => {
                                    if (topic.topicName === this.props.match.params.topic) {
                                        topic.resources.map((item) => {
                                            if (item.resourceName === this.props.match.params.name) {
                                                this.setState({
                                                    filePath: '/api/file/show/' + item.file.filename,
                                                    fileType: item.resourceName.split('.')[1],
                                                    loading: false
                                                })
                                            }
                                        })
                                    }
                                })
                            }
                        })
                    }
                })
            }
        }
    }

    onDocumentLoadSuccess = ({ numPages }) => {
        this.setState({
            numPages: numPages
        })
    }

    render() {
        return (
            <>
                {
                    this.state.loading
                        ?
                        <FloralLoading />
                        :
                        <div className="student-resource-container">
                            {
                                this.state.fileType === 'pdf'
                                    ?
                                    <div>
                                        <Document
                                            file={this.state.filePath}
                                            onLoadSuccess={this.onDocumentLoadSuccess}
                                        >
                                            <Page pageNumber={this.state.pageNumber} />
                                        </Document>
                                        <p>Page {this.state.pageNumber} of {this.state.numPages}</p>
                                    </div>
                                    :
                                    <FileViewer
                                        fileType={this.state.fileType}
                                        filePath={this.state.filePath}
                                    />
                            }
                        </div>
                }
            </>
        )
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        student: state.student.profileData
    }
}

function mapDispatchToProps(dispatch) {
    return {
        fetchUser: () => dispatch(fetchUser()),
        fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(IIGPResources);