import React from "react";
import { connect } from "react-redux";
import "./allQuestions.css";

import noQuestion from "../../assets/no_questions_image.svg";

import { Button, Modal, Container, Row, Col, Form, Spinner } from "react-bootstrap";

import { AiOutlinePlus } from "react-icons/ai";
import { FaPencilAlt, FaLevelUpAlt, FaTimes } from "react-icons/fa";
import { MdDelete } from "react-icons/md";
import { BsPlusCircle } from "react-icons/bs";
import { IoIosArrowDown } from "react-icons/io";

import CKEditor from "@ckeditor/ckeditor5-react";
import InlineEditor from "@ckeditor/ckeditor5-build-inline";
import MathJax from "react-mathjax-preview";

class AllQuestions extends React.Component {

    constructor(props) {
        super(props);
        this.accordionContent = [];
        this.state = {
            questionList: props.bankDetails.questions,
        };
    }

    handleOpenSection = (value) => {
        if (this.state.activeSection === "") {
            this.setState({
                activeSection: value,
            });
        } else {
            this.setState({
                activeSection: "",
            });
        }
    };

    render() {
        console.log(this.props.teacher);
        var bank = this.props.bankDetails;
        var i = 0;
        var levels = [];
        for (var x = 1; x <= 5; x++) {
            levels.push(x);
        }
        return (
            <div className='quizMaker-container'>
                <div className='quizMaker-header'>
                    <div className='quizMaker-header-left'>
                        <p>
                            <FaPencilAlt />
                        </p>
                        <div>
                            <h1>{bank.title}</h1>
                            <p>
                                {bank.subjectName}, {bank.className} {bank.sectionName}
                            </p>
                        </div>
                    </div>
                </div>

                {/* List of Questions & Levels*/}

                <div className='quizMaker-body'>
                    {this.state.questionList.map((section, index) => {
                        return (
                            <div className={this.state.activeSection === section.name ? "level-box active" : "level-box"}>
                                <div className='level-box-header'>
                                    <div>
                                        <h1>
                                            {section.name} - {section.questions.length} Questions
										</h1>
                                        <p
                                            className={this.state.activeSection === section.name ? "rotate" : "errect"}
                                            onClick={() => this.handleOpenSection(section.name)}>
                                            <IoIosArrowDown />
                                        </p>
                                    </div>
                                </div>
                                <div className='level-box-body'>
                                    {section.questions.length === 0 ? (
                                        <>
                                            <div className='ques-box'>
                                                <div className='ques-body'>
                                                    <img src={noQuestion} alt='no-questions' />
                                                </div>
                                            </div>
                                        </>
                                    ) : (
                                            section.questions.map((item, index) => {
                                                return (
                                                    <div className='ques-box'>
                                                        <div className='ques-index'>
                                                            <p>{index + 1}.</p>
                                                        </div>
                                                        <div className='ques-body'>
                                                            <div className='ques-body-ques'>
                                                                <MathJax math={item.question} />
                                                                <span>{item.code}</span>
                                                            </div>
                                                            <div className='ques-body-options'>
                                                                {item.options.map((option) => {
                                                                    return (
                                                                        <Form.Check
                                                                            className='r-in'
                                                                            type='radio'
                                                                            label={<MathJax math={option.value} />}
                                                                            disabled
                                                                            checked={option.answer}
                                                                        />
                                                                    );
                                                                })}
                                                            </div>
                                                            <div className='ques-body-marks'>
                                                                <div>
                                                                    <p>+{item.marks}</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                );
                                            })
                                        )}
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData,
    };
}

export default connect(mapStateToProps)(AllQuestions);
