import React from 'react';
import './schoolInfo.css';
import { Form, Button, Col, Image, Spinner } from 'react-bootstrap';
import { FaCheck } from 'react-icons/fa';

import { connect } from 'react-redux';

import {
    editSchool,
    editSchoolWithImage,
} from '../../../redux/actions/schoolAdminDashboard/schoolAdminActions';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

class SchoolInfo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showImage: "/api/file/show/" + props.school.logo.filename,
            schoolName: props.school.name,
            schoolBranch: props.school.branch,
            schoolEmail: props.school.email,
            schoolAddress: props.school.address,
            schoolContact: props.school.contact,
            schoolFax: props.school.fax,
            schoolPrincipal: props.school.principal.length > 0 ? props.school.principal[0].name : "",
            schoolDistrictDirector: props.school.districtOfficers.length > 0 ? props.school.districtOfficers[0].name : "",
            schoolWebsite: props.school.website,
            loading: false,
            imageEdited: false
        }
    }
    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleEditLogo = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                logo: f,
                showImage: URL.createObjectURL(f),
                imageEdited: true,
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleSave = async () => {
        this.setState({
            error: ""
        })
        if (this.state.editName === "" || this.state.editBranch === "") {
            this.setState({
                error: "Please fill all the details"
            })
        }
        else {
            this.setState({
                loading: true
            })
            const data = {
                name: this.state.schoolName,
                branch: this.state.schoolBranch,
                address: this.state.schoolAddress,
                email: this.state.schoolEmail,
                contact: this.state.schoolContact,
                fax: this.state.schoolFax,
                website: this.state.schoolWebsite,
                schoolId: this.props.school._id,
                logo: this.props.school.logo
            };
            console.log(this.props.school);
            var form_data = new FormData();
            form_data.append('file', this.state.logo);
            const check = this.state.imageEdited ? await this.props.editSchoolWithImage(form_data, data) : await this.props.editSchool(data);
            console.log(check);
            if (check) {
                this.setState({
                    loading: false
                })
                if (check.type === 'success') {
                    toaster.notify('School Information Updated Successfully', {
                        duration: 3000
                    })
                }
                else {
                    toaster.notify('Error', {
                        duration: 3000
                    })
                }
            }
        }
    }
    render() {
        console.log(this.props.school);
        return (
            <div className="school-info-container">
                <h1>School Information</h1>
                <Form>
                    <div className="container-group">
                        <Form.Group >
                            <div className="image-container">
                                <div className="image-picker">
                                    <input id="image-picker" onChange={e => this.handleEditLogo(e)} type="file" accept="image/*" />
                                    <img htmlFor="#image-picker" src={this.state.showImage} />
                                </div>
                            </div>
                        </Form.Group>
                    </div>
                    <div className="container-group">
                        <Form.Group className="container-fluid">
                            <Form.Label>School Name</Form.Label>
                            <Form.Control name="schoolName" type="text" placeholder="Enter School Name" value={this.state.schoolName} onChange={this.handleChange} />
                        </Form.Group>
                        <Form.Group className="container-fluid">
                            <Form.Label>School Branch Name</Form.Label>
                            <Form.Control name="schoolBranch" type="text" placeholder="Enter School Branch Name" value={this.state.schoolBranch} onChange={this.handleChange} />
                        </Form.Group>
                    </div>

                    <div className="container-group">
                        <Form.Group className="container-fluid">
                            <Form.Label>School Address</Form.Label>
                            <Form.Control name="schoolAddress" type="text" value={this.state.schoolAddress} onChange={this.handleChange} />
                        </Form.Group>
                        <Form.Group className="container-fluid">
                            <Form.Label>School Email</Form.Label>
                            <Form.Control name="schoolEmail" type="text" value={this.state.schoolEmail} onChange={this.handleChange} />
                        </Form.Group>
                    </div>
                    <div className="container-group">
                        <Form.Group className="container-fluid">
                            <Form.Label>School Contact No.</Form.Label>
                            <Form.Control name="schoolContact" type="text" value={this.state.schoolContact} onChange={this.handleChange} />
                        </Form.Group>
                        <Form.Group className="container-fluid">
                            <Form.Label>School Fax</Form.Label>
                            <Form.Control name="schoolFax" type="text" value={this.state.schoolFax} onChange={this.handleChange} />
                        </Form.Group>
                        <Form.Group className="container-fluid">
                            <Form.Label>School Website</Form.Label>
                            <Form.Control name="schoolWebsite" type="text" value={this.state.schoolWebsite} onChange={this.handleChange} />
                        </Form.Group>
                    </div>
                    <div className="container-group">
                        <Form.Group className="container-fluid">
                            <Form.Label>School Principle</Form.Label>
                            <Form.Control type="text" disabled value={this.state.schoolPrincipal} />
                        </Form.Group>
                        <Form.Group className="container-fluid">
                            <Form.Label>School District Director</Form.Label>
                            <Form.Control type="text" disabled value={this.state.schoolDistrictDirector} />
                        </Form.Group>
                    </div>
                    <div className="container-group">
                        <Form.Group className="container-fluid">
                            {
                                this.state.loading
                                    ?
                                    <Spinner animation="border" role="status" variant="success">
                                        <span className="sr-only">Loading...</span>
                                    </Spinner>
                                    :
                                    <Button onClick={this.handleSave} variant="success" className="submit-action"><FaCheck />Save Changes</Button>
                            }
                        </Form.Group>
                    </div>

                </Form>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        school: state.schoolAdmin.school,
        schoolError: state.schoolAdmin.schoolError,
        schoolSucc: state.schoolAdmin.schoolSucc
    };
}

function mapDispatchToProps(dispatch) {
    return {
        editSchool: (data) => dispatch(editSchool(data)),
        editSchoolWithImage: (form_data, data) => dispatch(editSchoolWithImage(form_data, data)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(SchoolInfo);