import axios from 'axios';

import { FETCH_DISTRICT_OFFICER_PROFILE, CREATE_PRINCIPAL } from './districtOfficerTypes';

export const fetch_district_officer_profile_data = (officerData) => async (dispatch) => {
    const res = await axios.post("/api/districtOfficer/profile", officerData);
    dispatch({ type: FETCH_DISTRICT_OFFICER_PROFILE, payload: res.data });
}

export const fetch_school_info = async (schoolData) => {
    const res = await axios.post("/api/districtOfficer/getSchoolInfo", schoolData);;
    return res.data;
}

export const create_principal = (principalData) => async (dispatch) => {
    const res = await axios.post("/api/districtOfficer/createPrincipal", principalData);
    dispatch({ type: CREATE_PRINCIPAL, payload: res.data });
    return res.data;

}

export const delete_principal = async (principalData) => {
    const res = await axios.post("/api/districtOfficer/deletePrincipal", principalData);
    return res.data;

}

export const fetch_principal_info = async (principalData) => {
    const res = await axios.post("/api/districtOfficer/getPrincipalInfo", principalData);
    return res.data;
}

export const edit_principal_info = async (principalData) => {
    const res = await axios.post("/api/districtOfficer/editPrincipal", principalData);
    return res.data;
}