import React from 'react';
import './classes.css';
import { connect } from 'react-redux'

import { Modal, Button, Form, Spinner, } from 'react-bootstrap';
import { FaCheck, FaPlus, FaInfo, FaInfoCircle } from "react-icons/fa";

import addImg from '../../assets/more (5).svg';

import { addClass } from '../../../redux/actions/schoolAdminDashboard/schoolAdminActions';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';
import axios from 'axios';

class Classes extends React.Component {
    constructor() {
        super();
        this.state = {
            showClass: true,
            showSections: false,
            showAddClass: false,
            showAddSection: false,
            showAddSubject: false,
            addingClass: false,
            className: "",
            sectionName: "",
            subjectName: "",
            subjectStartTime: "",
            subjectEndTime: "",
            cycle: "",
            class: "",
            showAddSection: false,
            subjects: [],
            sections: [],
            classes: [],
            subjectLab: false,
        }
    }

    async componentDidMount() {
        var res = await axios.get('/api/schoolAdmin/iigpClasses');
        if (res.data !== null) {
            console.log(res.data);
            this.setState({
                classes: res.data
            })
        }
    }
    handleShowAddClass = () => {
        this.setState({
            showAddClass: true
        })
    }

    handleHideAddClass = () => {
        this.setState({
            showAddClass: false
        })
    }

    handleAddClass = async () => {
        this.setState({
            error: ""
        })
        this.setState({
            addingClass: true
        })
        const data = {
            schoolId: this.props.school._id,
            className: this.state.className,
            sections: this.state.sections,
            cycle: this.state.cycle
        }
        console.log(data)
        const check = await this.props.addClass(data);
        if (check) {
            this.setState({
                addingClass: false
            })
            if (check.type === "success") {
                this.setState({
                    showAddClass: false,
                    sections: [],
                    className: "",
                    cycle: "",
                    subjects: []
                })
                toaster.notify("Class Added", {
                    duration: 3000
                })
            }
        }
    }

    handleShowSectionInfo = (e) => {
        this.setState({
            showAddSection: true,
            sectionName: e.name,
            subjects: e.subjects
        })
    }

    handleShowAddSection = () => {
        this.setState({
            showAddSection: true,
        })
    }

    handleHideAddSection = () => {
        this.setState({
            showAddSection: false
        })
    }


    handleAddSection = () => {
        var sections = this.state.sections;
        var found = false;
        sections.map(sec => {
            if (sec.name === this.state.sectionName) {
                found = true
            }
        })
        if (found === false) {

            var section = {
                name: this.state.sectionName,
                subjects: this.state.subjects
            }
            sections.push(section);
            this.setState({
                sections: sections,
                sectionName: "",
                subjects: [],
                sectionName: "",
                showAddSection: false
            });
            console.log(this.state.sections);
        }
        else {
            sections.map(sec => {
                if (sec.name === this.state.sectionName) {
                    sec.name = this.state.sectionName;
                    sec.subjects = this.state.subjects
                }
            })
            this.setState({
                sections: sections,
                sectionName: "",
                subjects: [],
                sectionName: "",
                showAddSection: false
            });
        }
    }

    handleShowAddSubject = () => {
        this.setState({
            showAddSubject: true
        })
    }

    handleHideAddSubject = () => {
        this.setState({
            showAddSubject: false
        })
    }

    handleAddSubject = () => {
        this.setState({
            error: ""
        });
        if (this.state.subjectName === "") {
            this.setState({
                error: "Please select a subject"
            })
        }
        else if (this.state.subjectStartTime === "") {
            this.setState({
                error: "Please fill all the details"
            })
        }
        else if (this.state.subjectEndTime === "") {
            this.setState({
                error: "Please fill all the details"
            })
        }
        else {
            var subjects = this.state.subjects;
            var found = false;
            subjects.map(sub => {
                if (sub.name === this.state.subjectName) {
                    found = true;
                }
            })
            if (found === false) {
                var subject = {
                    name: this.state.subjectName,
                    startTime: this.state.subjectStartTime,
                    endTime: this.state.subjectEndTime,
                    lab: this.state.subjectLab
                }
                subjects.push(subject);
                this.setState({
                    subjects: subjects,
                    showAddSubject: false,
                    subjectName: "",
                    subjectStartTime: "",
                    subjectEndTime: ""
                })
            }
            else {
                subjects.map(sub => {
                    if (sub.name === this.state.subjectName) {
                        sub.name = this.state.subjectName;
                        sub.startTime = this.state.subjectStartTime;
                        sub.endTime = this.state.subjectEndTime;
                    }
                })
                this.setState({
                    subjects: subjects,
                    showAddSubject: false,
                    subjectName: "",
                    subjectStartTime: "",
                    subjectEndTime: ""
                })
            }
        }
    }

    handleShowSubjectInfo = (e) => {
        this.setState({
            showAddSubject: true,
            subjectName: e.name,
            subjectStartTime: e.startTime,
            subjectEndTime: e.endTime
        })
    }

    handleShowSecton = (e) => {
        this.setState({
            showSections: true,
            showClass: false,
            classValue: e
        })
    }

    handleShowSectonInfo = (e) => {
        this.setState({
            showSections: false,
            sectionValue: e,
        })
    }

    handleSectionBack = () => {
        this.setState({
            showSections: true
        })
    }

    handleClassBack = () => {
        this.setState({
            showClass: true
        })
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleSubject = (e) => {
        this.state.classes.map(clas => {
            if (clas.className === this.state.className && this.state.cycle === clas.cycle) {
                clas.subjects.map(sub => {
                    if (sub.subjectName === e.target.value) {
                        this.setState({
                            subjectName: e.target.value,
                            subjectLab: sub.lab
                        })
                    }
                })
            }
        })
    }

    render() {
        var classes = this.props.school ? this.props.school.classes : [];
        return (
            <>
                {
                    this.state.showClass
                        ?
                        <div className="classes-container">
                            <div className="header-section">
                                <div className="left">
                                    <h1>Classes</h1>
                                    <p>TOTAL CLASSES REGISTERED: <span>{classes.length}</span></p>
                                </div>
                                <div className="right">
                                    <div onClick={this.handleShowAddClass} className="add-button">
                                        <img src={addImg} />
                                        <p>Add New Class</p>
                                    </div>
                                </div>
                            </div>
                            <div className="body">
                                <div className="class-list">
                                    <div className="cycle">
                                        <div className="cycle-header">
                                            <h1>1</h1>
                                        </div>
                                        <p className="sub-header">Class List</p>
                                        <div className="cycle-class-list">
                                            {
                                                classes && classes.map(item => {
                                                    if (item.cycle === '1') {
                                                        return (
                                                            <div onClick={() => this.handleShowSecton(item)} className="class-box">
                                                                <p>{item.name}</p>
                                                            </div>
                                                        )
                                                    }
                                                })
                                            }
                                        </div>
                                    </div>
                                    <div className="cycle">
                                        <div className="cycle-header">
                                            <h1>2</h1>
                                        </div>
                                        <p className="sub-header">Class List</p>
                                        <div className="cycle-class-list">
                                            {
                                                classes && classes.map(item => {
                                                    if (item.cycle === '2') {
                                                        return (
                                                            <div onClick={() => this.handleShowSecton(item)} className="class-box">
                                                                <p>{item.name}</p>
                                                            </div>
                                                        )
                                                    }
                                                })
                                            }
                                        </div>
                                    </div>
                                    <div className="cycle">
                                        <div className="cycle-header">
                                            <h1>3</h1>
                                        </div>
                                        <p className="sub-header">Class List</p>
                                        <div className="cycle-class-list">
                                            {
                                                classes && classes.map(item => {
                                                    if (item.cycle === '3') {
                                                        return (
                                                            <div onClick={() => this.handleShowSecton(item)} className="class-box">
                                                                <p>{item.name}</p>
                                                            </div>
                                                        )
                                                    }
                                                })
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        :
                        <>
                            {
                                this.state.showSections
                                    ?
                                    <div className="classes-container">
                                        <div className="header-section">
                                            <div className="left">
                                                <h1><i class="fas fa-arrow-left" onClick={this.handleClassBack}></i>Sections of {this.state.classValue.name}</h1>
                                                <p>TOTAL SECTION: <span>{
                                                    this.props.school && this.props.school.classes.map(item => {
                                                        if (item._id === this.state.classValue._id) {
                                                            console.log(item);
                                                            return (
                                                                <>
                                                                    {
                                                                        item.sections.length
                                                                    }
                                                                </>
                                                            )
                                                        }
                                                    })
                                                }</span></p>
                                            </div>
                                            <div className="right">
                                                <div onClick={this.handleShowEditClass} className="add-button">
                                                    <img src={addImg} />
                                                    <p>Edit Class Details</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="body">
                                            <div className="class-list">
                                                {
                                                    this.props.school && this.props.school.classes.map(item => {
                                                        if (item._id === this.state.classValue._id) {
                                                            console.log(item);
                                                            return (
                                                                <>
                                                                    {
                                                                        item.sections.map(sec => {
                                                                            return (
                                                                                <div className="cycle cycle-section">
                                                                                    <div className="cycle-header">
                                                                                        <h1>{sec.name}</h1>
                                                                                    </div>
                                                                                    <p className="sub-header">Subject List</p>
                                                                                    <div className="cycle-class-list">
                                                                                        {
                                                                                            sec.subjects.map(item => {
                                                                                                return (
                                                                                                    <div className="class-box">
                                                                                                        <p>{item.name}</p>
                                                                                                    </div>
                                                                                                )
                                                                                            })
                                                                                        }
                                                                                    </div>
                                                                                </div>
                                                                            )
                                                                        })
                                                                    }
                                                                </>
                                                            )
                                                        }
                                                    })
                                                }
                                            </div>
                                        </div>
                                    </div>
                                    :
                                    null
                            }
                        </>
                }
                {
                    this.state.showAddClass
                        ?
                        <Modal
                            show={this.state.showAddClass}
                            onHide={this.handleHideAddClass}
                            backdrop="static"
                            keyboard={false}
                            centered
                        >
                            <Modal.Header closeButton>
                                <Modal.Title>Add Class Informations </Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <Form>
                                    <Form.Group>
                                        <Form.Label>Select cycle</Form.Label>
                                        <Form.Control as="select" value={this.state.cycle} onChange={e => { this.setState({ cycle: e.target.value }) }}>
                                            <option value="">Select a cycle</option>
                                            <option value="1">CYCLE 1</option>
                                            <option value="2">CYCLE 2</option>
                                            <option value="3">CYCLE 3</option>
                                        </Form.Control>
                                    </Form.Group>
                                    {
                                        this.state.cycle
                                            ?
                                            <>
                                                <Form.Group>
                                                    <Form.Label>Select class</Form.Label>
                                                    <Form.Control as="select" value={this.state.className} onChange={e => { this.setState({ className: e.target.value }) }}>
                                                        <option value="">Select a grade</option>
                                                        {
                                                            this.state.classes.map(clas => {
                                                                if (clas.cycle === this.state.cycle) {
                                                                    return (
                                                                        <option value={clas.className}>{clas.className}</option>
                                                                    )
                                                                }
                                                            })
                                                        }
                                                    </Form.Control>
                                                </Form.Group>
                                                {
                                                    this.state.className
                                                        ?
                                                        <>
                                                            {
                                                                this.state.showAddSection
                                                                    ?
                                                                    <>
                                                                        <Form.Group>
                                                                            <Form.Label>Section Name</Form.Label>
                                                                            <Form.Control name="sectionName" value={this.state.sectionName} type="text" placeholder="Enter section name" onChange={e => this.setState({ sectionName: e.target.value })} />
                                                                        </Form.Group>
                                                                        {
                                                                            this.state.showAddSubject && this.state.sectionName.length > 0
                                                                                ?
                                                                                <>
                                                                                    <Form.Group>
                                                                                        <Form.Label>Choose Subject</Form.Label>
                                                                                        <Form.Control as="select" value={this.state.subjectName} onChange={this.handleSubject}>
                                                                                            <option value="">Select a subject</option>
                                                                                            {
                                                                                                this.state.classes.map(clas => {
                                                                                                    if (clas.className === this.state.className && this.state.cycle === clas.cycle) {
                                                                                                        return (
                                                                                                            <>
                                                                                                                {
                                                                                                                    clas.subjects.map(sub => {
                                                                                                                        console.log(sub);
                                                                                                                        return (
                                                                                                                            <option value={sub.subjectName}>{sub.subjectName}</option>
                                                                                                                        )
                                                                                                                    })
                                                                                                                }
                                                                                                            </>
                                                                                                        )
                                                                                                    }
                                                                                                })
                                                                                            }
                                                                                        </Form.Control>
                                                                                    </Form.Group>
                                                                                    <div className="container-group">
                                                                                        <Form.Group className="container-fluid">
                                                                                            <Form.Label>Set Subject Start Time</Form.Label>
                                                                                            <Form.Control name="sectionName" type="time" value={this.state.subjectStartTime} onChange={e => { this.setState({ subjectStartTime: e.target.value }) }} />
                                                                                        </Form.Group>
                                                                                        <Form.Group className="container-fluid">
                                                                                            <Form.Label>Set Subject End Time</Form.Label>
                                                                                            <Form.Control name="sectionName" type="time" value={this.state.subjectEndTime} onChange={e => { this.setState({ subjectEndTime: e.target.value }) }} />
                                                                                        </Form.Group>
                                                                                    </div>
                                                                                    <Form.Group>
                                                                                        <Button className="submit-action" onClick={this.handleAddSubject} variant="success"><FaCheck />Done with Subject</Button>
                                                                                    </Form.Group>
                                                                                </>
                                                                                :
                                                                                <>
                                                                                    {
                                                                                        this.state.sectionName.length > 0
                                                                                            ?
                                                                                            <>

                                                                                                <Form.Group>
                                                                                                    <div className="container-group-close">
                                                                                                        {
                                                                                                            this.state.subjects.map(sub => {
                                                                                                                return (
                                                                                                                    <Button className="submit-action less-margin" onClick={() => this.handleShowSubjectInfo(sub)} variant="outline-info">{sub.name}<FaInfoCircle /></Button>
                                                                                                                )
                                                                                                            })
                                                                                                        }
                                                                                                        <Button className="submit-action less-margin" onClick={this.handleShowAddSubject} variant="success"><FaPlus />Add Subject</Button>
                                                                                                    </div>
                                                                                                </Form.Group>
                                                                                                {
                                                                                                    this.state.subjects.length > 0
                                                                                                        ?
                                                                                                        <Form.Group>
                                                                                                            <Button className="submit-action" onClick={this.handleAddSection} variant="success"><FaCheck />Done with Section</Button>
                                                                                                        </Form.Group>
                                                                                                        :
                                                                                                        null
                                                                                                }
                                                                                            </>
                                                                                            :
                                                                                            null
                                                                                    }
                                                                                </>
                                                                        }
                                                                    </>
                                                                    :
                                                                    <>
                                                                        <Form.Group>
                                                                            <div className="container-group-close">
                                                                                {
                                                                                    this.state.sections.map(sec => {
                                                                                        return (
                                                                                            <Button className="submit-action less-margin" onClick={() => this.handleShowSectionInfo(sec)} variant="outline-info">{sec.name}<FaInfoCircle /></Button>
                                                                                        )
                                                                                    })
                                                                                }
                                                                                <Button className="submit-action less-margin" onClick={this.handleShowAddSection} variant="success"><FaPlus />Add Section</Button>
                                                                            </div>
                                                                        </Form.Group>
                                                                        {
                                                                            this.state.sections.length > 0
                                                                                ?
                                                                                <>
                                                                                    {
                                                                                        this.state.addingClass
                                                                                            ?
                                                                                            <Spinner animation="border" role="status" variant="success">
                                                                                                <span className="sr-only">Loading...</span>
                                                                                            </Spinner>
                                                                                            :
                                                                                            <Form.Group>
                                                                                                <Button className="submit-action" onClick={this.handleAddClass} variant="success"><FaCheck />Done with Class</Button>
                                                                                            </Form.Group>
                                                                                    }
                                                                                </>
                                                                                :
                                                                                null
                                                                        }
                                                                    </>
                                                            }

                                                        </>
                                                        :
                                                        null
                                                }
                                            </>
                                            :
                                            null
                                    }
                                </Form>
                                {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                                {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                                {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                            </Modal.Body>
                        </Modal>
                        :
                        null
                }
            </>
        )
    }
}
function mapStateToProps(state) {
    return {
        school: state.schoolAdmin.school,
        schoolError: state.schoolAdmin.schoolError,
        schoolSucc: state.schoolAdmin.schoolSucc
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addClass: (data) => dispatch(addClass(data)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Classes);