import React from 'react';

import './library.style.css';

import { RiSearchLine } from 'react-icons/ri';
import { MdTitle, MdSubject } from 'react-icons/md';
import { BsPencilSquare } from 'react-icons/bs';

import moment from 'moment';

class MyLibrary extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            resources: [
                {
                    image: "https://m.media-amazon.com/images/I/51mlO9iGkQL.jpg",
                    title: "Intellectual MATHEMATICS for kids",
                    authorName: "Zohaib Kibria",
                    subjectName: "Mathematics",
                    date: new Date(),
                    keywords: ["Maths", "GRADE 1", "Beginner"],
                    file: {
                        filename: "043d5f282ffd3d4f7b148963905e99a9.pdf",
                    },
                },
            ],
            acresources: [
                {
                    image: "https://m.media-amazon.com/images/I/51mlO9iGkQL.jpg",
                    title: "Intellectual MATHEMATICS for kids",
                    authorName: "Zohaib Kibria",
                    subjectName: "Mathematics",
                    date: new Date(),
                    keywords: ["Maths", "GRADE 1", "Beginner"],
                    file: {
                        filename: "043d5f282ffd3d4f7b148963905e99a9.pdf",
                    },
                },
            ],
            search: "",
            filters: ["title", "keywords", "author", "subject"],
            activeFilter: "title",
        }
    }

    handleChange = (e) => {
        this.setState(
            {
                [e.target.id]: e.target.value,
            },
            () => {
                this.handleSearch();
            }
        );
    };

    handleFilter = (e) => {
        this.setState(
            {
                activeFilter: e,
            },
            () => {
                this.handleSearch();
            }
        );
    };

    handleSearch = () => {
        var resources = this.state.acresources;
        var newResources = [];
        if (this.state.search.length > 0) {
            resources.map((resource) => {
                if (this.state.activeFilter === "title" && resource.title.includes(this.state.search)) {
                    newResources.push(resource);
                } else if (this.state.activeFilter === "keywords" && resource.keywords.includes(this.state.search)) {
                    newResources.push(resource);
                } else if (this.state.activeFilter === "subject" && resource.subjectName.includes(this.state.search)) {
                    newResources.push(resource);
                } else if (this.state.activeFilter === "author" && resource.authorName.includes(this.state.search)) {
                    newResources.push(resource);
                }
            });
            this.setState({
                resources: newResources,
            });
        } else {
            resources.map((resource) => {
                newResources.push(resource);
            });
            this.setState({
                resources: newResources,
            });
        }
    };


    render() {
        return (
            <div className="my-library-container">
                <div className="my-library-wrapper">
                    <div className="library-head">
                        <input type="text" placeholder="Search books..." value={this.state.search} onChange={this.handleChange} id='search'></input>
                        <div className={this.state.activeFilter === 'keywords' ? 'filter-box active' : 'filter-box'} onClick={() => this.handleFilter('keywords')}><p><RiSearchLine /></p>Keywords</div>
                        <div className={this.state.activeFilter === 'title' ? 'filter-box active' : 'filter-box'} onClick={() => this.handleFilter('title')}><p><MdTitle /></p>Title</div>
                        <div className={this.state.activeFilter === 'author' ? 'filter-box active' : 'filter-box'} onClick={() => this.handleFilter('author')}><p><BsPencilSquare /></p>Author</div>
                        <div className={this.state.activeFilter === 'subject' ? 'filter-box active' : 'filter-box'} onClick={() => this.handleFilter('subject')}><p><MdSubject /></p>Subject</div>
                    </div>
                    <div className="library-body">
                        {
                            this.state.resources.map((resource) => {
                                return (
                                    <a href={"/api/file/show/" + resource.file.filename} target='_blank' className='library-box'>
                                        <img src={resource.image} alt='icon' />
                                        <div className='topic-name'>
                                            <h1>{resource.title}</h1>
                                            <p>{resource.authorName} | {resource.subjectName}</p>
                                            <div className='keywords'>
                                                {
                                                    resource.keywords.map((k) => {
                                                        return <p>{k}</p>;
                                                    })
                                                }
                                            </div>
                                        </div>
                                        <div className='topic-date'>
                                            <p>Date: {moment(resource.date).format("DD-MM-YYYY")}</p>
                                            <button>Click to view</button>
                                        </div>
                                    </a>
                                );
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default MyLibrary;