import React from "react";
import "./superAdmin.style.css";
import menu from "../assets/open-menu.png";
import Home from "./Home/home";
import Schools from "./Schools/schools";
import DistrictOfficers from "./DistrictOfficers/districtOfficers";
import { connect } from 'react-redux'
import { fetchSchools, fetchClasses } from '../../redux/actions/superAdminDashboard/superAdminSchoolActions';
import { fetchDistrictOfficers } from '../../redux/actions/superAdminDashboard/superAdminDistrictActions';
import Quiz from "./quiz/quiz";
import Resource from "./resources/resource";

class SuperAdminDashboard extends React.Component {
    constructor() {
        super();
        this.state = {
            sidebar: "sidebar-full",
            content: "content-mini",
            tab: 1,
            currentTabName: "Home",
            showProfileMenu: false,
            showSubMenu1: false,
            currentUser: [],
            loading: true,
        };
    }

    componentDidMount() {
        this.props.fetchClasses();
        this.props.fetchSchools();
        this.props.fetchDistrictOfficers();
        // setInterval(() => {
        //     this.props.fetchClasses();
        //     this.props.fetchSchools();
        //     this.props.fetchDistrictOfficers();
        // }, (5000));
    }

    handleProfile = (data) => {
        this.setState({
            userData: data,
            loading: false,
        });
    };

    handleMenuToggle = () => {
        if (this.state.sidebar === "sidebar-full") {
            this.setState({
                sidebar: "sidebar-mini",
                content: "content-full",
            });
        } else {
            this.setState({
                sidebar: "sidebar-full",
                content: "content-mini",
            });
        }
    };

    render() {
        const auth = this.props.auth;
        return (
            <div className="layout-container">
                <div className="header">
                    <div className="header-menu">
                        <img
                            className="header-menu-icon"
                            onClick={this.handleMenuToggle}
                            src={menu}
                            alt="image"
                        />
                        <h1>IIGP | {this.state.currentTabName}</h1>
                    </div>
                    <div className="header-buttons">

                        <div className="profile">
                            <img
                                src={auth.currentUser.photo}
                                className="profile-logo"
                                onClick={() =>
                                    this.setState({
                                        showProfileMenu: !this.state.showProfileMenu,
                                    })
                                }
                            />
                            <p
                                onClick={() =>
                                    this.setState({
                                        showProfileMenu: !this.state.showProfileMenu,
                                    })
                                }
                                className="profile-name"
                            >
                                {auth.currentUser.name}
                            </p>
                            <i
                                className="fas fa-caret-down"
                                onClick={() =>
                                    this.setState({
                                        showProfileMenu: !this.state.showProfileMenu,
                                    })
                                }
                            ></i>
                            {this.state.showProfileMenu ? (
                                <div className="profile-menu">
                                    <p>Profile</p>
                                    <p>Settings</p>
                                    <a href="/api/logout">Log Out</a>
                                </div>
                            ) : null}
                        </div>
                    </div>
                </div>

                <div className="body">
                    <div className={this.state.sidebar}>
                        <div className="sidebar-content">
                            <h1>MENU</h1>

                            <div className="menu" onClick={() => this.setState({ tab: 1, currentTabName: "Home" })}>
                                <div className="menu-item">
                                    {this.state.tab === 1 ? <div className="active"></div> : null}
                                    <i class="fas fa-chart-pie"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>HOME</p>
                                    ) : null}
                                </div>
                            </div>

                            <div className="menu" onClick={() => this.setState({ tab: 2, currentTabName: "Schools" })}>
                                <div className="menu-item">
                                    {this.state.tab === 2 ? (
                                        <div className="active"></div>
                                    ) : null}
                                    <i class="fas fa-school"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>SCHOOLS</p>
                                    ) : null}
                                </div>
                            </div>

                            <div className="menu" onClick={() => this.setState({ tab: 3, currentTabName: "District Directors" })}>
                                <div className="menu-item">
                                    {this.state.tab === 3 ? (
                                        <div className="active"></div>
                                    ) : null}
                                    <i class="fas fa-user"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>DISTRICT DIRECTORS</p>
                                    ) : null}
                                </div>
                            </div>

                            <div className="menu" onClick={() => this.setState({ tab: 4, currentTabName: "Practices" })}>
                                <div className="menu-item">
                                    {this.state.tab === 4 ? (
                                        <div className="active"></div>
                                    ) : null}
                                    <i class="fas fa-user"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>PRACTICES</p>
                                    ) : null}
                                </div>
                            </div>

                            <div className="menu" onClick={() => this.setState({ tab: 5, currentTabName: "Resources" })}>
                                <div className="menu-item">
                                    {this.state.tab === 5 ? (
                                        <div className="active"></div>
                                    ) : null}
                                    <i class="fas fa-user"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>RESOURCES</p>
                                    ) : null}
                                </div>
                            </div>

                        </div>
                    </div>
                    <div className={this.state.content}>
                        {this.state.tab === 1 ? <Home /> : null}
                        {this.state.tab === 2 ? <Schools /> : null}
                        {this.state.tab === 3 ? <DistrictOfficers /> : null}
                        {this.state.tab === 4 ? <Quiz /> : null}
                        {this.state.tab === 5 ? <Resource /> : null}
                    </div>
                </div>
            </div>
        );
    }
}


function mapStateToProps(state) {
    return {
        auth: state.auth
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetchClasses: () => dispatch(fetchClasses()),
        fetchSchools: () => dispatch(fetchSchools()),
        fetchDistrictOfficers: () => dispatch(fetchDistrictOfficers())
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(SuperAdminDashboard);