import React from 'react'
import { connect } from 'react-redux';
import './chooseChapter.css';

import folderImg from '../../assets/folderIcon.svg';
import addImg from '../../assets/add.svg';

import { Modal, Button, Form, Spinner } from 'react-bootstrap';
import { addChapter } from '../../../../redux/actions/superAdminDashboard/superAdminSchoolActions';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

class ChooseChapter extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            showModal: false,
            chapterName: '',
            adding: false
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleSubmit = async () => {
        this.setState({
            adding: true
        })
        const data = {
            classId: this.props.class._id,
            subjectName: this.props.subject.subjectName,
            chapterName: this.state.chapterName
        }
        const check = await this.props.addChapter(data);
        if (check) {
            this.setState({
                adding: false
            })
            if (check.type === 'success') {
                toaster.notify("Chapter Added", {
                    duration: 3000
                })
                this.setState({
                    chapterName: "",
                    adding: false,
                    showModal: false,
                })
            }
        }
    }

    render() {
        const classes = this.props.classes;
        return (
            <div className="chooseChapter-container">

                {
                    classes && classes.map(clas => {
                        if (clas.className === this.props.class.className) {
                            return (
                                <>
                                    {
                                        clas.subjects.map(sub => {
                                            if (sub.subjectName === this.props.subject.subjectName) {
                                                return (
                                                    <>
                                                        {
                                                            sub.chapters.map(chapter => {
                                                                return (
                                                                    <div className="chapters-cardFolder-container" onClick={() => this.props.toggleShowTopic(chapter)}>
                                                                        <div className="chapters-cardFolder-img">
                                                                            <img src={folderImg} alt="folder-icon" />
                                                                        </div>
                                                                        <div className="chapters-cardFolder-name">
                                                                            <p>{chapter.chapterName}</p>
                                                                        </div>
                                                                    </div>
                                                                )
                                                            })
                                                        }
                                                    </>
                                                )
                                            }
                                        })
                                    }
                                </>
                            )
                        }
                    })
                }

                <div className="chapters-cardFolder-container" onClick={() => this.setState({ showModal: true })}>
                    <div className="chapters-cardFolder-img">
                        <img src={addImg} alt="folder-icon" style={{ height: "65px", boxShadow: "none" }} />
                    </div>
                    <div className="chapters-cardFolder-name">
                        <p>Add New Chapter</p>
                    </div>
                </div>

                <Modal
                    show={this.state.showModal}
                    onHide={() => this.setState({ showModal: false })}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Create Chapter Folder</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Folder Name (Chapter)</Form.Label>
                            <Form.Control type="text" name="chapterName" onChange={this.handleChange} />
                        </Form.Group>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.adding
                                ?
                                <Spinner animation="border" role="status" variant="primary">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button variant="primary" onClick={this.handleSubmit}>Create Folder</Button>
                        }
                    </Modal.Footer>
                </Modal>

            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        classes: state.superAdmin.classes
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addChapter: (data) => dispatch(addChapter(data)),
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(ChooseChapter);
