import React from "react";
import axios from "axios";
export default class Student extends React.Component {
	constructor() {
		super();
		this.state = {
			data: {},
		};
	}
	async componentDidMount() {
		console.log(this.props.id);
		var data = {
			id: this.props.id,
		};
		var res = await axios.post("/api/teacher/studentInfo", data);
		if (res.data !== null) {
			if (res.data.name) {
				this.setState({
					data: res.data,
				});
			}
		}
	}
	render() {
		return <>{this.state.data ? this.state.data.name : "Unknown"}</>;
	}
}
