import React from "react";

export default class Creativity extends React.Component {
	constructor(props) {
		super(props);
		var creativities = [];
		props.creativities.map((creativity) => {
			if (creativity.teacherId === props.teacherId && creativity.date === props.date) {
				creativities = creativity.status;
			}
		});
		console.log(creativities);
		this.state = {
			creativities: creativities,
		};
	}

	add = (e) => {
		var creativities = this.state.creativities;
		var push;
		if (!creativities.includes(e)) {
			creativities.push(e);
			push = true;
		} else {
			creativities = creativities.filter((item) => {
				return item !== e;
			});
			push = false;
		}
		this.setState({
			creativities: creativities,
		});
		console.log(creativities);
		this.props.addCreativity && this.props.addCreativity(creativities, this.props.data, push);
	};
	render() {
		return (
			<div className='behaviour-chart'>
				<div className='behaviour-list'>
					<div
						className={
							this.state.creativities.includes("synthesize ideas in original and surprising ways") ? "behaviour active" : "behaviour"
						}
						onClick={() => this.add("synthesize ideas in original and surprising ways")}>
						<i class='fas fa-check-circle'></i>
						<p>synthesize ideas in original and surprising ways</p>
					</div>
					<div
						className={this.state.creativities.includes("ask new questions to build upon an idea") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("ask new questions to build upon an idea")}>
						<i class='fas fa-check-circle'></i>
						<p>ask new questions to build upon an idea</p>
					</div>
					<div
						className={
							this.state.creativities.includes("brainstorm multiple ideas and solutions to problems") ? "behaviour active" : "behaviour"
						}
						onClick={() => this.add("brainstorm multiple ideas and solutions to problems")}>
						<i class='fas fa-check-circle'></i>
						<p>brainstorm multiple ideas and solutions to problems</p>
					</div>
					<div
						className={
							this.state.creativities.includes("communicate ideas in new and innovative ways") ? "behaviour active" : "behaviour"
						}
						onClick={() => this.add("communicate ideas in new and innovative ways")}>
						<i class='fas fa-check-circle'></i>
						<p>communicate ideas in new and innovative ways</p>
					</div>
				</div>
			</div>
		);
	}
}
