import React from 'react';

import './seating.style.css';

import { Form, Col } from 'react-bootstrap';

import axios from 'axios';

import GridLayout from "react-grid-layout";
import "react-grid-layout/css/styles.css";
import "react-resizable/css/styles.css";



class MySeating extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: false,
            subject: '',
            layout: [],
            windowWidth: window.innerWidth
        }
    }

    handleSetSubject = (e) => {
        this.setState({
            subject: e.target.value,
        }, () => {
            this.handleFetchChart();
        })
    }

    handleFetchChart = async () => {
        this.setState({
            loading: true
        })
        var data = {
            schoolId: this.props.student.school_id,
            grade: this.props.student.class,
            section: this.props.student.section,
            subject: this.state.subject
        }

        const check = await axios.post("/api/getSeatChart", data);
        if (check.data !== null) {
            this.setState({
                layout: check.data,
                loading: false
            })
        }
    }

    render() {
        console.log(this.props.student);
        return (
            <div className="my-seating-container">
                <div className="my-seating-wrapper">
                    <div className="select-sub">
                        <Form.Group className="container-fluid">
                            <Form.Label>Select Subject</Form.Label>
                            <Form.Control as="select" name="subject" onChange={this.handleSetSubject}>
                                <option selected hidden>Choose...</option>
                                <option value="Mathematics">Mathematics</option>
                                <option value="Science">Science</option>
                                <option value="English">English</option>
                                <option value="Islamic Studies">Islamic Studies</option>
                                <option value="Social Studies">Social Studies</option>
                                <option value="Information Technology">Information Technology</option>
                                <option value="Life Skills">Life Skills</option>
                                <option value="Music">Music</option>
                                <option value="Sports">Sports</option>
                                <option value="Arts">Arts</option>
                            </Form.Control>
                        </Form.Group>
                    </div>

                    <div className="seating-chart">
                        {
                            this.state.loading
                                ?
                                null
                                :
                                <GridLayout
                                    isResizable={false}
                                    isDroppable={false}
                                    isDraggable={false}
                                    className='drag-container'
                                    layout={this.state.layout}
                                    cols={8}
                                    rowHeight={30}
                                    width={this.state.windowWidth / 2.1}>
                                    {this.state.layout.map((l) => {
                                        return (
                                            <div key={l.i} className='seat'>
                                                <p>{l.data.name}</p>
                                            </div>
                                        );
                                    })}
                                </GridLayout>
                        }
                    </div>
                </div>
            </div>
        )
    }
}

export default MySeating;