    EDIT_SCHOOL_SUCC,
    ADD_SCHOOLADMIN_SUCC,
    ADD_SCHOOLADMIN_ERROR,
    ADD_SCHOOLDISTRICTOFFICER_SUCC,
    ADD_SCHOOLDISTRICTOFFICER_ERROR,
    EDIT_SCHOOLADMIN_SUCC,
    EDIT_SCHOOLADMIN_ERROR,
    REMOVE_SCHOOLADMIN_SUCC,
    REMOVE_SCHOOLADMIN_ERROR,
