export const fetchClasses = () => async (dispatch) => {
    const res = await axios.get("/api/showList/classes");
    dispatch({ type: FETCH_CLASSES, payload: res.data });
};

export const fetchSchools = () => async (dispatch) => {
    const res = await axios.get("/api/showList/School");
    dispatch({ type: FETCH_SCHOOLS, payload: res.data });
};

export const addSchool = (formData, data) => async (dispatch) => {
    const fileData = await axios.post("/api/file/upload", formData);
    data.logo = {
        filename: fileData.data.filename,
        id: fileData.data.id
    };
    console.log(data);
    const res = await axios.post("/api/createSchool", data);
    if (res.data.type === "error") {
        dispatch({ type: ADD_SCHOOL_ERROR, payload: res.data.error });
        return res.data
    } else {
        dispatch({ type: ADD_SCHOOL_SUCC, payload: res.data.success });
        const resp = await axios.get("/api/showList/School");
        dispatch({ type: FETCH_SCHOOLS, payload: resp.data });
        return res.data
    }
};


export const deleteSchool = (data) => async (dispatch) => {
    const deleteData = {
        fileId: data.logo.id
    }
    console.log(deleteData);
    const doneDelete = await axios.post('/api/file/delete', deleteData);
    if (deleteData) {
        const res = await axios.post("/api/deleteSchool", data);
        if (res.data.type === "error") {
            return res.data
        } else {
            dispatch({ type: DELETE_SCHOOL_SUCC, payload: res.data.success });
            const resp = await axios.get("/api/showList/School");
            dispatch({ type: FETCH_SCHOOLS, payload: resp.data });
            return res.data
        }
    }
};

