import React from 'react';
import { connect } from 'react-redux';
import './quiz.css';
import ChooseClass from './chooseClass/chooseClass';
import ChooseSubject from './chooseSubject/chooseSubject';

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";

import ChooseChapter from './chooseChapter/chooseChapter';
import TopicList from './topicList/topicList';
import ResourceList from './resourceList/resourceList';

import iigpLogo from '../assets/iigpqm.png';

class Resource extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            showSubList: false,
            showChapterList: false,
            showTopicList: false,
            ShowQuizMaker: false,
            choosenClassDetails: [],
            choosenSubejectDetails: [],
            choosenChapterDetails: [],
            choosenTopicDetails: []
        }
    }

    render() {
        const classes = this.props.classes;
        return (
            <div className="quiz-container">
                <div className="quiz-header">
                    {
                        !this.state.showSubList
                            ?
                            <div>
                                <h1>Resources Library</h1>
                            </div>
                            :
                            <>
                                {
                                    !this.state.showChapterList
                                        ?
                                        <div>
                                            <p onClick={() => this.setState({ showSubList: false })}><FaArrowLeft /></p>
                                            <h1>My Practice Library</h1>
                                            <p><FaAngleRight /></p>
                                            <h1>Subjects</h1>
                                        </div>
                                        :
                                        <>
                                            {
                                                !this.state.showTopicList
                                                    ?
                                                    <div>
                                                        <p onClick={() => this.setState({ showChapterList: false })}><FaArrowLeft /></p>
                                                        <h1>My Practice Library</h1>
                                                        <p><FaAngleRight /></p>
                                                        <h1>Subjects</h1>
                                                        <p><FaAngleRight /></p>
                                                        <h1>Chapters</h1>

                                                    </div>
                                                    :
                                                    <>
                                                        {
                                                            !this.state.ShowQuizMaker
                                                                ?
                                                                <div>
                                                                    <p onClick={() => this.setState({ showTopicList: false })}><FaArrowLeft /></p>
                                                                    <h1>My Practice Library</h1>
                                                                    <p><FaAngleRight /></p>
                                                                    <h1>Subjects</h1>
                                                                    <p><FaAngleRight /></p>
                                                                    <h1>Chapters</h1>
                                                                    <p><FaAngleRight /></p>
                                                                    <h1>Topics</h1>
                                                                </div>
                                                                :
                                                                <div>
                                                                    <p onClick={() => this.setState({ ShowQuizMaker: false })}><FaArrowLeft /></p>
                                                                    <img src={iigpLogo} alt="iigp-logo" />
                                                                </div>
                                                        }
                                                    </>
                                            }
                                        </>
                                }
                            </>
                    }

                </div>
                <div className="quiz-body">
                    {
                        !this.state.showSubList
                            ?
                            <ChooseClass toggleShowSubject={(item) => this.setState({ showSubList: true, choosenClassDetails: item })} classes={classes} />
                            :
                            <>
                                {
                                    !this.state.showChapterList
                                        ?
                                        <ChooseSubject toggleShowChapter={(item) => this.setState({ showChapterList: true, choosenSubejectDetails: item })} class={this.state.choosenClassDetails} subjects={this.state.choosenClassDetails.subjects} />
                                        :
                                        <>
                                            {
                                                !this.state.showTopicList
                                                    ?
                                                    <ChooseChapter toggleShowTopic={(item) => this.setState({ showTopicList: true, choosenChapterDetails: item })} class={this.state.choosenClassDetails} subject={this.state.choosenSubejectDetails} chapters={this.state.choosenSubejectDetails.chapters} />
                                                    :
                                                    <>
                                                        {
                                                            !this.state.ShowQuizMaker
                                                                ?
                                                                <TopicList toggleShowQuizMaker={(item) => this.setState({ ShowQuizMaker: true, choosenTopicDetails: item })} class={this.state.choosenClassDetails} subject={this.state.choosenSubejectDetails} chapter={this.state.choosenChapterDetails} topics={this.state.choosenChapterDetails.topics} />
                                                                :
                                                                <ResourceList topic={this.state.choosenTopicDetails} class={this.state.choosenClassDetails} subject={this.state.choosenSubejectDetails} chapter={this.state.choosenChapterDetails} topics={this.state.choosenChapterDetails.topics} />
                                                        }
                                                    </>
                                            }
                                        </>
                                }
                            </>
                    }
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        classes: state.superAdmin.classes,
    }
}

export default connect(mapStateToProps)(Resource);