import React from 'react';

import './navbar.style.css';

import { Link } from "react-router-dom";

import { Modal } from 'react-bootstrap';

import logo from "./assets/iigp.png";
import downArrow from "./assets/down-arrow.png";

import Lottie from "lottie-react-web";
import trophy from './assets/trophy.json';
import trophyLogo from './assets/trophy.svg';

import { Form, Col } from 'react-bootstrap';

import {MdKeyboardArrowDown} from 'react-icons/md';

import moment from 'moment';

class StudentNavbar extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            showMenu: false,
            showProfile: false
        }
    }

    handleShowProfile = () => {

        this.setState({
            showMenu: false,
            showProfile: true
        }, () => {
            var item = document.getElementById("trophy-container");
            item.classList.add("active");
            setTimeout(() => {
                var item2 = document.getElementById("trophy-container");
                if (item2) {
                    item2.classList.remove("active");
                }

            }, 2000);
        })
    }

    render() {
        return (
            <div className="iigp-navbar-container">
                <div className="iigp-logo">
                    <Link to={"/dashboard"}>
                        <img src={logo} alt="iigp-logo" />
                    </Link>
                </div>
                <div className="menu-links">
                    <Link to={"/dashboard"} className="link-box">
                        <p>Home</p>
                    </Link>
                    <Link
                        to={"/dashboard/learning/mathematics"}
                        className="link-box"
                    >
                        <p>Learning</p>
                    </Link>
                    <Link to={"/dashboard/analytics"} className="link-box">
                        <p>Analytics</p>
                    </Link>

                    <div className="link-box" onClick={() => this.setState({ showSchoolMenu: !this.state.showSchoolMenu })}>
                        <p>My School</p>
                        <span><MdKeyboardArrowDown /></span>
                    </div>

                    <div className={this.state.showSchoolMenu ? "nav-school-menu active" : "nav-school-menu"}>
                        <div className="menu-list">
                            <a href="/dashboard/mySchool/learningOrganizer">Learning Organizer</a>
                        </div>
                        <div className="menu-list">
                            <a href="/dashboard/mySchool/learningProgress">Learning Progress</a>
                        </div>
                    </div>

                    <div className="link-user" onClick={() => this.setState({ showMenu: !this.state.showMenu })} >
                        <img
                            src={"/api/file/show/" + this.props.student.photo.filename}
                            alt="user-icon"
                            id="user-icon"
                        />
                        <p>Welcome, {this.props.student.name}</p>
                        <span><MdKeyboardArrowDown /></span>
                    </div>

                    <div className={this.state.showMenu ? "nav-menu active" : "nav-menu"}>
                        <div className="menu-list" onClick={() => this.handleShowProfile()}>
                            <a>My Profile</a>
                        </div>
                        <div className="menu-list">
                            <a href="/api/logout">Logout</a>
                        </div>
                    </div>
                </div>

                <Modal
                    show={this.state.showProfile}
                    onHide={() => this.setState({ showProfile: false })}
                    keyboard={false}
                    size="lg"
                    aria-labelledby="contained-modal-title-vcenter"
                    centered
                >
                    <Modal.Body>
                        <div className="my-profile-container">
                            <div className="trophy-container" id="trophy-container">
                                <div className="trophy-logo">
                                    <img src={trophyLogo} alt="trpohy" />
                                </div>
                                <Lottie
                                    options={{ animationData: trophy, loop: false }}
                                    width={350}
                                    height={350}
                                />
                                <p>Congratulations ! You're top performer in your grade</p>
                            </div>
                            <div className="my-profile-head">
                                <div className="my-profile-left">
                                    <img src={"/api/file/show/" + this.props.student.photo.filename} alt="profile-pic" />
                                </div>
                                <div className="my-profile-right">
                                    <div className="my-profile-info">
                                        <div className="my-profile-name">
                                            <p>{this.props.student.name}</p>
                                        </div>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>Contact</Form.Label>
                                                <Form.Control type="text" value={this.props.student.contact} disabled />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Date of Birth</Form.Label>
                                                <Form.Control type="text" value={moment(this.props.student.dob).format('LL')} disabled />
                                            </Form.Group>
                                        </Form.Row>

                                        <Form.Group controlId="formBasicEmail">
                                            <Form.Label>Email Id</Form.Label>
                                            <Form.Control type="text" value={this.props.student.email} disabled />
                                        </Form.Group>

                                        <Form.Group controlId="formBasicEmail">
                                            <Form.Label>Address</Form.Label>
                                            <Form.Control type="text" value={this.props.student.address} disabled />
                                        </Form.Group>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>City</Form.Label>
                                                <Form.Control type="text" value={this.props.student.city} disabled />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>State</Form.Label>
                                                <Form.Control type="text" value={this.props.student.state} disabled />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Zip</Form.Label>
                                                <Form.Control type="text" value={this.props.student.zip} disabled />
                                            </Form.Group>
                                        </Form.Row>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>Grade</Form.Label>
                                                <Form.Control type="text" value={this.props.student.class} disabled />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Section</Form.Label>
                                                <Form.Control type="text" value={this.props.student.section} disabled />
                                            </Form.Group>
                                        </Form.Row>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}

export default StudentNavbar;