import axios from "axios";

import { FETCH_TEACHER_PROFILE } from "./teacherTypes";

export const fetchTeacher = (data) => async (dispatch) => {
	const res = await axios.post("/api/teacher/profile", data);
	dispatch({ type: FETCH_TEACHER_PROFILE, payload: res.data });
	return res.data;
};

export const editTeacher = (data) => async (dispatch) => {
	const res = await axios.post("/api/teacher/editProfile", data);
	if (res.data.type === "success") {
		var data2 = {
			id: data.id,
		};
		const res2 = await axios.post("/api/teacher/profile", data2);
		dispatch({ type: FETCH_TEACHER_PROFILE, payload: res2.data });
		return res.data;
	}
};

export const editTeacherWithImage = (formData, data) => async (dispatch) => {
	const deleteData = {
		fileId: data.photo.id,
	};
	console.log(deleteData);
	const doneDelete = await axios.post("/api/file/delete", deleteData);
	if (doneDelete) {
		const fileData = await axios.post("/api/file/upload", formData);
		data.photo = {
			filename: fileData.data.filename,
			id: fileData.data.id,
		};
		console.log(data);
		const res = await axios.post("/api/teacher/editProfile", data);
		if (res.data.type === "success") {
			var data2 = {
				id: data.id,
			};
			const res2 = await axios.post("/api/teacher/profile", data2);
			dispatch({ type: FETCH_TEACHER_PROFILE, payload: res2.data });
			return res.data;
		}
	}
};

