//RESOURCES

export const addResource = (formData, data) => async (dispatch) => {
	const fileData = await axios.post("/api/file/upload", formData);
	console.log(fileData);
	data.file = {
		filename: fileData.data.filename,
		id: fileData.data.id,
	};
	console.log(data);
	const res = await axios.post("/api/teacher/addResource", data);
	if (res.data.type === "success") {
		var data2 = {
			id: data.teacherId,
		};
		const res2 = await axios.post("/api/teacher/profile", data2);
		dispatch({ type: FETCH_TEACHER_PROFILE, payload: res2.data });
		return res.data;
	}
};

export const deleteResource = (data) => async (dispatch) => {
	const deleteData = {
		fileId: data.resource.file.id,
	};
	const doneDelete = await axios.post("/api/file/delete", deleteData);
	if (doneDelete) {
		const res = await axios.post("/api/teacher/deleteResource", data);
		if (res.data.type === "success") {
			var data2 = {
				id: data.teacherId,
			};
			const res2 = await axios.post("/api/teacher/profile", data2);
			dispatch({ type: FETCH_TEACHER_PROFILE, payload: res2.data });
			return res.data;
		}
	}
};

