import {
    FETCH_CLASSES,
    FETCH_SCHOOLS,
    ADD_SCHOOL_SUCC,
    ADD_SCHOOL_ERROR,
    DELETE_SCHOOL_SUCC,
    DELETE_SCHOOL_ERROR,
    EDIT_SCHOOL_SUCC,
    EDIT_SCHOOL_ERROR,
    ADD_SCHOOLADMIN_SUCC,
    ADD_SCHOOLADMIN_ERROR,
    REMOVE_SCHOOLADMIN_SUCC,
    REMOVE_SCHOOLADMIN_ERROR,
    EDIT_SCHOOLADMIN_SUCC,
    EDIT_SCHOOLADMIN_ERROR,
    ADD_SCHOOLDISTRICTOFFICER_SUCC,
    ADD_SCHOOLDISTRICTOFFICER_ERROR,
    REMOVE_SCHOOLDISTRICTOFFICER_SUCC,
    REMOVE_SCHOOLDISTRICTOFFICER_ERROR,
    ADD_CHAPTER_ERROR,
    ADD_CHAPTER_SUCC,
    ADD_TOPIC_ERROR,
    ADD_TOPIC_SUCC,
    UPDATE_PRACTICE_SUCC,
    UPDATE_PRACTICE_ERROR,
    ADD_RESOURCE_ERROR,
    ADD_RESOURCE_SUCC
} from "../actions/superAdminDashboard/superAdminTypes";

import {
    FETCH_OFFICERS,
    CREATE_OFFICER_SUCCESS,
    CREATE_OFFICER_ERROR,
    DELETE_OFFICER_SUCCESS,
    DELETE_OFFICER_ERROR
} from '../actions/superAdminDashboard/superAdminTypes';

const initialState = {
    classes: null,
    schools: null,
    schoolError: null,
    schoolSucc: null,
    officerData: null,
    officerError: null,
    officerSuccess: null
};

const superAdminReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_CLASSES:
            return {
                classes: action.payload,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case FETCH_SCHOOLS:
            return {
                classes: state.classes,
                schools: action.payload,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_SCHOOL_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_SCHOOL_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case DELETE_SCHOOL_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case DELETE_SCHOOL_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case EDIT_SCHOOL_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case EDIT_SCHOOL_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_SCHOOLADMIN_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_SCHOOLADMIN_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case EDIT_SCHOOLADMIN_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case EDIT_SCHOOLADMIN_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_SCHOOLDISTRICTOFFICER_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_SCHOOLDISTRICTOFFICER_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case REMOVE_SCHOOLADMIN_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case REMOVE_SCHOOLADMIN_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case REMOVE_SCHOOLDISTRICTOFFICER_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case REMOVE_SCHOOLDISTRICTOFFICER_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: action.payload,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case FETCH_OFFICERS:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: action.payload,
                officerError: null,
                officerSuccess: null
            }
        case CREATE_OFFICER_SUCCESS:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: action.payload
            }
        case CREATE_OFFICER_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: action.payload,
                officerSuccess: null
            }
        case DELETE_OFFICER_SUCCESS:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: action.payload
            }
        case DELETE_OFFICER_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: action.payload,
                officerSuccess: null
            }
        case ADD_CHAPTER_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_CHAPTER_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_TOPIC_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_TOPIC_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case UPDATE_PRACTICE_SUCC:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: action.payload,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case UPDATE_PRACTICE_ERROR:
            return {
                classes: state.classes,
                schools: state.schools,
                schoolError: null,
                schoolSucc: null,
                officerData: state.officerData,
                officerError: null,
                officerSuccess: null
            };
        case ADD_RESOURCE_SUCC:
            return {
                resources: state.resources,
            };
        case ADD_RESOURCE_ERROR:
            return {
                resources: state.resources,
            };
        default:
            return state;
    }
};

export default superAdminReducer;
