import React from "react";

export default class Problems extends React.Component {
	constructor(props) {
		super(props);
		var problems = [];
		props.problems.map((problem) => {
			if (problem.teacherId === props.teacherId && problem.date === props.date) {
				problems = problem.status;
			}
		});
		console.log(problems);
		this.state = {
			problems: problems,
		};
	}

	add = (e) => {
		var problems = this.state.problems;
		var push;
		if (!problems.includes(e)) {
			problems.push(e);
			push = true;
		} else {
			problems = problems.filter((item) => {
				return item !== e;
			});
			push = false;
		}
		this.setState({
			problems: problems,
		});
		console.log(problems);
		this.props.addProblem(problems, this.props.data, push);
	};
	render() {
		return (
			<div className='behaviour-chart'>
				<div className='behaviour-list'>
					<div
						className={this.state.problems.includes("Define the Problem") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Define the Problem")}>
						<i class='fas fa-check-circle'></i>
						<p>Define the Problem</p>
					</div>
					<div
						className={this.state.problems.includes("Brainstorm Solutions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Brainstorm Solutions")}>
						<i class='fas fa-check-circle'></i>
						<p>Brainstorm Solutions</p>
					</div>
					<div
						className={this.state.problems.includes("Pick a Solutions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Pick a Solutions")}>
						<i class='fas fa-check-circle'></i>
						<p>Pick a Solutions</p>
					</div>
					<div
						className={this.state.problems.includes("Implement the Solution") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Implement the Solution")}>
						<i class='fas fa-check-circle'></i>
						<p>Implement the Solution</p>
					</div>
					<div
						className={this.state.problems.includes("Review the Results") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("Review the Results")}>
						<i class='fas fa-check-circle'></i>
						<p>Review the Results</p>
					</div>
				</div>
			</div>
		);
	}
}
