import React from 'react';
import './students.css';
import { connect } from 'react-redux'

import { Modal, Button, Form, Spinner, Col } from 'react-bootstrap';
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from 'react-icons/io';
import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";

import Moment from 'react-moment'

import addImg from '../../assets/more (5).svg';
import imagepicker from '../../assets/photo.png';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

import {
    addStudent,
    showStudentInfo,
    editStudent,
    editStudentWithImage,
    deleteStudent
} from '../../../redux/actions/schoolAdminDashboard/schoolAdminActions';

class Students extends React.Component {
    constructor() {
        super();
        this.state = {
            showAdd: false,
            showDelete: false,
            showEdit: false,
            showView: false,
            name: "",
            dob: "",
            photo: [],
            address: "",
            blood: "",
            email: "",
            pname: "",
            pcontact: "",
            pemail: "",
            contact: '',
            class: "",
            section: "",
            city: "",
            state: "",
            zip: "",
            showImage: '',
            adding: false,
            deleting: false,
            editing: false,
            error: '',
            item: [],
            editname: "",
            editemail: "",
            editpemail: "",
            editphoto: [],
            editaddress: "",
            editblood: "",
            editdob: "",
            editcontact: '',
            editpemail: "",
            editclassName: "",
            editsectionName: "",
            editcity: "",
            editstate: "",
            editzip: "",
            editshowImage: '',
            imageEdited: false,
            showAssignSubject: false,
        }
    }

    componentDidMount() {
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handlePhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            console.log("88", typeof (e.target.files[0]));
            const f = e.target.files[0];
            this.setState({
                photo: f,
                showImage: URL.createObjectURL(f),
                error: ""
            })
        }
        else {
            this.setState({
                error: "Profile image size cannot be more than 1mb!"
            })
        }
    }



    handleAddStudent = async () => {
        this.setState({
            error: ""
        })
        if (this.state.showImage === "" || this.state.name === "" || this.state.email === "" || this.state.pemail === "" || this.state.address === "" || this.state.contact === "" || this.state.blood === "" || this.state.dob === "" || this.state.city === "" || this.state.state === "" || this.state.zip === "" || this.state.class === "" || this.state.section === "") {
            toaster.notify("Please fill all the details", {
                duration: 3000
            })
        }
        else {
            this.setState({
                adding: true
            })
            const data = {
                name: this.state.name,
                email: this.state.email,
                pname: this.state.pname,
                pcontact: this.state.pcontact,
                pemail: this.state.pemail,
                blood: this.state.blood,
                address: this.state.address,
                contact: this.state.contact,
                dob: this.state.dob,
                className: this.state.class,
                sectionName: this.state.section,
                city: this.state.city,
                state: this.state.state,
                zip: this.state.zip,
                schoolId: this.props.school._id
            }
            var form_data = new FormData();
            form_data.append('file', this.state.photo);
            const check = await this.props.addStudent(form_data, data);
            if (check) {
                this.setState({
                    adding: false
                })
                if (check.type === 'success') {
                    toaster.notify('New Student Added', {
                        duration: 3000
                    })
                    this.setState({
                        name: "",
                        email: "",
                        pemail: "",
                        blood: "",
                        address: "",
                        contact: "",
                        dob: "",
                        class: "",
                        section: "",
                        city: "",
                        state: "",
                        zip: "",
                        showImage: "",
                        showAdd: false
                    })
                }
                else if (check.type === 'error') {
                    toaster.notify(check.error, {
                        duration: 3000
                    })
                }
            }
        }
    }

    handleDeleteStudent = async () => {
        if (this.state.item) {
            this.setState({
                deleting: true,
                error: ""
            })
            const data = {
                id: this.state.item._id,
                photo: this.state.item.photo,
                schoolId: this.props.school._id
            };
            const check = await this.props.deleteStudent(data);
            if (check) {
                this.setState({
                    deleting: false
                })
                if (check.type === 'success') {
                    toaster.notify('Student removed', {
                        duration: 3000
                    })
                    this.setState({
                        showDelete: false,
                        item: []
                    })
                }
                else if (check.type === 'error') {
                    toaster.notify(check.error, {
                        duration: 3000
                    })
                }
            }
        }
        else {
            this.setState({
                error: "Could not find the item"
            })
        }
    }

    handleShowEditStudent = (e) => {
        this.setState({
            item: e,
            editblood: e.blood,
            editname: e.name,
            editemail: e.email,
            editphoto: [],
            editaddress: e.address,
            editpname: e.pname,
            editpcontact: e.pcontact,
            editpemail: e.pemail,
            editdob: e.dob,
            editcontact: e.contact,
            editclass: e.class,
            editsection: e.section,
            editcity: e.city,
            editstate: e.state,
            editzip: e.zip,
            editshowImage: "/api/file/show/" + e.photo.filename,
            showEdit: true,
            error: ""
        })
    }

    handleEditPhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                editPhoto: f,
                editshowImage: URL.createObjectURL(f),
                imageEdited: true,
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleEditStudent = async () => {
        this.setState({
            error: ""
        })
        if (this.state.editname === "" ||
            this.state.editaddress === "" ||
            this.state.editdob === "" ||
            this.state.editcontact === "" ||
            this.state.editcity === "" ||
            this.state.editstate === "" ||
            this.state.editzip === "") {
            toaster.notify("Please fill all the details", {
                duration: 3000
            })
        }
        else {
            this.setState({
                editing: true
            })
            const data = {
                name: this.state.editname,
                address: this.state.editaddress,
                email: this.state.editemail,
                pname: this.state.editpname,
                pcontact: this.state.editpcontact,
                dob: this.state.editdob,
                contact: this.state.editcontact,
                className: this.state.editclass,
                sectionName: this.state.editsection,
                blood: this.state.editblood,
                city: this.state.editcity,
                state: this.state.editstate,
                zip: this.state.editzip,
                id: this.state.item._id,
                schoolId: this.props.school._id,
                photo: this.state.item.photo
            };
            var form_data = new FormData();
            form_data.append('file', this.state.editPhoto);
            const check = this.state.imageEdited ? await this.props.editStudentWithImage(form_data, data) : await this.props.editStudent(data);
            if (check) {
                this.setState({
                    editing: false
                })
                if (check.type === 'success') {
                    toaster.notify('Student Information Updated', {
                        duration: 3000
                    })
                    this.setState({
                        editname: "",
                        editbranch: "",
                        editphoto: [],
                        editaddress: "",
                        editemail: "",
                        editpemail: "",
                        editcontact: '',
                        editfax: "",
                        editwebsite: "",
                        editshowImage: '',
                        showEdit: false
                    })
                }
            }
        }
    }

    render() {
        console.log(this.state.item.subjects);
        const school = this.props.school;
        const students = this.props.school ? this.props.school.students : null;
        const data = students;
        const columns = [
            {
                Header: "Name",
                accessor: "name",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Date of Birth",
                accessor: "dob",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Email",
                accessor: "email",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Contact No.",
                accessor: "contact",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Actions",
                Cell: (props) => {
                    return (
                        <div
                            style={{
                                width: "100%",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            <Button onClick={() => {
                                this.setState({
                                    showView: true,
                                    item: props.original,
                                    error: "",
                                    showAddAdmin: false,
                                    showEditAdmin: false,
                                    showAddOfficer: false
                                })
                            }} variant={"outline-dark"} className="action-buttons">Info</Button>
                            <Button onClick={() => this.handleShowEditStudent(props.original)} variant={"outline-info"} className="action-buttons">Edit</Button>
                            <Button onClick={() => {
                                this.setState({
                                    showDelete: true,
                                    item: props.original,
                                    error: ""
                                })
                            }} variant={"outline-danger"} className="action-buttons">Delete</Button>
                        </div>
                    );
                },
                sortable: false,
                filterable: false,
                minWidth: 150,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",

                },
            },
        ];

        return (
            <div className="students-container">
                <div className="students-header">
                    <div className="students-stats">
                        <h1>IIGP Students</h1>
                        <div className="officer-stats">
                            <p>TOTAL STUDENTS REGISTERED: <span>{this.props.school.students.length}</span></p>
                        </div>
                    </div>
                    <div className="students-add">
                        <div onClick={() => this.setState({
                            showAdd: true,
                            error: ""
                        })
                        }
                            className="add-officer-button">
                            <img src={addImg} alt="add-btn" />
                            <p>Add New Student</p>
                        </div>
                    </div>
                </div>
                <div className="students-body">
                    <ReactTable
                        data={data}
                        columns={columns}
                        defaultPageSize={10}
                        showPaginationTop
                        showPaginationBottom={false}
                        className="-striped -highlight"
                    />
                </div>
                <Modal
                    show={this.state.showAdd}
                    onHide={() => this.setState({
                        showAdd: false,
                        error: ""
                    })}
                    backdrop="static"
                    keyboard={false}
                    centered
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add Student Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <div className="image-container">
                                        <p>Photo</p>
                                        <div className="image-picker">
                                            <input id="image-picker" onChange={e => this.handlePhoto(e)} type="file" accept="image/*" />
                                            {
                                                this.state.showImage === ""
                                                    ?
                                                    <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                    :
                                                    <img htmlFor="#image-picker" src={this.state.showImage} />
                                            }
                                        </div>
                                    </div>

                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control name="name" type="text" placeholder="Enter name" value={this.state.name} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="email" type="text" placeholder="Enter email" value={this.state.email} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control name="address" type="text" placeholder="Enter address" value={this.state.address} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Date of birth</Form.Label>
                                    <Form.Control name="dob" type="date" value={this.state.dob} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Contact No.</Form.Label>
                                    <Form.Control name="contact" type="text" placeholder="Enter contact no." value={this.state.contact} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Blood Group</Form.Label>
                                    <Form.Control as="select" value={this.state.blood} onChange={e => { this.setState({ blood: e.target.value }) }}>
                                        <option value="" disabled hidden>Select an option</option>
                                        <option value="A+">A+</option>
                                        <option value="A-">A-</option>
                                        <option value="B+">B+</option>
                                        <option value="B-">B-</option>
                                        <option value="O+">O+</option>
                                        <option value="O-">O-</option>
                                        <option value="AB+">AB+</option>
                                        <option value="AB-">AB-</option>
                                    </Form.Control>
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Guardian Name</Form.Label>
                                    <Form.Control name="pname" type="text" placeholder="Enter name" value={this.state.pname} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Guardian Contact No.</Form.Label>
                                    <Form.Control name="pcontact" type="text" placeholder="Enter contact no." value={this.state.pcontact} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Guardian Email</Form.Label>
                                    <Form.Control name="pemail" type="text" placeholder="Enter parent email" value={this.state.pemail} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid" >
                                    <Form.Label>Select a Class</Form.Label>
                                    <Form.Control as="select" value={this.state.class} onChange={e => { this.setState({ class: e.target.value }) }}>
                                        <option value="" disabled hidden>Select a class</option>
                                        {
                                            school.classes.map(clas => {
                                                return (
                                                    <option value={clas.name}>{clas.name}</option>
                                                )
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Choose Section</Form.Label>
                                    <Form.Control as="select" value={this.state.section} onChange={e => { this.setState({ section: e.target.value }) }}>
                                        <option value="" disabled hidden>Select a section</option>
                                        {
                                            school.classes.map(clas => {
                                                if (clas.name === this.state.class) {
                                                    return (
                                                        <>
                                                            {
                                                                clas.sections.map(sec => {
                                                                    return (
                                                                        <option value={sec.name}>{sec.name}</option>
                                                                    )
                                                                })
                                                            }
                                                        </>
                                                    )
                                                }
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control name="city" type="text" placeholder="Enter city" value={this.state.city} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>State</Form.Label>
                                    <Form.Control name="state" type="text" placeholder="Enter state" value={this.state.state} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Zip</Form.Label>
                                    <Form.Control name="zip" type="text" placeholder="Enter zip" value={this.state.zip} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.adding
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleAddStudent} variant="success"><FaCheck />Add</Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showDelete}
                    onHide={() => this.setState({
                        showDelete: false,
                        error: ""
                    })}
                    dialogClassName="modal-70w"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>School Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group>
                                <Form.Label>School Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter school name" value={this.state.item.name} disabled />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>School Branch</Form.Label>
                                <Form.Control type="text" placeholder="Enter school branch name" value={this.state.item.branch} disabled />
                            </Form.Group>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.deleting
                                ?
                                <Spinner animation="border" role="status" variant="danger">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleDeleteStudent} variant="danger">Delete<FaRegTrashAlt /></Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showEdit}
                    onHide={() => this.setState({
                        showEdit: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Edit Student Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <div className="image-container">
                                        <p>Photo</p>
                                        <div className="image-picker">
                                            <input id="image-picker" onChange={e => this.handleEditPhoto(e)} type="file" accept="image/*" />
                                            {
                                                this.state.editshowImage === ""
                                                    ?
                                                    <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                    :
                                                    <img htmlFor="#image-picker" src={this.state.editshowImage} />
                                            }
                                        </div>
                                    </div>

                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control name="editname" type="text" placeholder="Enter name" value={this.state.editname} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="editemail" type="text" placeholder="Enter email" value={this.state.editemail} onChange={this.handleChange} disabled />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control name="editaddress" type="text" placeholder="Enter address" value={this.state.editaddress} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Date of birth</Form.Label>
                                    <Form.Control name="editdob" type="date" value={this.state.editdob} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Contact No.</Form.Label>
                                    <Form.Control name="editcontact" type="text" placeholder="Enter school contact no." value={this.state.editcontact} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Blood Group</Form.Label>
                                    <Form.Control as="select" value={this.state.editblood} onChange={e => { this.setState({ editblood: e.target.value }) }}>
                                        <option value="" disabled hidden>Select an option</option>
                                        <option value="A+">A+</option>
                                        <option value="A-">A-</option>
                                        <option value="B+">B+</option>
                                        <option value="B-">B-</option>
                                        <option value="O+">O+</option>
                                        <option value="O-">O-</option>
                                        <option value="AB+">AB+</option>
                                        <option value="AB-">AB-</option>
                                    </Form.Control>
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Guardian Name</Form.Label>
                                    <Form.Control name="editpname" type="text" placeholder="Enter name" value={this.state.editpname} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Guardian Contact No.</Form.Label>
                                    <Form.Control name="editpcontact" type="text" placeholder="Enter contact no." value={this.state.editpcontact} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Guardian Email</Form.Label>
                                    <Form.Control disabled name="editpemail" type="text" placeholder="Enter parent email" value={this.state.editpemail} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid" >
                                    <Form.Label>Select a Class</Form.Label>
                                    <Form.Control as="select" value={this.state.editclass} onChange={e => { this.setState({ editclass: e.target.value }) }}>
                                        <option value="" disabled hidden>Select a class</option>
                                        {
                                            school.classes.map(clas => {
                                                return (
                                                    <option value={clas.name}>{clas.name}</option>
                                                )
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Choose Section</Form.Label>
                                    <Form.Control as="select" value={this.state.editsection} onChange={e => { this.setState({ editsection: e.target.value }) }}>
                                        <option value="" disabled hidden>Select a section</option>
                                        {
                                            school.classes.map(clas => {
                                                if (clas.name === this.state.editclass) {
                                                    return (
                                                        <>
                                                            {
                                                                clas.sections.map(sec => {
                                                                    return (
                                                                        <option value={sec.name}>{sec.name}</option>
                                                                    )
                                                                })
                                                            }
                                                        </>
                                                    )
                                                }
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>

                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control name="editcity" type="text" placeholder="Enter city" value={this.state.editcity} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>State</Form.Label>
                                    <Form.Control name="editstate" type="text" placeholder="Enter state" value={this.state.editstate} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Zip</Form.Label>
                                    <Form.Control name="editzip" type="text" placeholder="Enter zip" value={this.state.editzip} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.editing
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleEditStudent} variant="success"><FaCheck />Save</Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showView}
                    onHide={() => this.setState({
                        showView: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Academic Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        school: state.schoolAdmin.school,
        schoolError: state.schoolAdmin.schoolError,
        schoolSucc: state.schoolAdmin.schoolSucc,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addStudent: (form_data, data) => dispatch(addStudent(form_data, data)),
        showStudentInfo: (data) => dispatch(showStudentInfo(data)),
        editStudentWithImage: (form_data, data) => dispatch(editStudentWithImage(form_data, data)),
        editStudent: (data) => dispatch(editStudent(data)),
        deleteStudent: (data) => dispatch(deleteStudent(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Students);