import React from "react";
import { connect } from "react-redux";
import "./library.css";
import moment from "moment";

class Library extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			resources: [
				{
					image: "https://m.media-amazon.com/images/I/51mlO9iGkQL.jpg",
					title: "GRADE 1 MATHEMATICS",
					authorName: "Zohaib Kibria",
					subjectName: "Mathematics",
					date: new Date(),
					keywords: ["Maths", "GRADE 1", "Beginner"],
					file: {
						filename: "043d5f282ffd3d4f7b148963905e99a9.pdf",
					},
				},
			],
			acresources: [
				{
					image: "https://m.media-amazon.com/images/I/51mlO9iGkQL.jpg",
					title: "GRADE 1 MATHEMATICS",
					authorName: "Zohaib Kibria",
					subjectName: "Mathematics",
					date: new Date(),
					keywords: ["Maths", "GRADE 1", "Beginner"],
					file: {
						filename: "043d5f282ffd3d4f7b148963905e99a9.pdf",
					},
				},
			],
			search: "",
			filters: ["title", "keywords", "author", "subject"],
			activeFilter: "title",
		};
	}

	handleChange = (e) => {
		this.setState(
			{
				[e.target.id]: e.target.value,
			},
			() => {
				this.handleSearch();
			}
		);
	};

	handleFilter = (e) => {
		this.setState(
			{
				activeFilter: e,
			},
			() => {
				this.handleSearch();
			}
		);
	};

	handleSearch = () => {
		var resources = this.state.acresources;
		var newResources = [];
		if (this.state.search.length > 0) {
			resources.map((resource) => {
				if (this.state.activeFilter === "title" && resource.title.includes(this.state.search)) {
					newResources.push(resource);
				} else if (this.state.activeFilter === "keywords" && resource.keywords.includes(this.state.search)) {
					newResources.push(resource);
				} else if (this.state.activeFilter === "subject" && resource.subjectName.includes(this.state.search)) {
					newResources.push(resource);
				} else if (this.state.activeFilter === "author" && resource.authorName.includes(this.state.search)) {
					newResources.push(resource);
				}
			});
			this.setState({
				resources: newResources,
			});
		} else {
			resources.map((resource) => {
				newResources.push(resource);
			});
			this.setState({
				resources: newResources,
			});
		}
	};

	render() {
		var teacher = this.props.teacher;
		return (
			<div className='library-container'>
				<h1>LIBRARY</h1>
				<div className='library-filter'>
					<input placeholder='Search by name or type' value={this.state.search} onChange={this.handleChange} id='search' />
					<div className='filters'>
						{this.state.filters.map((filter) => {
							if (this.state.activeFilter === filter) {
								return <p className='filter active-filter'>{filter}</p>;
							} else {
								return (
									<p onClick={() => this.handleFilter(filter)} className='filter'>
										{filter}
									</p>
								);
							}
						})}
					</div>
				</div>
				{this.state.resources.map((resource) => {
					return (
						<a href={"/api/file/show/" + resource.file.filename} target='_blank' className='library-box'>
							<img src={resource.image} alt='icon' />
							<div className='topic-name'>
								<h1>{resource.title}</h1>
								<p>
									{resource.authorName} | {resource.subjectName}
								</p>
								<div className='keywords'>
									{resource.keywords.map((k) => {
										return <p>{k}</p>;
									})}
								</div>
							</div>
							<div className='topic-date'>
								<p>Date: {moment(resource.date).format("DD-MM-YYYY")}</p>
								<button>click to view</button>
							</div>
						</a>
					);
				})}
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps)(Library);
