import React from 'react';

import './behaviour.style.css';

import { Form } from 'react-bootstrap';

import moment from 'moment';

class MyBehaviour extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            subject: '',
            format: '',
            date: '',
            month: '',
            behaviourArray: [],
            filteredArray: [],
            behaviours: []
        }
    }

    componentDidMount = () => {
        this.setState({
            behaviourArray: this.props.behaviour,
            loading: false
        })
    }

    handleSetSubject = (e) => {
        this.setState({
            subject: e.target.value
        })
    }

    handleFormat = (e) => {
        if (this.state.subject === '') {
            alert('Please select subject');
        }
        else {
            this.setState({
                format: e.target.value
            })
        }
    }

    handleSetDate = (e) => {
        this.setState({
            date: e.target.value
        }, () => {
            this.handleFilter('Day')
        })
    }

    handleSetMonth = (e) => {
        this.setState({
            month: e.target.value
        }, () => {
            this.handleFilter('Month');
        })
    }

    handleFilter = (format) => {

        var filteredArray = [], behaviours = [];

        if (format === 'Day') {
            this.state.behaviourArray.map((item) => {
                if (item.date === this.state.date) {
                    filteredArray.push(item);
                }
            })
        }

        if (format === 'Month') {
            this.state.behaviourArray.map((item) => {
                if (item.date.substring(0, item.date.length - 3) === this.state.month) {
                    filteredArray.push(item);
                }
            })
        }

        filteredArray.map((item) => {
            var behave = {};
            behave.date = item.date;
            behave.behaviour = item.status;
            behaviours.push(behave);
        })

        this.setState({
            behaviours: behaviours
        })
    }

    render() {
        return (
            <div className="my-behaviour-container">
                <div className="behaviour-wrapper">
                    {
                        this.state.loading
                            ?
                            null
                            :
                            <>
                                <div className="select-sub">
                                    <Form.Group className="container-fluid">
                                        <Form.Label>Select Subject</Form.Label>
                                        <Form.Control as="select" name="subject" onChange={this.handleSetSubject}>
                                            <option selected hidden>Choose...</option>
                                            <option value="Mathematics">Mathematics</option>
                                            <option value="Science">Science</option>
                                            <option value="English">English</option>
                                            <option value="Islamic Studies">Islamic Studies</option>
                                            <option value="Social Studies">Social Studies</option>
                                            <option value="Information Technology">Information Technology</option>
                                            <option value="Life Skills">Life Skills</option>
                                            <option value="Music">Music</option>
                                            <option value="Sports">Sports</option>
                                            <option value="Arts">Arts</option>
                                        </Form.Control>
                                    </Form.Group>
                                </div>

                                <div className="select-sub">
                                    <Form.Group className="container-fluid">
                                        <Form.Label>Select Format</Form.Label>
                                        <div key='inline-radio'>
                                            <Form.Check inline label="Day" type='radio' name="format" value="Day" onChange={this.handleFormat} />
                                            <Form.Check inline label="Monthly" type='radio' name="format" value="Monthly" onChange={this.handleFormat} />
                                        </div>
                                    </Form.Group>
                                </div>

                                {/* Select Day */}
                                {
                                    this.state.format === 'Day'
                                        ?
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Select Date</Form.Label>
                                            <Form.Control type="date" name="date" onChange={this.handleSetDate} />
                                        </Form.Group>
                                        :
                                        null
                                }

                                {/* Select Month */}
                                {
                                    this.state.format === 'Monthly'
                                        ?
                                        <Form.Group className="container-fluid">
                                            <Form.Label>Select Month</Form.Label>
                                            <Form.Control type="month" name="month" onChange={this.handleSetMonth} />
                                        </Form.Group>
                                        :
                                        null
                                }

                                <div className='behaviour-chart'>
                                    {
                                        this.state.behaviours.map((item) => (
                                            <div className="behaviour-card">
                                                <div className="behaviour-date">
                                                    <p>{moment(item.date).format('LL')}</p>
                                                </div>
                                                <div className='behaviour-list'>
                                                    <div className={item.behaviour.includes("worked hard") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>worked hard</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("asked thoughtful questions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>asked thoughtful questions</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("contributed to class discussions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>contributed to class discussions</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("contributed to group") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>contributed to group</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("following directions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>following directions</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("attentive during instructions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>attentive during instructions</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("assisted peer") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-plus-circle'></i>
                                                        <p>assisted peer</p>
                                                    </div>
                                                </div>

                                                <div className='behaviour-list'>
                                                    <div className={item.behaviour.includes("off tasks") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>off tasks</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("distracted others") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>distracted others</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("improper phone using") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>improper phone using</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("talking during instructions") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>talking during instructions</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("out of seat") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>out of seat</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("disrespectful") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>disrespectful</p>
                                                    </div>
                                                    <div className={item.behaviour.includes("unprepared for class") ? "behaviour active" : "behaviour"}>
                                                        <i class='fas fa-minus-circle'></i>
                                                        <p>unprepared for class</p>
                                                    </div>
                                                </div>
                                            </div>
                                        ))
                                    }
                                </div>
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default MyBehaviour;