import React from "react";
import { connect } from "react-redux";
import "./resource.css";
import pdf from "../assets/pdf.png";
import ppt from "../assets/ppt.png";
import doc from "../assets/doc.png";
import mp4 from "../assets/mp4.png";
import moment from "moment";

class Resource extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			resources: props.teacher ? props.teacher.resources : [],
			search: "",
			filters: ["all", "pdf", "doc", "ppt", "xls", "mp4", "mp3"],
			activeFilter: "all",
		};
	}

	handleChange = (e) => {
		this.setState(
			{
				[e.target.id]: e.target.value,
			},
			() => {
				this.handleSearch();
			}
		);
	};

	handleFilter = (e) => {
		this.setState(
			{
				activeFilter: e,
			},
			() => {
				this.handleSearch();
			}
		);
	};

	handleSearch = () => {
		var resources = this.props.teacher ? this.props.teacher.resources : [];
		var newResources = [];
		if (this.state.search.length > 0) {
			resources.map((resource) => {
				if (
					resource.resourceName.includes(this.state.search) ||
					resource.topicName.includes(this.state.search) ||
					resource.chapterName.includes(this.state.search) ||
					resource.className.includes(this.state.search) ||
					resource.subjectName.includes(this.state.search)
				) {
					if (this.state.activeFilter !== "all") {
						if (resource.resourceName.split(".")[1].includes(this.state.activeFilter)) {
							newResources.push(resource);
						}
					} else {
						newResources.push(resource);
					}
				}
			});
			this.setState(
				{
					resources: newResources,
				},
				() => {
					console.log(this.state.resources);
				}
			);
		} else {
			resources.map((resource) => {
				if (this.state.activeFilter !== "all") {
					if (resource.resourceName.split(".")[1].includes(this.state.activeFilter)) {
						newResources.push(resource);
					}
				} else {
					newResources.push(resource);
				}
			});
			this.setState(
				{
					resources: newResources,
				},
				() => {
					console.log(this.state.resources);
				}
			);
		}
	};

	render() {
		var teacher = this.props.teacher;
		return (
			<div className='resource-container'>
				<h1>RESOURCES</h1>
				<div className='resource-filter'>
					<input placeholder='Search by name or type' value={this.state.search} onChange={this.handleChange} id='search' />
					<div className='filters'>
						{this.state.filters.map((filter) => {
							if (this.state.activeFilter === filter) {
								return <p className='filter active-filter'>{filter}</p>;
							} else {
								return (
									<p onClick={() => this.handleFilter(filter)} className='filter'>
										{filter}
									</p>
								);
							}
						})}
					</div>
				</div>
				{this.state.resources.map((resource) => {
					if (
						resource.resourceName.split(".")[1].includes("pdf") ||
						resource.resourceName.split(".")[1].includes("doc") ||
						resource.resourceName.split(".")[1].includes("docx") ||
						resource.resourceName.split(".")[1].includes("ppt") ||
						resource.resourceName.split(".")[1].includes("pptx") ||
						resource.resourceName.split(".")[1].includes("txt") ||
						resource.resourceName.split(".")[1].includes("xls") ||
						resource.resourceName.split(".")[1].includes("xlsx")
					) {
						return (
							<a href={"/api/file/show/" + resource.file.filename} target='_blank' className='resource-box'>
								{resource.resourceName.split(".")[1].includes("pdf") ? <img src={pdf} alt='icon' /> : null}
								{resource.resourceName.split(".")[1].includes("doc") || resource.resourceName.split(".")[1].includes("docx") ? (
									<img src={doc} alt='icon' />
								) : null}
								{resource.resourceName.split(".")[1].includes("ppt") || resource.resourceName.split(".")[1].includes("pptx") ? (
									<img src={ppt} alt='icon' />
								) : null}
								<div className='topic-name'>
									<h1>{resource.resourceName}</h1>
									<p>
										{resource.className} | {resource.subjectName} | {resource.chapterName} | {resource.topicName} |{" "}
										{resource.code}
									</p>
								</div>
								<div className='topic-date'>
									<p>Date: {moment(resource.date).format("DD-MM-YYYY")}</p>
									<button>click to view</button>
								</div>
							</a>
						);
					}

					if (resource.resourceName.split(".")[1].includes("mp4")) {
						return (
							<div className='topicList-box' onClick={() => this.handleShowResource(resource)}>
								{resource.resourceName.split(".")[1].includes("mp4") ? <img src={mp4} alt='icon' /> : null}
								<div className='topic-name'>
									<h1>{resource.resourceName}</h1>
									<p>
										{resource.className} | {resource.subjectName} | {resource.chapterName} | {resource.topicName} |{" "}
										{resource.code}
									</p>
								</div>
								<div className='topic-date'>
									<p>Date: {moment(resource.date).format("DD-MM-YYYY")}</p>
									<button>click to view</button>
								</div>
							</div>
						);
					}
				})}
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps)(Resource);
