import React from 'react';

import './discussionRoom.css';

import moment from 'moment';

import { GoKebabVertical } from 'react-icons/go';
import { AiOutlinePlus } from 'react-icons/ai';

import { Modal, Button, Form } from "react-bootstrap";

import { connect } from "react-redux";
import { addMessage } from '../../../../redux/actions/teacherDashboard/teacherActions';

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import MessageBox from '../messageBox/messageBox';

class DiscussionRoom extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            addMsg: false,
            addReply: false,
            message: "",
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;

        this.setState({
            [name]: value
        })
    }

    handleSubmitMessage = async () => {
        const data = {
            message: this.state.message,
            userId: this.props.teacher._id,
            userRole: 'teacher',
            id: this.props.discussion._id
        }

        var check = await this.props.addMessage(data);
        if (check) {
            if (check.type === "success") {
                toaster.notify("Message Sent", {
                    duration: 3000,
                });
                this.setState({
                    message: "",
                    addMsg: false,
                });
            } else {
                toaster.notify("Message could not be sent !", {
                    duration: 3000,
                });
                this.setState({
                    message: "",
                    addMsg: false,
                });
            }
        }
    }

    render() {

        console.log(this.props.discussion);

        return (
            <div className="discussion-room-container">
                <div className="room-header">
                    <div className="room-info">
                        <h3>{this.props.discussion.title}
                            {
                                this.props.discussion.audience === 'students'
                                    ?
                                    <span> | {this.props.discussion.grade}, {this.props.discussion.section}</span>
                                    :
                                    <>
                                        {
                                            this.props.discussion.audience === 'faculty'
                                                ?
                                                <span> | Faculty</span>
                                                :
                                                <span>Parents</span>
                                        }
                                    </>
                            }
                        </h3>
                        <p>{moment(this.props.discussion.date).fromNow()}</p>
                    </div>
                    <span><GoKebabVertical /></span>
                </div>
                <div className="room-body">
                    {
                        this.props.teacher.discussions && this.props.teacher.discussions.map((item) => {
                            if (item._id === this.props.discussion._id) {
                                return (
                                    item.chats && item.chats.map((x) => {
                                        if (x.userId === this.props.teacher._id) {
                                            return (
                                                <div className="own-chat">
                                                    <MessageBox msg={x} teacherData={this.props.teacher} discId={this.props.discussion._id} />
                                                </div>
                                            )
                                        }

                                        else {
                                            return (
                                                <div className="other-chat">
                                                    <MessageBox msg={x} discId={this.props.discussion._id} />
                                                </div>
                                            )
                                        }

                                    })
                                )
                            }
                        })
                    }
                </div>

                <div className="add-msg-btn" onClick={() => this.setState({ addMsg: true })}>
                    <p><AiOutlinePlus /></p>
                </div>

                {/* For Message */}

                <Modal show={this.state.addMsg} onHide={() => this.setState({ addMsg: false })} backdrop='static' keyboard={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>Add Message</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form.Group controlId='formBasicEmail'>
                            <Form.Control as="textarea" rows={5} name='message' onChange={this.handleChange} placeholder="Start typing your message ..." />
                        </Form.Group>

                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant='primary' onClick={this.handleSubmitMessage}>
                            Send
						</Button>
                    </Modal.Footer>
                </Modal>

            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addMessage: (data) => dispatch(addMessage(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(DiscussionRoom);
