import React from 'react';

import './recognition.css';

import { connect } from 'react-redux'
import StudentCard from '../Students/studentCard/studentCard';

class Recognition extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            stuDetails : {}, 
            //exapnd view
            expand : false
        }
    }

    handleExpandView = (e) => {
        this.setState({
            stuDetails : e,
            expand : true
        })
    }

    render() {
        return(
            <div className = "recognition-container">
                <div className = "recognised-wrap">
                    <div className = "most-recognised">
                        <h1>Top Performing Student</h1>
                        <StudentCard 
                            student = {this.props.teacher.students[0]} 
                            isRecognition = {true}
                            topPerformer = {true}
                            expandView = {(value) => this.handleExpandView(value)}
                        />
                    </div>
                    <div className = "others">
                        {
                            this.props.teacher.students.map((item, index) => {
                                if (index > 0) {
                                    return (
                                        <StudentCard 
                                            student = {item} 
                                            isRecognition = {true}
                                            expandView = {(value) => this.handleExpandView(value)}
                                        />
                                    )
                                }
                            })
                        }
                    </div>
                    
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData
    };
}

export default connect(mapStateToProps)(Recognition);