import { combineReducers } from "redux";
import authReducer from "./authReducer";
import superAdminReducer from './superAdminReducer';
import schoolAdminReducer from './schoolAdminReducer';
import districtOfficerReducer from './districtOfficerReducer'
import studentReducer from "./studentReducer";
import teacherReducer from "./teacherReducer";
import parentReducer from "./parentReducer";

export default combineReducers({
    auth: authReducer,
    superAdmin: superAdminReducer,
    schoolAdmin: schoolAdminReducer,
    districtOfficer: districtOfficerReducer,
    student : studentReducer,
    teacher : teacherReducer,
    parent : parentReducer
});
