import React from 'react';

import './singleAnnouncement.css';

import Skeleton, { SkeletonTheme } from "react-loading-skeleton";

import axios from 'axios';

import moment from 'moment';

class SingleAnnouncement extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            teacher: {}
        }
    }

    componentDidMount = async () => {

        const data = {
            teacherId: this.props.announcement.teacherId
        }

        var response = await axios.post("/api/myschool/getTeacher", data);
        if (response.data !== null) {
            this.setState({
                teacher: response.data.result,
                loading: false
            })
        }
    }

    render() {
        return (
            <div className="singleAnnouncement-container">
                <div className="announcement-head">
                    <p>{this.props.announcement.title}</p>
                    <button>
                        {
                            this.props.announcement.type === 'whole'
                                ?
                                "For Whole Grade"
                                :
                                "For You"
                        }
                    </button>
                </div>
                <div className="announcement-body">
                    {
                        this.state.loading
                            ?
                            <>
                                <SkeletonTheme>
                                    <div style={{ width: "100%", display: "flex", alignItems: "center" }}>
                                        <p style={{ marginRight: "10px" }}><Skeleton circle={true} height={50} width={50} /></p>
                                        <p><Skeleton height={30} width={250} /></p>
                                    </div>
                                    <div>
                                        <p><Skeleton height={30} width={310} /></p>
                                    </div>
                                </SkeletonTheme>
                            </>
                            :
                            <>
                                <div className="teacher-info">
                                    <img src={"/api/file/show/" + this.state.teacher.photo.filename} alt="teacher-pic" />
                                    <p>{this.state.teacher.name}</p>
                                </div>
                                <p>Created On <span>{moment(this.props.announcement.doa).format('LL')}</span></p>
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default SingleAnnouncement;