import React, { useDebugValue } from 'react';
import './parentDashboard.css';
import {Modal} from 'react-bootstrap';

import FloralLoading from './loadingScreen/floralLoading';
import downArrow from './assets/down-arrow.png';

import { Spinner } from "react-bootstrap";

import logo from './assets/iigp.png';

import { connect } from 'react-redux';

import { fetch_parent_profile_data } from '../../redux/actions/parentDashboard/parentActions';

import OwlCarousel from 'react-owl-carousel';  
import 'owl.carousel/dist/assets/owl.carousel.css';  
import 'owl.carousel/dist/assets/owl.theme.default.css'; 

import Chart from "react-apexcharts";
import maths from './assets/math.svg';import science from './assets/science.svg';import english from './assets/english.svg';import islamic from './assets/islamic.svg';import computer from './assets/computer.svg';import skills from './assets/skills.svg';import arts from './assets/arts.svg';import sports from './assets/sports.svg';import music from './assets/music-note.svg';import social from './assets/social.svg';

import axios from 'axios';
import SubjectStats from './analytics/subjectStats';
import ChildCard from './childCard/childCard';

class ParentDashboard extends React.Component {
    
    constructor(props) {
        super(props);

        this.state = {
            loading : true,
            loadingSub : false,
            parentDetails : {},
            navTab : 'IIGP',
            studentId : '',
            analyticsData : [],
            showChildModal : false,
            childList : [],
            // SHOW SUBJECT ANALYTICS
            subTab : false,
            sub : '',
            // LAST 10 PRACTICE BAR GRAPH
            categories : [],
            data1 : [],
            // LAST SESSION RADIALBAR CHART
            totalRight : 0,
            totalWrong : 0,
            subject : '',
            chapter : '',
            topic : '',
        }
    }

    componentDidMount = async() => {
        if (this.props.auth.currentUser._id) {

            const parentData = {
                parentId : this.props.auth.currentUser.ref_id
            }

            const result = await this.props.fetch_parent_profile_data(parentData);
            
            if(result) {
                // Check if 2 or more child
                if (result.children.length > 1) {
                    this.setState({
                        childList : result.children,
                        showChildModal : true
                    })
                }

                // Only one child
                else {
                    const studentData = {
                        studentId : result.children[0].id
                    }
                    const res = await axios.post("/api/parent/analytics/getLastTen", studentData);
                    if (res.data !== null) {
                        var categories = [], data1 = [];
                        res.data.map((item) => {
                            categories.push(item.subject);
                            data1.push(item.score)
                        })
                        this.setState({
                            studentId : result.children[0].id,
                            // Last 10
                            categories : categories,
                            data1 : data1,
                            // Last session
                            totalRight : res.data[0].correct,
                            totalWrong : res.data[0].wrong,
                            subject : res.data[0].subject,
                            chapter : res.data[0].chapter,
                            topic : res.data[0].topic,
                            // storing parent details
                            loading : false,
                        })
                    }
                }
            }
        }
    }

    handleSelectChild = async(id) => {
        const studentData = {
            studentId : id
        }
        const res = await axios.post("/api/parent/analytics/getLastTen", studentData);
        if (res.data !== null) {
            var categories = [], data1 = [];
            res.data.map((item) => {
                categories.push(item.subject);
                data1.push(item.score)
            })
            this.setState({
                studentId : id,
                // Last 10
                categories : categories,
                data1 : data1,
                // Last session
                totalRight : res.data[0].correct,
                totalWrong : res.data[0].wrong,
                subject : res.data[0].subject,
                chapter : res.data[0].chapter,
                topic : res.data[0].topic,
                // storing parent details
                loading : false,
                showChildModal : false
            })
        }
    }

    handleFetchAnalytics = async(value) => {
        this.setState({
            loadingSub : true
        })
        const data = {
            studentId : this.state.studentId,
            subject : value
        }

        var response = await axios.post("/api/parent/analytics/subject", data);
        if (response.data !== null) {
            if (response.data.type === 'success') {
                this.setState({
                    analyticsData : response.data.data,
                    subTab : true,
                    loadingSub: false
                })
            }
        }
    }

    render() {
        var options = {
            chart: {
                id: "basic-bar"
            },
            xaxis: {
                categories : this.state.categories
            }
        };

        var series = [
            {
                name: "Total Marks",
                data: this.state.data1
            }
        ];

        var series2 = [(this.state.totalRight + this.state.totalWrong), this.state.totalRight, this.state.totalWrong];
        var options2 = {
                chart: {
                    height: 390,
                    type: 'radialBar',
                },
                plotOptions: {
                    radialBar: {
                        offsetY: 0,
                        startAngle: 0,
                        endAngle: 270,
                        hollow: {
                            margin: 5,
                            size: '30%',
                            background: 'transparent',
                            image: undefined,
                        },
                        dataLabels: {
                            name: {
                                show: false,
                            },
                            value: {
                                show: false,
                            }
                        },
                    }
                },
                colors: ['#1ab7ea', '#61ce70', '#E8594A'],
                labels: ['Total Attempts', 'Correct', 'Wrong'],
                legend: {
                    show: true,
                    floating: true,
                    fontSize: '16px',
                    position: 'left',
                    offsetX: 160,
                    offsetY: 15,
                    labels: {
                        useSeriesColors: true,
                    },
                    markers: {
                      size: 0
                    },
                    formatter: function(seriesName, opts) {
                      return seriesName + ":  " + opts.w.globals.series[opts.seriesIndex]
                    },
                    itemMargin: {
                        vertical: 3
                    }
                },
                responsive: [{
                    breakpoint: 480,
                    options: {
                        legend: {
                            show: false
                        }
                    }
                }]
            };
        return(
            <>
                {
                    this.state.showChildModal
                    ?
                    <div className = "child-select-overlay">
                        <Modal
                            show={this.state.showChildModal}
                            aria-labelledby="contained-modal-title-vcenter"
                            centered
                            backdrop="static"
                            keyboard={false}
                        >
                            <Modal.Header>
                                <Modal.Title>Welcome Parent</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>
                                <div className = "select-child-wrapper">
                                    <h1>Select any one</h1>
                                    <div className = "children-list-container">
                                        {
                                            this.state.childList.map((children) => {
                                                return <ChildCard studentId = {children} selectedId = {(value) => this.handleSelectChild(value)}/>
                                            })
                                        }
                                    </div>
                                </div>
                            </Modal.Body>
                        </Modal>
                    </div>
                    :
                    <>
                    {

                        this.state.loading
                        ?
                        <FloralLoading />
                        :
                        <div className = "parent-homepage-container">
                            <div className = "iigp-navbar-container">
                                <div className = "iigp-logo">
                                    <div><img src={logo} alt="iigp-logo"/></div>
                                </div>
                                <div className = "menu-links">
                                    <div className = "link-box" onClick = {() => this.setState({navTab : 'IIGP'})}>
                                        <p>IIGP</p>
                                    </div>
                                    <div className = "link-box" onClick = {() => this.setState({navTab : 'school'})}>
                                        <p>SCHOOL</p>
                                    </div>
                                    <div className = "link-user">
                                        {/* <img src={"/api/file/show/" + this.props.student.photo.filename} alt="user-icon" id="user-icon"/> */}
                                        {/* <p>Welcome, {this.props.student.name}</p> */}
                                        <p>Welcome, PARENT</p>
                                        <img src={downArrow} alt="down-arrow" id="arrow-icon"/>
                                    </div>  
                                </div>
                            </div>
                            <div className = "learning-breadcrumb">
                                <p>IIGP ANALYTICS</p>
                            </div>
                            <div className = "learning-choose-subject-breadcrumb">
                                <OwlCarousel className = "owl-theme" items={7} dots ={false}>
                                    <div className = "subject-option" onClick = {() => this.handleFetchAnalytics('Mathematics')}>
                                        <img src = {maths} alt="subject"/>
                                        <p>Mathematics</p>
                                    </div>

                                    <div className = "subject-option" onClick = {() => this.handleFetchAnalytics('Science')}>
                                        <img src = {science} alt="subject"/>
                                        <p>Science</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {english} alt="subject"/>
                                        <p>English</p>
                                    </div>
                                    
                                    <div className = "subject-option">
                                        <img src = {islamic} alt="subject"/>
                                        <p>Islamic Studies</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {social} alt="subject"/>
                                        <p>Social Studies</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {computer} alt="subject"/>
                                        <p>Information Technology</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {skills} alt="subject"/>
                                        <p>Life Skills</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {sports} alt="subject"/>
                                        <p>Sports</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {music} alt="subject"/>
                                        <p>Music</p>
                                    </div>

                                    <div className = "subject-option">
                                        <img src = {arts} alt="subject"/>
                                        <p>Arts</p>
                                    </div>
                                </OwlCarousel>
                            </div>
                            {
                                this.state.subTab
                                ?
                                <>
                                {
                                    this.state.loadingSub
                                    ?
                                    <div className = "sub-loading-container">
                                        <Spinner animation='border' role='status' variant='primary'>
                                        <span className='sr-only'>Loading...</span>
                                        </Spinner>
                                    </div>
                                    :
                                    <SubjectStats Adata = {this.state.analyticsData}/>
                                }
                                </>
                                :
                                <div className = "basic-analytics">
                                    <div className = "last-ten-analytics">
                                        <h1>Last 10 practices by user</h1>
                                        <Chart
                                            options = {options}
                                            series = {series}
                                            type = "bar"
                                            width = "600"
                                        />
                                    </div>
                                    <div className = "latest-analytics">
                                        <h1>Last practice session - {this.state.subject}</h1>
                                        <Chart options={options2} series={series2} type="radialBar" width={750} height={350} />
                                        <h5>{this.state.chapter} - {this.state.topic}</h5>
                                    </div>
                                </div>
                            }
                        </div>
                    }
                    </>
                }
            </>
        )
    }
}

function mapStateToProps(state) {
    return {
        auth : state.auth,              
        parent : state.parent.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetch_parent_profile_data : (parentData) => dispatch(fetch_parent_profile_data(parentData))     
    }
}

export  default connect(mapStateToProps, mapDispatchToProps)(ParentDashboard);