import React from "react";
import ReactTable from "react-table-6";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { connect } from "react-redux";
import { addAttendance } from "../../../redux/actions/teacherDashboard/teacherActions";
import axios from "axios";

class AttendanceList extends React.Component {
	constructor() {
		super();
		this.state = {
			showAdd: false,
			showEdit: false,
			showDelete: false,
			attend: null,
			date: new Date(),
		};
	}

	handleAttendance = async (e, d) => {
		const data = {
			className: this.props.sub.class,
			sectionName: this.props.sub.section,
			subjectName: this.props.sub.subject,
			status: e,
			teacherId: this.props.teacher._id,
			studentId: d._id,
			date: new Date(this.state.date).getFullYear() + "-" + new Date(this.state.date).getMonth() + "-" + new Date(this.state.date).getDate(),
		};
		console.log(data);
		const check = await this.props.addAttendance(data);
		console.log(check);
		if (check) {
			this.setState({
				loading: false,
			});
			if (check.type === "success") {
				toaster.notify("Attendance updated", {
					duration: 3000,
				});
				this.setState({
					note: "",
				});
			} else {
				toaster.notify(check.error, {
					duration: 3000,
				});
			}
		}
	};

	onChangeDate = (e) => {
		this.setState({
			date: e,
		});
	};

	render() {
		const school = this.props.teacher.schoolInfo;
		var students = [];
		this.props.teacher.students.map((stud) => {
			if (stud.class === this.props.sub.class) {
				students.push(stud);
			}
		});
		const data = students;
		const columns = [
			{
				Header: "Photo",
				Cell: (props) => {
					return (
						<div
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}>
							<img className='student-photo' src={"/api/file/show/" + props.original.photo.filename} />
						</div>
					);
				},
				sortable: false,
				filterable: false,
				width: 100,
				style: {
					display: "flex",
					alignItems: "center",
				},
			},
			{
				Header: "Name",
				accessor: "name",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Contact No.",
				accessor: "contact",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Actions",
				Cell: (props) => {
					var selectDate =
						new Date(this.state.date).getFullYear() +
						"-" +
						new Date(this.state.date).getMonth() +
						"-" +
						new Date(this.state.date).getDate();

					var attendance = {};
					var attendances = props.original.attendance;
					if (attendances.length > 0) {
						attendances.map((a) => {
							if (
								a.className === this.props.sub.class &&
								a.sectionName === this.props.sub.section &&
								a.subjectName === this.props.sub.subject &&
								a.date === selectDate
							) {
								attendance = a;
							}
						});
					}
					return (
						<div
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}>
							<div
								onClick={() => this.handleAttendance("Absent", props.original)}
								className={attendance.status === "Absent" ? "attendance-action active" : "attendance-action"}>
								<p>Absent</p>
							</div>
							<div
								onClick={() => this.handleAttendance("Present", props.original)}
								className={attendance.status === "Present" ? "attendance-action active" : "attendance-action"}>
								<p>Present</p>
							</div>
						</div>
					);
				},
				sortable: false,
				filterable: false,
				minWidth: 150,
				style: {
					color: "#fff",
					display: "flex",
					alignItems: "center",
				},
			},
		];
		return (
			<div className='attendance-container'>
				<div className='attendance-list'>
					<ReactTable
						data={data}
						columns={columns}
						defaultPageSize={10}
						showPaginationTop
						showPaginationBottom={false}
						className='-striped -highlight'
					/>
				</div>
				<div className='attendance-calender'>
					<Calendar maxDate={new Date()} onChange={this.onChangeDate} value={this.state.date} />
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addAttendance: (data) => dispatch(addAttendance(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(AttendanceList);
