import React from 'react';
import './assignments.style.css';

import { Form, Col } from 'react-bootstrap';

import icon from './assignment.svg';

import moment from 'moment';

class MyAssignments extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            subject: '',
            assignmentArray: [],
            filteredArray: [],
        }
    }

    componentDidMount = () => {
        this.setState({
            assignmentArray: this.props.assignments,
            filteredArray: this.props.assignments,
            loading: false
        })
    }

    handleSetSubject = (e) => {
        this.setState({
            subject: e.target.value
        }, () => {
            this.handleFilterArray();
        })
    }

    handleFilterArray = () => {
        var newArray = [];
        this.state.assignmentArray.length > 0 && this.state.assignmentArray.map((item) => {
            if (item.subjectName === this.state.subject) {
                newArray.push(item);
            }
        })

        this.setState({
            filteredArray: newArray
        })
    }

    render() {
        return (
            <div className="my-assignments-container">
                <div className="assignments-wrapper">
                    {
                        !this.state.loading
                            ?
                            <>
                                <div className="select-sub">
                                    <Form.Group className="container-fluid">
                                        <Form.Label>Select Subject</Form.Label>
                                        <Form.Control as="select" name="subject" onChange={this.handleSetSubject}>
                                            <option selected hidden>Choose...</option>
                                            <option value="Mathematics">Mathematics</option>
                                            <option value="Science">Science</option>
                                            <option value="English">English</option>
                                            <option value="Islamic Studies">Islamic Studies</option>
                                            <option value="Social Studies">Social Studies</option>
                                            <option value="Information Technology">Information Technology</option>
                                            <option value="Life Skills">Life Skills</option>
                                            <option value="Music">Music</option>
                                            <option value="Sports">Sports</option>
                                            <option value="Arts">Arts</option>
                                        </Form.Control>
                                    </Form.Group>
                                </div>

                                <div className="assignment-list">
                                    {
                                        this.state.filteredArray.length > 0 && this.state.filteredArray.map((item) => {
                                            return (
                                                <div className="singleAssignment-container">
                                                    <img src={icon} alt="icon" />
                                                    <div className="assignment-head">
                                                        <p>{item.title}</p>
                                                        <p className="objectives">{item.objectives}</p>
                                                        <h5>Subject : {item.subjectName}</h5>
                                                        <span>Start Date : <i>{moment(item.startDate).format('LL')}</i></span>
                                                        <span>End Date : <i>{moment(item.endDate).format('LL')}</i></span>
                                                    </div>
                                                </div>
                                            )
                                        })
                                    }
                                </div>
                            </>
                            :
                            null
                    }
                </div>
            </div>
        )
    }
}

export default MyAssignments;