import { FETCH_DISTRICT_OFFICER_PROFILE, FETCH_SCHOOL_INFO } from '../actions/districtOfficerDashboard/districtOfficerTypes';

const initialState = {
    profileData: null,
}

const districtOfficerReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_DISTRICT_OFFICER_PROFILE:
            return {
                profileData: action.payload,
            }
        default:
            return state
    }
}

export default districtOfficerReducer;