import React from 'react';
import './teachers.css';
import { connect } from 'react-redux'

import { Modal, Button, Form, Spinner, Col } from 'react-bootstrap';
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from 'react-icons/io';
import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";

import Moment from 'react-moment'

import addImg from '../../assets/more (5).svg';
import imagepicker from '../../assets/photo.png';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';
import axios from 'axios';

import {
    addTeacher,
    showTeacherInfo,
    editTeacher,
    editTeacherWithImage,
    assignTeacher,
    reassignTeacher,
    deleteTeacher
} from '../../../redux/actions/schoolAdminDashboard/schoolAdminActions';

class Teachers extends React.Component {
    constructor() {
        super();
        this.state = {
            showAdd: false,
            showDelete: false,
            showEdit: false,
            showView: false,
            classes: [],
            name: "",
            dob: "",
            photo: [],
            address: "",
            email: "",
            contact: '',
            qualification: "",
            experience: "",
            city: "",
            state: "",
            zip: "",
            showImage: '',
            adding: false,
            deleting: false,
            editing: false,
            error: '',
            item: [],
            editname: "",
            editemail: "",
            editphoto: [],
            editaddress: "",
            editdob: "",
            editcontact: '',
            editqualification: "",
            editexperience: 0,
            editcity: "",
            editstate: "",
            editzip: "",
            editshowImage: '',
            imageEdited: false,
            showAssignSubject: false,
            className: "",
            sectionName: "",
            subjectName: "",
            lab: false
        }
    }

    async componentDidMount() {
        var res = await axios.get('/api/schoolAdmin/iigpClasses');
        if (res.data !== null) {
            console.log(res.data);
            this.setState({
                classes: res.data
            })
        }
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handlePhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            console.log(e.target.files[0]);
            const f = e.target.files[0];
            this.setState({
                photo: f,
                showImage: URL.createObjectURL(f),
                error: ""
            })
        }
        else {
            this.setState({
                error: "Profile image size cannot be more than 1mb!"
            })
        }
    }



    handleAddTeacher = async () => {
        this.setState({
            error: ""
        })
        if (this.state.showImage === "" || this.state.name === "" || this.state.email === "" || this.state.address === "" || this.state.contact === "" || this.state.dob === "" || this.state.city === "" || this.state.state === "" || this.state.zip === "" || this.state.qualification === "" || this.state.experience === "") {
            toaster.notify("Please fill all the details", {
                duration: 3000
            })
        }
        else {
            this.setState({
                adding: true
            })
            const data = {
                name: this.state.name,
                email: this.state.email,
                address: this.state.address,
                contact: this.state.contact,
                dob: this.state.dob,
                qualification: this.state.qualification,
                experience: this.state.experience,
                city: this.state.city,
                state: this.state.state,
                zip: this.state.zip,
                schoolId: this.props.school._id
            }
            var form_data = new FormData();
            form_data.append('file', this.state.photo);
            const check = await this.props.addTeacher(form_data, data);
            if (check) {
                this.setState({
                    adding: false
                })
                if (check.type === 'success') {
                    toaster.notify('New Teacher Added', {
                        duration: 3000
                    })
                    this.setState({
                        name: "",
                        email: "",
                        address: "",
                        contact: "",
                        dob: "",
                        qualification: "",
                        experience: "",
                        city: "",
                        state: "",
                        zip: "",
                        showImage: "",
                        showAdd: false
                    })
                }
            }
        }
    }

    handleDeleteTeacher = async () => {
        if (this.state.item) {
            this.setState({
                deleting: true,
                error: ""
            })
            const data = {
                id: this.state.item._id,
                photo: this.state.item.photo,
                schoolId: this.props.school._id
            };
            const check = await this.props.deleteTeacher(data);
            if (check) {
                this.setState({
                    deleting: false
                })
                if (check.type === 'success') {
                    toaster.notify('Teacher removed', {
                        duration: 3000
                    })
                    this.setState({
                        showDelete: false,
                        item: []
                    })
                }
            }
        }
        else {
            this.setState({
                error: "Could not find the item"
            })
        }
    }

    handleShowEditTeacher = (e) => {
        this.setState({
            item: e,
            editname: e.name,
            editemail: e.email,
            editphoto: [],
            editaddress: e.address,
            editdob: e.dob,
            editcontact: e.contact,
            editqualification: e.qualification,
            editexperience: e.experience,
            editcity: e.city,
            editstate: e.state,
            editzip: e.zip,
            editshowImage: "/api/file/show/" + e.photo.filename,
            showEdit: true,
            error: ""
        })
    }

    handleEditPhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                editPhoto: f,
                editshowImage: URL.createObjectURL(f),
                imageEdited: true,
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleEditTeacher = async () => {
        this.setState({
            error: ""
        })
        if (this.state.editname === "" ||
            this.state.editaddress === "" ||
            this.state.editdob === "" ||
            this.state.editcontact === "" ||
            this.state.editqualification === "" ||
            this.state.experience === "" ||
            this.state.editcity === "" ||
            this.state.editstate === "" ||
            this.state.editzip === "") {
            toaster.notify("Please fill all the details", {
                duration: 3000
            })
        }
        else {
            this.setState({
                editing: true
            })
            const data = {
                name: this.state.editname,
                address: this.state.editaddress,
                email: this.state.editemail,
                dob: this.state.editdob,
                contact: this.state.editcontact,
                qualification: this.state.editqualification,
                experience: this.state.editexperience,
                city: this.state.editcity,
                state: this.state.editstate,
                zip: this.state.editzip,
                id: this.state.item._id,
                schoolId: this.props.school._id,
                photo: this.state.item.photo
            };
            var form_data = new FormData();
            form_data.append('file', this.state.editPhoto);
            const check = this.state.imageEdited ? await this.props.editTeacherWithImage(form_data, data) : await this.props.editTeacher(data);
            if (check) {
                this.setState({
                    editing: false
                })
                if (check.type === 'success') {
                    toaster.notify('Teacher Information Updated', {
                        duration: 3000
                    })
                    this.setState({
                        editname: "",
                        editbranch: "",
                        editlogo: [],
                        editaddress: "",
                        editemail: "",
                        editcontact: '',
                        editfax: "",
                        editwebsite: "",
                        editshowImage: '',
                        showEdit: false
                    })
                }
            }
        }
    }

    handleShowAssignSubject = () => {
        this.setState({
            showAssignSubject: true
        })
    }

    handleAssignSubject = async () => {
        var found = false;
        this.state.item.subjects.map(sub => {
            if (sub.class === this.state.className && sub.section === this.state.sectionName && sub.subject === this.state.subjectName) {
                found = true;
            }
        })
        if (found) {
            toaster.notify('Already Assigned!', {
                duration: 3000
            })
        }
        else {
            this.setState({
                assigning: true
            });
            const data = {
                id: this.state.item._id,
                className: this.state.className,
                sectionName: this.state.sectionName,
                subjectName: this.state.subjectName,
                lab: this.state.lab,
                schoolId: this.props.school._id
            }
            const check = await this.props.assignTeacher(data);
            if (check) {
                this.setState({
                    assigning: false
                })
                if (check.type === 'success') {
                    this.setState({
                        showAssignSubject: false,
                        className: "",
                        sectionName: "",
                        subjectName: ""
                    })
                    toaster.notify('Subject assigned to teacher!', {
                        duration: 3000
                    })
                    this.props.school.teachers && this.props.school.teachers.map(teacher => {
                        if (teacher._id === this.state.item._id) {
                            this.setState({
                                item: teacher
                            })
                        }
                    })
                }

            }
        }
    }

    handleReassignTeacher = async (clas, sec, sub) => {
        const data = {
            id: this.state.item._id,
            className: clas,
            sectionName: sec,
            subjectName: sub,
            schoolId: this.props.school._id
        }
        const check = await this.props.reassignTeacher(data);
        if (check) {
            this.setState({
                assigning: false
            })
            if (check.type === 'success') {
                toaster.notify('Teacher Reassigned!', {
                    duration: 3000
                })
                this.setState({
                    showAssignSubject: false,
                    className: "",
                    sectionName: "",
                    subjectName: ""
                })
                this.props.school.teachers && this.props.school.teachers.map(teacher => {
                    if (teacher._id === this.state.item._id) {
                        this.setState({
                            item: teacher
                        })
                    }
                })
            }
        }
    }

    handleSubject = (e) => {
        this.props.school.classes.map(clas => {
            if (clas.name === this.state.className) {
                clas.sections.map(sec => {
                    if (sec.name === this.state.sectionName) {
                        sec.subjects.map(sub => {
                            if (sub.name === e.target.value) {
                                this.setState({
                                    subjectName: e.target.value,
                                    lab: sub.lab
                                })
                            }
                        })
                    }
                })
            }
        })
    }

    render() {
        console.log(this.state.item.subjects);
        const school = this.props.school;
        const teachers = this.props.school ? this.props.school.teachers : null;
        const data = teachers;
        const columns = [
            {
                Header: "Name",
                accessor: "name",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Date of Birth",
                accessor: "dob",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Email",
                accessor: "email",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Contact No.",
                accessor: "contact",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: 'center',
                },
            },
            {
                Header: "Actions",
                Cell: (props) => {
                    return (
                        <div
                            style={{
                                width: "100%",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            <Button onClick={() => {
                                this.setState({
                                    showView: true,
                                    item: props.original,
                                    error: "",
                                    showAddAdmin: false,
                                    showEditAdmin: false,
                                    showAddOfficer: false
                                })
                            }} variant={"outline-dark"} className="action-buttons">Info</Button>
                            <Button onClick={() => this.handleShowEditTeacher(props.original)} variant={"outline-info"} className="action-buttons">Edit</Button>
                            <Button onClick={() => {
                                this.setState({
                                    showDelete: true,
                                    item: props.original,
                                    error: ""
                                })
                            }} variant={"outline-danger"} className="action-buttons">Delete</Button>
                        </div>
                    );
                },
                sortable: false,
                filterable: false,
                minWidth: 150,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",

                },
            },
        ];

        return (
            <div className="teachers-container">
                <div className="teachers-header">
                    <div className="teachers-stats">
                        <h1>IIGP Teachers</h1>
                        <div className="officer-stats">
                            <p>TOTAL TEACHERS REGISTERED: <span>5</span></p>
                        </div>
                    </div>
                    <div className="teachers-add">
                        <div onClick={() => this.setState({
                            showAdd: true,
                            error: ""
                        })
                        }
                            className="add-officer-button">
                            <img src={addImg} alt="add-btn" />
                            <p>Add New Teacher</p>
                        </div>
                    </div>
                </div>
                <div className="teachers-body">
                    <ReactTable
                        data={data}
                        columns={columns}
                        defaultPageSize={10}
                        showPaginationTop
                        showPaginationBottom={false}
                        className="-striped -highlight"
                    />
                </div>
                <Modal
                    show={this.state.showAdd}
                    onHide={() => this.setState({
                        showAdd: false,
                        error: ""
                    })}
                    backdrop="static"
                    keyboard={false}
                    centered
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add Teacher Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <div className="image-container">
                                        <p>Photo</p>
                                        <div className="image-picker">
                                            <input id="image-picker" onChange={e => this.handlePhoto(e)} type="file" accept="image/*" />
                                            {
                                                this.state.showImage === ""
                                                    ?
                                                    <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                    :
                                                    <img htmlFor="#image-picker" src={this.state.showImage} />
                                            }
                                        </div>
                                    </div>

                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control name="name" type="text" placeholder="Enter teacher name" value={this.state.name} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="email" type="text" placeholder="Enter teacher email" value={this.state.email} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control name="address" type="text" placeholder="Enter teacher address" value={this.state.address} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Date of birth</Form.Label>
                                    <Form.Control name="dob" type="date" value={this.state.dob} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Contact No.</Form.Label>
                                    <Form.Control name="contact" type="text" placeholder="Enter school contact no." value={this.state.contact} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Qualification</Form.Label>
                                    <Form.Control name="qualification" type="text" placeholder="Enter teacher qualification" value={this.state.qualification} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Experience</Form.Label>
                                    <Form.Control name="experience" type="number" min={0} max={100} placeholder="Enter in years" value={this.state.experience} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control name="city" type="text" placeholder="Enter city" value={this.state.city} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>State</Form.Label>
                                    <Form.Control name="state" type="text" placeholder="Enter state" value={this.state.state} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Zip</Form.Label>
                                    <Form.Control name="zip" type="text" placeholder="Enter zip" value={this.state.zip} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.adding
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleAddTeacher} variant="success"><FaCheck />Add</Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showDelete}
                    onHide={() => this.setState({
                        showDelete: false,
                        error: ""
                    })}
                    dialogClassName="modal-70w"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>School Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group>
                                <Form.Label>School Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter school name" value={this.state.item.name} disabled />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>School Branch</Form.Label>
                                <Form.Control type="text" placeholder="Enter school branch name" value={this.state.item.branch} disabled />
                            </Form.Group>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.deleting
                                ?
                                <Spinner animation="border" role="status" variant="danger">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleDeleteTeacher} variant="danger">Delete<FaRegTrashAlt /></Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showEdit}
                    onHide={() => this.setState({
                        showEdit: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Edit Teacher Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <div className="image-container">
                                        <p>Photo</p>
                                        <div className="image-picker">
                                            <input id="image-picker" onChange={e => this.handleEditPhoto(e)} type="file" accept="image/*" />
                                            {
                                                this.state.editshowImage === ""
                                                    ?
                                                    <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                    :
                                                    <img htmlFor="#image-picker" src={this.state.editshowImage} />
                                            }
                                        </div>
                                    </div>

                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control name="editname" type="text" placeholder="Enter teacher name" value={this.state.editname} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="editemail" type="text" placeholder="Enter teacher email" value={this.state.editemail} onChange={this.handleChange} disabled />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control name="editaddress" type="text" placeholder="Enter teacher address" value={this.state.editaddress} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Date of birth</Form.Label>
                                    <Form.Control name="editdob" type="date" value={this.state.editdob} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Contact No.</Form.Label>
                                    <Form.Control name="editcontact" type="text" placeholder="Enter school contact no." value={this.state.editcontact} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Qualification</Form.Label>
                                    <Form.Control name="editqualification" type="text" placeholder="Enter teacher qualification" value={this.state.editqualification} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Experience</Form.Label>
                                    <Form.Control name="editexperience" type="number" min={0} max={100} placeholder="Enter in years" value={this.state.editexperience} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control name="editcity" type="text" placeholder="Enter city" value={this.state.editcity} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>State</Form.Label>
                                    <Form.Control name="editstate" type="text" placeholder="Enter state" value={this.state.editstate} onChange={this.handleChange} />
                                </Form.Group>
                                <Form.Group className="container-fluid">
                                    <Form.Label>Zip</Form.Label>
                                    <Form.Control name="editzip" type="text" placeholder="Enter zip" value={this.state.editzip} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.editing
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleEditTeacher} variant="success"><FaCheck />Save</Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showView}
                    onHide={() => this.setState({
                        showView: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Teaching Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            {
                                !this.state.showAssignSubject
                                    ?
                                    <div className="container-group-close">
                                        {
                                            this.state.item.subjects && this.state.item.subjects.map(sub => {
                                                return (
                                                    <Form.Group>
                                                        <Button onClick={() => this.handleReassignTeacher(sub.class, sub.section, sub.subject)} variant="outline-danger" className="submit-action"><FaRegTrashAlt />{sub.class + " " + sub.section + " " + sub.subject}</Button>
                                                    </Form.Group>
                                                )
                                            })
                                        }
                                        {
                                            !this.state.showAssignSubject
                                                ?
                                                <Form.Group>
                                                    <Button onClick={this.handleShowAssignSubject} variant="success" className="submit-action"><FaPlus />Assign Subject</Button>
                                                </Form.Group>
                                                :
                                                null
                                        }
                                    </div>
                                    :
                                    <>
                                        <Form.Group >
                                            <Form.Label>Select a Class</Form.Label>
                                            <Form.Control as="select" value={this.state.className} onChange={e => { this.setState({ className: e.target.value }) }}>
                                                <option value="" disabled hidden>Select a class</option>
                                                {
                                                    school.classes.map(clas => {
                                                        return (
                                                            <option value={clas.name}>{clas.name}</option>
                                                        )
                                                    })
                                                }
                                            </Form.Control>
                                        </Form.Group>
                                        {
                                            this.state.className !== ""
                                                ?
                                                <Form.Group>
                                                    <Form.Label>Choose Section</Form.Label>
                                                    <Form.Control as="select" value={this.state.sectionName} onChange={e => { this.setState({ sectionName: e.target.value }) }}>
                                                        <option value="" disabled hidden>Select a section</option>
                                                        {
                                                            school.classes.map(clas => {
                                                                if (clas.name === this.state.className) {
                                                                    return (
                                                                        <>
                                                                            {
                                                                                clas.sections.map(sec => {
                                                                                    return (
                                                                                        <option value={sec.name}>{sec.name}</option>
                                                                                    )
                                                                                })
                                                                            }
                                                                        </>
                                                                    )
                                                                }
                                                            })
                                                        }
                                                    </Form.Control>
                                                </Form.Group>
                                                :
                                                null
                                        }
                                        {
                                            this.state.sectionName !== ""
                                                ?
                                                <Form.Group>
                                                    <Form.Label>Choose a Subject</Form.Label>
                                                    <Form.Control as="select" value={this.state.subjectName} onChange={this.handleSubject}>
                                                        <option value="" disabled hidden>Select a subject</option>
                                                        {
                                                            school.classes.map(clas => {
                                                                if (clas.name === this.state.className) {
                                                                    return (
                                                                        <>
                                                                            {
                                                                                clas.sections.map(sec => {
                                                                                    if (sec.name === this.state.sectionName) {
                                                                                        return (
                                                                                            <>
                                                                                                {
                                                                                                    sec.subjects.map(sub => {
                                                                                                        return (
                                                                                                            <option value={sub.name}>{sub.name}</option>
                                                                                                        )
                                                                                                    })
                                                                                                }
                                                                                            </>
                                                                                        )
                                                                                    }
                                                                                })
                                                                            }
                                                                        </>
                                                                    )
                                                                }
                                                            })
                                                        }
                                                    </Form.Control>
                                                </Form.Group>
                                                :
                                                null
                                        }
                                        <Form.Group>
                                            <div className="container-group-close">
                                                {
                                                    this.state.subjectName !== ''
                                                        ?
                                                        <>
                                                            {
                                                                this.state.assigning
                                                                    ?
                                                                    <Spinner animation="border" role="status" variant="success">
                                                                        <span className="sr-only">Loading...</span>
                                                                    </Spinner>
                                                                    :
                                                                    <Button onClick={this.handleAssignSubject} variant="success" className="submit-action"><FaCheck /> Assign</Button>
                                                            }
                                                        </>
                                                        :
                                                        null
                                                }
                                                {
                                                    this.state.assigning
                                                        ?
                                                        null
                                                        :
                                                        <Button onClick={() => this.setState({
                                                            showAssignSubject: false
                                                        })} variant="danger" className="submit-action"><IoMdClose />Cancel</Button>
                                                }

                                            </div>
                                        </Form.Group>
                                    </>
                            }
                        </Form>
                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        school: state.schoolAdmin.school,
        schoolError: state.schoolAdmin.schoolError,
        schoolSucc: state.schoolAdmin.schoolSucc,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addTeacher: (form_data, data) => dispatch(addTeacher(form_data, data)),
        showTeacherInfo: (data) => dispatch(showTeacherInfo(data)),
        editTeacherWithImage: (form_data, data) => dispatch(editTeacherWithImage(form_data, data)),
        editTeacher: (data) => dispatch(editTeacher(data)),
        assignTeacher: (data) => dispatch(assignTeacher(data)),
        reassignTeacher: (data) => dispatch(reassignTeacher(data)),
        deleteTeacher: (data) => dispatch(deleteTeacher(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Teachers);