import React from 'react';
import './profile.css';
import { Form, Button, Col, Container, FormGroup, Spinner, Modal } from 'react-bootstrap';
import { FaCheck } from 'react-icons/fa';
import { BsFillLockFill } from 'react-icons/bs';
import { connect } from 'react-redux';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

import { editSchoolAdminWithImage, editSchoolAdmin, editSchoolAdminPassWord } from '../../../redux/actions/schoolAdminDashboard/schoolAdminActions';

class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            schoolAdminName: props.schoolAdmin.name,
            showImage: "/api/file/show/" + props.schoolAdmin.photo.filename,
            schoolAdminEmail: props.schoolAdmin.email,
            schoolAdminAddress: props.schoolAdmin.address,
            schoolAdminContact: props.schoolAdmin.contact,
            schoolAdminDob: props.schoolAdmin.dob,
            schoolAdminCity: props.schoolAdmin.city,
            schoolAdminState: props.schoolAdmin.state,
            schoolAdminZip: props.schoolAdmin.zip,
            schoolAdminQualification: props.schoolAdmin.qualification,
            schoolAdminExperience: props.schoolAdmin.experience,
            loading: false,
            showChangePassword: false,
            changingPassword: false,
            spassword: "",
            password: ""
        }
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleEditPhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                photo: f,
                showImage: URL.createObjectURL(f),
                imageEdited: true,
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleSave = async () => {
        this.setState({
            error: ""
        })
        if (this.state.schoolAdminName === "" || this.state.schoolAdminContact === "") {
            this.setState({
                error: "Please fill all the details"
            })
        }
        else {
            this.setState({
                loading: true
            })
            const data = {
                name: this.state.schoolAdminName,
                email: this.state.schoolAdminEmail,
                address: this.state.schoolAdminAddress,
                contact: this.state.schoolAdminContact,
                dob: this.state.schoolAdminDob,
                qualification: this.state.schoolAdminQualification,
                experience: this.state.schoolAdminExperience,
                city: this.state.schoolAdminCity,
                state: this.state.schoolAdminState,
                zip: this.state.schoolAdminZip,
                id: this.props.schoolAdmin._id,
                photo: this.props.schoolAdmin.photo
            };
            console.log(this.props.school);
            var form_data = new FormData();
            form_data.append('file', this.state.photo);
            const check = this.state.imageEdited ? await this.props.editSchoolAdminWithImage(form_data, data) : await this.props.editSchoolAdmin(data);
            console.log(check);
            if (check) {
                this.setState({
                    loading: false
                })
                if (check.type === 'success') {
                    toaster.notify('Profile Information Updated Successfully', {
                        duration: 3000
                    })
                }
                else {
                    toaster.notify('Error', {
                        duration: 3000
                    })
                }
            }
        }
    }


    handleEditPassword = async () => {
        this.setState({
            error: ""
        })
        if (this.state.password.length < 6 || this.state.spassword.length < 6) {
            toaster.notify('Password must be atleast 6 characters!', {
                duration: 3000
            })
        }
        else if (this.state.password !== this.state.spassword) {
            toaster.notify('Password mismatch!', {
                duration: 3000
            })
        }
        else {
            this.setState({
                changingPassword: true
            })
            const data = {
                password: this.state.password,
                email: this.state.schoolAdminEmail
            };
            console.log(this.props.school);
            const check = await this.props.editSchoolAdminPassWord(data);
            console.log(check);
            if (check) {
                this.setState({
                    changingPassword: false
                })
                if (check.type === 'success') {
                    toaster.notify('Password Updated Successfully', {
                        duration: 3000
                    })
                    this.setState({
                        showChangePassword: false
                    })
                }
                else {
                    toaster.notify('Error in changing the password', {
                        duration: 3000
                    })
                }
            }
        }
    }



    render() {
        return (
            <>
                <div className="profile-container">
                    <h1>Personal Information</h1>
                    <Form>
                        <div className="container-group">
                            <Form.Group >
                                <div className="image-container">
                                    <div className="image-picker">
                                        <input id="image-picker" onChange={e => this.handleEditPhoto(e)} type="file" accept="image/*" />
                                        <img htmlFor="#image-picker" src={this.state.showImage} />
                                    </div>
                                </div>
                            </Form.Group>
                        </div>
                        <div className="container-group">
                            <Form.Group className="container-fluid">
                                <Form.Label>Name</Form.Label>
                                <Form.Control name="schoolAdminName" type="text" placeholder="Enter your name" value={this.state.schoolAdminName} onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group className="container-fluid">
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="text" disabled value={this.state.schoolAdminEmail} />
                            </Form.Group>
                        </div>
                        <div className="container-group">
                            <Form.Group className="container-fluid">
                                <Form.Label>Address</Form.Label>
                                <Form.Control name="schoolAdminAddress" type="text" placeholder="Enter your address" value={this.state.schoolAdminAddress} onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group className="container-fluid">
                                <Form.Label>Contact No.</Form.Label>
                                <Form.Control name="schoolAdminContact" type="text" placeholder="Enter your contact no." value={this.state.schoolAdminContact} onChange={this.handleChange} />
                            </Form.Group>
                        </div>
                        <div className="container-group">
                            <Form.Group className="container-fluid">
                                <Form.Label>City</Form.Label>
                                <Form.Control name="schoolAdminCity" type="text" value={this.state.schoolAdminCity} onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group className="container-fluid">
                                <Form.Label>District</Form.Label>
                                <Form.Control name="schoolAdminState" type="text" value={this.state.schoolAdminState} onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group className="container-fluid">
                                <Form.Label>Pin Code</Form.Label>
                                <Form.Control name="schoolAdminZip" type="text" value={this.state.schoolAdminZip} onChange={this.handleChange} />
                            </Form.Group>
                        </div>
                        <div className="container-group">
                            <Form.Group className="container-fluid">
                                <Form.Label>Date of Birth</Form.Label>
                                <Form.Control name="schoolAdminDob" type="date" value={this.state.schoolAdminDob} onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group className="container-fluid">
                                <Form.Label>Qualification</Form.Label>
                                <Form.Control name="schoolAdminQualification" type="text" placeholder="Enter your highest Qualification" value={this.state.schoolAdminQualification} onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group className="container-fluid">
                                <Form.Label>Experience</Form.Label>
                                <Form.Control name="schoolAdminExperience" type="text" placeholder="Enter your experience in years" value={this.state.schoolAdminExperience} onChange={this.handleChange} />
                            </Form.Group>
                        </div>
                        <div className="container-group-close">
                            <Form.Group>
                                {
                                    this.state.loading
                                        ?
                                        <Spinner animation="border" role="status" variant="success">
                                            <span className="sr-only">Loading...</span>
                                        </Spinner>
                                        :
                                        <Button onClick={this.handleSave} variant="success" className="submit-action"><FaCheck />Save Changes</Button>
                                }
                            </Form.Group>
                            <Form.Group>
                                <Button onClick={() => this.setState({ showChangePassword: true })} variant="danger" className="submit-action"><BsFillLockFill /> Change Password</Button>
                            </Form.Group>
                        </div>
                    </Form>
                </div>
                <Modal
                    show={this.state.showChangePassword}
                    onHide={() => this.setState({
                        showChangePassword: false
                    })}
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Create New Password </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group>
                                <Form.Label>New Password</Form.Label>
                                <Form.Control name="spassword" value={this.state.spassword} type="text" placeholder="Enter new password" onChange={this.handleChange} />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>Retype Password</Form.Label>
                                <Form.Control name="password" value={this.state.password} type="password" placeholder="Enter new password again" onChange={this.handleChange} />
                            </Form.Group>
                        </Form>
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.changingPassword
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleEditPassword} variant="success"><FaCheck />Change</Button>
                        }
                    </Modal.Footer>
                </Modal>
            </>
        )
    }
}

function mapStateToProps(state) {
    return {
        schoolAdmin: state.schoolAdmin.schoolAdmin,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        editSchoolAdmin: (data) => dispatch(editSchoolAdmin(data)),
        editSchoolAdminWithImage: (form_data, data) => dispatch(editSchoolAdminWithImage(form_data, data)),
        editSchoolAdminPassWord: (data) => dispatch(editSchoolAdminPassWord(data))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Profile);