import React, { Component } from "react";
import "./learningOrganizer.css";

import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import FloralLoading from "../loadingScreen/floralLoading";

import attendance from "./assets/immigration.svg";

import exam from "./assets/exam.svg";
import Calender from "./assets/schedule.svg";
import Polls from "./assets/polling.svg";
import Announcement from "./assets/loudspeaker.svg";
import Forum from './assets/discussion.svg';
import library from './assets/library.svg';
import online from './assets/lesson.svg';

import lab from './assets/flask.svg';
import presentation from './assets/flipchart.svg';
import seat from './assets/seat.svg';

import { connect } from "react-redux";
import { fetch_student_profile_data } from '../../../redux/actions/studentDashboard/studentActions';
import { fetchUser } from "../../../redux/actions/authActions";

import StudentNavbar from "../websiteComponents/Navbar/navbar.component";

import MyAttendance from "./components/attendance/attendance.component";
import MyPolls from "./components/polls/polls.component";
import MyAnnouncements from './components/announcements/announcements';
import MyCalendar from "./components/calendar/myCalendar";
import MyDiscussions from "./components/discussion/discussion";
import MySeating from "./components/seating/seating.component";
import MyLibrary from "./components/library/library.component";

class LearningOrganizer extends Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			tab: 'My Attendance'
		};
	}

	componentDidMount = async () => {
		const currentUser = await this.props.fetchUser();
		if (currentUser) {

			const studentData = {
				studentId: currentUser.ref_id
			}

			const result = await this.props.fetch_student_profile_data(studentData);

			if (result) {
				this.setState({
					loading: false
				})
			}
		}
	}

	render() {
		console.log(this.props.student);
		return (
			<>
				{
					this.state.loading
						?
						(
							<FloralLoading />
						)
						:
						(
							<div className="myschool-container">
								<StudentNavbar student={this.props.student} />
								<div className="myschool-subject">
									<p>My School / Learning Organizer</p>
								</div>
								<div className="myschool-activity">
									<OwlCarousel className="owl-theme" items={8} dots={false}>
										<div className="subject" onClick={() => this.setState({ tab: 'My Attendance' })}>
											<img src={attendance} alt="img" />
											<p>Attendance</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Library' })}>
											<img src={library} alt="img" />
											<p>Library</p>
										</div>

										{
											this.props.student.class.toLowerCase() === 'grade 10' ||
												this.props.student.class.toLowerCase() === 'grade 11' ||
												this.props.student.class.toLowerCase() === 'grade 12'
												?
												<div className="subject" onClick={() => this.setState({ tab: 'My Lab Works' })}>
													<img src={lab} alt="img" />
													<p>Lab Work</p>
												</div>
												:
												null
										}

										<div className="subject" onClick={() => this.setState({ tab: 'My Presentation' })}>
											<img src={presentation} alt="img" />
											<p>Presentation</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Online Lessons' })}>
											<img src={online} alt="img" />
											<p>Online Lesson</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Exams' })}>
											<img src={exam} alt="img" />
											<p>Exams</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Calendar' })}>
											<img src={Calender} alt="img" />
											<p>Calendar</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Polls' })}>
											<img src={Polls} alt="img" />
											<p>Polls</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Announcements' })}>
											<img src={Announcement} alt="img" />
											<p>Announcements</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Discussions' })}>
											<img src={Forum} alt="img" />
											<p>Discussion</p>
										</div>
										<div className="subject" onClick={() => this.setState({ tab: 'My Seating Chart' })}>
											<img src={seat} alt="img" />
											<p>Seating Chart</p>
										</div>
									</OwlCarousel>
								</div>

								<div className="learning-grade-breadcrumb">
									<p>{this.state.tab}</p>
								</div>

								<div className="myschool-tabs">

									{/* My Attendance */}
									{
										this.state.tab === 'My Attendance'
											?
											<MyAttendance attendance={this.props.student.attendance} />
											:
											null
									}

									{/* My Library */}
									{
										this.state.tab === 'My Library'
											?
											<MyLibrary />
											:
											null
									}

									{/* My Polls */}
									{
										this.state.tab === 'My Polls'
											?
											<MyPolls polls={this.props.student.polls} />
											:
											null
									}

									{/* My Announcements */}
									{
										this.state.tab === 'My Announcements'
											?
											<MyAnnouncements announcements={this.props.student.announcements} studentId={this.props.student._id} />
											:
											null
									}

									{/* My Calendar */}
									{
										this.state.tab === 'My Calendar'
											?
											<MyCalendar calendarEvents={this.props.student.calendarEvents} />
											:
											null
									}

									{/* My Discussions */}

									{
										this.state.tab === 'My Discussions'
											?
											<MyDiscussions student={this.props.student} />
											:
											null
									}

									{/* My Seating Chart */}

									{
										this.state.tab === 'My Seating Chart'
											?
											<MySeating student={this.props.student} />
											:
											null
									}

								</div>
							</div>
						)
				}
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		student: state.student.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		fetchUser: () => dispatch(fetchUser()),
		fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(LearningOrganizer);