import React from 'react';

import './abilities.css';
import good from './assets/good-idea.svg';
import good1 from './assets/reading-book.svg';
import good2 from './assets/productivity.svg';

import MyCreativity from './sections/creativity/creativity';
import MyEntrepreneurship from './sections/entrepreneurship/entrepreneurship';
import MyProblem from './sections/problem/problem';

class MyAbilities extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            creativity: [],
            problem: [],
            entrepreneurship: [],
            tab: 'Creativity'
        }
    }

    componentDidMount = () => {
        this.setState({
            creativity: this.props.creativity,
            problem: this.props.problem,
            entrepreneurship: this.props.entrepreneurship,
            loading: false
        })
    }

    handleSetTab = (e) => {
        this.setState({
            tab: e
        }, () => {
            this.props.setSubTab(e);
        })
    }

    render() {
        return (
            <div className="my-abilities-container">
                <div className="abilities-wrapper">
                    {
                        this.state.loading
                            ?
                            null
                            :
                            <>
                                <div className="abilities-tab">
                                    <div className={this.state.tab === 'Creativity' ? "tab active" : "tab"} onClick={() => this.handleSetTab('Creativity')}>
                                        <img src={good} alt="good" alt="icon" />
                                        <p>Creativity</p>
                                    </div>
                                    <div className={this.state.tab === 'Entrepreneurship' ? "tab active" : "tab"} onClick={() => this.handleSetTab('Entrepreneurship')}>
                                        <img src={good1} alt="good" alt="icon" />
                                        <p>Entrepreneurship Skill</p>
                                    </div>
                                    <div className={this.state.tab === 'Problem Solving' ? "tab active" : "tab"} onClick={() => this.handleSetTab('Problem Solving')}>
                                        <img src={good2} alt="good" alt="icon" />
                                        <p>Problem Solving</p>
                                    </div>
                                </div>
                                <div className="abilities-body">
                                    {
                                        this.state.tab === 'Creativity'
                                            ?
                                            <MyCreativity creativity={this.state.creativity} />
                                            :
                                            null
                                    }

                                    {
                                        this.state.tab === 'Entrepreneurship'
                                            ?
                                            <MyEntrepreneurship entrepreneurship={this.state.entrepreneurship} />
                                            :
                                            null
                                    }

                                    {
                                        this.state.tab === 'Problem Solving'
                                            ?
                                            <MyProblem problem={this.state.problem} />
                                            :
                                            null
                                    }
                                </div>
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default MyAbilities;