import React from "react";

import "./chooseClass.css";

import folderImg from "../../assets/folderIcon.svg";

class ChooseClass extends React.Component {
	render() {
		const classes = this.props.classes;
		return (
			<div className='chooseClass-container'>
				{classes &&
					classes.map((clas) => {
						return (
							<div className='classes-cardFolder-container' onClick={() => this.props.toggleShowSubject(clas)}>
								<div className='classes-cardFolder-img'>
									<img src={folderImg} alt='folder-icon' />
								</div>
								<div className='classes-cardFolder-name'>
									<p>{clas.className}</p>
								</div>
							</div>
						);
					})}
			</div>
		);
	}
}

export default ChooseClass;
