import React from 'react';

import './discussion.css';

import Lottie from "lottie-react-web";
import loadAnimation from './assets/7774-loading.json';
import MyDiscussionRoom from './discussionRoom/discussionRoom';

import moment from 'moment';

class MyDiscussions extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            discussions: [],
            discussion: null,
            NF: false,
            showChat: false,
        }
    }

    componentDidMount = async () => {

        if (this.props.student.discussions.length > 0) {
            this.setState({
                discussions: this.props.student.discussions,
                loading: false
            })
        }
        else {
            this.setState({
                NF: true,
                loading: false
            })
        }
    }

    handleShowDiscussionRoom = (item) => {
        this.setState({
            discussion: item,
            showChat: true
        })
    }

    render() {
        return (
            <div className="my-discussions-container">
                <div className="discussions-wrapper">
                    {
                        this.state.loading
                            ?
                            <Lottie
                                options={{ animationData: loadAnimation }}
                                width={250}
                                height={250}
                            />
                            :
                            <>
                                {
                                    this.state.NF
                                        ?
                                        <p>Nothing Found</p>
                                        :
                                        <>
                                            {
                                                this.state.showChat
                                                    ?
                                                    <MyDiscussionRoom discussion={this.state.discussion} goBack={(value) => this.setState({ showChat: value })} />
                                                    :
                                                    <>
                                                        {
                                                            this.state.discussions && this.state.discussions.map((item) => {
                                                                return (
                                                                    <div className="my-discussion-card" onClick={() => this.handleShowDiscussionRoom(item)}>
                                                                        <h3>{item.title}</h3>
                                                                        <h5>{item.grade}, {item.section}</h5>
                                                                        <p>{moment(item.date).fromNow()}</p>
                                                                        <span>(Click to enter discussion room)</span>
                                                                    </div>
                                                                )
                                                            })
                                                        }
                                                    </>
                                            }
                                        </>
                                }
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default MyDiscussions;