import React from "react";
import { connect } from "react-redux";
import "./teacherDashboard.css";
import menu from "../assets/open-menu.png";
import Profile from "./Profile/profile";
import SchoolInfo from "./SchoolInfo/schoolInfo";
import Students from "./Students/students";
import StudentsBehaviour from "./studentsBehaviour/studentBehaviour";
import Lesson from "./Lesson/lesson";
import Resource from "./resources/resource";
import Attendance from "./Attendance/attendance";
import Exam from "./Exam/exam";
import QuestionBank from "./QuestionBank/questionBank";
import HomeWork from "./homework/homework";
import Assignment from "./assigment/assignments";
import LabWorks from "./labwork/labworks";
import Seating from "./Seating/seating";
import CareerPath from "./careerPath/careerPath";
import Todo from "./todo/todo";
import StudentParticipation from "./participation/studentParticipation";
import { fetchTeacher } from "../../redux/actions/teacherDashboard/teacherActions";
import Library from "./library/library";
import Announcement from "./announcement/announcement";
import Poll from "./poll/poll";
import CalendarMy from "./calendar/calendar";
import StudentCreativity from "./studentCreativity/studentCreativity";
import StudentProblemSolving from "./studentProblemSolving/studentProblemSolving";
import StudentEntrepreneurship from "./studentEntrepreneurship/studentEntrepreneurship";
import Recognition from "./recognition/recognition";
import DiscussionGroup from "./discussionGroups/discussionGroup";

import FloralLoading from "./loadingScreen/floralLoading";

class TeacherDashboard extends React.Component {
	constructor() {
		super();
		this.state = {
			sidebar: "sidebar-full",
			content: "content-mini",
			tab: 1,
			currentTabName: "Home",
			showProfileMenu: false,
			showSubMenu1: false,
			currentUser: [],
			loading: true,
			showTeaching: false,
			showMyClasses: false,
			showLearning: false,
		};
	}

	async componentDidMount() {
		const data = {
			id: this.props.auth.currentUser.ref_id,
		};

		const teacher = await this.props.fetchTeacher(data);

		if (teacher !== null) {
			this.setState(
				{
					loading: false,
				},
				() => {
					// setInterval(async () => {
					// 	const data = {
					// 		id: this.props.auth.currentUser.ref_id,
					// 	};
					// 	await this.props.fetchTeacher(data);
					// }, 5000);
				}
			);
		}
	}

	render() {
		const teacher = this.props.teacher.profileData;
		var showLabwork = false;
		teacher &&
			teacher.subjects.map((sub) => {
				if (sub.lab) {
					showLabwork = true;
				}
			});
		return (
			<>
				{this.state.loading ? (
					<FloralLoading />
				) : (
					<div className='teacher-layout-container'>
						<div className='header'>
							<div className='header-menu'>
								<img className='header-menu-icon' onClick={this.handleMenuToggle} src={menu} alt='brand-logo' />
								<h1>IIGP | {this.state.currentTabName}</h1>
							</div>
							<div className='header-buttons'>
								<div className='profile'>
									{teacher && teacher.photo ? (
										<img
											src={"/api/file/show/" + teacher.photo.filename}
											alt='profile-pic'
											className='profile-logo'
											onClick={() =>
												this.setState({
													showProfileMenu: !this.state.showProfileMenu,
												})
											}
										/>
									) : null}
									<p
										onClick={() =>
											this.setState({
												showProfileMenu: !this.state.showProfileMenu,
											})
										}
										className='profile-name'>
										{teacher && teacher.name}
									</p>
									<i
										className='fas fa-caret-down'
										onClick={() =>
											this.setState({
												showProfileMenu: !this.state.showProfileMenu,
											})
										}></i>
									{this.state.showProfileMenu ? (
										<div className='profile-menu'>
											<a href='/api/logout'>Log Out</a>
										</div>
									) : null}
								</div>
							</div>
						</div>

						<div className='body'>
							<div className={this.state.sidebar}>
								<div className='sidebar-content'>
									<h1>MENU</h1>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: 1,
												currentTabName: "Home",
												showLearning: false,
												showTeaching: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === 1 ? <div className='active'></div> : null}
											<i className='fas fa-chart-pie'></i>
											{this.state.sidebar === "sidebar-full" ? <p>HOME</p> : null}
										</div>
									</div>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: "Profile",
												showTeaching: false,
												currentTabName: "Personal Info",
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === "Profile" ? <div className='active'></div> : null}
											<i className='fas fa-user'></i>
											{this.state.sidebar === "sidebar-full" ? <p>PERSONAL INFO</p> : null}
										</div>
									</div>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: "SchoolInfo",
												showTeaching: false,
												currentTabName: "School Info",
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === "SchoolInfo" ? <div className='active'></div> : null}
											<i className='fas fa-school'></i>
											{this.state.sidebar === "sidebar-full" ? <p>SCHOOL INFO</p> : null}
										</div>
									</div>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												showTeaching: !this.state.showTeaching,
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											<i className='fas fa-chalkboard-teacher'></i>
											{this.state.sidebar === "sidebar-full" ? <p>Teaching</p> : null}
											{this.state.showTeaching ? <i className='fas fa-caret-up'></i> : <i className='fas fa-caret-down'></i>}
										</div>
									</div>
									{this.state.showTeaching ? (
										<div className='sub-menu'>
											<div
												className='sub-menu-item'
												onClick={() =>
													this.setState({
														showMyClasses: !this.state.showMyClasses,
													})
												}>
												{this.state.sidebar === "sidebar-full" ? <p>My Classes</p> : null}
												{this.state.showMyClasses ? (
													<i className='fas fa-caret-up'></i>
												) : (
													<i className='fas fa-caret-down'></i>
												)}
											</div>
											{this.state.showMyClasses ? (
												<div className='sub-menu'>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "Students",
																currentTabName: "Student List",
															})
														}>
														{this.state.tab === "Students" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Student List</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "StudentsBehaviour",
																currentTabName: "Student Behaviour",
															})
														}>
														{this.state.tab === "StudentsBehaviour" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Student Behaviour</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "StudentCreativity",
																currentTabName: "Student Creativity",
															})
														}>
														{this.state.tab === "StudentCreativity" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Student Creativity</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "StudentProblemSolving",
																currentTabName: "Student ProblemSolving",
															})
														}>
														{this.state.tab === "StudentProblemSolving" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Student ProblemSolving</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "StudentEntrepreneurship",
																currentTabName: "Student Entrepreneurship",
															})
														}>
														{this.state.tab === "StudentEntrepreneurship" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Student Entrepreneurship</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "Attendance",
																currentTabName: "Student Attendance",
															})
														}>
														{this.state.tab === "Attendance" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Attendance</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "Seating",
																currentTabName: "Student Seating Plan",
															})
														}>
														{this.state.tab === "Seating" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Seating Plan</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "CareerPath",
																currentTabName: "Career Path",
															})
														}>
														{this.state.tab === "CareerPath" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Career Path</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() =>
															this.setState({
																tab: "Participation",
																currentTabName: "Participation",
															})
														}>
														{this.state.tab === "Participation" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Participation</p> : null}
													</div>
												</div>
											) : null}
											<div
												className='sub-menu-item'
												onClick={() => this.setState({ tab: "Lesson", currentTabName: "Lesson Book" })}>
												{this.state.tab === "Lesson" ? <div className='active'></div> : null}
												{this.state.sidebar === "sidebar-full" ? <p>Lesson Book</p> : null}
											</div>
											<div
												className='sub-menu-item'
												onClick={() => this.setState({ tab: "Resource", currentTabName: "Resources" })}>
												{this.state.tab === "Resource" ? <div className='active'></div> : null}
												{this.state.sidebar === "sidebar-full" ? <p>Resources</p> : null}
											</div>
											<div
												className='sub-menu-item'
												onClick={() => this.setState({ tab: "Library", currentTabName: "Library" })}>
												{this.state.tab === "Library" ? <div className='active'></div> : null}
												{this.state.sidebar === "sidebar-full" ? <p>Library</p> : null}
											</div>
											<div
												className='sub-menu-item'
												onClick={() => this.setState({ tab: "Recognition", currentTabName: "Recognition" })}>
												{this.state.tab === "Recognition" ? <div className='active'></div> : null}
												{this.state.sidebar === "sidebar-full" ? <p>Recognition</p> : null}
											</div>
										</div>
									) : null}
									<div
										className='menu'
										onClick={() =>
											this.setState({
												showLearning: !this.state.showLearning,
												showTeaching: false,
											})
										}>
										<div className='menu-item'>
											<i className='fab fa-leanpub'></i>
											{this.state.sidebar === "sidebar-full" ? <p>Learning</p> : null}
											{this.state.showLearning ? <i className='fas fa-caret-up'></i> : <i className='fas fa-caret-down'></i>}
										</div>
									</div>
									{this.state.showLearning ? (
										<div className='sub-menu'>
											<div
												className='sub-menu-item'
												onClick={() =>
													this.setState({
														showat: !this.state.showat,
													})
												}>
												{this.state.sidebar === "sidebar-full" ? <p>Assessment Tools</p> : null}
												{this.state.showat ? <i className='fas fa-caret-up'></i> : <i className='fas fa-caret-down'></i>}
											</div>
											{this.state.showat ? (
												<div className='sub-menu'>
													<div
														className='sub-menu-item'
														onClick={() => this.setState({ tab: "Exam", currentTabName: "Examination" })}>
														{this.state.tab === "Exam" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Examination</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() => this.setState({ tab: "Question Bank", currentTabName: "Question Bank" })}>
														{this.state.tab === "Question Bank" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Question Bank</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() => this.setState({ tab: "Homework", currentTabName: "Homework" })}>
														{this.state.tab === "Homework" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Homework</p> : null}
													</div>
													<div
														className='sub-menu-item'
														onClick={() => this.setState({ tab: "Assignment", currentTabName: "Assigment" })}>
														{this.state.tab === "Assignment" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Assignment</p> : null}
													</div>
													{showLabwork ? (
														<div
															className='sub-menu-item'
															onClick={() => this.setState({ tab: "Lab Work", currentTabName: "Lab Work" })}>
															{this.state.tab === "Lab Work" ? <div className='active'></div> : null}
															{this.state.sidebar === "sidebar-full" ? <p>Lab Work</p> : null}
														</div>
													) : null}
													<div
														className='sub-menu-item'
														onClick={() => this.setState({ tab: "Presentation", currentTabName: "Presentation" })}>
														{this.state.tab === "Presentation" ? <div className='active'></div> : null}
														{this.state.sidebar === "sidebar-full" ? <p>Presentation</p> : null}
													</div>
												</div>
											) : null}

											<div
												className='sub-menu-item'
												onClick={() => this.setState({ tab: "DiscussionGroup", currentTabName: "Discussion Groups" })}>
												{this.state.tab === "DiscussionGroup" ? <div className='active'></div> : null}
												{this.state.sidebar === "sidebar-full" ? <p>Discussion Groups</p> : null}
											</div>
										</div>
									) : null}
									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: "Todo",
												showTeaching: false,
												currentTabName: "TODO",
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === "Todo" ? <div className='active'></div> : null}
											<i className='fas fa-list-alt'></i>
											{this.state.sidebar === "sidebar-full" ? <p>TODO</p> : null}
										</div>
									</div>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: "Announcement",
												showTeaching: false,
												currentTabName: "ANNOUNCEMENT",
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === "Announcement" ? <div className='active'></div> : null}
											<i className='fas fa-bullhorn'></i>
											{this.state.sidebar === "sidebar-full" ? <p>ANNOUNCEMENT</p> : null}
										</div>
									</div>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: "Poll",
												showTeaching: false,
												currentTabName: "POLL",
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === "Poll" ? <div className='active'></div> : null}
											<i className='fas fa-vote-yea'></i>
											{this.state.sidebar === "sidebar-full" ? <p>POLL</p> : null}
										</div>
									</div>

									<div
										className='menu'
										onClick={() =>
											this.setState({
												tab: "CalendarMy",
												showTeaching: false,
												currentTabName: "CALENDAR",
												showLearning: false,
											})
										}>
										<div className='menu-item'>
											{this.state.tab === "CalendarMy" ? <div className='active'></div> : null}
											<i className='far fa-calendar-check'></i>
											{this.state.sidebar === "sidebar-full" ? <p>CALENDAR</p> : null}
										</div>
									</div>
								</div>
							</div>
							<div className={this.state.content}>
								{/* {this.state.tab === 1 ? <Home /> : null} */}
								{this.state.tab === "Profile" ? <Profile /> : null}
								{this.state.tab === "SchoolInfo" ? <SchoolInfo /> : null}
								{this.state.tab === "Students" ? <Students /> : null}
								{this.state.tab === "StudentsBehaviour" ? <StudentsBehaviour /> : null}
								{this.state.tab === "Attendance" ? <Attendance /> : null}
								{this.state.tab === "Seating" ? <Seating /> : null}
								{this.state.tab === "CareerPath" ? <CareerPath /> : null}
								{this.state.tab === "Lesson" ? <Lesson /> : null}
								{this.state.tab === "Resource" ? <Resource /> : null}
								{this.state.tab === "Library" ? <Library /> : null}
								{this.state.tab === "Recognition" ? <Recognition /> : null}
								{this.state.tab === "Exam" ? <Exam /> : null}
								{this.state.tab === "Question Bank" ? <QuestionBank /> : null}
								{this.state.tab === "Homework" ? <HomeWork /> : null}
								{this.state.tab === "Assignment" ? <Assignment /> : null}
								{this.state.tab === "Lab Work" ? <LabWorks /> : null}
								{this.state.tab === "DiscussionGroup" ? <DiscussionGroup /> : null}
								{this.state.tab === "Todo" ? <Todo /> : null}
								{this.state.tab === "Announcement" ? <Announcement /> : null}
								{this.state.tab === "Poll" ? <Poll /> : null}
								{this.state.tab === "CalendarMy" ? <CalendarMy /> : null}
								{this.state.tab === "StudentCreativity" ? <StudentCreativity /> : null}
								{this.state.tab === "StudentProblemSolving" ? <StudentProblemSolving /> : null}
								{this.state.tab === "StudentEntrepreneurship" ? <StudentEntrepreneurship /> : null}
								{this.state.tab === "Participation" ? <StudentParticipation /> : null}
							</div>
						</div>
					</div>
				)}
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		teacher: state.teacher,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		fetchTeacher: (data) => dispatch(fetchTeacher(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(TeacherDashboard);
