import React from "react";

import "./assignmentsList.css";

import { Modal, Button, Form, Spinner } from "react-bootstrap";
import { AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine, RiEditLine } from "react-icons/ri";

import { connect } from "react-redux";

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { addAssignment, deleteAssignment, updateAssignment } from "../../../../redux/actions/teacherDashboard/teacherActions";
import penPaper from "../../assets/penPaper.svg";

import moment from "moment";

class AssignmentList extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			showModal: false,
			assignmentName: "",
			assignmentObjectives: "",
			startDate: "",
			endDate: "",
			adding: false,
			saving: false,
			showEdit: false,
			selected: {},
			sAssignmentName: "",
			sAssignmentObjectives: "",
			sStartDate: "",
			sEndDate: "",
		};
	}

	handleChange = (e) => {
		const { name, value } = e.target;
		this.setState({
			[name]: value,
		});
	};

	handleSubmit = async () => {
		this.setState({
			adding: true,
		});

		const data = {
			schoolId: this.props.school,
			teacherId: this.props.teacher._id,
			className: this.props.sub.class,
			sectionName: this.props.sub.section,
			subjectName: this.props.sub.subject,
			title: this.state.assignmentName,
			objectives: this.state.assignmentObjectives,
			startDate: this.state.startDate,
			endDate: this.state.endDate,
		};

		const check = await this.props.addAssignment(data);

		if (check) {
			this.setState({
				adding: false,
			});
			if (check.type === "success") {
				toaster.notify("Assignment Added", {
					duration: 3000,
				});
				this.setState({
					chapterName: "",
					adding: false,
					showModal: false,
				});
			} else {
				toaster.notify("Assignment Could Not Be Added", {
					duration: 3000,
				});
				this.setState({
					chapterName: "",
					adding: false,
					showModal: false,
				});
			}
		}
	};

	handleDeleteAssignment = async (assignmentId) => {
		const data = {
			assignmentId: assignmentId,
			teacherId: this.props.teacher._id,
		};

		const check = await this.props.deleteAssignment(data);

		if (check) {
			if (check.type === "success") {
				toaster.notify("Assignment Deleted", {
					duration: 3000,
				});
			} else {
				toaster.notify("Assignment Could Not Be Deleted", {
					duration: 3000,
				});
			}
		}
	};

	showEdit = (e) => {
		this.setState({
			showEdit: true,
			selected: e,
			sAssignmentName: e.title,
			sAssignmentObjectives: e.objectives,
			sStartDate: e.startDate,
			sEndDate: e.endDate,
		});
	};

	handleSubmitEdit = async () => {
		this.setState({
			saving: true,
		});

		const data = {
			teacherId: this.props.teacher._id,
			assignmentId: this.state.selected._id,
			title: this.state.sAssignmentName,
			objectives: this.state.sAssignmentObjectives,
			startDate: this.state.sStartDate,
			endDate: this.state.sEndDate,
		};

		const check = await this.props.updateAssignment(data);

		if (check) {
			this.setState({
				saving: false,
			});
			if (check.type === "success") {
				toaster.notify("Assignment Added", {
					duration: 3000,
				});
				this.setState({
					showEdit: false,
				});
			} else {
				toaster.notify("Assignment Could Not Be Added", {
					duration: 3000,
				});
			}
		}
	};

	render() {
		console.log(this.props.sub);
		return (
			<div className='topicList-container'>
				<div className='topicList-header'>
					<Button variant='primary' onClick={() => this.setState({ showModal: true })}>
						<AiOutlinePlus />
						Create Assignment
					</Button>
				</div>

				{this.props.teacher.assignments &&
					this.props.teacher.assignments.map((assignment) => {
						if (
							assignment.subjectName === this.props.sub.subject &&
							assignment.className === this.props.sub.class &&
							assignment.sectionName === this.props.sub.section
						) {
							return (
								<div onClick={() => this.props.showStudents(assignment)} className='topicList-box'>
									<img src={penPaper} alt='' />
									<div className='topic-name'>
										<h1>{assignment.title}</h1>
										<p>
											{assignment.subjectName}, {assignment.className} {assignment.sectionName}
										</p>
										<p>Start Date : {moment(assignment.startDate).format("LLL")}</p>
										<p>End Date : {moment(assignment.endDate).format("LLL")}</p>
									</div>
									<div className='topic-date'>
										<p>Date : {moment(new Date(assignment.startDate)).format("LLLL")}</p>
										<span style={{ color: "#616ccc" }} onClick={() => this.showEdit(assignment)}>
											<RiEditLine />
										</span>
										<span style={{ color: "#de3700" }} onClick={() => this.handleDeleteAssignment(assignment._id)}>
											<RiDeleteBinLine />
										</span>
									</div>
								</div>
							);
						}
					})}

				<Modal show={this.state.showModal} onHide={() => this.setState({ showModal: false })} backdrop='static' keyboard={false}>
					<Modal.Header closeButton>
						<Modal.Title>Create Assignment</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment Name</Form.Label>
							<Form.Control type='text' name='assignmentName' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment Objectives</Form.Label>
							<Form.Control as='textarea' rows={3} name='assignmentObjectives' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment Start Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='startDate' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment End Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='endDate' onChange={this.handleChange} />
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button variant='primary' onClick={this.handleSubmit}>
							{this.state.adding ? <Spinner animation='border' variant='light' /> : "Create"}
						</Button>
					</Modal.Footer>
				</Modal>

				<Modal show={this.state.showEdit} onHide={() => this.setState({ showEdit: false })} backdrop='static' keyboard={false}>
					<Modal.Header closeButton>
						<Modal.Title>Edit Assignment</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment Name</Form.Label>
							<Form.Control type='text' name='sAssignmentName' onChange={this.handleChange} value={this.state.sAssignmentName} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment Objectives</Form.Label>
							<Form.Control
								as='textarea'
								rows={3}
								name='sAssignmentObjectives'
								onChange={this.handleChange}
								value={this.state.sAssignmentObjectives}
							/>
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment Start Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='sStartDate' onChange={this.handleChange} value={this.state.sStartDate} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Assignment End Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='sEndDate' onChange={this.handleChange} value={this.state.sEndDate} />
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button variant='primary' onClick={this.handleSubmitEdit}>
							{this.state.saving ? <Spinner animation='border' variant='light' /> : "Update"}
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addAssignment: (data) => dispatch(addAssignment(data)),
		deleteAssignment: (data) => dispatch(deleteAssignment(data)),
		updateAssignment: (data) => dispatch(updateAssignment(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(AssignmentList);
