import React from 'react';

import './polls.style.css';

import axios from 'axios';

import Lottie from "lottie-react-web";
import loadAnimation from './assets/7774-loading.json';
import SinglePoll from './singlePoll/singlePoll';

class MyPolls extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            polls: []
        }
    }

    componentDidMount = async () => {
        this.setState({
            polls: this.props.polls,
            loading: false
        })
    }

    render() {
        return (

            <div className="my-polls-container">
                <div className="polls-wrapper">
                    {
                        this.state.loading
                            ?
                            <Lottie
                                options={{ animationData: loadAnimation }}
                                width={250}
                                height={250}
                            />
                            :
                            <>
                                {
                                    this.state.polls.length > 0
                                        ?
                                        <>
                                            {
                                                this.state.polls.map((item) => {
                                                    return <SinglePoll poll={item} studentId={this.props.studentId} />
                                                })

                                            }
                                        </>
                                        :
                                        <p>No Polls Right Now</p>
                                }
                            </>
                    }
                </div>
            </div>
        )
    }
}

export default MyPolls;