import React from "react";

import "./labworks.css";

import folderImg from "../assets/folderIcon.svg";

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";

import iigpLogo from "../assets/iigpqm.png";
// import QuizMaker from './quizMaker/quizMaker';

import { connect } from "react-redux";
import LabWorkList from "./labworksList/labworksList";
import StudentTable from "./studentTable";

class LabWorks extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			current: {},
			selected: {},
			assignmentDetails: {},
			ShowLabWorkMaker: false,
			showLabWorkList: false,
		};
	}

	handleBox = (e) => {
		this.setState({
			current: e,
			showLabWorkList: true,
		});
	};

	showStudents = (e) => {
		this.setState({
			selected: e,
			showTable: true,
			ShowAssignmentMaker: false,
		});
	};

	handleRefresh = () => {
		this.setState(
			{
				showTable: false,
				labwork: {},
			},
			() => {
				this.props.teacher.labworks.map((labwork) => {
					if (
						labwork.subjectName === this.state.current.subject &&
						labwork.className === this.state.current.class &&
						labwork.sectionName === this.state.current.section
					) {
						this.setState({
							showTable: true,
							selected: labwork,
						});
					}
				});
			}
		);
	};

	render() {
		console.log(this.props.teacher);
		var teacher = this.props.teacher;
		return (
			<div className='quiz-container'>
				<div className='quiz-header'>
					{!this.state.showAssignmentList ? (
						<div>
							<h1>My LabWork Library</h1>
						</div>
					) : (
						<>
							{!this.state.ShowAssignmentMaker ? (
								<div>
									<p onClick={() => this.setState({ showAssignmentList: false })}>
										<FaArrowLeft />
									</p>
									<h1>My LabWork Library</h1>
									<p>
										<FaAngleRight />
									</p>
									<h1>LabWorks</h1>
								</div>
							) : null}
						</>
					)}
				</div>

				<div className='quiz-body'>
					{!this.state.showLabWorkList ? (
						<div className='chooseClass-container'>
							{teacher &&
								teacher.subjects.map((sub) => {
									if (sub.lab) {
										return (
											<div className='classes-cardFolder-container' onClick={() => this.handleBox(sub)}>
												<div className='classes-cardFolder-img'>
													<img src={folderImg} alt='folder-icon' />
												</div>
												<div className='classes-cardFolder-name'>
													<p>
														{sub.class} {sub.section} | {sub.subject}
													</p>
												</div>
											</div>
										);
									}
								})}
						</div>
					) : (
						<>
							{!this.state.showTable ? (
								<LabWorkList sub={this.state.current} school={teacher.school_id} showStudents={this.showStudents} />
							) : (
								<StudentTable
									teacher={teacher}
									sub={this.state.current}
									labwork={this.state.selected}
									handleRefresh={this.handleRefresh}
								/>
							)}
						</>
					)}
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps)(LabWorks);
