import "bootstrap/dist/css/bootstrap.min.css";
import React from "react";
import { BrowserRouter, Route } from "react-router-dom";
import { fetchUser } from "./redux/actions/authActions";

import Login from "./layout/Login";
import Layout from "./layout/layout";
import { connect } from "react-redux";
import "./App.css";
import IIGPHome from "./layout/IIGPHome/iigpHome";
import StudentLearning from "./layout/studentWebsite/learning/studentLearning";
import IIGPPractices from "./layout/studentWebsite/practice/practice";
import IIGPResources from "./layout/studentWebsite/resources/resources";
import StudentAnalytics from "./layout/studentWebsite/analytics/studentAnalytics";
import SubjectAnalytics from "./layout/studentWebsite/analytics/subjectAnalytics/subjectAnalytics";
import staticAnalytics from "./layout/IIGPHome/staticAnalytics/staticAnalytics.page";
import LearningOrganizer from "./layout/studentWebsite/mySchool/learningOrganizer";
import LearningProgress from './layout/studentWebsite/mySchool/learningProgress';

class App extends React.Component {
	componentDidMount() {
		this.props.fetchUser();
	}
	render() {
		return (
			<div className="App">
				<BrowserRouter>
					<Route exact path="/" component={IIGPHome} />
					<Route exact path="/analytics" component={staticAnalytics} />
					<Route exact path="/login" component={Login} />
					<Route exact path="/dashboard" component={Layout} />
					<Route
						exact
						path="/dashboard/learning/:subject"
						component={StudentLearning}
					/>
					<Route
						exact
						path="/dashboard/learning/:subject/:chapter/:topic/practice/:id"
						component={IIGPPractices}
					/>
					<Route
						exact
						path="/dashboard/learning/:subject/:chapter/:topic/resource/:name"
						component={IIGPResources}
					/>
					<Route
						exact
						path="/dashboard/analytics"
						component={StudentAnalytics}
					/>
					<Route
						exact
						path="/dashboard/analytics/:subject"
						component={SubjectAnalytics}
					/>
					<Route exact path="/dashboard/mySchool/learningOrganizer" component={LearningOrganizer} />
					<Route exact path="/dashboard/mySchool/learningProgress" component={LearningProgress} />
				</BrowserRouter>
			</div>
		);
	}
}

function mapDispatchToProps(dispatch) {
	return {
		fetchUser: () => dispatch(fetchUser()),
	};
}

export default connect(null, mapDispatchToProps)(App);
