import React from 'react';

import './messageBox.css';

import axios from 'axios';

import moment from 'moment';

import { GoCommentDiscussion } from 'react-icons/go';

import { Modal, Button, Form } from "react-bootstrap";

import ReplyBox from '../replyBox/replyBox';

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";

import { connect } from "react-redux";

import { addReply } from '../../../../../../redux/actions/studentDashboard/studentActions';

class MessageBox extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            addReply: false,
            showComments: false,
            ownerInfo: {},
            reply: ''
        }
    }

    componentDidMount = async () => {

        if (this.props.msg.userId === this.props.student._id) {
            this.setState({
                ownerInfo: this.props.student,
                loading: false
            })
        }

        else {

            if (this.props.msg.userRole === 'student') {

                const data = {
                    id: this.props.msg.userId
                }

                var result = await axios.post("/api/student/profile", data);

                if (result) {
                    this.setState({
                        ownerInfo: result.data,
                        loading: false
                    })
                }
            }

            else {

                const data = {
                    id: this.props.msg.userId,
                    role: this.props.msg.userRole
                }

                var result2 = await axios.post("/api/myschool/getOtherUser", data);
                if (result2) {
                    this.setState({
                        ownerInfo: result2.data,
                        loading: false
                    })
                }
            }
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;

        this.setState({
            [name]: value
        })
    }

    handleSubmitReply = async () => {

        const data = {
            reply: this.state.reply,
            userId: this.props.student._id,
            userRole: 'student',
            msgId: this.props.msg._id,
            discId: this.props.discId
        }

        var check = await this.props.addReply(data);

        if (check) {
            if (check.type === "success") {
                toaster.notify("Reply Sent", {
                    duration: 3000,
                });
                this.setState({
                    reply: "",
                    addReply: false,
                });
            }
            else {
                toaster.notify("Reply could not be sent !", {
                    duration: 3000,
                });
                this.setState({
                    reply: "",
                    addReply: false,
                });
            }
        }
    }

    render() {
        return (
            <div className="message-box-container">
                <div className="msg-owner-info">
                    {
                        this.state.loading
                            ?
                            null
                            :
                            <>
                                <img src={"/api/file/show/" + this.state.ownerInfo.photo.filename} alt="user-dp" />
                                <div>
                                    <p>{this.state.ownerInfo.name}</p>
                                    <p>{moment(this.props.msg.date).fromNow()}</p>
                                </div>
                            </>
                    }
                </div>
                <p>{this.props.msg.message}</p>
                <div className="msg-total-reply">
                    <p onClick={() => this.setState({ showComments: !this.state.showComments })}><GoCommentDiscussion /> {this.props.msg.reply.length} Comments</p>
                    <span onClick={() => this.setState({ addReply: true })}>Reply</span>
                </div>
                <div className={this.state.showComments ? "msg-reply-section active" : "msg-reply-section"}>
                    {
                        this.props.msg.reply.length > 0 && this.props.msg.reply.map((item) => {
                            return <ReplyBox reply={item} />
                        })
                    }
                </div>

                {/* For Reply */}

                <Modal show={this.state.addReply} onHide={() => this.setState({ addReply: false })} backdrop='static' keyboard={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>Add Reply</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form.Group controlId='formBasicEmail'>
                            <Form.Control as="textarea" rows={5} name='reply' onChange={this.handleChange} placeholder="Start typing your reply ..." />
                        </Form.Group>

                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant='primary' onClick={this.handleSubmitReply}>
                            Send
						</Button>
                    </Modal.Footer>
                </Modal>

            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        student: state.student.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addReply: (data) => dispatch(addReply(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(MessageBox);