import React from "react";

import "./assignments.css";

import folderImg from "../assets/folderIcon.svg";

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";

import iigpLogo from "../assets/iigpqm.png";
// import QuizMaker from './quizMaker/quizMaker';

import { connect } from "react-redux";
import AssignmentList from "./assignmentsList/assignmentsList";
import StudentTable from "./studentTable";

class Assignment extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			current: {},
			assignmentDetails: {},
			ShowAssignmentMaker: false,
			showAssignmentList: false,
			selected: {},
		};
	}

	handleBox = (e) => {
		this.setState({
			current: e,
			showAssignmentList: true,
		});
	};

	showStudents = (e) => {
		this.setState({
			selected: e,
			showTable: true,
			ShowAssignmentMaker: false,
		});
	};

	handleRefresh = () => {
		this.setState(
			{
				showTable: false,
				assignment: {},
			},
			() => {
				this.props.teacher.assignments.map((assignment) => {
					if (
						assignment.subjectName === this.state.current.subject &&
						assignment.className === this.state.current.class &&
						assignment.sectionName === this.state.current.section
					) {
						this.setState({
							showTable: true,
							selected: assignment,
						});
					}
				});
			}
		);
	};

	render() {
		console.log(this.props.teacher);
		var teacher = this.props.teacher;
		return (
			<div className='quiz-container'>
				<div className='quiz-header'>
					{!this.state.showAssignmentList ? (
						<div>
							<h1>My Assignment Library</h1>
						</div>
					) : (
						<>
							{!this.state.showTable ? (
								<div>
									<p onClick={() => this.setState({ showAssignmentList: false })}>
										<FaArrowLeft />
									</p>
									<h1>My Assignment Library</h1>
									<p>
										<FaAngleRight />
									</p>
									<h1>Assignments</h1>
								</div>
							) : (
								<div>
									<p onClick={() => this.setState({ showTable: false })}>
										<FaArrowLeft />
									</p>
									<h1>My Assignment Library</h1>
									<p>
										<FaAngleRight />
									</p>
									<h1>Assignments</h1>
									<p>
										<FaAngleRight />
									</p>
									<h1>Student List</h1>
								</div>
							)}
						</>
					)}
				</div>

				<div className='quiz-body'>
					{!this.state.showAssignmentList ? (
						<div className='chooseClass-container'>
							{teacher &&
								teacher.subjects.map((sub) => {
									return (
										<div className='classes-cardFolder-container' onClick={() => this.handleBox(sub)}>
											<div className='classes-cardFolder-img'>
												<img src={folderImg} alt='folder-icon' />
											</div>
											<div className='classes-cardFolder-name'>
												<p>
													{sub.class} {sub.section} | {sub.subject}
												</p>
											</div>
										</div>
									);
								})}
						</div>
					) : (
						<>
							{!this.state.showTable ? (
								<AssignmentList sub={this.state.current} school={teacher.school_id} showStudents={this.showStudents} />
							) : (
								<StudentTable
									teacher={teacher}
									sub={this.state.current}
									assignment={this.state.selected}
									handleRefresh={this.handleRefresh}
								/>
							)}
						</>
					)}
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps)(Assignment);
