import React from "react";

export default class Participation extends React.Component {
	constructor(props) {
		super(props);
		var participations = [];
		console.log(props.participations);
		props.participations.map((participation) => {
			console.log(participation.date);
			if (participation.teacherId === props.teacherId && participation.date === props.date) {
				participations = participation.status;
			}
		});
		console.log(participations);
		this.state = {
			participations: participations,
		};
	}

	add = (e) => {
		var participations = this.state.participations;
		var push;
		if (!participations.includes(e)) {
			participations.push(e);
			push = true;
		} else {
			participations = participations.filter((item) => {
				return item !== e;
			});
			push = false;
		}
		this.setState({
			participations: participations,
		});
		console.log(participations);
		this.props.addParticipation(participations, this.props.data, push);
	};
	render() {
		return (
			<div className='behaviour-chart'>
				<div className='behaviour-list'>
					<div
						className={this.state.participations.includes("raised questions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("raised questions")}>
						<i class='fas fa-check-circle'></i>
						<p>raised questions</p>
					</div>
					<div
						className={this.state.participations.includes("answered questions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("answered questions")}>
						<i class='fas fa-check-circle'></i>
						<p>answered questions</p>
					</div>
					<div
						className={this.state.participations.includes("participated in class discussion") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("participated in class discussion")}>
						<i class='fas fa-check-circle'></i>
						<p>participated in class discussion</p>
					</div>
				</div>
			</div>
		);
	}
}
