import React from 'react';
import './students.css';
import { connect } from 'react-redux'

import "react-table-6/react-table.css";
import 'toasted-notes/src/styles.css';
import StudentTable from './studentTable';
import folderImg from '../assets/folderIcon.svg';

class CareerPath extends React.Component {
    constructor() {
        super();
        this.state = {
            showAdd: false,
            showDelete: false,
            showEdit: false,
            showView: false,
            name: "",
            dob: "",
            photo: [],
            address: "",
            blood: "",
            email: "",
            pname: "",
            pcontact: "",
            pemail: "",
            contact: '',
            class: "",
            section: "",
            city: "",
            state: "",
            zip: "",
            showImage: '',
            adding: false,
            deleting: false,
            editing: false,
            error: '',
            item: [],
            editname: "",
            editemail: "",
            editpemail: "",
            editphoto: [],
            editaddress: "",
            editblood: "",
            editdob: "",
            editcontact: '',
            editpemail: "",
            editclassName: "",
            editsectionName: "",
            editcity: "",
            editstate: "",
            editzip: "",
            editshowImage: '',
            imageEdited: false,
            showAssignSubject: false,
            showTable: false
        }
    }

    handleBox = (e) => {
        this.setState({
            current: e,
            showTable: true
        })
    }

    render() {
        var teacher = this.props.teacher;
        return (
            <div className="students-container">
                {!this.state.showTable ? (
                    <div className="students-header">
                        <div className="students-stats">
                            <h1>Student Career Path</h1>
                        </div>
                    </div>
                ) : null}
                {
                    !this.state.showTable
                        ?
                        <div className="chooseClass-container" style={{ padding: "1rem" }}>
                            {
                                teacher && teacher.subjects.map(sub => {
                                    return (

                                        <div className="classes-cardFolder-container" onClick={() => this.handleBox(sub.class)}>
                                            <div className="classes-cardFolder-img">
                                                <img src={folderImg} alt="folder-icon" />
                                            </div>
                                            <div className="classes-cardFolder-name">
                                                <p>{sub.class} {sub.section}</p>
                                                <p>{sub.subject}</p>
                                            </div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                        :
                        <StudentTable teacher={teacher} class={this.state.current} />
                }
            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData
    };
}

export default connect(mapStateToProps)(CareerPath);