import React from 'react';
import '../staticAnalytics/staticAnalytics.style.css';
import illustration1 from '../staticAnalytics/asset/illustration-01.png';
import illustration2 from '../staticAnalytics/asset/illustration-02.png'
import  Footer from '../components/footer/footer';


class staticAnalytics extends React.Component {
    render() {
      return(
        <div className="container">
          <div className="staticAnalytics-cont">
              <h1>IIGP Analytics</h1>
              <h3>Reports that are more than just numbers</h3>
              <div className="boxes-cont">
                    <div className="box-1">
                        <div className="left-col">
                            <div className="benefit-content">
                                <h2>Immediate insights into children's performance</h2>
                                <p>Discover children's strengths and weaknesses in a whole new way! Every time children practise, IXL Analytics keeps you in the loop about their progress. Real-time, relevant data solves the mystery of how children are doing and empowers you to take action to help them.</p>
                            </div>
                        </div>
                        <div className="right-col">
                            <img src={illustration1} alt="illu-1"></img>
                        </div>
                    </div>
                    <div className="box-2">
                        <div className="right-col">
                            <img src={illustration2} alt="illu-2"></img>
                        </div>
                        <div className="left-col">
                            <div className="benefit-content">
                                <h2>Valuable information at your fingertips</h2>
                                <p>IXL transforms your children's practice data into straightforward insights that help you focus on what's important. Just a few clicks will reveal your children's growth, trouble spots and even their readiness for standardised testing.</p>
                            </div>
                        </div>
                    </div>
                    <div className="box-3">
                        <div className="left-col">
                            <div className="benefit-content">
                                <h2>Immediate insights into children's performance</h2>
                                <p>Discover children's strengths and weaknesses in a whole new way! Every time children practise, IXL Analytics keeps you in the loop about their progress. Real-time, relevant data solves the mystery of how children are doing and empowers you to take action to help them.</p>
                            </div>
                        </div>
                        <div className="right-col">
                            <img src={illustration1} alt="illu-1"></img>
                        </div>
                    </div>
              </div>
              <div className="staticAnalytics-unlock-cont">
                  <h1>Unlock the power of IIGP Analytics</h1>
                  <div className="btn">
                  <button>Join IIGP</button>
                  </div>
              </div>
          </div>
          < Footer />
        </div>
      )
    }
  }

export default staticAnalytics;