import React from "react";

import "./questionList.css";

import { connect } from "react-redux";

import penPaper from "../../assets/penPaper.svg";

import moment from "moment";

class QuestionList extends React.Component {

    render() {
        return (
            <div className='topicList-container'>

                {this.props.teacher.questionBank.map((bank) => {
                    if (bank.subjectName === this.props.subject && bank.className === this.props.class && bank.sectionName === this.props.section) {
                        return (
                            <div className='topicList-box'>
                                <img src={penPaper} alt='' />
                                <div className='topic-name' onClick={() => this.props.activateAllQuestions(true, bank)}>
                                    <h1>{bank.title}</h1>
                                    <p>
                                        {bank.subjectName}, {bank.className} {bank.sectionName}
                                    </p>
                                </div>
                                <div className='topic-date'>
                                    <p>Date: {moment(new Date(bank.date)).format("LLLL")}</p>
                                </div>
                            </div>
                        );
                    }
                })}
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData,
    };
}

export default connect(mapStateToProps)(QuestionList);
