import React from 'react';
import './schools.css';
import { connect } from 'react-redux'

import { Modal, Button, Form, Spinner, Col } from 'react-bootstrap';
import { IoIosClose } from "react-icons/io";
import { FaCheck, FaRegTrashAlt, FaInfoCircle } from "react-icons/fa";
import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";

import Moment from 'react-moment';

import addImg from '../../assets/more (5).svg';
import imagepicker from '../../assets/photo.png';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

import {
    addSchool,
    fetchSchools,
    deleteSchool,
    editSchool,
    editSchoolWithImage,
    addSchoolAdmin,
    addSchoolDistrictOfficer,
    removeSchoolAdmin,
    removeDistrictOfficer,
    showSchoolAdminInfo,
    editSchoolAdminWithImage,
    editSchoolAdmin,
} from '../../../redux/actions/superAdminDashboard/superAdminSchoolActions';

class Schools extends React.Component {
    constructor() {
        super();
        this.state = {
            showAdd: false,
            showDelete: false,
            showEdit: false,
            showView: false,
            name: "",
            branch: "",
            logo: [],
            address: "",
            email: "",
            contact: '',
            fax: "",
            website: "",
            showImage: '',
            adding: false,
            deleting: false,
            editing: false,
            error: '',
            item: [],
            editname: "",
            editbranch: "",
            editlogo: [],
            editaddress: "",
            editemail: "",
            editcontact: '',
            editfax: "",
            editwebsite: "",
            editshowImage: '',
            showAddAdmin: false,
            schooladminname: "",
            schooladminemail: "",
            schooladminphoto: [],
            schooladminaddress: "",
            schooladmindob: "",
            schooladmincontact: '',
            schooladminqualification: "",
            schooladminexperience: 0,
            schooladmincity: "",
            schooladminstate: "",
            schooladminzip: "",
            schooladminshowImage: '',
            showEditAdmin: false,
            showAddOfficer: false,
            officer: "",
            addingOfficer: false,
            removingAdmin: false,
            removingOfficer: false,
            imageEdited: false,
            adminInfo: [],
            editschooladminname: "",
            editschooladminemail: "",
            editschooladminphoto: [],
            editschooladminaddress: "",
            editschooladmindob: "",
            editschooladmincontact: '',
            editschooladminqualification: "",
            editschooladminexperience: 0,
            editschooladmincity: "",
            editschooladminstate: "",
            editschooladminzip: "",
            editschooladminshowImage: '',
            editingAdmin: false,
            adminimageEdited: false,
        }
    }

    componentDidMount() {
    }

    handleChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }

    handleLogo = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            console.log(e.target.files[0]);
            const f = e.target.files[0];
            this.setState({
                logo: f,
                showImage: URL.createObjectURL(f),
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleEditLogo = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                editlogo: f,
                editshowImage: URL.createObjectURL(f),
                imageEdited: true,
                error: ""
            })
        }
        else {
            this.setState({
                error: "Logo image size cannot be more than 1mb!"
            })
        }
    }

    handleAddSchool = async () => {
        this.setState({
            error: ""
        })
        if (this.state.showImage === "" || this.state.name === "" || this.state.branch === "" || this.state.address === "" || this.state.email === "" || this.state.contact === "" || this.state.fax === "" || this.state.website === "") {
            this.setState({
                error: "Please fill all the details"
            })
        }
        else {
            this.setState({
                adding: true
            })
            const data = {
                name: this.state.name,
                branch: this.state.branch,
                address: this.state.address,
                email: this.state.email,
                contact: this.state.contact,
                fax: this.state.fax,
                website: this.state.website,
            }
            var form_data = new FormData();
            form_data.append('file', this.state.logo);
            const check = await this.props.addSchool(form_data, data);
            if (check) {
                this.setState({
                    adding: false
                })
                if (check.type === 'success') {
                    toaster.notify("School Added", {
                        duration: 3000
                    })
                    this.setState({
                        name: "",
                        branch: "",
                        logo: [],
                        address: "",
                        email: "",
                        contact: '',
                        fax: "",
                        website: "",
                        showImage: '',
                        showAdd: false
                    })
                }
            }
        }
    }

    handleDeleteSchool = async () => {
        if (this.state.item) {
            this.setState({
                deleting: true,
                error: ""
            })
            const data = {
                id: this.state.item._id,
                logo: this.state.item.logo
            };
            const check = await this.props.deleteSchool(data);
            if (check) {
                this.setState({
                    deleting: false
                })
                if (check.type === 'success') {
                    this.setState({
                        showDelete: false,
                        item: []
                    })
                    toaster.notify("School Removed", {
                        duration: 3000
                    })
                }
            }
        }
        else {
            this.setState({
                error: "Could not find the item"
            })
        }
    }

    handleEditSchool = async () => {
        this.setState({
            error: ""
        })
        if (this.state.editName === "" || this.state.editBranch === "") {
            toaster.notify("Please fill all the details", {
                duration: 3000
            })
        }
        else {
            this.setState({
                editing: true
            })
            const data = {
                name: this.state.editname,
                branch: this.state.editbranch,
                address: this.state.editaddress,
                email: this.state.editemail,
                contact: this.state.editcontact,
                fax: this.state.editfax,
                website: this.state.editwebsite,
                id: this.state.item._id,
                logo: this.state.item.logo
            };
            var form_data = new FormData();
            form_data.append('file', this.state.editlogo);
            const check = this.state.imageEdited ? await this.props.editSchoolWithImage(form_data, data) : await this.props.editSchool(data);
            if (check) {
                this.setState({
                    editing: false
                })
                if (check.type === 'success') {
                    this.setState({
                        editname: "",
                        editbranch: "",
                        editlogo: [],
                        editaddress: "",
                        editemail: "",
                        editcontact: '',
                        editfax: "",
                        editwebsite: "",
                        editshowImage: '',
                        showEdit: false
                    })
                    toaster.notify("School Information Added", {
                        duration: 3000
                    })
                }
            }
        }
    }

    handleAdminPhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            console.log(e.target.files[0]);
            const f = e.target.files[0];
            this.setState({
                adminPhoto: f,
                schooladminshowImage: URL.createObjectURL(f),
                error: ""
            })
        }
        else {
            this.setState({
                error: "Profile image size cannot be more than 1mb!"
            })
        }
    }

    handleAddAdministrator = async () => {
        this.setState({
            error: ""
        })
        if (this.state.schooladminshowImage === "" ||
            this.state.schooladminname === "" ||
            this.state.schooladminemail === "" ||
            this.state.schooladminaddress === "" ||
            this.state.schooladmincontact === "" ||
            this.state.schooladmindob === "" ||
            this.state.schooladminqualification === "" ||
            this.state.schooladmincity === "" ||
            this.state.schooladminstate === "" ||
            this.state.schooladminzip === "") {
            this.setState({
                error: "Please fill all the details"
            })
        }
        else {
            this.setState({
                addingAdmin: true
            })
            const data = {
                name: this.state.schooladminname,
                email: this.state.schooladminemail,
                address: this.state.schooladminaddress,
                contact: this.state.schooladmincontact,
                dob: this.state.schooladmindob,
                qualification: this.state.schooladminqualification,
                experience: this.state.schooladminexperience,
                city: this.state.schooladmincity,
                state: this.state.schooladminstate,
                zip: this.state.schooladminzip,
                id: this.state.item._id,
            };
            var form_data = new FormData();
            form_data.append('file', this.state.adminPhoto);
            const check = await this.props.addSchoolAdmin(form_data, data);
            if (check) {
                this.setState({
                    addingAdmin: false
                })
                if (check.type === 'success') {
                    this.setState({
                        schooladminname: "",
                        schooladminemail: "",
                        schooladminphoto: [],
                        schooladminaddress: "",
                        schooladmindob: "",
                        schooladmincontact: '',
                        schooladminqualification: "",
                        schooladminexperience: 0,
                        schooladmincity: "",
                        schooladminstate: "",
                        schooladminzip: "",
                        schooladminshowImage: '',
                        showAddAdmin: false,
                        showView: false
                    })
                    toaster.notify("School Administrator Assigned", {
                        duration: 3000
                    })
                }
            }
        }
    }

    handleAssignDistrictOfficer = async () => {
        this.setState({
            error: ""
        })
        if (this.state.officer === "") {
            this.setState({
                error: "Please select an officer"
            })
        }
        else {
            console.log(this.state.officer);
            this.setState({
                addingOfficer: true
            })
            var officerId = this.state.officer;
            var schoolId = this.state.item._id;
            const data = {
                officerId, schoolId
            };
            const check = await this.props.addSchoolDistrictOfficer(data);
            if (check) {
                this.setState({
                    addingOfficer: false
                })
                if (check.type === 'success') {
                    this.setState({
                        officer: "",
                        showAddOfficer: false,
                        showView: false
                    })
                    toaster.notify("District Director Assigned", {
                        duration: 3000
                    })
                }
            }
        }
    }

    handleShowSchoolAdminInfo = async (e) => {
        const data = {
            id: e
        }
        const result = await this.props.showSchoolAdminInfo(data);
        this.setState({
            adminInfo: result,
            showAddAdmin: false,
            showEditAdmin: !this.state.showEditAdmin,
            editschooladminname: result.name,
            editschooladminemail: result.email,
            editschooladminphoto: [],
            editschooladminaddress: result.address,
            editschooladmindob: result.dob,
            editschooladmincontact: result.contact,
            editschooladminqualification: result.qualification,
            editschooladminexperience: result.experience,
            editschooladmincity: result.city,
            editschooladminstate: result.state,
            editschooladminzip: result.zip,
            editschooladminshowImage: "/api/file/show/" + result.photo.filename,
        })
    }

    handleEditAdminPhoto = (e) => {
        console.log(e.target.files[0]);
        if (e.target.files[0].size / 1024 / 1024 <= 1) {
            const f = e.target.files[0];
            this.setState({
                editschooladminphoto: f,
                editschooladminshowImage: URL.createObjectURL(f),
                adminimageEdited: true,
                error: ""
            })
        }
        else {
            this.setState({
                error: "Profile image size cannot be more than 1mb!"
            })
        }
    }

    handleEditAdministrator = async () => {
        this.setState({
            error: ""
        })
        if (this.state.editschooladminshowImage === "" ||
            this.state.editschooladminname === "" ||
            this.state.editschooladminemail === "" ||
            this.state.editschooladminaddress === "" ||
            this.state.editschooladmincontact === "" ||
            this.state.editschooladmindob === "" ||
            this.state.editschooladminqualification === "" ||
            this.state.editschooladminexperience === "" ||
            this.state.editschooladmincity === "" ||
            this.state.editschooladminstate === "" ||
            this.state.editschooladminzip === "") {
            this.setState({
                error: "Please fill all the details!"
            })
        }
        else {
            this.setState({
                editingAdmin: true
            })
            const data = {
                name: this.state.editschooladminname,
                email: this.state.editschooladminemail,
                address: this.state.editschooladminaddress,
                contact: this.state.editschooladmincontact,
                dob: this.state.editschooladmindob,
                qualification: this.state.editschooladminqualification,
                experience: this.state.editschooladminexperience,
                city: this.state.editschooladmincity,
                state: this.state.editschooladminstate,
                zip: this.state.editschooladminzip,
                id: this.state.adminInfo._id,
                photo: this.state.adminInfo.photo
            };
            var form_data = new FormData();
            form_data.append('file', this.state.editschooladminphoto);
            console.log(this.state.editschooladminphoto)
            const check = this.state.adminimageEdited ? await this.props.editSchoolAdminWithImage(form_data, data) : await this.props.editSchoolAdmin(data);
            if (check) {
                this.setState({
                    editingAdmin: false
                })
                if (check.type === 'success') {
                    this.setState({
                        adminInfo: [],
                        editschooladminname: "",
                        editschooladminemail: "",
                        editschooladminphoto: [],
                        editschooladminaddress: "",
                        editschooladmindob: "",
                        editschooladmincontact: '',
                        editschooladminqualification: "",
                        editschooladminexperience: 0,
                        editschooladmincity: "",
                        editschooladminstate: "",
                        editschooladminzip: "",
                        editschooladminshowImage: '',
                        showEditAdmin: false,
                        showView: false,
                        adminimageEdited: false
                    })
                    toaster.notify("School Administrator Information Updated", {
                        duration: 3000
                    })
                }
            }
        }
    }

    handleRemoveSchoolAdmin = async () => {
        this.setState({
            removingAdmin: true
        })
        const data = {
            schoolAdminId: this.state.adminInfo._id,
            schoolId: this.state.item._id,
            photoId: this.state.adminInfo.photo.id
        }
        const check = await this.props.removeSchoolAdmin(data)
        if (check) {
            this.setState({
                removingAdmin: false
            })
            if (check.type === 'success') {
                this.setState({
                    item: [],
                    adminInfo: [],
                    showView: false,
                })
                toaster.notify("School Administrator Removed", {
                    duration: 3000
                })
            }
        }
    }

    handleRemoveDistrictOfficer = async (e) => {
        console.log(e);
        this.setState({
            removingOfficer: true
        })
        const data = {
            officerId: e,
            schoolId: this.state.item._id
        }
        const check = await this.props.removeDistrictOfficer(data)
        if (check) {
            this.setState({
                removingOfficer: false
            })
            if (check.type === 'success') {
                this.setState({
                    item: [],
                    showView: false
                })
                toaster.notify("District Officer Removed", {
                    duration: 3000
                })
            }
        }
    }

    render() {
        const schools = this.props.schools;
        console.log(schools);
        const data = schools;
        const columns = [
            {
                Header: "School Name",
                accessor: "name",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Branch",
                accessor: "branch",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "District Officer",
                accessor: 'districtOfficers[0].name',
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Administrator",
                accessor: "administrators[0].name",
                filterable: true,
                sortable: false,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Total Teachers",
                accessor: "teachers.length",
                filterable: true,
                sortable: false,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Total Students",
                accessor: "students.length",
                filterable: true,
                sortable: false,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Registration Date",
                Cell: (props) => {
                    console.log(props);
                    return (
                        <Moment format='MMMM Do YYYY, h:mm:ss a'>
                            {props.original.doa}
                        </Moment>
                    );
                },
                filterable: false,
                sortable: false,
                style: {
                    display: "flex",
                    alignItems: "center",
                },

            },
            {
                Header: "Status",
                sortable: false,
                Cell: (props) => {
                    console.log(props);
                    return (
                        <>
                            {
                                props.original.status
                                    ?
                                    <div
                                        style={{
                                            padding: "5px 15px",
                                            backgroundColor: "#61CE70",
                                            borderRadius: "20px",
                                        }}
                                    >
                                        <p
                                            style={{
                                                fontSize: "12px",
                                                fontWeight: "500",
                                                textShadow: "1.5px 1.5px rgba(0, 0, 0, 0.1)",
                                            }}
                                        >
                                            Active
                                        </p>
                                    </div>
                                    :
                                    <div
                                        style={{
                                            padding: "5px 15px",
                                            backgroundColor: "#a5a5a5",
                                            borderRadius: "20px",
                                        }}
                                    >
                                        <p
                                            style={{
                                                fontSize: "12px",
                                                fontWeight: "500",
                                                textShadow: "1.5px 1.5px rgba(0, 0, 0, 0.1)",
                                            }}
                                        >
                                            Inactive
                                        </p>
                                    </div>
                            }
                        </>
                    );
                },
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                },
            },
            {
                Header: "Actions",
                Cell: (props) => {
                    return (
                        <div
                            style={{
                                width: "100%",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            <Button onClick={() => {
                                this.setState({
                                    showView: true,
                                    item: props.original,
                                    error: "",
                                    showAddAdmin: false,
                                    showEditAdmin: false,
                                    showAddOfficer: false
                                })
                            }} variant={"outline-dark"} className="action-buttons">Info</Button>
                            <Button onClick={() => {
                                this.setState({
                                    showEdit: true,
                                    item: props.original,
                                    editname: props.original.name,
                                    editbranch: props.original.branch,
                                    editaddress: props.original.address,
                                    editemail: props.original.email,
                                    editcontact: props.original.contact,
                                    editfax: props.original.fax,
                                    editwebsite: props.original.website,
                                    editshowImage: "/api/file/show/" + props.original.logo.filename,
                                    error: "",
                                    imageEdited: false
                                })
                            }} variant={"outline-info"} className="action-buttons">Edit</Button>
                            <Button onClick={() => {
                                this.setState({
                                    showDelete: true,
                                    item: props.original,
                                    error: ""
                                })
                            }} variant={"outline-danger"} className="action-buttons">Delete</Button>
                        </div>
                    );
                },
                sortable: false,
                filterable: false,
                minWidth: 150,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",

                },
            },
        ];

        return (
            <div className="schools-container">
                <div className="schools-header-section">
                    <div className="schools-stats">
                        <h1>IIGP Schools</h1>
                        <div className="officer-stats">
                            <p>TOTAL SCHOOLS REGISTERED: <span>{schools.length}</span></p>
                        </div>
                    </div>
                    <div className="schools-add">
                        <div onClick={() => this.setState({
                            showAdd: true,
                            error: ""
                        })
                        }
                            className="add-officer-button">
                            <img src={addImg} alt="add-btn" />
                            <p>Add New School</p>
                        </div>
                    </div>
                </div>
                <div className="schools-body">
                    <ReactTable
                        data={data}
                        columns={columns}
                        defaultPageSize={10}
                        showPaginationTop
                        showPaginationBottom={false}
                        className="-striped -highlight"
                    />
                </div>
                <Modal
                    show={this.state.showAdd}
                    onHide={() => this.setState({
                        showAdd: false,
                        error: ""
                    })}
                    // dialogClassName="modal-70w"
                    backdrop="static"
                    keyboard={false}
                    centered
                    size="lg"
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add School Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <div className="image-container">
                                        <p>School Logo</p>
                                        <div className="image-picker">
                                            <input id="image-picker" onChange={e => this.handleLogo(e)} type="file" accept="image/*" />
                                            {
                                                this.state.showImage === ""
                                                    ?
                                                    <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                    :
                                                    <img htmlFor="#image-picker" src={this.state.showImage} />
                                            }
                                        </div>
                                    </div>

                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control name="name" type="text" placeholder="Enter school name" value={this.state.name} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Branch</Form.Label>
                                    <Form.Control name="branch" type="text" placeholder="Enter school branch name" value={this.state.branch} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control name="address" type="text" placeholder="Enter school address" value={this.state.address} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="email" type="text" placeholder="Enter school email" value={this.state.email} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Contact No.</Form.Label>
                                    <Form.Control name="contact" type="text" placeholder="Enter school contact no." value={this.state.contact} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Fax</Form.Label>
                                    <Form.Control name="fax" type="text" placeholder="Enter school fax" value={this.state.fax} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Website</Form.Label>
                                    <Form.Control name="website" type="text" placeholder="Enter school website" value={this.state.website} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.adding
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleAddSchool} variant="success"><FaCheck />Add</Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showDelete}
                    onHide={() => this.setState({
                        showDelete: false,
                        error: ""
                    })}
                    dialogClassName="modal-70w"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>School Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <Form.Group>
                                <Form.Label>School Name</Form.Label>
                                <Form.Control type="text" placeholder="Enter school name" value={this.state.item.name} disabled />
                            </Form.Group>
                            <Form.Group>
                                <Form.Label>School Branch</Form.Label>
                                <Form.Control type="text" placeholder="Enter school branch name" value={this.state.item.branch} disabled />
                            </Form.Group>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.deleting
                                ?
                                <Spinner animation="border" role="status" variant="danger">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleDeleteSchool} variant="danger">Delete<FaRegTrashAlt /></Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showEdit}
                    onHide={() => this.setState({
                        showEdit: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Edit School Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <div className="image-container">
                                        <p>School Logo</p>
                                        <div className="image-picker">
                                            <input id="image-picker" onChange={e => this.handleEditLogo(e)} type="file" accept="image/*" />
                                            {
                                                this.state.editshowImage === ""
                                                    ?
                                                    <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                    :
                                                    <img htmlFor="#image-picker" src={this.state.editshowImage} />
                                            }
                                        </div>
                                    </div>

                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Name</Form.Label>
                                    <Form.Control name="editname" type="text" placeholder="Enter school name" value={this.state.editname} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Branch</Form.Label>
                                    <Form.Control name="editbranch" type="text" placeholder="Enter school branch name" value={this.state.editbranch} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control name="editaddress" type="text" placeholder="Enter school address" value={this.state.editaddress} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Email</Form.Label>
                                    <Form.Control name="editemail" type="text" placeholder="Enter school email" value={this.state.editemail} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Contact No.</Form.Label>
                                    <Form.Control name="editcontact" type="text" placeholder="Enter school contact no." value={this.state.editcontact} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                            <div className="container-group">
                                <Form.Group className="container-fluid">
                                    <Form.Label>Fax</Form.Label>
                                    <Form.Control name="editfax" type="text" placeholder="Enter school fax" value={this.state.editfax} onChange={this.handleChange} />
                                </Form.Group>

                                <Form.Group className="container-fluid">
                                    <Form.Label>Website</Form.Label>
                                    <Form.Control name="editwebsite" type="text" placeholder="Enter school website" value={this.state.editwebsite} onChange={this.handleChange} />
                                </Form.Group>
                            </div>
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                    <Modal.Footer>
                        {
                            this.state.editing
                                ?
                                <Spinner animation="border" role="status" variant="success">
                                    <span className="sr-only">Loading...</span>
                                </Spinner>
                                :
                                <Button className="submit-action" onClick={this.handleEditSchool} variant="success"><FaCheck />Save</Button>
                        }
                    </Modal.Footer>
                </Modal>
                <Modal
                    show={this.state.showView}
                    onHide={() => this.setState({
                        showView: false,
                        error: ""
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>School Informations </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <Form>
                            {
                                this.state.item.administrators && this.state.item.administrators.length > 0
                                    ?
                                    <Form.Group>
                                        <Form.Label>List of School Administrators Assigned</Form.Label>
                                        <div className="school-info-list">
                                            {
                                                this.state.removingAdmin === false
                                                    ?
                                                    <>
                                                        {this.state.item.administrators.map(admin => {
                                                            return (
                                                                <Button onClick={() => this.handleShowSchoolAdminInfo(admin.id)} className="submit-action" variant={"outline-info"}>{admin.name}<FaInfoCircle /></Button>
                                                            )
                                                        })
                                                        }
                                                    </>
                                                    :
                                                    <Spinner animation="border" role="status" variant="success">
                                                        <span className="sr-only">Loading...</span>
                                                    </Spinner>
                                            }
                                        </div>
                                    </Form.Group>
                                    :
                                    null
                            }
                            {
                                this.state.item.districtOfficers && this.state.item.districtOfficers.length > 0
                                    ?
                                    <Form.Group>
                                        <Form.Label>List of District Officers Assigned</Form.Label>
                                        <div className="school-info-list">
                                            {
                                                this.state.removingOfficer === false
                                                    ?
                                                    <>
                                                        {
                                                            this.state.item.districtOfficers.map(officer => {
                                                                return (
                                                                    <Button onClick={() => this.handleRemoveDistrictOfficer(officer.id)} className="submit-action" variant={"outline-danger"}>{officer.name}<FaRegTrashAlt /></Button>
                                                                )
                                                            })
                                                        }
                                                    </>
                                                    :
                                                    <Spinner animation="border" role="status" variant="success">
                                                        <span className="sr-only">Loading...</span>
                                                    </Spinner>
                                            }
                                        </div>
                                    </Form.Group>
                                    :
                                    null
                            }
                            <Form.Row>
                                <Form.Group as={Col} className="d-flex justify-content-md-center">
                                    <Button className="tab-action-button" onClick={() => this.setState({
                                        showAddAdmin: !this.state.showAddAdmin,
                                        showAddOfficer: false,
                                        adminInfo: [],
                                        editschooladminname: "",
                                        editschooladminemail: "",
                                        editschooladminphoto: [],
                                        editschooladminaddress: "",
                                        editschooladmindob: "",
                                        editschooladmincontact: '',
                                        editschooladminqualification: "",
                                        editschooladminexperience: 0,
                                        editschooladmincity: "",
                                        editschooladminstate: "",
                                        editschooladminzip: "",
                                        editschooladminshowImage: '',
                                        showEditAdmin: false
                                    })} variant={this.state.showAddAdmin ? "dark" : "outline-primary"}>Add School Administrator</Button>
                                </Form.Group>
                                <Form.Group as={Col} className="d-flex justify-content-md-center">
                                    <Button className="tab-action-button" onClick={() => this.setState({ showAddOfficer: !this.state.showAddOfficer, showAddAdmin: false })} variant={this.state.showAddOfficer ? "dark" : "outline-primary"}>Assign district Officer</Button>
                                </Form.Group>
                            </Form.Row>
                            {
                                this.state.showAddAdmin
                                    ?
                                    <>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <div className="image-container">
                                                    <p>Profile Image</p>
                                                    <div className="image-picker">
                                                        <input id="image-picker" onChange={e => this.handleAdminPhoto(e)} type="file" accept="image/*" />
                                                        {
                                                            this.state.schooladminshowImage === ""
                                                                ?
                                                                <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                                :
                                                                <img htmlFor="#image-picker" src={this.state.schooladminshowImage} />
                                                        }
                                                    </div>
                                                </div>

                                            </Form.Group>

                                            <Form.Group className="container-fluid">
                                                <Form.Label>Name</Form.Label>
                                                <Form.Control name="schooladminname" type="text" placeholder="Enter name" value={this.state.schooladminname} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Email</Form.Label>
                                                <Form.Control name="schooladminemail" type="text" placeholder="Enter email id" value={this.state.schooladminemail} onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group className="container-fluid">
                                                <Form.Label>Address</Form.Label>
                                                <Form.Control name="schooladminaddress" type="text" placeholder="Enter address" value={this.state.schooladminaddress} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Contact No.</Form.Label>
                                                <Form.Control name="schooladmincontact" type="text" placeholder="Enter contact no." value={this.state.schooladmincontact} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Date of birth</Form.Label>
                                                <Form.Control name="schooladmindob" type="date" placeholder="Enter date of birth" value={this.state.schooladmindob} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Qualification</Form.Label>
                                                <Form.Control name="schooladminqualification" type="text" placeholder="Enter qualification" value={this.state.schooladminqualification} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Experience</Form.Label>
                                                <Form.Control name="schooladminexperience" type="number" min={0} max={100} placeholder="Enter in years" value={this.state.schooladminexperience} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <Form.Label>City</Form.Label>
                                                <Form.Control name="schooladmincity" type="text" placeholder="Enter city" value={this.state.schooladmincity} onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group className="container-fluid">
                                                <Form.Label>State</Form.Label>
                                                <Form.Control name="schooladminstate" type="text" placeholder="Enter state" value={this.state.schooladminstate} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Zip Code</Form.Label>
                                                <Form.Control name="schooladminzip" type="text" placeholder="Enter zip code" value={this.state.schooladminzip} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="d-flex justify-content-md-end">
                                                {
                                                    this.state.addingAdmin
                                                        ?
                                                        <Spinner animation="border" role="status" variant="success">
                                                            <span className="sr-only">Loading...</span>
                                                        </Spinner>
                                                        :
                                                        <Button className="submit-action" onClick={this.handleAddAdministrator} variant="success"><FaCheck />Add</Button>
                                                }
                                            </Form.Group>
                                        </div>
                                    </>
                                    :
                                    null
                            }
                            {
                                this.state.showEditAdmin
                                    ?
                                    <>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <div className="image-container">
                                                    <p>Profile Image</p>
                                                    <div className="image-picker">
                                                        <input id="image-picker" onChange={e => this.handleEditAdminPhoto(e)} type="file" accept="image/*" />
                                                        {
                                                            this.state.editschooladminshowImage === ""
                                                                ?
                                                                <img htmlFor="#image-picker" src={imagepicker} className="image-icon" />
                                                                :
                                                                <img htmlFor="#image-picker" src={this.state.editschooladminshowImage} />
                                                        }
                                                    </div>
                                                </div>

                                            </Form.Group>

                                            <Form.Group className="container-fluid">
                                                <Form.Label>Name</Form.Label>
                                                <Form.Control name="editschooladminname" type="text" placeholder="Enter name" value={this.state.editschooladminname} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Email</Form.Label>
                                                <Form.Control name="editschooladminemail" type="text" placeholder="Enter email id" value={this.state.editschooladminemail} onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group className="container-fluid">
                                                <Form.Label>Address</Form.Label>
                                                <Form.Control name="editschooladminaddress" type="text" placeholder="Enter address" value={this.state.editschooladminaddress} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Contact No.</Form.Label>
                                                <Form.Control name="editschooladmincontact" type="text" placeholder="Enter contact no." value={this.state.editschooladmincontact} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Date of birth</Form.Label>
                                                <Form.Control name="editschooladmindob" type="date" placeholder="Enter date of birth" value={this.state.editschooladmindob} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Qualification</Form.Label>
                                                <Form.Control name="editschooladminqualification" type="text" placeholder="Enter qualification" value={this.state.editschooladminqualification} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Experience</Form.Label>
                                                <Form.Control name="editschooladminexperience" type="number" min={0} max={100} placeholder="Enter in years" value={this.state.editschooladminexperience} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="container-fluid">
                                                <Form.Label>City</Form.Label>
                                                <Form.Control name="editschooladmincity" type="text" placeholder="Enter city" value={this.state.editschooladmincity} onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group className="container-fluid">
                                                <Form.Label>State</Form.Label>
                                                <Form.Control name="editschooladminstate" type="text" placeholder="Enter state" value={this.state.editschooladminstate} onChange={this.handleChange} />
                                            </Form.Group>
                                            <Form.Group className="container-fluid">
                                                <Form.Label>Zip Code</Form.Label>
                                                <Form.Control name="editschooladminzip" type="text" placeholder="Enter zip code" value={this.state.editschooladminzip} onChange={this.handleChange} />
                                            </Form.Group>
                                        </div>
                                        <div className="container-group">
                                            <Form.Group className="d-flex justify-content-md-end">
                                                {
                                                    this.state.editingAdmin
                                                        ?
                                                        <Spinner animation="border" role="status" variant="success">
                                                            <span className="sr-only">Loading...</span>
                                                        </Spinner>
                                                        :
                                                        <>
                                                            <Button className="submit-action" onClick={this.handleEditAdministrator} variant="success"><FaCheck />Save</Button>
                                                            <Button className="submit-action" onClick={this.handleRemoveSchoolAdmin} variant="danger"><FaRegTrashAlt />Remove</Button>
                                                        </>
                                                }
                                            </Form.Group>
                                        </div>
                                    </>
                                    :
                                    null
                            }
                            {
                                this.state.showAddOfficer
                                    ?
                                    <>
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label>Select a District Officer</Form.Label>
                                            <Form.Control as="select" value={this.state.officer} onChange={e => this.setState({ officer: e.target.value })}>
                                                <option value="">Please select an option</option>
                                                {
                                                    this.props.officer.map(off => {
                                                        if (this.state.item.districtOfficers.filter(x => x.id === off._id).length === 0) {
                                                            return (
                                                                <option value={off._id}>{off.firstName} {off.lastName}</option>
                                                            )
                                                        }
                                                    })
                                                }
                                            </Form.Control>
                                        </Form.Group>
                                        <Form.Group className="d-flex justify-content-md-end">
                                            {
                                                this.state.addingOfficer
                                                    ?
                                                    <Spinner animation="border" role="status" variant="success">
                                                        <span className="sr-only">Loading...</span>
                                                    </Spinner>
                                                    :
                                                    <Button className="submit-action" onClick={this.handleAssignDistrictOfficer} variant="success"><FaCheck />Assign</Button>
                                            }
                                        </Form.Group>
                                    </>
                                    :
                                    null
                            }
                        </Form>
                        {this.props.schoolError ? <p className="danger-text">{this.props.schoolError}</p> : null}
                        {this.props.schoolSucc ? <p className="success-text">{this.props.schoolSucc}</p> : null}
                        {this.state.error ? <p className="danger-text">{this.state.error}</p> : null}
                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}
function mapStateToProps(state) {
    return {
        schools: state.superAdmin.schools,
        schoolError: state.superAdmin.schoolError,
        schoolSucc: state.superAdmin.schoolSucc,
        officer: state.superAdmin.officerData
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetchSchools: () => dispatch(fetchSchools()),
        addSchool: (form_data, data) => dispatch(addSchool(form_data, data)),
        deleteSchool: (data) => dispatch(deleteSchool(data)),
        editSchool: (data) => dispatch(editSchool(data)),
        editSchoolWithImage: (form_data, data) => dispatch(editSchoolWithImage(form_data, data)),
        addSchoolAdmin: (form_data, data) => dispatch(addSchoolAdmin(form_data, data)),
        addSchoolDistrictOfficer: (data) => dispatch(addSchoolDistrictOfficer(data)),
        removeSchoolAdmin: (data) => dispatch(removeSchoolAdmin(data)),
        removeDistrictOfficer: (data) => dispatch(removeDistrictOfficer(data)),
        showSchoolAdminInfo: (data) => dispatch(showSchoolAdminInfo(data)),
        editSchoolAdminWithImage: (form_data, data) => dispatch(editSchoolAdminWithImage(form_data, data)),
        editSchoolAdmin: (data) => dispatch(editSchoolAdmin(data)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Schools);