import React from "react";
import "./nav.css";
import { Link } from "react-router-dom";
import logo from "../../assets/iigp.png";

class IIGPNav extends React.Component {
  render() {
    return (
      <div className="iigp-nav-container">
        <div className="iigp-logo-wrapper">
          <img src={logo} alt="logo" />
        </div>
        <div className="iigp-menu-wrapper">
          <div className="link-box">
            <p>Home</p>
          </div>
          <Link to={"/iigp/learning/"} className="link-box">
            <p>Learning</p>
          </Link>
          <div className="link-box">
            <p>Analytics</p>
          </div>
          <Link to="/login" className="link-box">
            <button>Login</button>
          </Link>
        </div>
      </div>
    );
  }
}

export default IIGPNav;
