import React from "react";

import { Modal, Button, Form, Spinner, Col } from "react-bootstrap";
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoMdClose } from "react-icons/io";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import ReactTable from "react-table-6";
import { addLabWorkRemark } from "../../../redux/actions/teacherDashboard/teacherActions";
import { connect } from "react-redux";
import moment from "moment";
class StudentTable extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			showNote: false,
			note: "",
			observation: "",
			savingNote: false,
			date: new Date(),
			selectedStudent: {},
			labwork: this.props.labwork,
		};
	}

	handleChange = (e) => {
		this.setState({
			[e.target.name]: e.target.value,
		});
	};

	handleNote = (e, f) => {
		this.setState(
			{
				note: e.remark ? e.remark : "",
				observation: e.observation ? e.observation : "",
				selectedStudent: f,
				showNote: true,
			},
			() => {
				console.log(this.state.note, this.state.selectedStudent);
			}
		);
	};

	handleRemark = async () => {
		if (this.state.note !== "") {
			this.setState({
				saving: true,
				showNote: false,
			});
			var data = {
				id: this.state.selectedStudent._id,
				remark: this.state.note,
				observation: this.state.observation,
				labworkId: this.props.labwork._id,
				teacherId: this.props.teacher._id,
			};
			const check = await this.props.addLabWorkRemark(data);
			console.log(check);
			if (check) {
				this.setState({
					saving: false,
				});
				if (check.type === "success") {
					toaster.notify("Remark updated", {
						duration: 3000,
					});
					this.setState(
						{
							note: "",
						},
						() => {
							this.props.handleRefresh();
						}
					);
				} else {
					toaster.notify(check.error, {
						duration: 3000,
					});
				}
			}
		} else {
			toaster.notify("Please add a remark", {
				duration: 3000,
			});
		}
	};

	render() {
		console.log(this.props.teacher);
		const school = this.props.teacher.schoolInfo;
		var students = [];
		this.props.teacher.students.map((stud) => {
			if (stud.class === this.props.sub.class) {
				students.push(stud);
			}
		});
		const data = students;
		return (
			<div className='students-container'>
				<div className='student-list'>
					{data.map((d) => {
						var remark = "";
						var stu = {};
						console.log(this.state.labwork);
						this.state.labwork.attend.map((stud) => {
							if (stud.id === d._id) {
								remark = stud.remark;
								stu = stud;
							}
						});
						return (
							<div className='student-box'>
								<div className='student-box-body'>
									<img src={"/api/file/show/" + d.photo.filename} alt='profile-pic' />
									<h1>{d.name}</h1>
									<p>{remark === "" ? "No remark given" : remark}</p>
									<button onClick={() => this.handleNote(stu, d)} className='addRemark'>
										<i class='fas fa-sticky-note'></i>
									</button>
								</div>
							</div>
						);
					})}
				</div>
				<Modal
					show={this.state.showNote}
					onHide={() =>
						this.setState({
							showNote: false,
						})
					}
					backdrop='static'
					keyboard={false}
					centered>
					<Modal.Header closeButton>
						<Modal.Title>Add Information</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<p>Observation</p>
						<textarea name='observation' onChange={this.handleChange} value={this.state.observation} className='note-text-area' />
						<p>Remark</p>
						<textarea name='note' onChange={this.handleChange} value={this.state.note} className='note-text-area' />
					</Modal.Body>
					<Modal.Footer>
						<Button onClick={this.handleRemark} className='submit-action' variant='success'>
							<FaCheck />
							Save
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addLabWorkRemark: (data) => dispatch(addLabWorkRemark(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentTable);
