import React from 'react';

import './home.css';

import { connect } from 'react-redux';

class Home extends React.Component {

    render() {
        return (
            <div>
                Home
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth,
    }
}

export default connect(mapStateToProps)(Home);