import { FETCH_TEACHER_PROFILE } from '../actions/teacherDashboard/teacherTypes';

const initialState = {
    profileData: null,
}

const teacherReducer = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_TEACHER_PROFILE:
            return {
                profileData: action.payload,
            }
        default:
            return state
    }
}

export default teacherReducer;