import React from 'react';

import './floralLoading.css';

import loadAnimation from '../assets/floral_loading.json';

import Lottie from "lottie-react-web";

class FloralLoading extends React.Component {
    render() {
        return(
            <div className = "floral-loading-container">
                 <Lottie
                    options={{ animationData: loadAnimation}}
                    width={500}
                    height={500}
                />
            </div>
        )
    }
}

export default FloralLoading;