import React from "react";

import "./homeworkList.css";

import { Modal, Button, Form } from "react-bootstrap";
import { AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine } from "react-icons/ri";

import { connect } from "react-redux";

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { addHomework, deleteHomework } from "../../../../redux/actions/teacherDashboard/teacherActions";
import penPaper from "../../assets/penPaper.svg";

import moment from "moment";

class HomeworkList extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			showModal: false,
			homeworkName: "",
			homeworkDuration: "",
			startDate: "",
			endDate: "",
			adding: false,
		};
	}

	handleChange = (e) => {
		const { name, value } = e.target;
		this.setState({
			[name]: value,
		});
	};

	handleSubmit = async () => {
		this.setState({
			adding: true,
		});

		const data = {
			schoolId: this.props.school,
			teacherId: this.props.teacher._id,
			className: this.props.class,
			sectionName: this.props.section,
			subjectName: this.props.subject,
			title: this.state.homeworkName,
			duration: this.state.homeworkDuration,
			startDate: this.state.startDate,
			endDate: this.state.endDate,
		};

		const check = await this.props.addHomework(data);

		if (check) {
			this.setState({
				adding: false,
			});
			if (check.type === "success") {
				toaster.notify("Homework Added", {
					duration: 3000,
				});
				this.setState({
					chapterName: "",
					adding: false,
					showModal: false,
				});
			} else {
				toaster.notify("Homework Could Not Be Added", {
					duration: 3000,
				});
				this.setState({
					chapterName: "",
					adding: false,
					showModal: false,
				});
			}
		}
	};

	handleDeleteHomework = async (homeworkId) => {
		const data = {
			homeworkId: homeworkId,
			teacherId: this.props.teacher._id,
		};

		const check = await this.props.deleteHomework(data);

		if (check) {
			if (check.type === "success") {
				toaster.notify("Homework Deleted", {
					duration: 3000,
				});
			} else {
				toaster.notify("Homework Could Not Be Deleted", {
					duration: 3000,
				});
			}
		}
	};

	render() {
		console.log(this.props.teacher);
		return (
			<div className='topicList-container'>
				<div className='topicList-header'>
					<Button variant='primary' onClick={() => this.setState({ showModal: true })}>
						<AiOutlinePlus />
						Create Homework
					</Button>
				</div>

				{this.props.teacher.homeworks.map((homework) => {
					if (
						homework.subjectName === this.props.subject &&
						homework.className === this.props.class &&
						homework.sectionName === this.props.section
					) {
						return (
							<div className='topicList-box'>
								<img src={penPaper} alt='' />
								<div className='topic-name' onClick={() => this.props.activateHomeworkMaker(true, homework)}>
									<h1>{homework.title}</h1>
									<p>
										{homework.subjectName}, {homework.className} {homework.sectionName}
									</p>
								</div>
								<div className='topic-date'>
									<p>Date : {moment(new Date(homework.startDate)).format("LLLL")}</p>
									<span onClick={() => this.handleDeleteHomework(homework._id)}>
										<RiDeleteBinLine />
									</span>
								</div>
							</div>
						);
					}
				})}

				<Modal show={this.state.showModal} onHide={() => this.setState({ showModal: false })} backdrop='static' keyboard={false}>
					<Modal.Header closeButton>
						<Modal.Title>Create Homework</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Homework Name</Form.Label>
							<Form.Control type='text' name='homeworkName' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Homework Duration (In Minutes)</Form.Label>
							<Form.Control type='number' min={0} name='homeworkDuration' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Homework Start Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='startDate' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Homework End Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='endDate' onChange={this.handleChange} />
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button variant='primary' onClick={this.handleSubmit}>
							Create
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addHomework: (data) => dispatch(addHomework(data)),
		deleteHomework: (data) => dispatch(deleteHomework(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(HomeworkList);
