import React from "react";
import "./studentCard.css";

import { AiOutlineArrowsAlt } from "react-icons/ai";
import { IoIosStats, IoIosInformationCircleOutline } from "react-icons/io";
import { BsCircleFill, BsCircleHalf, BsInfoCircle } from "react-icons/bs";

import plus from "../assets/plus.svg";
import minus from "../assets/minus.svg";

import ReactTooltip from "react-tooltip";

import { Modal, Card } from "react-bootstrap";

import trophy from "../assets/trophy.svg";

class StudentCard extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			openAttendance: false,
			openBehaviour: false,

			date: new Date(),
			details: {},
		};
	}

	render() {
		return (
			<div className='studentCard-container'>
				<div className='stuCard-head'>
					{this.props.isRecognition ? null : (
						<AiOutlineArrowsAlt
							onClick={() => this.props.expandView(this.props.student)}
							style={{ cursor: "pointer", color: "#fff", fontSize: "30px" }}
						/>
					)}
				</div>
				<div className='stuCard-body'>
					<div className='stuCard-dp'>
						<img src={"/api/file/show/" + this.props.student.photo.filename} alt='profile-pic' />
					</div>
					<div className='stuCard-name'>
						<p>{this.props.student.name}</p>
					</div>
					{this.props.isRecognition ? null : (
						<div className='stuCard-footer'>
							<button onClick={() => this.props.showStats(this.props.student)}>
								<IoIosStats />
								Analytics
							</button>
							<button onClick={() => this.props.showDetails(this.props.student)}>
								<BsInfoCircle />
								Student Information
							</button>
						</div>
					)}
					<div className='plus-minus-section'>
						<div className='plus-min-dots'>
							<div>
								<img src={plus} alt='plus' />
								<span>{this.props.student.positivePoints}</span>
							</div>
							{this.props.isRecognition && this.props.topPerformer ? <img src={trophy} alt='trophy' /> : null}
							<div>
								<img src={minus} alt='plus' />
								<span>{this.props.student.negativePoints}</span>
							</div>
						</div>
						<div className='coloured-dots'>
							<p data-tip data-for='Creativity' style={{ color: "	#FF0000" }}>
								<BsCircleHalf />
							</p>
							<ReactTooltip id='Creativity' place='top' type='dark' effect='solid'>
								<span>Creativity</span>
							</ReactTooltip>

							<p data-tip data-for='Entrepreneurship' style={{ color: "#FF7F00" }}>
								<BsCircleHalf />
							</p>
							<ReactTooltip id='Entrepreneurship' place='top' type='dark' effect='solid'>
								<span>Entrepreneurship</span>
							</ReactTooltip>

							<p data-tip data-for='Problem' style={{ color: "rgba(244, 208, 63, 1)" }}>
								<BsCircleHalf />
							</p>
							<ReactTooltip id='Problem' place='top' type='dark' effect='solid'>
								<span>Problem Solving</span>
							</ReactTooltip>

							<p data-tip data-for='Assignments' style={{ color: "#2ECC71" }}>
								<BsCircleFill />
							</p>
							<ReactTooltip id='Assignments' place='top' type='dark' effect='solid'>
								<span>Assignments</span>
							</ReactTooltip>

							<p data-tip data-for='Practices' style={{ color: "#3498DB" }}>
								<BsCircleFill />
							</p>
							<ReactTooltip id='Practices' place='top' type='dark' effect='solid'>
								<span>Practices</span>
							</ReactTooltip>

							<p data-tip data-for='Attendance' style={{ color: "#5B2C6F" }} onClick={() => this.setState({ openAttendance: true })}>
								<BsCircleFill />
							</p>
							<ReactTooltip id='Attendance' place='top' type='dark' effect='solid'>
								<span>Attendance</span>
							</ReactTooltip>

							<p data-tip data-for='Behaviour' style={{ color: "#AF7AC5" }} onClick={() => this.setState({ openBehaviour: true })}>
								<BsCircleFill />
							</p>
							<ReactTooltip id='Behaviour' place='top' type='dark' effect='solid'>
								<span>Behaviour</span>
							</ReactTooltip>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default StudentCard;
