import axios from "axios";
import {
    ADD_CLASS_ERROR,
    ADD_CLASS_SUCC,
    ADD_TEACHER_ERROR,
    ADD_TEACHER_SUCC,
    EDIT_TEACHER_ERROR,
    EDIT_TEACHER_SUCC,
    FETCH_SCHOOLADMIN,
    FETCH_SCHOOL,
    EDIT_SCHOOL_ERROR,
    EDIT_SCHOOL_SUCC,
    EDIT_ADMINPASSWORD_ERROR,
    EDIT_ADMINPASSWORD_SUCC,
    ASSIGN_TEACHER_ERROR,
    ASSIGN_TEACHER_SUCC,
    REMOVE_TEACHER_ERROR,
    REMOVE_TEACHER_SUCC,
    ADD_STUDENT_ERROR,
    ADD_STUDENT_SUCC,
    EDIT_STUDENT_ERROR,
    EDIT_STUDENT_SUCC,
    REMOVE_STUDENT_ERROR,
    REMOVE_STUDENT_SUCC
} from "./schoolAdminTypes";

export const fetchSchoolAdmin = (data) => async (dispatch) => {
    const res = await axios.post("/api/schoolAdmin/info", data);
    dispatch({ type: FETCH_SCHOOLADMIN, payload: res.data });
    return res.data;
}

export const fetchSchool = (data) => async (dispatch) => {
    const res = await axios.post("/api/schoolAdmin/schoolInfo", data);
    console.log(res.data);
    dispatch({ type: FETCH_SCHOOL, payload: res.data });
}

export const addClass = (data) => async (dispatch) => {
    const res = await axios.post("/api/schoolAdmin/addClass", data);
    if (res.data.type === "error") {
        dispatch({ type: ADD_CLASS_ERROR, payload: res.data.error });
        return res.data
    } else {
        dispatch({ type: ADD_CLASS_SUCC, payload: res.data.success });
        const resp = await axios.post("/api/schoolAdmin/schoolInfo", data);
        dispatch({ type: FETCH_SCHOOL, payload: resp.data });
        return res.data
    }
};


export const addTeacher = (formData, data) => async (dispatch) => {
    const fileData = await axios.post("/api/file/upload", formData);
    data.photo = {
        filename: fileData.data.filename,
        id: fileData.data.id
    };
    console.log(data);
    const res = await axios.post("/api/schoolAdmin/addTeacher", data);
    if (res.data.type === "error") {
        dispatch({ type: ADD_TEACHER_ERROR, payload: res.data.error });
        return res.data
    } else {
        dispatch({ type: ADD_TEACHER_SUCC, payload: res.data.success });
        const resp = await axios.post("/api/schoolAdmin/schoolInfo", data);
        dispatch({ type: FETCH_SCHOOL, payload: resp.data });
        return res.data
    }
};

