import React from "react";

import "./homework.css";

import folderImg from "../assets/folderIcon.svg";

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";

import iigpLogo from "../assets/iigpqm.png";
// import QuizMaker from './quizMaker/quizMaker';

import { connect } from "react-redux";
import HomeworkList from "./homeworkList/homeworkList";
import HomeworkMaker from "./homeworkMaker/homeworkMaker";

class Homework extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			currentClass: "",
			currentSection: "",
			currentSubject: "",
			homeworkDetails: {},
			ShowHomeworkMaker: false,
			showHomeworkList: false,
		};
	}

	handleBox = (e) => {
		this.setState({
			currentClass: e.class,
			currentSection: e.section,
			currentSubject: e.subject,
			showHomeworkList: true,
		});
	};

	render() {
		console.log(this.props.teacher);
		var teacher = this.props.teacher;
		return (
			<div className='quiz-container'>
				<div className='quiz-header'>
					{!this.state.showHomeworkList ? (
						<div>
							<h1>My Homework Library</h1>
						</div>
					) : (
							<>
								{!this.state.ShowHomeworkMaker ? (
									<div>
										<p onClick={() => this.setState({ showHomeworkList: false })}>
											<FaArrowLeft />
										</p>
										<h1>My Homework Library</h1>
										<p>
											<FaAngleRight />
										</p>
										<h1>Homeworks</h1>
									</div>
								) : (
										<div>
											<p onClick={() => this.setState({ ShowHomeworkMaker: false })}>
												<FaArrowLeft />
											</p>
											<img src={iigpLogo} alt='iigp-logo' />
										</div>
									)}
							</>
						)}
				</div>

				<div className='quiz-body'>
					{!this.state.showHomeworkList ? (
						<div className='chooseClass-container'>
							{teacher &&
								teacher.subjects.map((sub) => {
									return (
										<div className='classes-cardFolder-container' onClick={() => this.handleBox(sub)}>
											<div className='classes-cardFolder-img'>
												<img src={folderImg} alt='folder-icon' />
											</div>
											<div className='classes-cardFolder-name'>
												<p>
													{sub.class} {sub.section} | {sub.subject}
												</p>
											</div>
										</div>
									);
								})}
						</div>
					) : (
							<>
								{!this.state.ShowHomeworkMaker ? (
									<HomeworkList
										activateHomeworkMaker={(value, homeworkDetails) =>
											this.setState({ ShowHomeworkMaker: value, homeworkDetails: homeworkDetails })
										}
										class={this.state.currentClass}
										section={this.state.currentSection}
										subject={this.state.currentSubject}
										school={teacher.school_id}
									/>
								) : (
										<HomeworkMaker homeworkDetails={this.state.homeworkDetails} />
									)}
							</>
						)}
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

export default connect(mapStateToProps)(Homework);
