import React from "react";
import "./schoolAdmin.style.css";

import list from "../assets/list.svg";
import menu from "../assets/more.svg";
import Home from "./Home/home";
import SchoolInfo from "./SchoolInfo/schoolInfo";
import Profile from "./Profile/profile";
import Teachers from "./Teachers/teachers";
import Students from "./Students/students";
import Classes from "./Classes/classes";
import Gallery from "./Gallery/gallery";
import { connect } from "react-redux";
import { fetchSchoolAdmin, fetchSchool } from "../../redux/actions/schoolAdminDashboard/schoolAdminActions";

class SchoolAdminDashboard extends React.Component {
	constructor() {
		super();
		this.state = {
			sidebar: "sidebar-full",
			content: "content-mini",
			tab: 1,
			currentTabName: "Home",
			showProfileMenu: false,
			showSubMenu1: false,
			currentUser: [],
			loading: true,
		};
	}

	async componentDidMount() {
		const data = {
			id: this.props.auth.currentUser.ref_id,
		};
		const schoolAdmin = await this.props.fetchSchoolAdmin(data);
		if (schoolAdmin._id) {
			const data2 = {
				schoolId: schoolAdmin.school_id,
			};
			this.props.fetchSchool(data2);
		}
		// setInterval(async () => {
		//     const data = {
		//         id: this.props.auth.currentUser.ref_id
		//     }
		//     const schoolAdmin = await this.props.fetchSchoolAdmin(data);
		//     if (schoolAdmin._id) {
		//         const data2 = {
		//             schoolId: schoolAdmin.school_id
		//         }
		//         this.props.fetchSchool(data2);
		//     }
		// }, 5000);
	}

	handleProfile = (data) => {
		this.setState({
			userData: data,
			loading: false,
		});
	};

	handleMenuToggle = () => {
		if (this.state.sidebar === "sidebar-full") {
			this.setState({
				sidebar: "sidebar-mini",
				content: "content-full",
			});
		} else {
			this.setState({
				sidebar: "sidebar-full",
				content: "content-mini",
			});
		}
	};

	render() {
		var schoolAdmin = this.props.schoolAdmin ? this.props.schoolAdmin : null;
		return (
			<div className='layout-container'>
				<div className='header'>
					<div className='header-menu'>
						<img
							className='header-menu-icon'
							onClick={this.handleMenuToggle}
							src={this.state.sidebar === "sidebar-mini" ? list : menu}
							alt='image'
						/>
						<h1>SCHOOL ADMIN | {this.state.currentTabName}</h1>
					</div>
					<div className='header-buttons'>
						<div className='profile'>
							<img
								src={schoolAdmin ? "/api/file/show/" + schoolAdmin.photo.filename : null}
								className='profile-logo'
								onClick={() =>
									this.setState({
										showProfileMenu: !this.state.showProfileMenu,
									})
								}
							/>
							<p
								onClick={() =>
									this.setState({
										showProfileMenu: !this.state.showProfileMenu,
									})
								}
								className='profile-name'>
								{schoolAdmin ? schoolAdmin.name : null}
							</p>
							<i
								className='fas fa-caret-down'
								onClick={() =>
									this.setState({
										showProfileMenu: !this.state.showProfileMenu,
									})
								}></i>
							{this.state.showProfileMenu ? (
								<div className='profile-menu'>
									<p>Profile</p>
									<p>Settings</p>
									<a href='/api/logout'>Log Out</a>
								</div>
							) : null}
						</div>
					</div>
				</div>

				<div className='body'>
					<div className={this.state.sidebar}>
						<div className='sidebar-content'>
							<h1>MENU</h1>

							<div className='menu' onClick={() => this.setState({ tab: 1, currentTabName: "Home" })}>
								<div className='menu-item'>
									{this.state.tab === 1 ? <div className='active'></div> : null}
									<i class='fas fa-chart-pie'></i>
									{this.state.sidebar === "sidebar-full" ? <p>HOME</p> : null}
								</div>
							</div>

							<div className='menu' onClick={() => this.setState({ tab: 2, currentTabName: "School Info" })}>
								<div className='menu-item'>
									{this.state.tab === 2 ? <div className='active'></div> : null}
									<i class='fas fa-chart-pie'></i>
									{this.state.sidebar === "sidebar-full" ? <p>SCHOOL INFO</p> : null}
								</div>
							</div>

							<div className='menu' onClick={() => this.setState({ tab: 3, currentTabName: "Personal Info" })}>
								<div className='menu-item'>
									{this.state.tab === 3 ? <div className='active'></div> : null}
									<i class='fas fa-chart-pie'></i>
									{this.state.sidebar === "sidebar-full" ? <p>PERSONAL INFO</p> : null}
								</div>
							</div>

							<div className='menu' onClick={() => this.setState({ tab: 4, currentTabName: "Teachers List" })}>
								<div className='menu-item'>
									{this.state.tab === 4 ? <div className='active'></div> : null}
									<i class='fas fa-chart-pie'></i>
									{this.state.sidebar === "sidebar-full" ? <p>TEACHERS</p> : null}
								</div>
							</div>

							<div className='menu' onClick={() => this.setState({ tab: 5, currentTabName: "Students List" })}>
								<div className='menu-item'>
									{this.state.tab === 5 ? <div className='active'></div> : null}
									<i class='fas fa-chart-pie'></i>
									{this.state.sidebar === "sidebar-full" ? <p>STUDENTS</p> : null}
								</div>
							</div>

							<div className='menu' onClick={() => this.setState({ tab: 6, currentTabName: "Classes" })}>
								<div className='menu-item'>
									{this.state.tab === 6 ? <div className='active'></div> : null}
									<i class='fas fa-chart-pie'></i>
									{this.state.sidebar === "sidebar-full" ? <p>CLASSES</p> : null}
								</div>
							</div>

							{/* <div className="menu" onClick={() => this.setState({ tab: 7, currentTabName: "Gallery" })}>
                                <div className="menu-item">
                                    {this.state.tab === 7 ? <div className="active"></div> : null}
                                    <i class="fas fa-chart-pie"></i>
                                    {this.state.sidebar === "sidebar-full" ? (
                                        <p>SCHOOL GALLERY</p>
                                    ) : null}
                                </div>
                            </div> */}
						</div>
					</div>
					<div className={this.state.content}>
						{this.state.tab === 1 ? <Home /> : null}
						{this.state.tab === 2 ? <SchoolInfo /> : null}
						{this.state.tab === 3 ? <Profile /> : null}
						{this.state.tab === 4 ? <Teachers /> : null}
						{this.state.tab === 5 ? <Students /> : null}
						{this.state.tab === 6 ? <Classes /> : null}
						{/* {this.state.tab === 7 ? <Gallery /> : null} */}
					</div>
				</div>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		schoolAdmin: state.schoolAdmin.schoolAdmin,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		fetchSchoolAdmin: (data) => dispatch(fetchSchoolAdmin(data)),
		fetchSchool: (data) => dispatch(fetchSchool(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(SchoolAdminDashboard);
