import React from "react";

export default class Behaviour extends React.Component {
	constructor(props) {
		super(props);
		var behaviours = [];
		props.behaviours.map((behaviour) => {
			if (behaviour.teacherId === props.teacherId && behaviour.date === props.date) {
				behaviours = behaviour.status;
			}
		});
		console.log(behaviours);
		this.state = {
			behaviours: behaviours,
		};
	}

	add = (e, t) => {
		var behaviours = this.state.behaviours;
		var push;
		if (!behaviours.includes(e)) {
			behaviours.push(e);
			push = true;
		} else {
			behaviours = behaviours.filter((item) => {
				return item !== e;
			});
			push = false;
		}
		this.setState({
			behaviours: behaviours,
		});
		console.log(behaviours);
		this.props.addBehaviour(behaviours, this.props.data, t, push);
	};
	render() {
		// console.log(this.state.behaviours.includes("worked hard"), this.state.behaviours);
		console.log(this.props.behaviours, this.props.date);
		return (
			<div className='behaviour-chart'>
				<div className='behaviour-list'>
					<div
						className={this.state.behaviours.includes("worked hard") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("worked hard", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>worked hard</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("asked thoughtful questions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("asked thoughtful questions", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>asked thoughtful questions</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("contributed to class discussions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("contributed to class discussions", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>contributed to class discussions</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("contributed to group") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("contributed to group", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>contributed to group</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("following directions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("following directions", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>following directions</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("attentive during instructions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("attentive during instructions", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>attentive during instructions</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("assisted peer") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("assisted peer", "Positive")}>
						<i class='fas fa-plus-circle'></i>
						<p>assisted peer</p>
					</div>
				</div>
				<div className='behaviour-list'>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("off tasks") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("off tasks", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>off tasks</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("distracted others") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("distracted others", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>distracted others</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("improper phone using") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("improper phone using", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>improper phone using</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("talking during instructions") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("talking during instructions", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>talking during instructions</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("out of seat") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("out of seat", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>out of seat</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("disrespectful") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("disrespectful", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>disrespectful</p>
					</div>
					<div
						className='behaviour'
						className={this.state.behaviours.includes("unprepared for class") ? "behaviour active" : "behaviour"}
						onClick={() => this.add("unprepared for class", "Negative")}>
						<i class='fas fa-minus-circle'></i>
						<p>unprepared for class</p>
					</div>
				</div>
			</div>
		);
	}
}
