import React from "react";

import './dicussionGroup.css';
import { connect } from "react-redux";

import { Modal, Button, Form } from "react-bootstrap";

import logo from './assets/iigp.png';

import { AiOutlinePlus } from "react-icons/ai";

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";

import { addDiscussionGroup } from '../../../redux/actions/teacherDashboard/teacherActions';

import { RiDeleteBinLine } from 'react-icons/ri';
import { FaArrowLeft } from "react-icons/fa";

import moment from 'moment';

import DiscussionRoom from './discussionRoom/discussionRoom.js';

class DiscussionGroup extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            showChat: false,
            title: '',
            audience: '',
            grade: '',
            section: '',
            discussion: {}
        };
    }

    handleChange = (e) => {
        var { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleSubmit = async () => {
        var data = {
            ownerId: this.props.teacher._id,
            ownerRole: "teacher",
            schoolId: this.props.teacher.school_id,
            title: this.state.title,
            audience: this.state.audience,
            grade: this.state.grade,
            section: this.state.section,
        }

        var check = await this.props.addDiscussionGroup(data);
        if (check) {
            if (check.type === "success") {
                toaster.notify("Group created", {
                    duration: 3000,
                });
                this.setState({
                    title: "",
                    audience: '',
                    grade: '',
                    section: '',
                    showModal: false,
                });
            } else {
                toaster.notify("Group could not be created !", {
                    duration: 3000,
                });
                this.setState({
                    title: "",
                    audience: '',
                    grade: '',
                    section: '',
                    showModal: false,
                });
            }
        }
    }

    handleShowDiscussionRoom = (item) => {
        this.setState({
            discussion: item,
            showChat: true
        })
    }

    render() {

        console.log(this.props.teacher);

        var gradeList = [];
        this.props.teacher.subjects && this.props.teacher.subjects.map((sub) => {
            if (gradeList.includes(sub.class) === false) {
                gradeList.push(sub.class);
            }
        })

        var visitedGrade = [], visitedSection = [], filteredData = [];

        for (var i = 0; i < this.props.teacher.subjects.length; i++) {
            if (visitedGrade.includes(this.props.teacher.subjects[i].class) === false) {
                visitedGrade.push(this.props.teacher.subjects[i].class);
                visitedSection.push(this.props.teacher.subjects[i].section);

                var grade = {};
                grade.class = this.props.teacher.subjects[i].class;
                grade.sections = [];
                grade.sections.push(this.props.teacher.subjects[i].section)

                filteredData.push(grade);
            }
            else {
                // Old Class
                if (visitedSection.includes(this.props.teacher.subjects[i].section) === false) {
                    // new section
                    visitedSection.push(this.props.teacher.subjects[i].section);

                    filteredData.map((item) => {
                        if (item.class === this.props.teacher.subjects[i].class) {
                            item.sections.push(this.props.teacher.subjects[i].section)
                        }
                    })
                }
            }
        }

        return (
            <div className='students-container'>
                <div className="discussion-list-head">
                    <div>
                        {
                            this.state.showChat
                                ?
                                <p onClick={() => this.setState({ showChat: false })}><FaArrowLeft /></p>
                                :
                                null
                        }
                        <img src={logo} alt="logo" />
                    </div>
                    {
                        this.state.showChat
                            ?
                            null
                            :
                            <Button variant='primary' onClick={() => this.setState({ showModal: true })}>
                                <AiOutlinePlus />
                                Create New Group
                            </Button>
                    }
                </div>
                <div className="discussion-list-body">
                    {
                        this.state.showChat
                            ?
                            <DiscussionRoom discussion={this.state.discussion} />
                            :
                            <>
                                <div className="discussion-user-section">
                                    <div className="discussion-user-section-head">
                                        <p>Faculty</p>
                                    </div>
                                    <div className="discussion-user-section-body">
                                        {
                                            this.props.teacher.discussions && this.props.teacher.discussions.length > 0 && this.props.teacher.discussions.map((item) => {
                                                if (item.audience === 'faculty') {
                                                    return (
                                                        <div className="discussion-group-card" onClick={() => this.handleShowDiscussionRoom(item)}>
                                                            <h3>{item.title}</h3>
                                                            <p>{moment(item.date).fromNow()}</p>
                                                            <span>(Click to enter discussion room)</span>
                                                        </div>
                                                    )
                                                }
                                            })
                                        }
                                    </div>
                                </div>
                                <div className="discussion-user-section">
                                    <div className="discussion-user-section-head">
                                        <p>Students</p>
                                    </div>
                                    <div className="discussion-user-section-body">
                                        {
                                            this.props.teacher.discussions && this.props.teacher.discussions.length > 0 && this.props.teacher.discussions.map((item) => {
                                                if (item.audience === 'students') {
                                                    return (
                                                        <div className="discussion-group-card" onClick={() => this.handleShowDiscussionRoom(item)}>
                                                            <h3>{item.title}</h3>
                                                            <h5>{item.grade}, {item.section}</h5>
                                                            <p>{moment(item.date).fromNow()}</p>
                                                            <span>(Click to enter discussion room)</span>
                                                        </div>
                                                    )
                                                }
                                            })
                                        }
                                    </div>
                                </div>
                                <div className="discussion-user-section">
                                    <div className="discussion-user-section-head">
                                        <p>Parents</p>
                                    </div>
                                    <div className="discussion-user-section-body">
                                        {
                                            this.props.teacher.discussions && this.props.teacher.discussions.length > 0 && this.props.teacher.discussions.map((item) => {
                                                if (item.audience === 'parents') {
                                                    return (
                                                        <div className="discussion-group-card" onClick={() => this.handleShowDiscussionRoom(item)}>
                                                            <h3>{item.title}</h3>
                                                            <p>{moment(item.date).fromNow()}</p>
                                                            <span>(Click to enter discussion room)</span>
                                                        </div>
                                                    )
                                                }
                                            })
                                        }
                                    </div>
                                </div>
                            </>
                    }
                </div>

                <Modal show={this.state.showModal} onHide={() => this.setState({ showModal: false })} backdrop='static' keyboard={false}>
                    <Modal.Header closeButton>
                        <Modal.Title>Create Group</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>

                        <Form.Group controlId='formBasicEmail'>
                            <Form.Label>Group Title</Form.Label>
                            <Form.Control type='text' name='title' onChange={this.handleChange} placeholder="Enter title of the group" />
                        </Form.Group>

                        <Form.Group>
                            <Form.Control as="select" name="audience" onChange={this.handleChange}>
                                <option selected hidden>Select Audience</option>
                                <option value="faculty">Faculty</option>
                                <option value="students">Students</option>
                                <option value="parents">Parents</option>
                            </Form.Control>
                        </Form.Group>

                        {
                            this.state.audience === 'students' || this.state.audience === 'parents'
                                ?
                                <Form.Group>
                                    <Form.Control as="select" name="grade" onChange={this.handleChange}>
                                        <option selected hidden>{this.state.grade === '' ? 'Select Grade' : this.state.grade}</option>
                                        {
                                            gradeList.map((item) => {
                                                return <option value={item}>{item}</option>
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                                :
                                null
                        }

                        {
                            this.state.grade !== ''
                                ?
                                <>
                                    {
                                        filteredData.map((item) => {
                                            if (item.class === this.state.grade) {
                                                return <Form.Group>
                                                    <Form.Control as="select" name="section" onChange={this.handleChange}>
                                                        <option selected hidden>{this.state.section === '' ? 'Select Section' : this.state.section}</option>
                                                        {
                                                            item.sections.map((item2) => {
                                                                return <option value={item2}>{item2}</option>
                                                            })
                                                        }
                                                    </Form.Control>
                                                </Form.Group>
                                            }
                                        })
                                    }
                                </>
                                :
                                null
                        }

                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant='primary' onClick={this.handleSubmit}>
                            Create
						</Button>
                    </Modal.Footer>
                </Modal>
            </div>
        );
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addDiscussionGroup: (data) => dispatch(addDiscussionGroup(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(DiscussionGroup);
