import React from 'react';

import './juicerLoading.css';

import Lottie from "lottie-react-web";
import juicer from '../../../assets/juicer.json';

class JuicerLoading extends React.Component {
    render() {
        return(
            <div className = "loading-board">
                <h1>Your data is being juiced up !</h1>
                <Lottie
                    options={{ animationData: juicer }}
                    height = {200}
                    style={{transform: `scale(2)`}}
                />
                <p>Please wait while we create your statistics...</p>
            </div>
        )
    }
}

export default JuicerLoading;