import React from "react";
import { connect } from "react-redux";
import IIGPNav from "./components/nav/nav";
import analytics from "../IIGPHome/assets/analytics-icon.svg";
import curriculum from "../IIGPHome/assets/curriculum-3-elem.svg";
import awards from "../IIGPHome/assets/awards-icon.svg";
import ReactTypingEffect from "react-typing-effect";
import { Redirect } from "react-router-dom";
import "./iigpHome.css";
import Footer from "./components/footer/footer";

class IIGPHome extends React.Component {
	render() {
		if (this.props.auth.currentUser) {
			return <Redirect to='/dashboard' />;
		}
		return (
			<div className='iigp-home-container'>
				<IIGPNav />
				<div className='iigp-ads-banner'>
					<div className='ads-content'>
						<h1>كل عام ونتم بخيرأ</h1>
						<h1>
							<ReactTypingEffect text={["Personalized", "Adaptive", "Immersive"]} speed={100} eraseDelay={1000} typingDelay={1000} />
						</h1>
						<button>Learn more</button>
						<p>
							الآن ، بدلاً من إعطاء واجبات منزلية ، أقوم بتعيين تدريب على IIGP لمدة 15-20 دقيقة في الليلة. يحصل الطلاب على نقاط مقابل
							الوقت الذي يقضونه في التدريب ، وعدد الميداليات التي حصل عليها ، وتحسين الدرجات.
						</p>
					</div>
				</div>
				<div className='membership-cont'>
					<h1>IIGP Membership</h1>
					<p>Explore the benefits of becoming an IIGP member!</p>
					<div className='membership-box'>
						<div className='membership-box1'>
							<img src={analytics} alt='img' />
							<h5>Analytics</h5>
							<p>
								Get unprecedented insight into student performance with IIGP Analytics. View real-time data on usage, trouble spots
								and more!
							</p>
							<a href=''>
								View insights<i class='fas fa-chevron-right'></i>
							</a>
						</div>
						<div className='membership-box1'>
							<img src={curriculum} alt='img' />
							<h5>curriculum</h5>
							<p>
								Get unprecedented insight into student performance with IIGP Analytics. View real-time data on usage, trouble spots
								and more!
							</p>
							<a href=''>
								Browse Skills<i class='fas fa-chevron-right'></i>
							</a>
						</div>
						<div className='membership-box1'>
							<img src={awards} alt='img' />
							<h5>Awards</h5>
							<p>
								Get unprecedented insight into student performance with IIGP Analytics. View real-time data on usage, trouble spots
								and more!
							</p>
							<a href=''>
								Earn Rewards<i class='fas fa-chevron-right'></i>
							</a>
						</div>
					</div>
					<p>Join the millions of students using IIGP worldwide. Make learning fun!</p>
					<button>Join Now</button>
				</div>
				<Footer />
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
	};
}

export default connect(mapStateToProps)(IIGPHome);
