import React from 'react';

import './calendar.css';

import { Modal, Button, Form, Col } from 'react-bootstrap';
import { FaPlus } from 'react-icons/fa';

import { Calendar, momentLocalizer } from 'react-big-calendar';
import moment from 'moment';
import 'react-big-calendar/lib/css/react-big-calendar.css';
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";

import { connect } from "react-redux";
import { addCalendarEvent, deleteCalendarEvent } from '../../../redux/actions/teacherDashboard/teacherActions';

const localizer = momentLocalizer(moment);

class CalendarMy extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            openAdd: false,
            adding: false,
            title: '',
            start: '',
            end: '',
            audience: '',
            grade: ''
        }
    }

    handleChange = (e) => {
        var { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleAddEvents = async () => {

        this.setState({
            adding: true,
        });

        var newEvent = {}, data = {};
        newEvent.title = this.state.title;
        newEvent.audience = this.state.audience;
        newEvent.grade = this.state.grade;
        newEvent.start = this.state.start;
        newEvent.end = this.state.end;
        newEvent.allDay = true;

        data = {
            userId: this.props.teacher._id,
            event: newEvent,
            schoolId: this.props.teacher.school_id
        }

        var check = await this.props.addCalendarEvent(data);
        if (check) {
            if (check.type === "success") {
                toaster.notify("Event created", {
                    duration: 3000,
                });
                this.setState({
                    title: "",
                    start: '',
                    end: '',
                    audience: '',
                    grade: '',
                    adding: false,
                    openAdd: false,
                });
            } else {
                toaster.notify("Event could not be made !", {
                    duration: 3000,
                });
                this.setState({
                    title: "",
                    start: '',
                    end: '',
                    audience: '',
                    grade: '',
                    adding: false,
                    openAdd: false,
                });
            }
        }
    };

    render() {

        var gradeList = [];
        this.props.teacher.subjects.map((sub) => {
            if (gradeList.includes(sub.class) === false) {
                gradeList.push(sub.class);
            }
        })

        var events = this.props.teacher.calendar;

        return (
            <div className="students-container">
                <div className="students-header">
                    <div className="students-stats">
                        <h1>My Calendar</h1>
                    </div>
                    <div className="students-btns">
                        <Button variant="primary" onClick={() => this.setState({ openAdd: true })}><FaPlus />Add Event</Button>
                    </div>
                </div>
                <div className="students-body">
                    <div className="calendar-view">
                        <Calendar
                            localizer={localizer}
                            events={events}
                            startAccessor="start"
                            endAccessor="end"
                        />
                    </div>
                </div>

                {/* Add More Poll Modal */}
                <Modal
                    show={this.state.openAdd}
                    onHide={() => this.setState({
                        openAdd: false,
                    })}
                    size="lg"
                    backdrop="static"
                    keyboard={false}
                    centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Add New Event</Modal.Title>
                    </Modal.Header>

                    <Modal.Body>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Event Title</Form.Label>
                            <Form.Control type="text" placeholder="Enter title" name="title" onChange={this.handleChange} />
                        </Form.Group>

                        <Form.Group>
                            <Form.Control as="select" name="audience" onChange={this.handleChange}>
                                <option selected hidden>Select Audience</option>
                                <option value="faculty">Faculty</option>
                                <option value="students">Students</option>
                                <option value="parents">Parents</option>
                            </Form.Control>
                        </Form.Group>

                        {
                            this.state.audience === 'students' || this.state.audience === 'parents'
                                ?
                                <Form.Group>
                                    <Form.Control as="select" name="grade" onChange={this.handleChange}>
                                        <option selected hidden>{this.state.grade === '' ? 'Select Grade' : this.state.grade}</option>
                                        {
                                            gradeList.map((item) => {
                                                return <option value={item}>{item}</option>
                                            })
                                        }
                                    </Form.Control>
                                </Form.Group>
                                :
                                null
                        }

                        <Form.Row>
                            <Form.Group as={Col} controlId="formGridEmail">
                                <Form.Label>Start Date</Form.Label>
                                <Form.Control type="date" name="start" onChange={this.handleChange} />
                            </Form.Group>

                            <Form.Group as={Col} controlId="formGridPassword">
                                <Form.Label>End Date</Form.Label>
                                <Form.Control type="date" name="end" onChange={this.handleChange} />
                            </Form.Group>
                        </Form.Row>

                        <Form.Group>
                            <Button variant="info" onClick={this.handleAddEvents}>Add</Button>
                        </Form.Group>

                    </Modal.Body>
                </Modal>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        addCalendarEvent: (data) => dispatch(addCalendarEvent(data)),
        deleteCalendarEvent: (data) => dispatch(deleteCalendarEvent(data)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(CalendarMy);
