import React from "react";
import "./studentAnalytics.css";

import { connect } from "react-redux";
import { fetchUser } from "../../../redux/actions/authActions";
import { fetch_student_profile_data } from "../../../redux/actions/studentDashboard/studentActions";

import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import FloralLoading from "../loadingScreen/floralLoading";

import logo from "../assets/iigp.png";
import man from "../assets/man.png";
import downArrow from "../assets/down-arrow.png";

import { Link } from "react-router-dom";

import Chart from "react-apexcharts";
import maths from "./assets/math.svg";
import science from "./assets/science.svg";
import english from "./assets/english.svg";
import islamic from "./assets/islamic.svg";
import computer from "./assets/computer.svg";
import skills from "./assets/skills.svg";
import arts from "./assets/arts.svg";
import sports from "./assets/sports.svg";
import music from "./assets/music-note.svg";
import social from "./assets/social.svg";

import axios from "axios";
import StudentNavbar from "../websiteComponents/Navbar/navbar.component";

class StudentAnalytics extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			loading: true,
			// LAST 10 PRACTICE BAR GRAPH
			categories: [],
			data1: [],
			// LAST SESSION RADIALBAR CHART
			totalRight: 0,
			totalWrong: 0,
			subject: "",
			chapter: "",
			topic: "",
		};
	}

	componentDidMount = async () => {
		const currentUser = await this.props.fetchUser();
		if (currentUser) {
			console.log(currentUser.ref_id);
			const studentData = {
				studentId: currentUser.ref_id,
			};
			const result = await this.props.fetch_student_profile_data(studentData);
			if (result) {
				// API fetch for last 10 reports and latest reports
				const res = await axios.post("/api/analytics/getLastTen", studentData);
				if (res.data !== null) {
					var categories = [],
						data1 = [];
					res.data.map((item) => {
						categories.push(item.subject);
						data1.push(item.score);
					});
					this.setState({
						categories: categories,
						data1: data1,
						totalRight: res.data[0].correct,
						totalWrong: res.data[0].wrong,
						subject: res.data[0].subject,
						chapter: res.data[0].chapter,
						topic: res.data[0].topic,
						loading: false,
					});
				}
			}
		}
	};

	render() {
		var options = {
			chart: {
				id: "basic-bar",
			},
			xaxis: {
				categories: this.state.categories,
			},
		};

		var series = [
			{
				name: "Total Marks",
				data: this.state.data1,
			},
		];

		var series2 = [
			this.state.totalRight + this.state.totalWrong,
			this.state.totalRight,
			this.state.totalWrong,
		];
		var options2 = {
			chart: {
				height: 390,
				type: "radialBar",
			},
			plotOptions: {
				radialBar: {
					offsetY: 0,
					startAngle: 0,
					endAngle: 270,
					hollow: {
						margin: 5,
						size: "30%",
						background: "transparent",
						image: undefined,
					},
					dataLabels: {
						name: {
							show: false,
						},
						value: {
							show: false,
						},
					},
				},
			},
			colors: ["#1ab7ea", "#61ce70", "#E8594A"],
			labels: ["Total Attempts", "Correct", "Wrong"],
			legend: {
				show: true,
				floating: true,
				fontSize: "16px",
				position: "left",
				offsetX: 160,
				offsetY: 15,
				labels: {
					useSeriesColors: true,
				},
				markers: {
					size: 0,
				},
				formatter: function (seriesName, opts) {
					return seriesName + ":  " + opts.w.globals.series[opts.seriesIndex];
				},
				itemMargin: {
					vertical: 3,
				},
			},
			responsive: [
				{
					breakpoint: 480,
					options: {
						legend: {
							show: false,
						},
					},
				},
			],
		};
		return (
			<>
				{this.state.loading ? (
					<FloralLoading />
				) : (
						<div className="student-analytics-container">
							<StudentNavbar student={this.props.student} />
							<div className="learning-breadcrumb">
								<p>My Learning Analysis</p>
							</div>
							<div className="learning-choose-subject-breadcrumb">
								<OwlCarousel className="owl-theme" items={7} dots={false}>
									<Link
										to={"/dashboard/analytics/mathematics"}
										className="subject-option"
									>
										<img src={maths} alt="subject" />
										<p>Mathematics</p>
									</Link>

									<Link
										to={"/dashboard/analytics/science"}
										className="subject-option"
									>
										<img src={science} alt="subject" />
										<p>Science</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={english} alt="subject" />
										<p>English</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={islamic} alt="subject" />
										<p>Islamic Studies</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={social} alt="subject" />
										<p>Social Studies</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={computer} alt="subject" />
										<p>Information Technology</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={skills} alt="subject" />
										<p>Life Skills</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={sports} alt="subject" />
										<p>Sports</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={music} alt="subject" />
										<p>Music</p>
									</Link>

									<Link to={"#"} className="subject-option">
										<img src={arts} alt="subject" />
										<p>Arts</p>
									</Link>
								</OwlCarousel>
							</div>
							<div className="basic-analytics">
								<div className="last-ten-analytics">
									<h1>Last 10 practices by user</h1>
									<Chart
										options={options}
										series={series}
										type="bar"
										width="600"
									/>
								</div>
								<div className="latest-analytics">
									<h1>Last practice session - {this.state.subject}</h1>
									<Chart
										options={options2}
										series={series2}
										type="radialBar"
										width={750}
										height={350}
									/>
									<h5>
										{this.state.chapter} - {this.state.topic}
									</h5>
								</div>
							</div>
						</div>
					)}
			</>
		);
	}
}

function mapStateToProps(state) {
	return {
		auth: state.auth,
		student: state.student.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		fetchUser: () => dispatch(fetchUser()),
		fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentAnalytics);
