import React from 'react';

import './schools.css';

import { Form, Col, Button, Modal, Spinner } from 'react-bootstrap';
import Moment from 'react-moment';
import addImg from '../../assets/more (5).svg';
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaArrowLeft } from "react-icons/fa";

import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

import { connect } from 'react-redux';

import { fetch_school_info, create_principal, delete_principal, fetch_principal_info, edit_principal_info } from '../../../redux/actions/districtOfficerDashboard/districtOfficerActions';

class Schools extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            schoolID: '',
            confirmView: false,
            showAdd: false,
            confirmDelete: false,
            schoolInfo: '',
            pId: '',
            pInfo: '',
            fname: '',
            lname: '',
            email: '',
            address: '',
            contact: '',
            city: '',
            state: '',
            zip: '',
            qualification: '',
            experience: '',
            loading: false,
            e_fname: '',
            e_lname: '',
            e_email: '',
            e_address: '',
            e_contact: '',
            e_city: '',
            e_state: '',
            e_zip: '',
            e_qualification: '',
            e_experience: '',
        }
    }

    handleShowSchoolInfo = async (id) => {
        const schoolData = {
            schoolID: id
        }
        const result = await fetch_school_info(schoolData);
        this.setState({
            confirmView: true,
            schoolInfo: result,
            schoolID: id
        })

        if (this.state.schoolInfo.principal.length > 0) {
            const principalData = {
                principalId: this.state.schoolInfo.principal[0].id
            }
            const answer = await fetch_principal_info(principalData);
            this.setState({
                pInfo: answer,
                e_fname: answer.firstName,
                e_lname: answer.lastName,
                e_email: answer.email,
                e_address: answer.address,
                e_contact: answer.contact,
                e_city: answer.city,
                e_state: answer.state,
                e_zip: answer.zip,
                e_qualification: answer.qualification,
                e_experience: answer.experience,
            })
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleAddPrincipal = async () => {
        this.setState({
            loading: true
        })

        const principalData = {
            fname: this.state.fname,
            lname: this.state.lname,
            address: this.state.address,
            contact: this.state.contact,
            email: this.state.email,
            city: this.state.city,
            state: this.state.state,
            zip: this.state.zip,
            qualification: this.state.qualification,
            experience: this.state.experience,
            schoolID: this.state.schoolID,
        }
        const princi = await this.props.create_principal(principalData);
        if (princi) {
            const schoolData = {
                schoolID: this.state.schoolID
            }
            const result = await fetch_school_info(schoolData);
            this.setState({
                loading: false,
                confirmView: true,
                schoolInfo: result,
                showAdd: false
            })

            const principalData = {
                principalId: this.state.schoolInfo.principal[0].id
            }
            const answer = await fetch_principal_info(principalData);
            this.setState({
                pInfo: answer,
                e_fname: answer.firstName,
                e_lname: answer.lastName,
                e_email: answer.email,
                e_address: answer.address,
                e_contact: answer.contact,
                e_city: answer.city,
                e_state: answer.state,
                e_zip: answer.zip,
                e_qualification: answer.qualification,
                e_experience: answer.experience,
            })
            toaster.notify('Principal added successfully', {
                duration: 3000
            })

        }
    }

    handleDeletePrincipal = async () => {
        this.setState({
            loading: true
        })
        const principalData = {
            principalId: this.state.pInfo._id,
            schoolId: this.state.schoolID
        }

        const result = await delete_principal(principalData);
        if (result.type === "success") {
            const schoolData = {
                schoolID: this.state.schoolID
            }
            const outcome = await fetch_school_info(schoolData);
            this.setState({
                loading: false,
                confirmDelete: false,
                schoolInfo: outcome,
            })
            toaster.notify('Principal deleted successfully', {
                duration: 3000
            })
        }
    }

    handleEdit = async () => {
        this.setState({
            loading: true
        })
        const principalData = {
            fname: this.state.e_fname,
            lname: this.state.e_lname,
            contact: this.state.e_contact,
            address: this.state.e_address,
            city: this.state.e_city,
            state: this.state.e_state,
            zip: this.state.e_zip,
            qualification: this.state.e_qualification,
            experience: this.state.e_experience,
            principalId: this.state.pInfo._id,
            schoolId: this.state.schoolID
        }

        const result = await edit_principal_info(principalData);
        if (result.type === "success") {
            const princiData = {
                principalId: this.state.schoolInfo.principal[0].id
            }
            const answer = await fetch_principal_info(princiData);
            this.setState({
                pInfo: answer,
                e_fname: answer.firstName,
                e_lname: answer.lastName,
                e_email: answer.email,
                e_address: answer.address,
                e_contact: answer.contact,
                e_city: answer.city,
                e_state: answer.state,
                e_zip: answer.zip,
                e_qualification: answer.qualification,
                e_experience: answer.experience,
                loading: false
            })
            toaster.notify('Principal information edited successfully', {
                duration: 3000
            })

        }
    }

    render() {
        const districtOfficer = this.props.districtOfficer;
        const data = districtOfficer.schoolList;
        const columns = [
            {
                Header: "School Name",
                accessor: "name",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center"
                },
            },
            {
                Header: "Actions",
                Cell: (props) => {
                    return (
                        <div
                            style={{
                                width: "100%",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            <Button onClick={() => this.handleShowSchoolInfo(props.original.id)} variant={"outline-dark"} className="action-buttons">View</Button>
                        </div>
                    );
                },
                sortable: false,
                filterable: false,
                width: 150,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",
                },
            },
        ];

        return (
            <div className="schools-container">
                <div className="schools-header">
                    <div className="schools-stats">
                        <h1>IIGP Schools</h1>
                        <div className="officer-stats">
                            <p>TOTAL SCHOOLS UNDER YOU : <span>{data.length}</span></p>
                        </div>
                    </div>
                </div>
                <div className="schools-body">
                    {
                        this.state.confirmView
                            ?
                            <div className="school-info-container">
                                <Button className="back-button" variant="dark" type="submit" onClick={() => this.setState({ confirmView: false })}><FaArrowLeft /></Button>
                                <div className="info-header">
                                    <h1>School Information</h1>
                                    {
                                        this.state.schoolInfo.principal.length > 0
                                            ?
                                            null
                                            :
                                            <Button variant="primary" onClick={() => this.setState({ showAdd: true })}><img src={addImg} /> Add Principal</Button>
                                    }
                                </div>

                                {/* Show school information */}
                                <div>
                                    <Form.Row>
                                        <Form.Group as={Col}>
                                            <Form.Label>School Name</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.name} readOnly />
                                        </Form.Group>

                                        <Form.Group as={Col}>
                                            <Form.Label>School Branch</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.branch} readOnly />
                                        </Form.Group>
                                    </Form.Row>

                                    <Form.Row>
                                        <Form.Group as={Col}>
                                            <Form.Label>School Address</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.address} readOnly />
                                        </Form.Group>

                                        <Form.Group as={Col}>
                                            <Form.Label>School Email</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.email} readOnly />
                                        </Form.Group>
                                    </Form.Row>


                                    <Form.Row>
                                        <Form.Group as={Col}>
                                            <Form.Label>School Contact</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.contact} readOnly />
                                        </Form.Group>

                                        <Form.Group as={Col}>
                                            <Form.Label>School Fax</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.fax} readOnly />
                                        </Form.Group>
                                    </Form.Row>

                                    <Form.Row>
                                        <Form.Group as={Col}>
                                            <Form.Label>City</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.city} readOnly />
                                        </Form.Group>

                                        <Form.Group as={Col}>
                                            <Form.Label>District / Province</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.state} readOnly />
                                        </Form.Group>

                                        <Form.Group as={Col}>
                                            <Form.Label>Zip</Form.Label>
                                            <Form.Control value={this.state.schoolInfo.zip} readOnly />
                                        </Form.Group>
                                    </Form.Row>
                                </div>

                                {/* Display principal information if available */}
                                {
                                    this.state.schoolInfo.principal.length > 0
                                        ?
                                        <div className="principal-section">
                                            <h1>Principal Information</h1>
                                            <Form.Row>
                                                <Form.Group as={Col}>
                                                    <Form.Label>First Name</Form.Label>
                                                    <Form.Control value={this.state.e_fname} name="e_fname" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col}>
                                                    <Form.Label>Last Name</Form.Label>
                                                    <Form.Control value={this.state.e_lname} name="e_lname" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Row>
                                                <Form.Group as={Col}>
                                                    <Form.Label>Address</Form.Label>
                                                    <Form.Control value={this.state.e_address} name="e_address" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col}>
                                                    <Form.Label>Contact</Form.Label>
                                                    <Form.Control value={this.state.e_contact} name="e_contact" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Row>
                                                <Form.Group as={Col}>
                                                    <Form.Label>Email</Form.Label>
                                                    <Form.Control value={this.state.e_email} readOnly />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Row>
                                                <Form.Group as={Col}>
                                                    <Form.Label>City</Form.Label>
                                                    <Form.Control value={this.state.e_city} name="e_city" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col}>
                                                    <Form.Label>District / Province</Form.Label>
                                                    <Form.Control as="select" defaultValue={this.state.e_state} name="e_state" onChange={this.handleChange}>
                                                        <option value={this.state.e_state} selected disabled hidden>{this.state.e_state}</option>
                                                        <option value="Ad Dakhiliyah">Ad Dakhiliyah</option>
                                                        <option value="Ad Dhahirah">Ad Dhahirah</option>
                                                        <option value="Al Batinah North">Al Batinah North</option>
                                                        <option value="Al Batinah South">Al Batinah South</option>
                                                        <option value="Al Buraimi">Al Buraimi</option>
                                                        <option value="Ash Sharqiyah North">Ash Sharqiyah North</option>
                                                        <option value="Ash Sharqiyah South">Ash Sharqiyah South</option>
                                                        <option value="Dhofar">Dhofar</option>
                                                        <option value="Muscat">Muscat</option>
                                                        <option value="Musandam">Musandam</option>
                                                    </Form.Control>
                                                </Form.Group>

                                                <Form.Group as={Col}>
                                                    <Form.Label>Zip</Form.Label>
                                                    <Form.Control value={this.state.e_zip} name="e_zip" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Row>
                                                <Form.Group as={Col}>
                                                    <Form.Label>Qualification</Form.Label>
                                                    <Form.Control value={this.state.e_qualification} name="e_qualification" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col}>
                                                    <Form.Label>Experience (In years)</Form.Label>
                                                    <Form.Control value={this.state.e_experience} name="e_experience" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Button className="mr-2" variant="danger" onClick={() => this.setState({ confirmDelete: true })}>Remove <FaRegTrashAlt /></Button>
                                            <Button variant="outline-success" onClick={this.handleEdit}>
                                                {
                                                    this.state.loading
                                                        ?
                                                        <Spinner animation="border" variant="success" size="sm" />
                                                        :
                                                        "Save changes"
                                                }
                                            </Button>

                                        </div>
                                        :
                                        null
                                }

                                {/* Add Principal modal */}
                                <Modal
                                    size="lg"
                                    aria-labelledby="contained-modal-title-vcenter"
                                    centered
                                    show={this.state.showAdd}
                                    onHide={() => this.setState({ showAdd: false })}
                                    backdrop="static"
                                    keyboard={false}
                                >
                                    <Modal.Header closeButton>
                                        <Modal.Title>Add Principal Informations</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>
                                        <Form>
                                            <Form.Row>
                                                <Form.Group as={Col} controlId="formGridEmail">
                                                    <Form.Label>First Name</Form.Label>
                                                    <Form.Control type="text" placeholder="Enter First Name" name="fname" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col} controlId="formGridPassword">
                                                    <Form.Label>Last Name</Form.Label>
                                                    <Form.Control type="text" placeholder="Enter Last Name" name="lname" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Row>
                                                <Form.Group as={Col} controlId="formGridEmail">
                                                    <Form.Label>Email</Form.Label>
                                                    <Form.Control type="email" placeholder="Enter email" name="email" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col} controlId="formGridPassword">
                                                    <Form.Label>Contact</Form.Label>
                                                    <Form.Control type="text" placeholder="Enter Contact Number" name="contact" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Group controlId="formGridAddress1">
                                                <Form.Label>Address</Form.Label>
                                                <Form.Control placeholder="Enter Full Address" name="address" onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Row>
                                                <Form.Group as={Col} controlId="formGridCity">
                                                    <Form.Label>City</Form.Label>
                                                    <Form.Control placeholder="Enter City" name="city" onChange={this.handleChange}
                                                    />
                                                </Form.Group>

                                                <Form.Group as={Col} controlId="formGridState">
                                                    <Form.Label>District / Province</Form.Label>
                                                    <Form.Control as="select" defaultValue="Choose..." name="state" onChange={this.handleChange}>
                                                        <option value="Choose..." selected disabled hidden>Choose</option>
                                                        <option value="Ad Dakhiliyah">Ad Dakhiliyah</option>
                                                        <option value="Ad Dhahirah">Ad Dhahirah</option>
                                                        <option value="Al Batinah North">Al Batinah North</option>
                                                        <option value="Al Batinah South">Al Batinah South</option>
                                                        <option value="Al Buraimi">Al Buraimi</option>
                                                        <option value="Ash Sharqiyah North">Ash Sharqiyah North</option>
                                                        <option value="Ash Sharqiyah South">Ash Sharqiyah South</option>
                                                        <option value="Dhofar">Dhofar</option>
                                                        <option value="Muscat">Muscat</option>
                                                        <option value="Musandam">Musandam</option>
                                                    </Form.Control>
                                                </Form.Group>

                                                <Form.Group as={Col} controlId="formGridZip">
                                                    <Form.Label>Zip</Form.Label>
                                                    <Form.Control placeholder="Enter Zip Code" name="zip" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>

                                            <Form.Row>
                                                <Form.Group as={Col} controlId="formGridEmail">
                                                    <Form.Label>Qualification</Form.Label>
                                                    <Form.Control type="text" placeholder="Enter Qualification" name="qualification" onChange={this.handleChange} />
                                                </Form.Group>

                                                <Form.Group as={Col} controlId="formGridPassword">
                                                    <Form.Label>Experience (In Years)</Form.Label>
                                                    <Form.Control type="text" placeholder="Enter Years Of Experience" name="experience" onChange={this.handleChange} />
                                                </Form.Group>
                                            </Form.Row>
                                        </Form>
                                    </Modal.Body>
                                    <Modal.Footer>
                                        <Button variant="success" onClick={this.handleAddPrincipal}>
                                            {
                                                this.state.loading
                                                    ?
                                                    <Spinner animation="border" variant="light" size="sm" />
                                                    :
                                                    "Assign Principal"
                                            }
                                        </Button>
                                    </Modal.Footer>
                                </Modal>

                                {/* Ask delete principal confirmation */}

                                <Modal show={this.state.confirmDelete} onHide={() => this.setState({ confirmDelete: false })}>
                                    <Modal.Header closeButton>
                                        <Modal.Title>Delete Principal</Modal.Title>
                                    </Modal.Header>
                                    <Modal.Body>Are you sure you want to delete the principal ?</Modal.Body>
                                    <Modal.Footer>
                                        <Button variant="secondary" onClick={() => this.setState({ confirmDelete: false })}>
                                            Cancel
                                        </Button>
                                        <Button variant="success" onClick={this.handleDeletePrincipal}>
                                            {
                                                this.state.loading
                                                    ?
                                                    <Spinner animation="border" variant="light" size="sm" />
                                                    :
                                                    "Yes, delete"
                                            }
                                        </Button>
                                    </Modal.Footer>
                                </Modal>
                            </div>
                            :
                            <ReactTable
                                data={data}
                                columns={columns}
                                defaultPageSize={10}
                                showPaginationTop
                                showPaginationBottom={false}
                                className="-striped -highlight"
                            />
                    }
                </div>
            </div >
        )
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.auth,
        districtOfficer: state.districtOfficer.profileData,
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        create_principal: principalData => dispatch(create_principal(principalData))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Schools);