import React from 'react';

import './questionBank.css';

import folderImg from '../assets/folderIcon.svg';

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";

import { connect } from 'react-redux';
import QuestionList from './questionList/questionList';
import AllQuestions from './allQuestions/allQuestions';

class QuestionBank extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            currentClass: '',
            currentSection: '',
            currentSubject: '',
            bankDetails: {},
            ShowAllQuestions: false,
            showQuestionList: false,
        }
    }

    handleBox = (e) => {
        this.setState({
            currentClass: e.class,
            currentSection: e.section,
            currentSubject: e.subject,
            showQuestionList: true
        })
    }

    render() {
        var teacher = this.props.teacher;
        return (
            <div className="quiz-container">

                <div className="quiz-header">
                    {
                        !this.state.showQuestionList
                            ?
                            <div>
                                <h1>My Questions Bank</h1>
                            </div>
                            :
                            <>
                                {
                                    !this.state.ShowAllQuestions
                                        ?
                                        <div>
                                            <p onClick={() => this.setState({ showQuestionList: false })}><FaArrowLeft /></p>
                                            <h1>My Questions Bank</h1>
                                            <p><FaAngleRight /></p>
                                            <h1>Questions</h1>
                                        </div>
                                        :
                                        <div>
                                            <p onClick={() => this.setState({ ShowAllQuestions: false })}><FaArrowLeft /></p>
                                            {/* <img src={iigpLogo} alt="iigp-logo" /> */}
                                        </div>
                                }
                            </>


                    }
                </div>

                <div className="quiz-body">
                    {
                        !this.state.showQuestionList
                            ?
                            <div className="chooseClass-container">
                                {
                                    teacher && teacher.subjects.map(sub => {
                                        return (
                                            <div className="classes-cardFolder-container" onClick={() => this.handleBox(sub)}>
                                                <div className="classes-cardFolder-img">
                                                    <img src={folderImg} alt="folder-icon" />
                                                </div>
                                                <div className="classes-cardFolder-name">
                                                    <p>{sub.class} {sub.section} | {sub.subject}</p>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            :
                            <>
                                {
                                    !this.state.ShowAllQuestions
                                        ?
                                        <QuestionList activateAllQuestions={(value, bankDetails) => this.setState({ ShowAllQuestions: value, bankDetails: bankDetails })} class={this.state.currentClass} section={this.state.currentSection} subject={this.state.currentSubject} school={teacher.school_id} />
                                        :
                                        <AllQuestions bankDetails={this.state.bankDetails} classname={this.state.currentClass} subject={this.state.currentSubject} />
                                }
                            </>

                    }
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData
    };
}

export default connect(mapStateToProps)(QuestionBank);