import React, { Component } from "react";
import "./learningProgress.css";

import OwlCarousel from "react-owl-carousel";
import "owl.carousel/dist/assets/owl.carousel.css";
import "owl.carousel/dist/assets/owl.theme.default.css";

import FloralLoading from "../loadingScreen/floralLoading";

import Behavior from "./assets/feedback.svg";
import Abilities from "./assets/behavior.svg";
import homework from './assets/homework.svg';
import Assignment from "./assets/document.svg";
import part from './assets/collaboration.svg';

import { connect } from "react-redux";
import { fetch_student_profile_data } from '../../../redux/actions/studentDashboard/studentActions';
import { fetchUser } from "../../../redux/actions/authActions";

import StudentNavbar from "../websiteComponents/Navbar/navbar.component";

import MyBehaviour from './components/behaviour/behaviour.component';
import MyAbilities from './components/abilities/abilities';
import MyParticipation from "./components/participation/participation.component";
import MyAssignments from "./components/assignments/assignments";

class LearningProgress extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            tab: 'My Behaviour',
            subTab: 'Creativity'
        };
    }

    componentDidMount = async () => {
        const currentUser = await this.props.fetchUser();
        if (currentUser) {

            const studentData = {
                studentId: currentUser.ref_id
            }

            const result = await this.props.fetch_student_profile_data(studentData);

            if (result) {
                this.setState({
                    loading: false
                })
            }
        }
    }

    render() {
        return (
            <>
                {
                    this.state.loading
                        ?
                        (
                            <FloralLoading />
                        )
                        :
                        (
                            <div className="myschool-container">
                                <StudentNavbar student={this.props.student} />
                                <div className="myschool-subject">
                                    <p>My School / Learning Progress</p>
                                </div>
                                <div className="myschool-activity">
                                    <OwlCarousel className="owl-theme" items={8} dots={false}>

                                        <div className="subject" onClick={() => this.setState({ tab: 'My Behaviour' })}>
                                            <img src={Behavior} alt="img" />
                                            <p>Behaviour</p>
                                        </div>
                                        <div className="subject" onClick={() => this.setState({ tab: 'My Skills' })}>
                                            <img src={Abilities} alt="img" />
                                            <p>Skills</p>
                                        </div>
                                        <div className="subject" onClick={() => this.setState({ tab: 'My Assignments' })}>
                                            <img src={Assignment} alt="img" />
                                            <p>Assignments</p>
                                        </div>
                                        <div className="subject" onClick={() => this.setState({ tab: 'My Homework' })}>
                                            <img src={homework} alt="img" />
                                            <p>Homework</p>
                                        </div>
                                        <div className="subject" onClick={() => this.setState({ tab: 'My Participation' })}>
                                            <img src={part} alt="img" />
                                            <p>Participation</p>
                                        </div>

                                    </OwlCarousel>
                                </div>

                                <div className="learning-grade-breadcrumb">
                                    <p>
                                        {this.state.tab}
                                        {
                                            this.state.tab === 'My Skills'
                                                ?
                                                " / " + this.state.subTab
                                                :
                                                null
                                        }
                                    </p>
                                </div>

                                <div className="myschool-tabs">

                                    {/* My Behaviour */}
                                    {
                                        this.state.tab === 'My Behaviour'
                                            ?
                                            <MyBehaviour behaviour={this.props.student.behaviour} />
                                            :
                                            null
                                    }

                                    {/* My Abilities */}
                                    {
                                        this.state.tab === 'My Skills'
                                            ?
                                            <MyAbilities
                                                creativity={this.props.student.creativity}
                                                problem={this.props.student.problemSolving}
                                                entrepreneurship={this.props.student.entrepreneurship}
                                                setSubTab={(value) => this.setState({ subTab: value })}
                                            />
                                            :
                                            null
                                    }

                                    {/* My Participation */}
                                    {
                                        this.state.tab === 'My Participation'
                                            ?
                                            <MyParticipation participation={this.props.student.participation} />
                                            :
                                            null
                                    }

                                    {/* My Assignments */}
                                    {
                                        this.state.tab === 'My Assignments'
                                            ?
                                            <MyAssignments assignments={this.props.student.assignments} />
                                            :
                                            null
                                    }

                                </div>
                            </div>
                        )
                }
            </>
        );
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        student: state.student.profileData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        fetchUser: () => dispatch(fetchUser()),
        fetch_student_profile_data: (studentData) => dispatch(fetch_student_profile_data(studentData)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(LearningProgress);