import React from 'react';
import './districtOfficers.css';

import ReactTable from 'react-table-6';
import "react-table-6/react-table.css";

import { Form, Col, Button, Modal, Spinner } from 'react-bootstrap';

import Moment from 'react-moment';

import addImg from '../../assets/more (5).svg';
import exportImg from '../../assets/export.svg';

import toaster from 'toasted-notes';
import 'toasted-notes/src/styles.css';

import { connect } from 'react-redux';
import { createDistrictOfficer, fetchDistrictOfficers, deleteDistrictOfficer } from '../../../redux/actions/superAdminDashboard/superAdminDistrictActions';

class DistrictOfficers extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            showAdd: false,
            confirmDelete: false,
            officerId: '',
            fName: '',
            lName: '',
            email: '',
            password: '',
            address: '',
            contact: '',
            city: '',
            state: '',
            zip: '',
            experience: '',
            qualification: '',
            loading: false
        }
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        })
    }

    handleAddOfficer = async () => {

        this.setState({
            loading: true
        })
        const officerData = {
            fName: this.state.fName,
            lName: this.state.lName,
            email: this.state.email,
            password: this.state.password,
            address: this.state.address,
            contact: this.state.contact,
            city: this.state.city,
            state: this.state.state,
            zip: this.state.zip,
            qualification: this.state.qualification,
            experience: this.state.experience
        }
        const officer = await this.props.createDistrictOfficer(officerData);
        if (officer) {
            this.setState({
                showAdd: false
            })
            if (officer.type === 'success') {
                this.setState({
                    fName: '',
                    lName: '',
                    email: '',
                    password: '',
                    address: '',
                    contact: '',
                    city: '',
                    state: '',
                    zip: '',
                    experience: '',
                    qualification: '',
                    showAdd: false,
                    loading: false
                })
                toaster.notify('District director added successfully', {
                    duration: 3000
                })
            }
        }

    }

    handleViewOfficer = (id) => {
        console.log(id);
    }

    handleDeleteOfficer = async () => {
        this.setState({
            loading: true
        })
        const officerData = {
            officerId: this.state.officerId
        }

        const check = await this.props.deleteDistrictOfficer(officerData);
        if (check) {
            this.setState({
                confirmDelete: false,
                loading: false
            })
            toaster.notify('Principal deleted successfully', {
                duration: 3000
            })
        }
    }

    render() {
        const districtOfficers = this.props.districtOfficers;
        const data = districtOfficers;
        console.log(data);
        const columns = [
            {
                Header: "First Name",
                accessor: "firstName",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Last Name",
                accessor: "lastName",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Contact",
                accessor: "contact",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Email",
                accessor: "email",
                sortable: false,
                filterable: true,
                style: {
                    display: "flex",
                    alignItems: "center",
                },
            },
            {
                Header: "Registration Date",
                Cell: (props) => {
                    console.log(props);
                    return (
                        <Moment format='MMMM Do YYYY, h:mm:ss a'>
                            {props.original.doa}
                        </Moment>
                    );
                },
                filterable: false,
                sortable: false,
                style: {
                    display: "flex",
                    alignItems: "center",
                },

            },
            {
                Header: "Status",
                Cell: (props) => {
                    return (
                        <>
                            {
                                props.original.status
                                    ?

                                    <div
                                        style={{
                                            padding: "5px 10px",
                                            backgroundColor: "#61CE70",
                                            borderRadius: "20px",
                                        }}
                                    >
                                        <p
                                            style={{
                                                fontSize: "12px",
                                                fontWeight: "500",
                                                textShadow: "1.5px 1.5px rgba(0, 0, 0, 0.1)",
                                            }}
                                        >
                                            Active
                                        </p>
                                    </div>
                                    :
                                    <div
                                        style={{
                                            padding: "5px 10px",
                                            backgroundColor: "#a5a5a5",
                                            borderRadius: "20px",
                                        }}
                                    >
                                        <p
                                            style={{
                                                fontSize: "12px",
                                                fontWeight: "500",
                                                textShadow: "1.5px 1.5px rgba(0, 0, 0, 0.1)",
                                            }}
                                        >
                                            Inactive
                                        </p>
                                    </div>

                            }
                        </>
                    );
                },
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                },
            },
            {
                Header: "Actions",
                Cell: (props) => {
                    return (
                        <div
                            style={{
                                width: "100%",
                                display: "flex",
                                alignItems: "center",
                                justifyContent: "center",
                            }}
                        >
                            <Button onClick={() => this.handleViewOfficer(props.original._id)} variant={"outline-dark"} className="action-buttons">View</Button>
                            <Button onClick={() => this.setState({ officerId: props.original._id, confirmDelete: true })} variant={"outline-danger"} className="action-buttons">Delete</Button>

                        </div>
                    );
                },
                sortable: false,
                filterable: false,
                minWidth: 100,
                style: {
                    color: "#fff",
                    display: "flex",
                    alignItems: "center",
                },
            },
        ];
        return (
            <>
                <div className="district-officer-container">
                    <div className="district-officer-header">
                        <div className="district-officer-stats">
                            <h1>IIGP District Directors</h1>
                            <div className="officer-stats">
                                <p>TOTAL DISTRICT DIRECTORS REGISTERED: <span>{data.length}</span></p>
                            </div>
                        </div>
                        <div className="district-officer-add">
                            <div className="add-officer-button" onClick={() => this.setState({ showAdd: true })}>
                                <img src={addImg} alt="add-btn" />
                                <p>Add New Director</p>
                            </div>
                            <div className="import-data">
                                <img src={exportImg} alt="export-img" />
                                <p>EXPORT DATA</p>
                            </div>
                        </div>
                    </div>
                    <div className="district-officer-body">
                        {
                            this.state.showAdd
                                ?
                                <div className="add-officer-form">
                                    <Form>
                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>First Name</Form.Label>
                                                <Form.Control type="text" placeholder="Enter first name" name="fName" onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Last Name</Form.Label>
                                                <Form.Control type="text" placeholder="Enter last name" name="lName" onChange={this.handleChange} />
                                            </Form.Group>
                                        </Form.Row>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>Email</Form.Label>
                                                <Form.Control type="email" placeholder="Enter email" name="email" onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Password</Form.Label>
                                                <Form.Control type="password" placeholder="Password" name="password" onChange={this.handleChange} />
                                            </Form.Group>
                                        </Form.Row>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>Address</Form.Label>
                                                <Form.Control type="text" placeholder="Enter address" name="address" onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Contact</Form.Label>
                                                <Form.Control type="text" placeholder="Enter contact number" name="contact" onChange={this.handleChange} />
                                            </Form.Group>
                                        </Form.Row>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridCity">
                                                <Form.Label>City</Form.Label>
                                                <Form.Control type="text" placeholder="Enter city" name="city" onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridState">
                                                <Form.Label>District / Province</Form.Label>
                                                <Form.Control as="select" defaultValue="Choose..." name="state" onChange={this.handleChange}>
                                                    <option value="Choose..." selected disabled hidden>Choose</option>
                                                    <option value="Ad Dakhiliyah">Ad Dakhiliyah</option>
                                                    <option value="Ad Dhahirah">Ad Dhahirah</option>
                                                    <option value="Al Batinah North">Al Batinah North</option>
                                                    <option value="Al Batinah South">Al Batinah South</option>
                                                    <option value="Al Buraimi">Al Buraimi</option>
                                                    <option value="Ash Sharqiyah North">Ash Sharqiyah North</option>
                                                    <option value="Ash Sharqiyah South">Ash Sharqiyah South</option>
                                                    <option value="Dhofar">Dhofar</option>
                                                    <option value="Muscat">Muscat</option>
                                                    <option value="Musandam">Musandam</option>
                                                </Form.Control>
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridZip">
                                                <Form.Label>Zip</Form.Label>
                                                <Form.Control type="text" placeholder="Enter zipcode" name="zip" onChange={this.handleChange} />
                                            </Form.Group>
                                        </Form.Row>

                                        <Form.Row>
                                            <Form.Group as={Col} controlId="formGridEmail">
                                                <Form.Label>Qualification</Form.Label>
                                                <Form.Control type="text" placeholder="Enter highest qualification" name="qualification" onChange={this.handleChange} />
                                            </Form.Group>

                                            <Form.Group as={Col} controlId="formGridPassword">
                                                <Form.Label>Experience (In Years)</Form.Label>
                                                <Form.Control type="text" placeholder="Enter experience in years" name="experience" onChange={this.handleChange} />
                                            </Form.Group>
                                        </Form.Row>

                                        <Button variant="secondary" className="mr-2" onClick={() => this.setState({ showAdd: false })}>
                                            Cancel
                                        </Button>
                                        <Button variant="success" onClick={this.handleAddOfficer}>
                                            {
                                                this.state.loading
                                                    ?
                                                    <Spinner animation="border" variant="light" size="sm" />
                                                    :
                                                    "Submit"
                                            }
                                        </Button>
                                    </Form>
                                </div>
                                :
                                <ReactTable
                                    data={data}
                                    columns={columns}
                                    defaultPageSize={10}
                                    showPaginationTop
                                    showPaginationBottom={false}
                                    className="-striped -highlight"
                                />
                        }
                    </div>
                    <Modal show={this.state.confirmDelete} onHide={() => this.setState({ confirmDelete: false })}>
                        <Modal.Header closeButton>
                            <Modal.Title>Delete Officer</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>Are you sure you want to delete the officer ?</Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={() => this.setState({ confirmDelete: false })}>
                                Cancel
                            </Button>
                            <Button variant="success" onClick={this.handleDeleteOfficer}>
                                {
                                    this.state.loading
                                        ?
                                        <Spinner animation="border" variant="light" size="sm" />
                                        :
                                        "Yes, Delete"
                                }
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state) => {
    return {
        districtOfficers: state.superAdmin.officerData
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        fetchDistrictOfficers: () => dispatch(fetchDistrictOfficers()),
        createDistrictOfficer: (officerData) => dispatch(createDistrictOfficer(officerData)),
        deleteDistrictOfficer: (officerData) => dispatch(deleteDistrictOfficer(officerData))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(DistrictOfficers);