import React from "react";

import "./labworksList.css";

import { Modal, Button, Form, Spinner } from "react-bootstrap";
import { AiOutlinePlus } from "react-icons/ai";
import { RiDeleteBinLine, RiEditLine } from "react-icons/ri";

import { connect } from "react-redux";

import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";
import { addLabWork, deleteLabWork, updateLabWork } from "../../../../redux/actions/teacherDashboard/teacherActions";
import penPaper from "../../assets/penPaper.svg";

import moment from "moment";

class LabWorkList extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			showModal: false,
			name: "",
			objectives: "",
			startDate: "",
			endDate: "",
			adding: false,
			saving: false,
			showEdit: false,
			selected: {},
			sLabWorkName: "",
			sLabWorkObjectives: "",
			sStartDate: "",
			sEndDate: "",
		};
	}

	handleChange = (e) => {
		const { name, value } = e.target;
		this.setState({
			[name]: value,
		});
	};

	handleSubmit = async () => {
		this.setState({
			adding: true,
		});

		const data = {
			schoolId: this.props.school,
			teacherId: this.props.teacher._id,
			className: this.props.sub.class,
			sectionName: this.props.sub.section,
			subjectName: this.props.sub.subject,
			title: this.state.assignmentName,
			objectives: this.state.assignmentObjectives,
			startDate: this.state.startDate,
			endDate: this.state.endDate,
		};

		const check = await this.props.addLabWork(data);

		if (check) {
			this.setState({
				adding: false,
			});
			if (check.type === "success") {
				toaster.notify("Labwork Added", {
					duration: 3000,
				});
				this.setState({
					chapterName: "",
					adding: false,
					showModal: false,
				});
			} else {
				toaster.notify("Labwork Could Not Be Added", {
					duration: 3000,
				});
				this.setState({
					chapterName: "",
					adding: false,
					showModal: false,
				});
			}
		}
	};

	handleDeleteLabWork = async (labworkId) => {
		const data = {
			labworkId: labworkId,
			teacherId: this.props.teacher._id,
		};

		const check = await this.props.deleteLabWork(data);

		if (check) {
			if (check.type === "success") {
				toaster.notify("Labwork Deleted", {
					duration: 3000,
				});
			} else {
				toaster.notify("Labwork Could Not Be Deleted", {
					duration: 3000,
				});
			}
		}
	};

	showEdit = (e) => {
		this.setState({
			showEdit: true,
			selected: e,
			sLabWorkName: e.title,
			sLabWorkObjectives: e.objectives,
			sStartDate: e.startDate,
			sEndDate: e.endDate,
		});
	};

	handleSubmitEdit = async () => {
		this.setState({
			saving: true,
		});

		const data = {
			teacherId: this.props.teacher._id,
			labworkId: this.state.selected._id,
			title: this.state.sLabWorkName,
			objectives: this.state.sLabWorkObjectives,
			startDate: this.state.sStartDate,
			endDate: this.state.sEndDate,
		};

		const check = await this.props.updateLabWork(data);

		if (check) {
			this.setState({
				saving: false,
			});
			if (check.type === "success") {
				toaster.notify("Labwork Added", {
					duration: 3000,
				});
				this.setState({
					showEdit: false,
				});
			} else {
				toaster.notify("Labwork Could Not Be Added", {
					duration: 3000,
				});
			}
		}
	};

	render() {
		console.log(this.props.teacher);
		return (
			<div className='topicList-container'>
				<div className='topicList-header'>
					<Button variant='primary' onClick={() => this.setState({ showModal: true })}>
						<AiOutlinePlus />
						Create Labwork
					</Button>
				</div>

				{this.props.teacher.labworks &&
					this.props.teacher.labworks.map((labwork) => {
						if (
							labwork.subjectName === this.props.sub.subject &&
							labwork.className === this.props.sub.class &&
							labwork.sectionName === this.props.sub.section
						) {
							return (
								<div onClick={() => this.props.showStudents(labwork)} className='topicList-box'>
									<img src={penPaper} alt='' />
									<div className='topic-name'>
										<h1>{labwork.title}</h1>
										<p>
											{labwork.subjectName} | {labwork.className} {labwork.sectionName}
										</p>
										<p>Start Date : {moment(labwork.startDate).format("LLL")}</p>
										<p>End Date : {moment(labwork.endDate).format("LLL")}</p>
									</div>
									<div className='topic-date'>
										<p>Date : {moment(new Date(labwork.startDate)).format("LLLL")}</p>
										<span style={{ color: "#616ccc" }} onClick={() => this.showEdit(labwork)}>
											<RiEditLine />
										</span>
										<span style={{ color: "#de3700" }} onClick={() => this.handleDeleteLabWork(labwork._id)}>
											<RiDeleteBinLine />
										</span>
									</div>
								</div>
							);
						}
					})}

				<Modal show={this.state.showModal} onHide={() => this.setState({ showModal: false })} backdrop='static' keyboard={false}>
					<Modal.Header closeButton>
						<Modal.Title>Create Labwork</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Labwork Name</Form.Label>
							<Form.Control type='text' name='name' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Labwork Objectives</Form.Label>
							<Form.Control as='textarea' rows={3} name='objectives' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Start Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='startDate' onChange={this.handleChange} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>End Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='endDate' onChange={this.handleChange} />
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button variant='primary' onClick={this.handleSubmit}>
							{this.state.adding ? <Spinner animation='border' variant='light' /> : "Create"}
						</Button>
					</Modal.Footer>
				</Modal>

				<Modal show={this.state.showEdit} onHide={() => this.setState({ showEdit: false })} backdrop='static' keyboard={false}>
					<Modal.Header closeButton>
						<Modal.Title>Edit Labwork</Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Name</Form.Label>
							<Form.Control type='text' name='sLabWorkName' onChange={this.handleChange} value={this.state.sLabWorkName} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Objectives</Form.Label>
							<Form.Control
								as='textarea'
								rows={3}
								name='sLabWorkObjectives'
								onChange={this.handleChange}
								value={this.state.sLabWorkObjectives}
							/>
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>Start Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='sStartDate' onChange={this.handleChange} value={this.state.sStartDate} />
						</Form.Group>
						<Form.Group controlId='formBasicEmail'>
							<Form.Label>End Date and Time</Form.Label>
							<Form.Control type='datetime-local' name='sEndDate' onChange={this.handleChange} value={this.state.sEndDate} />
						</Form.Group>
					</Modal.Body>
					<Modal.Footer>
						<Button variant='primary' onClick={this.handleSubmitEdit}>
							{this.state.saving ? <Spinner animation='border' variant='light' /> : "Update"}
						</Button>
					</Modal.Footer>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addLabWork: (data) => dispatch(addLabWork(data)),
		deleteLabWork: (data) => dispatch(deleteLabWork(data)),
		updateLabWork: (data) => dispatch(updateLabWork(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(LabWorkList);
