import React from 'react';

import './replyBox.css';

import axios from 'axios';

import moment from 'moment';

import { connect } from "react-redux";

class ReplyBox extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            ownerInfo: {},
        }
    }

    componentDidMount = async () => {

        if (this.props.reply.userId === this.props.student._id) {
            this.setState({
                ownerInfo: this.props.student,
                loading: false
            })
        }

        else {
            if (this.props.reply.userRole === 'student') {

                const data = {
                    id: this.props.reply.userId
                }

                var result = await axios.post("/api/student/profile", data);

                if (result) {
                    // console.log(result);
                    this.setState({
                        ownerInfo: result.data,
                        loading: false
                    })
                }
            }

            else {

                const data = {
                    id: this.props.reply.userId,
                    role: this.props.reply.userRole
                }

                var result2 = await axios.post("/api/myschool/getOtherUser", data);
                if (result2) {
                    console.log(result2);
                    this.setState({
                        ownerInfo: result2.data,
                        loading: false
                    })
                }
            }
        }
    }

    render() {
        return (

            <div className="reply-box-container">
                <div className="reply-owner-info">
                    {
                        this.state.loading
                            ?
                            null
                            :
                            <>
                                <img src={"/api/file/show/" + this.state.ownerInfo.photo.filename} alt="user-dp" />
                                <div>
                                    <p>{this.state.ownerInfo.name}</p>
                                    <p>{moment(this.props.reply.date).fromNow()}</p>
                                </div>
                            </>
                    }
                </div>

                <p>{this.props.reply.reply}</p>

            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        auth: state.auth,
        student: state.student.profileData,
    };
}
export default connect(mapStateToProps, null)(ReplyBox);