import axios from 'axios';

import { FETCH_PARENT_PROFILE } from './parentTypes';

export const fetch_parent_profile_data = (parentData) => async (dispatch) => {
    const res = await axios.post("/api/parent/profile" , parentData);
    dispatch({ type : FETCH_PARENT_PROFILE, payload : res.data});
    return res.data;
}


