import React from "react";

import "./announcement.css";
import { Modal, Button, Form, Spinner, Col } from "react-bootstrap";
import { FaCheck, FaRegTrashAlt, FaInfoCircle, FaPlus, FaCross } from "react-icons/fa";
import { IoIosPeople } from "react-icons/io";
import ReactTable from "react-table-6";

import moment from "moment";
import toaster from "toasted-notes";
import "toasted-notes/src/styles.css";

import { connect } from "react-redux";
import { addAnnouncement, deleteAnnouncement } from "../../../redux/actions/teacherDashboard/teacherActions.js";

class Announcement extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			openAddAnnouncement: false,
			title: "",
			adding: false,
			grade: "",
			type: "",
			studentId: "",
		};
	}

	handleChange = (e) => {
		var { name, value } = e.target;
		this.setState({
			[name]: value,
		});
	};

	handleSubmit = async () => {
		this.setState({
			adding: true,
		});

		var newTask = {}, data = {};
		newTask.title = this.state.title;
		newTask.grade = this.state.grade;
		newTask.type = this.state.type;
		newTask.studentId = this.state.studentId;

		data = {
			teacherId: this.props.teacher._id,
			news: newTask,
		};

		const check = await this.props.addAnnouncement(data);
		if (check) {
			if (check.type === "success") {
				toaster.notify("Announcement created", {
					duration: 3000,
				});
				this.setState({
					title: "",
					adding: false,
					openAddAnnouncement: false,
				});
			} else {
				toaster.notify("Announcement could not be made !", {
					duration: 3000,
				});
				this.setState({
					title: "",
					adding: false,
					openAddAnnouncement: false,
				});
			}
		}
	};

	handleDelete = async (e) => {
		var data = {
			teacherId: this.props.teacher._id,
			id: e,
		};

		const check = await this.props.deleteAnnouncement(data);
		if (check) {
			if (check.type === "success") {
				toaster.notify("Announcement Deleted", {
					duration: 3000,
				});
			} else {
				toaster.notify("Announcement Could Not Be Deleted", {
					duration: 3000,
				});
			}
		}
	};

	render() {
		console.log(this.props.teacher);
		var gradeList = [];
		this.props.teacher.subjects.map((sub) => {
			if (gradeList.includes(sub.class) === false) {
				gradeList.push(sub.class);
			}
		});

		const data = this.props.teacher.announcements;
		const columns = [
			{
				Header: "Title",
				accessor: "title",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Grade",
				accessor: "grade",
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Audience",
				accessor: "type",
				Cell: (props) => {
					if (props.original.type === "whole") {
						return (
							<div
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}>
								<p
									style={{
										width: "150px",
										backgroundColor: "#17A2B8",
										color: "white",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
										borderRadius: "6px",
									}}>
									Whole Class
								</p>
							</div>
						);
					} else {
						return (
							<div
								style={{
									width: "100%",
									display: "flex",
									alignItems: "center",
									justifyContent: "center",
								}}>
								<p
									style={{
										width: "150px",
										backgroundColor: "#FFC107",
										color: "white",
										display: "flex",
										alignItems: "center",
										justifyContent: "center",
										borderRadius: "6px",
									}}>
									Individual
								</p>
							</div>
						);
					}
				},
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Created At",
				Cell: (props) => {
					return (
						<div
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}>
							{moment(props.original.doa).format("LLLL")}
						</div>
					);
				},
				sortable: false,
				filterable: true,
				style: {
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
				},
			},
			{
				Header: "Actions",
				Cell: (props) => {
					return (
						<div
							style={{
								width: "100%",
								display: "flex",
								alignItems: "center",
								justifyContent: "center",
							}}>
							<Button onClick={() => this.handleDelete(props.original._id)} variant={"outline-danger"} className='action-buttons'>
								Delete
							</Button>
						</div>
					);
				},
				sortable: false,
				filterable: false,
				minWidth: 150,
				style: {
					color: "#fff",
					display: "flex",
					alignItems: "center",
				},
			},
		];

		return (
			<div className='students-container'>
				<div className='students-header'>
					<div className='students-stats'>
						<h1>My Announcements</h1>
					</div>
					<div className='students-btns'>
						<Button variant='primary' onClick={() => this.setState({ openAddAnnouncement: true })}>
							<FaPlus /> Add Announcement
						</Button>
					</div>
				</div>
				<div className='students-body'>
					<ReactTable
						data={data}
						columns={columns}
						defaultPageSize={10}
						showPaginationTop
						showPaginationBottom={false}
						className='-striped -highlight'
					/>
				</div>
				<Modal
					show={this.state.openAddAnnouncement}
					onHide={() =>
						this.setState({
							openAddAnnouncement: false,
						})
					}
					size='lg'
					backdrop='static'
					keyboard={false}
					centered>
					<Modal.Header closeButton>
						<Modal.Title>Add New Announcement </Modal.Title>
					</Modal.Header>

					<Modal.Body>
						<Form.Group className='container-fluid'>
							<Form.Label>What is the announcement ?</Form.Label>
							<Form.Control
								name='title'
								as='textarea'
								rows={3}
								placeholder='Type your announcement...'
								value={this.state.title}
								onChange={this.handleChange}
							/>
						</Form.Group>

						<Form.Group className='container-fluid'>
							<Form.Control as='select' name='grade' onChange={this.handleChange}>
								<option selected hidden>
									Select Grade
								</option>
								{gradeList.map((item) => {
									return <option value={item}>{item}</option>;
								})}
							</Form.Control>
						</Form.Group>

						{this.state.grade !== "" ? (
							<Form.Group className='container-fluid'>
								<input type='radio' name='type' value='whole' onChange={this.handleChange} /> Whole Class
								<input type='radio' name='type' value='individual' onChange={this.handleChange} /> Individual
							</Form.Group>
						) : null}

						<Form.Group className='container-fluid'>
							{this.state.type === "individual" ? (
								<Form.Control as='select' name='studentId' onChange={this.handleChange}>
									<option selected hidden>
										Select Student
									</option>
									{this.props.teacher.students.map((item) => {
										if (item.class === this.state.grade) {
											return <option value={item._id}>{item.name}</option>;
										}
									})}
								</Form.Control>
							) : null}
						</Form.Group>

						<Form.Group className='container-fluid'>
							<Button variant='success' onClick={this.handleSubmit}>
								Submit
							</Button>
						</Form.Group>
					</Modal.Body>
				</Modal>
			</div>
		);
	}
}

function mapStateToProps(state) {
	return {
		teacher: state.teacher.profileData,
	};
}

function mapDispatchToProps(dispatch) {
	return {
		addAnnouncement: (data) => dispatch(addAnnouncement(data)),
		deleteAnnouncement: (data) => dispatch(deleteAnnouncement(data)),
	};
}

export default connect(mapStateToProps, mapDispatchToProps)(Announcement);
