import React from 'react';

import './exam.css';

import folderImg from '../assets/folderIcon.svg';

import { FaArrowLeft, FaAngleRight } from "react-icons/fa";

import iigpLogo from '../assets/iigpqm.png';
// import QuizMaker from './quizMaker/quizMaker';

import { connect } from 'react-redux';
import ExamList from './examList/examList';
import ExamMaker from './examMaker/examMaker';

class Exam extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            currentClass: '',
            currentSection: '',
            currentSubject: '',
            examDetails: {},
            ShowExamMaker: false,
            showExamList: false,
        }
    }

    handleBox = (e) => {
        this.setState({
            currentClass: e.class,
            currentSection: e.section,
            currentSubject: e.subject,
            showExamList: true
        })
    }

    render() {
        var teacher = this.props.teacher;
        return (
            <div className="quiz-container">

                <div className="quiz-header">
                    {
                        !this.state.showExamList
                            ?
                            <div>
                                <h1>My Exam Bank</h1>
                            </div>
                            :
                            <>
                                {
                                    !this.state.ShowExamMaker
                                        ?
                                        <div>
                                            <p onClick={() => this.setState({ showExamList: false })}><FaArrowLeft /></p>
                                            <h1>My Exam Bank</h1>
                                            <p><FaAngleRight /></p>
                                            <h1>Exams</h1>
                                        </div>
                                        :
                                        <div>
                                            <p onClick={() => this.setState({ ShowExamMaker: false })}><FaArrowLeft /></p>
                                            <img src={iigpLogo} alt="iigp-logo" />
                                        </div>
                                }
                            </>
                    }
                </div>

                <div className="quiz-body">
                    {
                        !this.state.showExamList
                            ?
                            <div className="chooseClass-container">
                                {
                                    teacher && teacher.subjects.map(sub => {
                                        return (
                                            <div className="classes-cardFolder-container" onClick={() => this.handleBox(sub)}>
                                                <div className="classes-cardFolder-img">
                                                    <img src={folderImg} alt="folder-icon" />
                                                </div>
                                                <div className="classes-cardFolder-name">
                                                    <p>{sub.class} {sub.section} | {sub.subject}</p>
                                                </div>
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            :
                            <>
                                {
                                    !this.state.ShowExamMaker
                                        ?
                                        <ExamList activateExamMaker={(value, examDetails) => this.setState({ ShowExamMaker: value, examDetails: examDetails })} class={this.state.currentClass} section={this.state.currentSection} subject={this.state.currentSubject} school={teacher.school_id} />
                                        :
                                        <ExamMaker examDetails={this.state.examDetails} classname={this.state.currentClass} subject={this.state.currentSubject} />
                                }
                            </>
                    }
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    return {
        teacher: state.teacher.profileData
    };
}

export default connect(mapStateToProps)(Exam);